import {
	fetchOrganizationYearList,
	fetchRePlanManagement,
	editRePlanManagement,
} from "@/api/index.js";

const rePlanManagementStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		startYear: "",
		endYear: "",
		successMessage: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
			state.startYear = data.items[data.items.length - 1];
			state.endYear = data.items[0];
		},
		SET_RE_PLAN_MANAGEMENT_LIST: (state, data) => {
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.startYear = "";
			state.endYear = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_RE_PLAN_MANAGEMENT_LIST: ({ commit }, info) => {
			// 중장기 RE 계획 관리 조회
			return fetchRePlanManagement(info)
				.then(({ data }) => {
					commit("SET_RE_PLAN_MANAGEMENT_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_RE_PLAN_MANAGEMENT: (info) => {
			// 중장기 RE 계획 관리 일괄 수정
			return editRePlanManagement(info.state.financialPerformanceList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default rePlanManagementStore;
