import {
	fetchBauFacilityYear,
	editBauFacilityYear,
	fetchBauFacilityMonth,
} from "@/api/index.js";

const slsexFcltsStore = {
	namespaced: true,
	state: {
		facilityYearList: [],
		facilityYearCount: 0,
		facilityMonthList: [],
		facilityMonthCount: 0,
	},
	mutations: {
		SET_BAU_FACILITY_YEAR: (state, data) => {
			state.facilityYearList = data.items;
			state.facilityYearCount = data.count;
		},
		SET_BAU_FACILITY_MONTH: (state, data) => {
			state.facilityMonthList = data.items;
			state.facilityMonthCount = data.count;
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_BAU_FACILITY_YEAR: ({ commit }, info) => {
			// 다년도 기존시설 예상배출량 조회
			return fetchBauFacilityYear(info)
				.then(({ data }) => {
					commit("SET_BAU_FACILITY_YEAR", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_BAU_FACILITY_YEAR: ({ commit }, info) => {
			// 다년도 기존시설 예상배출량 추가 및 수정
			return editBauFacilityYear(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_BAU_FACILITY_MONTH: ({ commit }, info) => {
			// 단년도 기존시설 예상배출량 조회
			return fetchBauFacilityMonth(info)
				.then(({ data }) => {
					commit("SET_BAU_FACILITY_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
	},
};

export default slsexFcltsStore;
