<template>
	<form-element>
		<!-- kendo form box-->
		<fieldset class="k-form-fieldset">
			<div class="grid-code form">
				<div class="forminfo d-flex justify-content-between">
					<!-- kendo form title-->
					<span>사업장 정보</span>
					<!-- kendo form button box-->
					<div class="formButtonBox k-form-buttons ml-auto2">
						<basic-button
							v-if="org_parent_id == ''"
							class="inquiry"
							@click="addData"
							:type="'button'"
							title="생산팀 추가"
							:backgroundColor="'#FFFFFF'"
							:fontColor="'#F37321'"
							:borderColor="'#F37321'"
							:wsize="'120px'"
							>생산팀 추가</basic-button
						>
						<basic-button
							v-if="org_parent_id == ''"
							class="resets"
							@click="deleteData"
							:type="'button'"
							title="삭제"
							:backgroundColor="'#F9F9F9'"
							:fontColor="'#5A5B68'"
							:borderColor="'#5A5B68'"
							:wsize="'90px'"
							>삭제</basic-button
						>
						<basic-button
							class="resets"
							@click="clear"
							:type="'button'"
							title="취소"
							:backgroundColor="'#F9F9F9'"
							:fontColor="'#5A5B68'"
							:borderColor="'#5A5B68'"
							:wsize="'90px'"
							>취소</basic-button
						>
						<kbutton
							class="submitbutton"
							type="submit"
							:disabled="!kendoForm.allowSubmit"
							:class="{ activesd: !kendoForm.allowSubmit }"
							>저장</kbutton
						>
					</div>
				</div>
				<!-- kendo form field 명칭 setting-->
				<div class="inputsBox">
					<div>
						<field
							:id="'corporate_name'"
							:name="'corporate_name'"
							:component="'myTemplate'"
							:validator="nameValidator"
							:style="{ marginTop: 0 }"
						>
							<template v-slot:myTemplate="{ props }">
								<input-cell
									v-bind="props"
									@change="props.onChange"
									@blur="props.onBlur"
									@focus="props.onFocus"
									:title="'명칭'"
								/>
							</template>
						</field>
					</div>
					<div class="d-flex">
						<div class="content con1">
							<!-- kendo form field 사업자 등록번호 setting-->
							<field
								:id="'company_registration_no'"
								:name="'company_registration_no'"
								:component="'myTemplate'"
								:validator="companyRegNoValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'사업자 등록번호'"
									/>
								</template>
							</field>
							<!-- kendo form field 업종 setting-->
							<field
								:id="'industry'"
								:name="'industry'"
								:component="'myTemplate'"
								:validator="industryValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'업종(대표업종)'"
									/>
								</template>
							</field>
							<!-- kendo form field 소재지 setting-->
							<field
								:id="'address'"
								:name="'address'"
								:component="'myTemplate'"
								:validator="addressValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'소재지'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당부서 setting-->
							<field
								:id="'department'"
								:name="'department'"
								:component="'myTemplate'"
								:validator="onerValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당부서'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 직급 setting-->
							<field
								:id="'position'"
								:name="'position'"
								:component="'myTemplate'"
								:validator="positionValueValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 직급'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 휴대폰 setting-->
							<field
								:id="'manager_mobile'"
								:name="'manager_mobile'"
								:component="'myTemplate'"
								:validator="mobileValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 휴대폰'"
										:mask="'000-0000-0000'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 주요 생산제품 또는 처리물질 setting-->
							<field
								:id="'product'"
								:name="'product'"
								:component="'myTemplate'"
								:validator="productValueValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'주요 생산제품 또는 처리물질'"
									/>
								</template>
							</field>
							<!-- kendo form field 종업원수 setting-->
							<field
								:id="'employees'"
								:name="'employees'"
								:component="'myTemplate'"
								:validator="employeesValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'상시 종업원수'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 당해년도 에너지 비용 setting-->
							<field
								:id="'energy_cost'"
								:name="'energy_cost'"
								:component="'myTemplate'"
								:validator="energyCostValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'당해년도 에너지 비용(백만원)'"
										class="telbox"
									/>
								</template>
							</field>
						</div>

						<div class="content con2">
							<!-- kendo form field 사업장 일련번호 setting-->
							<field
								:id="'serial_no'"
								:name="'serial_no'"
								:component="'myTemplate'"
								:validator="serialNoValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'사업장 일련번호'"
									/>
								</template>
							</field>
							<!-- kendo form field 대표자 setting-->
							<field
								:id="'representation'"
								:name="'representation'"
								:component="'myTemplate'"
								:validator="representationValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'대표자'"
									/>
								</template>
							</field>

							<!-- kendo form field 전화번호 setting-->
							<field
								:id="'phone'"
								:name="'phone'"
								:component="'myTemplate'"
								:validator="phoneValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'전화번호'"
										:mask="'000-000-0000'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 setting-->
							<field
								:id="'manager'"
								:name="'manager'"
								:component="'myTemplate'"
								:validator="managerValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 전화번호 setting-->
							<field
								:id="'manager_phone'"
								:name="'manager_phone'"
								:component="'myTemplate'"
								:validator="phoneValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 전화번호'"
										:mask="'000-000-0000'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 이메일 setting-->
							<field
								:id="'manager_email'"
								:name="'manager_email'"
								:type="'email'"
								:component="'myTemplate'"
								:validator="emailValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 이메일'"
									/>
								</template>
							</field>
							<!-- kendo form field 처리량 setting-->
							<field
								:id="'throughput'"
								:name="'throughput'"
								:component="'myTemplate'"
								:validator="throughputValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'연간 생산량 또는 처리량(MT)'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 당해년도 매출액(백만원) setting'-->
							<field
								:id="'sales_amount'"
								:name="'sales_amount'"
								:component="'myTemplate'"
								:validator="totalPayValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'당해년도 매출액(백만원)'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 자본금 setting'-->
							<field
								:id="'capital'"
								:name="'capital'"
								:component="'myTemplate'"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'자본금(백만원)'"
										class="telbox"
										:must="'N'"
									/>
								</template>
							</field>
						</div>
					</div>
					<!-- kendo form 필수항목 info'-->
					<div class="lastText">
						<p><span style="color: #f37321">*</span> 는 필수 항목입니다.</p>
					</div>
				</div>
			</div>
		</fieldset>
	</form-element>
</template>

<script>
import { Field, FormElement } from "@progress/kendo-vue-form";
import { Button } from "@progress/kendo-vue-buttons";
import BasicButton from "../../components/common/button/BasicButton.vue";
import InputCell from "../../components/form/InputCell.vue";
import NumericTextBoxCell from "../../components/form/NumericTextBoxCell.vue";

// 유효성 기능을 명시
import {
	nameValidator,
	companyRegNoValidator,
	industryValidator,
	addressValidator,
	onerValidator,
	positionValueValidator,
	mobileValidator,
	productValueValidator,
	employeesValidator,
	energyCostValidator,
	representationValidator,
	phoneValidator,
	managerValidator,
	emailValidator,
	throughputValidator,
	totalPayValidator,
	serialNoValidator,
} from "../../validator/Validator";

export default {
	name: "Workplace",
	components: {
		"form-element": FormElement,
		kbutton: Button,
		field: Field,
		BasicButton,
		InputCell,
		NumericTextBoxCell,
	},
	data() {
		return {
			nameValidator,
			companyRegNoValidator,
			industryValidator,
			addressValidator,
			onerValidator,
			positionValueValidator,
			mobileValidator,
			productValueValidator,
			employeesValidator,
			energyCostValidator,
			representationValidator,
			phoneValidator,
			managerValidator,
			emailValidator,
			throughputValidator,
			totalPayValidator,
			serialNoValidator,
		};
	},
	inject: {
		kendoForm: {
			default: {},
		},
	},
	computed: {
		org_parent_id() {
			return this.$store.state.organizationStore.org_parent_id;
		},
		org_id() {
			return this.$store.state.organizationStore.org_id;
		},
		depth() {
			return this.$store.state.organizationStore.depth;
		},
	},
	methods: {
		/* 취소 */
		clear() {
			// 폼초기화 함수
			this.kendoForm.onFormReset();
		},
		/* 생산팀 추가 */
		addData() {
			console.log("[addData]");
			this.$store.state.organizationStore.depth = this.depth + 1;
			this.$store.state.organizationStore.org_parent_id = this.org_id;
			this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_INIT");
		},
		deleteData(dataItem) {
			this.$emit("deleteData", dataItem);
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.grid-code {
	&.form {
		padding: 20px;

		.k-input-inner {
			&::placeholder {
				color: #f37321 !important;
			}
		}

		.forminfo {
			height: 40px;

			span {
				font-family: AppleSDGothicNeoB00;
				font-size: 16px;
				color: #222222;
				display: flex;
				align-items: end;
			}

			.formButtonBox {
				button {
					margin-right: 0px;

					&:last-child {
						margin-right: 0px;
					}
				}
			}
		}

		.k-form-field {
			margin-top: 20px !important;

			.k-form-field-wrap {
				position: relative;

				.k-input {
					box-shadow: none !important;
				}

				.k-form-error {
					position: absolute;
					top: 0;
				}
			}
		}
	}

	.lastText {
		margin-top: 20px;
		margin-left: auto;
		font-size: 14px;
		font-family: AppleSDGothicNeoSB00;
		width: max-content;
	}

	.content {
		flex: 1;
	}
}
</style>
