<template>
	<!-- 사업부 성과 -->
	<Grid
		v-if="type == 'anotherType'"
		ref="grid"
		:style="{ height: '100%' }"
		:data-items="gridData"
		:edit-field="'inEdit'"
		:sortable="true"
		:resizable="true"
		:sort="sort"
		:pageable="pageable"
		:take="take"
		:skip="skip"
		:page-size="limit"
		:total="total"
		:expand-field="'expanded'"
		@itemchange="itemChange"
		@datastatechange="dataStateChange"
		@sortchange="sortChangeHandler"
		@pagechange="pageChangeHandler"
		:cell-render="myCellTemplates1"
		:columns="columns"
	>
		<template v-slot:myTemplate="{ props }">
			<custom
				:data-item="props.dataItem"
				@edit="edit"
				@save="save"
				@remove="remove"
				@cancel="cancel"
			/>
		</template>

		<template v-slot:cellmodals="{ props }">
			<cellmodal :data-item="props.dataItem" @clickEvent="clickEvent" />
		</template>
		<template v-slot:FileFormCell="{ props }">
			<FileFormCell :data-item="props.dataItem" @edit="edit" @save="save" />
		</template>

		<template v-slot:dropdownCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="dropdownData"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.group_id)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCodeChange(e, props.dataItem)"
			/>
		</template>

		<template v-slot:dropdownEmissionCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="dropdownEmission"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.emission_code_no)"
				:field="props.field"
				@change="(e) => ddEmissionChange(e, props.dataItem)"
			/>
		</template>
		<template v-slot:dropdownActivityCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="dropdownActivity"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.activity_code_no)"
				:field="props.field"
				@change="(e) => ddActivityChange(e, props.dataItem)"
			/>
		</template>
		<template v-slot:dropdownParameterGroupCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="dropdownParameterGroup"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.group_id)"
				:field="props.field"
				@change="(e) => ddParameterGroupChange(e, props.dataItem)"
			/>
		</template>
		<template
			v-slot:dropdownParameterCalculationCell="{ props }"
