				:data-item-id="String(props.dataItem.measure_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddMeasureChange(e, props.dataItem)"
			/>
		</template>
		<template v-slot:dropdownVersionCell="{ props }" class="gridSelect">
			<dropdown-list-cell
				:data="props.dataItem.history_version"
				:data-item="props.dataItem"
				:field="props.field"
