					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>{{ addButtonText }}</basic-button
				>
			</div>
			<div v-if="showlimit" class="k-command-cell">
				<dropdownlist
