					:value="limit"
					@change="limitChange"
				/>
			</div>
			<ZoomButton v-if="zoomMode" @edit="rowClick" @remove="closeEdit" />
		</grid-toolbar>
		<grid-norecords>데이터가 없습니다.</grid-norecords>
	</Grid>
