					class="paraGroup"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					:wsize="'140px'"
					><img :src="uploadicon" alt="" /> {{ popupButtonText }}</basic-button
				>
				<basic-button
					v-if="addButtonText2"
					@click="toggleDialog"
					class="paraGroup"
					:backgroundColor="'#FFFFFF'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					:wsize="'140px'"
					><img :src="downloadicon" alt="" /> {{ addButtonText2 }}</basic-button
				>
				<basic-button
					v-if="addButtonText4"
					@click="toggleDialog"
					class="paraGroup"
					:backgroundColor="'#49A872'"
					:fontColor="'#FFFFFF'"
					:borderColor="''"
					:wsize="'100px'"
					>{{ addButtonText4 }}</basic-button
				>

				<basic-button
					v-if="addButtonText5"
					@click="insert"
					:wsize="'100px'"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>{{ addButtonText5 }}</basic-button
				>
			</div>
			<div v-else>
				<!-- <basic-button
					v-if="popupButtonText"
					@click="toggleDialog"
					class="paraGroup"
					:backgroundColor="'#49A872'"
					:fontColor="'#FFFFFF'"
					:borderColor="''"
					:wsize="'160px'"
					>{{ popupButtonText }}</basic-button
				> -->
				<basic-button
					v-if="addButtonText"
					@click="insert"
					:wsize="'130px'"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>{{ addButtonText }}</basic-button
				>
			</div>

			<div v-if="showlimit" class="k-command-cell">
				<dropdownlist
					:data-items="limitData"
					:value="limit"
					@change="limitChange"
				/>
			</div>
			<!-- 추가 버튼 -->
			<basic-button
				v-if="insertButtonText"
				@click="insert"
				:wsize="'120px'"
				class="paraAdd"
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				>{{ insertButtonText }}</basic-button
			>
			<ZoomButton
				v-if="zoomMode"
				@Expansion="ExpansionGrid"
				@reduction="reductions"
			/>
		</grid-toolbar>
		<grid-norecords>데이터가 없습니다.</grid-norecords>
	</Grid>
	<modal
		v-if="visibleDialog"
		:message="'해당 데이터를 삭제하시겠습니까?'"
		@close="deleteDialog"
	>
		<template v-slot:actionBar>
			<dialog-actions-bar>
				<kbutton @click="deleteDialog">취소</kbutton>
				<kbutton @click="deleteData">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>
<script>
import ZoomButton from "../../components/common/button/ZoomButton";
import { Grid, GridToolbar, GridNoRecords } from "@progress/kendo-vue-grid";
import { process } from "@progress/kendo-data-query";
import BasicButton from "../common/button/BasicButton.vue";
import CheckboxCell from "./cell/CheckboxCell.vue";
import CheckboxEditCell from "./cell/CheckboxEditCell.vue";
import CommandCell from "./cell/CommandCell.vue";
import DropdownCell from "./cell/DropDownCell.vue";
import DropdownListCell from "./cell/DropDownListCell.vue";
import FileFormCell from "./cell/FileFormCell.vue";
import Modal from "../modal/Modal.vue";
import downloadicon from "../../assets/img/icon/downloadicon.png";
import uploadicon from "../../assets/img/icon/uploadicon.png";
import DatepickerCell from "./cell/DatePickerCell.vue";
import cellmodal from "./cell/cellClickModal.vue";

import { DropDownList } from "@progress/kendo-vue-dropdowns";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

export default {
	name: "TheTable",
	emits: [
		"toggleDialog",
		"pageChange",
		"limitChange",
		"isEditableTrueChange",
		"isEditableFalseChange",
		"clickEvents",
		"activityChange",
		"changeYear",
	],
	props: {
		title: String,
		rowspanNum: Number,
		rowspanNum2: Number,
		custumRowsSpanNum: Number,
		popupButtonText: String,
		addButtonText4: String,
		addButtonText5: String,
		addButtonText: String,
		addButtonText2: String,
		addButtonText3: String,
		dataItems: Object,
		// group: Array,
		dropdownData: Object,
		dropdownEmission: Array,
		dropdownActivity: Array,
		dropdownParameterGroup: Array,
		dropdownParameterCalculation: Array,
		dropdownMeasure: Array,
		dropdownparameterInput: Array,
		columns: Array,
		actionName: String,
		removeCheck: Boolean,
		showlimit: Boolean,
		pageable: Boolean,
		wsize: String,
		type: String,
		zoomMode: Boolean,
		disabledSatate: Boolean,
		hauto: Boolean,
		emsprAdjstButtonText: String, //배출량 산정 버튼 추가
		pwremCfcntButtonText: String,
		mngMnPrm: String, // 메뉴 권한 관리 버튼
		insertButtonText: String,
		yearValue: String,
		yearDataItems: Array, // 기준연도 그리드 데이터
	},
	components: {
		cellmodal: cellmodal,
		ZoomButton,
		FileFormCell,
		Grid: Grid,
		"grid-toolbar": GridToolbar,
		"grid-norecords": GridNoRecords,
		custom: CommandCell,
		CheckboxCell,
		CheckboxEditCell,
		BasicButton,
		DropdownCell,
		DropdownListCell,
		DatepickerCell,
		Modal,
		dropdownlist: DropDownList,
		"dialog-actions-bar": DialogActionsBar,
		kbutton: Button,
	},
	computed: {
		gridDataProcess() {
			let gridDataProcess = [];
			if ("data" in this.gridData) {
				gridDataProcess = this.gridData.data;
			} else {
				gridDataProcess = this.gridData;
			}
			return gridDataProcess;
		},
	},
	data: function () {
		return {
			myCellTemplates1: "rowspans1",
			token: this.$cookie.getCookie("accesstoken"),
			downloadicon,
			uploadicon,
			sort: [],
			gridData: [],
			skip: 0,
			take: this.limit,
			total: this.dataItems.count,
			limit: 10,
			limitData: [10, 20, 30, 40, 50],
			updatedData: [],
			versionData: [],
			editID: null,
			visibleDialog: false,
			stateDeleteData: null,
		};
	},

	created() {
		this.updatedData = this.dataItems.items;
		this.getData();
		if (this.group) {
			this.collapsedGroups[0] = this.gridData.data
				.filter((a) => a.value !== this.dataItems[0].emission_code_name)
				.map((i) => i.value);
		}
	},
	methods: {
		customHandler: function (e) {
			console.log("customHandler", e);
		},
		ExpansionGrid(e) {
			this.$emit("Expansions2", e);
		},

		reductions(data) {
			console.log(data, "2");
		},
		getNestedValue: function (fieldName, dataItem) {
			const path = fieldName.split(".");
			let data = dataItem;

			path.forEach((a) => {
				data = data ? data[a] : undefined;
			});

			return data;
		},
		checkboxEdit: function (e, dataItem) {
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			if (e.value != undefined) {
				item.use_yn = e.value;
				this.gridData = updatedData;
			}
		},
		sortChangeHandler: function (e) {
			// 정렬
			this.sort = e.sort;
		},
		ddCodeChange: function (e, dataItem) {
			// 코드 그룹 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.group_id = e.target.value.id;
			item.group_name = e.target.value.text;
			this.gridData = updatedData;
		},
		ddEmissionChange: function (e, dataItem) {
			// 배출활동코드 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.emission_code_no = e.target.value.id;
			item.emission_code_name = e.target.value.text;
			this.gridData = updatedData;
		},
		ddActivityChange: function (e, dataItem) {
			// 활동자료코드 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.activity_code_no = e.target.value.id;
			item.activity_code_name = e.target.value.text;
			this.gridData = updatedData;
		},
		ddParameterGroupChange: function (e, dataItem) {
			// 파라미터그룹 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.group_id = e.target.value.id;
			item.group_no = e.target.value.id;
			item.group_name = e.target.value.text;
			item.input_code_no = e.target.value.input_code_no;
			item.input_code_name = e.target.value.input_code_name;
			this.gridData = updatedData;
		},
		ddParameterCalculationChange: function (e, dataItem) {
			// 파라미터 산정등급코드 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.tier_no = e.target.value.id;
			item.tier_name = e.target.value.text;
			this.gridData = updatedData;
		},
		ddMeasureChange: function (e, dataItem) {
			// 단위코드 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.measure_no = e.target.value.id;
			item.measure_name = e.target.value.text;
			this.gridData = updatedData;
		},
		ddVersionChange: async function (e, dataItem) {
			// 파라미터 버전 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			await this.$store.dispatch("FETCH_PARAMETER_VERSION", {
				token: this.token,
				parameter_id: item.id,
				version: e.target.value,
			});
			const versionData = this.$store.state.parameterVersion;
			item.emission_code_no = versionData.emission_code_no;
			item.emission_code_name = versionData.emission_code_name;
			item.activity_code_no = versionData.activity_code_no;
			item.activity_code_name = versionData.activity_code_name;
			item.group_id = versionData.group_id;
			item.group_name = versionData.group_name;
			item.input_code_no = versionData.input_code_no;
			item.input_code_name = versionData.input_code_name;
			item.tier_no = versionData.tier_no;
			item.tier_name = versionData.tier_name;
			item.parameter_value = versionData.parameter_value;
			item.version = e.target.value;
			item.measure_no = versionData.measure_no;
			item.measure_name = versionData.measure_name;
			item.remarks = versionData.remarks;
			this.gridData = updatedData;
		},
		ddParameterInputChange: function (e, dataItem) {
			// 파라미터 입력구분코드 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.input_code_no = e.target.value.id;
			item.input_code_name = e.target.value.text;
			this.gridData = updatedData;
		},
		itemChange: function (e) {
			const updatedData = this.gridData.slice();

			const item = this.update(updatedData, e.dataItem);
			item[e.field] = e.value;
			this.gridData = updatedData;
		},
		datePickerHandler: function (e, dataItem) {
			// 날짜 선택
			const updatedData = this.gridDataProcess.slice();
			const item = this.update(updatedData, dataItem);
			item.disposal_date = e.value;
			if (new Date() > e.value) {
				item.state = "폐기";
			} else {
				item.state = "진행중";
			}
			this.gridData = updatedData;
		},
		changeYear: function (e) {
			this.$emit("changeYear", e);
		},
		insert() {
			// 데이터 추가
			let id = 1;
			this.$emit("isEditableTrueChange");
			const dataItem = {
				inEdit: true,
				isNew: true,
			};
			dataItem.create_user = "sfsdf";
			// dataItem.create_user = "admin";
			dataItem.create_date = new Date();
			dataItem.update_user = "admin";
			dataItem.update_date = new Date();
			if (this.actionName == "CODE") {
				this.updatedData.forEach((p) => {
					if (p.code_id) {
						id = Math.max(parseInt(p.code_id) + 1, id);
					}
				});
				dataItem.code_id = id;
				dataItem.use_yn = true;
			}
			if (
				this.actionName == "CODE_GROUP" ||
				this.actionName == "PARAMETER_GROUP"
			) {
				this.updatedData.forEach((p) => {
					if (p.group_id) {
						id = Math.max(parseInt(p.group_id) + 1, id);
					}
				});
				dataItem.group_id = id;
			}
			if (this.actionName == "PARAMETER") {
				this.updatedData.forEach((p) => {
					if (p.parameter_id) {
						id = Math.max(parseInt(p.parameter_id) + 1, id);
					}
				});
				dataItem.parameter_id = id;
				dataItem.version = 1;
			}
			this.gridData.splice(0, 0, dataItem);
		},
		edit: function (e) {
			e.dataItem.inEdit = true;

			if (this.gridName == "newMngmn") {
				// 한 행씩 편집 가능
				this.cancelAll();
			}

			// 데이터 편집

			// 데이터 추가/편집 구분
			// if (this.gridName == "newMngmn") {
			// 	if (e.dataItem.operation_start > new Date()) {
			// 		this.$emit("isEditableChange", true);
			// 	} else {
			// 		this.$emit("isEditableChange", false);
			// 	}
			// } else {
			// 	this.$emit("isEditableChange", false);
			// }
		},
		clickEvent: function (e) {
			this.$emit("clickEvents", e);
		},
		save: function (e) {
			let index = 0;
			// 데이터 저장
			if (e.dataItem.id) {
				index = this.gridDataProcess.findIndex((p) => p.id === e.dataItem.id);
			} else {
				index = this.gridDataProcess.findIndex((p) => p.no === e.dataItem.no);
			}
			let item = this.gridDataProcess[index];
			let updated = Object.assign(
				this.update(this.gridDataProcess.slice(), item),
				{ inEdit: undefined, isNew: undefined }
			);
			let updateDataIndex = this.updatedData.findIndex(
				(p) => p.id === e.dataItem.id
			);
			const data = {};
			if ("id" in updated) {
				// 데이터 수정
				data.id = updated.id;
				data.data = updated;
				updated.update_date = new Date();
				if (this.gridName == "calculation") {
					updated.version += 1;
				}
				this.$emit("edit", data);
				if (this.gridName == "facilityEmission") {
					data.data.disposal_date = new Date(data.data.disposal_date);
					if (this.$refs.detailGrid && this.$refs.detailGrid.returnData()) {
						this.$emit("detailEdit", {
							id: e.dataItem.fac_emi_id,
							data: this.$refs.detailGrid.returnData(),
						});
					}
				}
			} else {
				// 데이터 추가
				data.data = updated;
				this.$emit("insert", data);
				if (this.gridName == "facilityEmission") {
					data.data.disposal_date = new Date(data.data.disposal_date);
					if (this.$refs.detailGrid && this.$refs.detailGrid.returnData()) {
						this.$emit("detailEdit", {
							id: e.dataItem.fac_emi_id,
							data: this.$refs.detailGrid.returnData(),
						});
					}
				}
				// updated.id = this.newDataId;
				this.total += 1;
			}
			this.refreshIndex = updateDataIndex;
			this.gridDataProcess.splice(index, 1, updated);
			this.updatedData.splice(updateDataIndex, 1, updated);
		},
		update(data, item, remove) {
			// 데이터 업데이트
			let updated;
			if (item) {
				let itemid = item.id;
				let index = data.findIndex(
					(p) =>
						JSON.stringify({
							...p,
						}) === JSON.stringify(item) ||
						(itemid && p.id === itemid)
				);
				if (index >= 0) {
					updated = Object.assign({}, item);
					data[index] = updated;
				} else {
					let id = 1;
					data.forEach((p) => {
						id = Math.max(p.id + 1, id);
					});
					updated = Object.assign({}, item, {
						id: id,
					});
					data.unshift(updated);
					index = 0;
				}

				if (remove) {
					data = data.splice(index, 1);
				}
				return data[index];
			}
		},
		cancel: function (e) {
			// 데이터 편집 취소
			if (e.dataItem.id) {
				let index = this.gridDataProcess.findIndex(
					(p) => p.id === e.dataItem.id
				);
				let updateDataIndex = this.updatedData.findIndex(
					(p) => p.id === e.dataItem.id
				);
				let updated = Object.assign(this.updatedData[updateDataIndex], {
					inEdit: undefined,
				});
				this.gridDataProcess.splice(index, 1, updated);
			} else {
				let index = this.gridDataProcess.findIndex(
					(p) => JSON.stringify(e.dataItem) === JSON.stringify(p)
				);
				this.gridDataProcess.splice(index, 1);
			}
			if (this.gridName == "facilityEmission") {
				this.$refs.detailGrid.cancelAll();
			}
		},
		cancelAll() {
			// 편집 상태 초기화
			let editedItems = this.updatedData.filter((p) => p.inEdit === true);
			if (editedItems.length) {
				editedItems.forEach((item) => {
					item.inEdit = undefined;
				});
			}
			this.getData();
		},
		remove(e) {
			// 삭제
			this.stateDeleteData = e;
			if (this.removeCheck) {
				this.deleteDialog();
			} else {
				this.deleteData();
			}
		},
		deleteData() {
			// 데이터 삭제
			if (this.visibleDialog == true) {
				this.deleteDialog();
			}
			this.stateDeleteData.dataItem.inEdit = undefined;
			this.update(this.gridData, this.stateDeleteData.dataItem, true);
			this.update(this.updatedData, this.stateDeleteData.dataItem, true);
			const removeData = {
				id: this.stateDeleteData.dataItem.id,
				token: this.token,
			};
			this.$store.dispatch(`DELETE_${this.actionName}`, removeData);
			this.total -= 1;
		},
		returnData: function () {
			return this.gridData;
		},
		refreshData: function () {
			// 데이터 새로고침
			this.total = this.dataItems.count;
			this.updatedData = this.dataItems.items;
			this.getData();
		},
		getData: function () {
			this.gridData = process(this.updatedData, {
				sort: this.sort,
			}).data;
		},
		createAppState: function (dataState) {
			this.sort = dataState.sort;
			this.getData();
		},
		dataStateChange: function (event) {
			this.createAppState(event.data);
		},
		toggleDialog() {
			this.$emit("toggleDialog");
		},
		deleteDialog() {
			// 데이터 삭제 여부 확인 팝업
			this.visibleDialog = !this.visibleDialog;
		},
		limitChange(event) {
			// limit 설정
			this.skip = 0;
			this.take = event.target.value;
			this.limit = event.target.value;
			this.$emit("limitChange", event.target.value);
		},
		pageChangeHandler: function (event) {
			// 페이지네이션
			this.skip = event.page.skip;
			this.take = event.page.take;
			this.$emit("pageChange", this.skip / this.take + 1);
		},
	},
};
</script>

<style lang="scss" scoped>
