import {
	fetchMenuList,
	fetchMenuPermissions,
	editMenuPermissions,
} from "@/api/index.js";

const menuStore = {
	namespaced: true,
	state: {
		menuList: [
			{
				menu_id: 1,
				menu_name: " ",
				depth: 1,
				sub_menu: [
					{
						menu_id: 2,
						menu_name: " ",
						depth: 2,
						sub_menu: [
							{
								menu_id: 3,
								menu_name: " ",
								depth: 3,
							},
						],
					},
				],
			},
		],
		menuCount: 0,
		menuPermissionsList: [],
		menuPermissionsCount: 0,
		menu_id: "",
	},
	mutations: {
		SET_MENU_LIST: (state, data) => {
			state.menuList = data.items;
			state.menuCount = data.count;
		},
		SET_MENU_PERMISSIONS: (state, data) => {
			state.menuPermissionsList = data.items;
			state.menuPermissionsCount = data.count;
		},
		SET_MENU_LIST_INIT: (state) => {
			state.menuList = [];
			state.menuCount = "";
		},
	},
	actions: {
		FETCH_MENU_LIST: ({ commit }) => {
			// 메뉴 목록 조회
			return fetchMenuList()
				.then(({ data }) => {
					commit("SET_MENU_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_MENU_PERMISSIONS: ({ commit }, info) => {
			// 메뉴 권한 목록 조회
			return fetchMenuPermissions(info)
				.then(({ data }) => {
					commit("SET_MENU_PERMISSIONS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_MENU_PERMISSIONS: (info) => {
			// 메뉴 권한 일괄 수정
			return editMenuPermissions(info.state.menuPermissionsList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_MENU_LIST_INIT: ({ commit }) => {
			return commit("SET_MENU_LIST_INIT");
		},
	},
};

export default menuStore;
