import { accountSignIn, editParameter, deleteParameter } from "../api/index.js";

export default {
	ACCOUNT_SIGNIN: ({ commit }, userInfo) => {
		// 로그인 처리
		return accountSignIn(userInfo)
			.then(({ data }) => {
				commit("SET_ACCOUNT_SIGNIN", data);
				return data;
			})
			.catch((error) => {
				switch (error.response.status) {
					case 401:
						alert(error.response.data.message);
						break;
					default:
						console.log(error);
				}
			});
	},
	EDIT_PARAMETER: ({ commit }, info) => {
		// 활동자료 수정
		editParameter(info)
			.then(({ data }) => {
				commit("SET_SUCCESS", data);
				return data;
			})
			.catch((error) => console.log(error));
	},
	DELETE_PARAMETER: ({ commit }, info) => {
		// 활동자료 삭제
		return deleteParameter(info)
			.then(({ data }) => {
				commit("SET_SUCCESS", data);
				return data;
			})
			.catch((error) => console.log(error));
	},
	ADD_TAB_VIEW: ({ commit }, view) => {
		// 탭 추가
		if (view.name != "SignIn" && view.name != "DashBoard") {
			commit("ADD_TAB_VIEW", view);
		}
	},
	ADD_CACHED_VIEW: ({ commit }, view) => {
		// 탭 캐시 추가
		commit("ADD_CACHED_VIEW", view);
	},
	DEL_TAB_VIEW: ({ commit, state }, view) => {
		// 탭 삭제
		return new Promise((resolve) => {
			commit("DEL_TAB_VIEW", view);
			resolve({
				tabsView: [...state.tabsView],
			});
		});
	},
	DEL_CACHED_VIEW: ({ commit, state }, view) => {
		// 탭 캐시 삭제
		return new Promise((resolve) => {
			commit("DEL_CACHED_VIEW", view);
			resolve([...state.cachedViews]);
		});
	},
	SET_PARENT_MENU: ({ commit }, menu) => {
		commit("SET_PARENT_MENU", menu);
	},
};
