<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획기간</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="periodList"
													:value="periodValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="periodChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준 년도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="AddQuotaGrid"
					title="추가할당 관리"
					gridName="addQuota"
					:dataItems="$store.state.addQuotaStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					:selectWorkplace="workplaceDropDownList"
					:selectProductionTeam="productionTeamDropDownList"
					:selectFacility="facilityDropDownList"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:deleteDataCheck="true"
					insertButtonText="추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "AddQuota",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			newDataId: "0",
			searchicon,
			searchData: null,
			isNoData: false,
			yearList: [2021, 2022, 2023, 2024, 2025],
			year: "",
			periodList: [{ id: "01", text: "3차" }],
			periodValue: { id: "01", text: "3차" },
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			workplaceDropDownList: [],
			productionTeamDropDownList: [],
			facilityDropDownList: [],
			attchFile: null,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "workplace_name",
					title: "사업장",
					minResizableWidth: 160,
					width: 160,
					cell: "dropdownWorkplaceCell",
				},
				{
					field: "production_team_name",
					title: "생산팀",
					cell: "dropdownProductionTeamCell",
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "facility_name",
					title: "배출시설",
					cell: "dropdownFacilityCell",
					minResizableWidth: 180,
					width: 180,
				},
				{
					field: "facility_serial_no",
					title: "시설일련번호",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "expect_quota",
					title: "추가할당(예상)(tCO2-eq)",
					minResizableWidth: 220,
					width: 220,
				},
				{
					field: "confirm_quota",
					title: "추가할당(확정)(tCO2-eq)",
					minResizableWidth: 250,
					width: 250,
				},
				{
					field: "remarks",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "file_url",
					title: "근거자료",
					cell: "FileFormCell",
					minResizableWidth: 220,
					width: 220,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
	},
	watch: {
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.year = new Date().getFullYear();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceDropDownList = [];
						data.items.map((v) =>
							this.workplaceDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamDropDownList = [];
						data.items.map((v) =>
							this.productionTeamDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						this.fetchSearchList(5, data.items[0].org_id);
					} else if (depth == 5) {
						// 배출시설 SET
						this.facilityDropDownList = [];
						data.items.map((v) =>
							this.facilityDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?period=" + this.periodValue.id;
			this.searchData += "&year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("addQuotaStore/FETCH_ADD_QUOTA_LIST", this.searchData)
				.then((data) => {
					if (this.$refs.AddQuotaGrid !== null) {
						if (data.items.length < 1) {
							this.isNoData = true;
						}
						this.$refs.AddQuotaGrid.refreshData();
					}
				});
		},
		periodChange(e) {
			this.periodValue = e.target.value;
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			this.workplaceDropDownList = [];
			this.workplaceDropDownList.push(event.value);
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
			this.productionTeamDropDownList = [];
			this.productionTeamDropDownList.push(event.value);
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.year = new Date().getFullYear();
		},
		insert: function (data) {
			const formData = new FormData();
			formData.append("facility_id", data.data.facility_id);
			formData.append("year", this.year);
			formData.append("expect_quota", data.data.expect_quota);
			formData.append("confirm_quota", data.data.confirm_quota);
			formData.append("remarks", data.data.remarks);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("addQuotaStore/INSERT_ADD_QUOTA", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		edit: function (data) {
			const formData = new FormData();
			formData.append("addquota_id", data.data.id);
			formData.append("facility_id", data.data.facility_id);
			formData.append("year", this.year);
			formData.append("expect_quota", data.data.expect_quota);
			formData.append("confirm_quota", data.data.confirm_quota);
			formData.append("remarks", data.data.remarks);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("addQuotaStore/INSERT_ADD_QUOTA", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		remove: function (id) {
			this.$store.dispatch("addQuotaStore/DELETE_ADD_QUOTA", id);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"addQuotaStore/DELETE_ADD_QUOTA_FILE",
				e.addquota_id
			);
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
