import {
	fetchEmissionTradingYearList,
	fetchAddQuota,
	insertAddQuota,
	deleteAddQuota,
	deleteAddQuotaFile,
} from "@/api/index.js";

const addQuotaStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ADD_QUOTA_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_ADD_QUOTA_LIST: (state, data) => {
			state.dataList = data.items;
			state.dataList.map((v) => {
				v.id = v.addquota_id;
			});
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ADD_QUOTA_YEAR_LIST: ({ commit }) => {
			// 내부 배출권거래의 선택 가능한 기준연도 목록 조회
			return fetchEmissionTradingYearList()
				.then(({ data }) => {
					return commit("SET_ADD_QUOTA_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_ADD_QUOTA_LIST: ({ commit }, info) => {
			// 추가할당량 관리 조회
			return fetchAddQuota(info)
				.then(({ data }) => {
					commit("SET_ADD_QUOTA_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_ADD_QUOTA: ({ commit }, info) => {
			// 추가할당량 관리 추가 및 수정
			return insertAddQuota(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_ADD_QUOTA: ({ commit }, id) => {
			// 추가할당량 관리 삭제
			return deleteAddQuota(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_ADD_QUOTA_FILE: ({ commit }, id) => {
			// 추가할당량 관리 근거자료 삭제
			return deleteAddQuotaFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default addQuotaStore;
