<template>
	<div class="zoomBox">
		<div class="zoomWrap d-flex" v-if="zoomModeType">
			<div
				class="zoom"
				:style="{ backgroundColor: btnBgColor }"
				@click="editHandler"
			>
				<img :src="zoomImageValue" alt="" />
			</div>

			<div class="setting" v-if="settingMode" @click="settingHandler">
				<img :src="settingIcon" alt="" />
			</div>
		</div>
		<div class="zoomWrap d-flex" v-else>
			<div
				class="zoom"
				:style="{ backgroundColor: btnBgColor }"
				@click="editHandler"
			>
				<img :src="zoomImageValue" alt="" />
			</div>
		</div>
	</div>
	<setting-Modals
		v-if="$store.state.settingModal"
		@close="settingHandler"
		:title="'데시보드 설정'"
		class="settingModalWarp"
	>
		<template v-slot:grid>
			<div class="code-modal h-100 p-0">
				<div class="grid-code h-100">
					<div class="ml-auto2 mb-20">
						<BasicButton
							:backgroundColor="'#ffffff'"
							:fontColor="'#49A872'"
							:borderColor="'#49A872'"
							class="mr-10 paraAdd"
							>저장</BasicButton
						>
					</div>
					<ul class="settingModalUl d-flex w-100">
						<li
							class="settingModalLi"
							v-for="(settingList, index) in settingList"
							:key="index"
						>
							<div class="imgBox">
								<img :src="chartimage" alt="" />
								<div class="blackBox">
									<span class="imgBoxText">{{ index + 1 }}</span>
								</div>
							</div>
							<p class="mt-10">
								<span>{{ index + 1 }}.</span> {{ settingList.title }}
							</p>
						</li>
					</ul>
				</div>
			</div>
		</template>
	</setting-Modals>
</template>

<script>
import ZoomImg from "../../../assets/img/icon/zoom.png";
import chartimage from "../../../assets/img/chartimage.png";
import settingIcon from "../../../assets/img/icon/setting.png";
import CancleImg from "../../../assets/img/icon/minuse.png";
import zoomImageValue from "../../../assets/img/icon/zoom.png";
import ZoomImgActive from "../../../assets/img/icon/zoomactive.png";
import CancleImageValue from "../../../assets/img/icon/minuse.png";
import CancleImgActive from "../../../assets/img/icon/plusactive.png";
import settingModals from "../../modal/Modal.vue";
import BasicButton from "../button/BasicButton.vue";

export default {
	props: {
		dataItem: Object,
		className: String,
		zoomModeType: String,
		settingMode: Boolean,
	},
	components: {
		settingModals: settingModals,
		BasicButton,
	},
	data() {
		return {
			ZoomImg,
			chartimage,
			settingIcon,
			CancleImg,
			ZoomImgActive,
			CancleImgActive,
			zoomImageValue,
			CancleImageValue,
			btnBgColor: "#FFFFFF",
			btnBgActiveColor: "#6D6F7A",
			btnBgInactiveColor: "#FFFFFF",
			settingList: [
				{
					id: "",
					imgSrc: "",
					title: "한화 솔루션 차트",
				},
				{
					id: "",
					imgSrc: "",
					title: "한화 솔루션 차트",
				},
				{
					id: "",
					imgSrc: "",
					title: "한화 솔루션 차트",
				},
				{
					id: "",
					imgSrc: "",
					title: "한화 솔루션 차트",
				},
				{
					id: "",
					imgSrc: "",
					title: "한화 솔루션 차트",
				},
				{
					id: "",
					imgSrc: "",
					title: "한화 솔루션 차트",
				},
			],
		};
	},
	methods: {
		editHandler: function (event) {
			this.$emit("Expansion", event.target);
			let targetZoomBox = event.target.closest(".zoom");
			let imgState = targetZoomBox.querySelector("img").classList;
			imgState.toggle("active");
			if (imgState.contains("active")) {
				this.zoomImageValue = this.ZoomImgActive;
				this.btnBgColor = this.btnBgActiveColor;
			} else {
				this.zoomImageValue = this.ZoomImg;
				this.btnBgColor = this.btnBgInactiveColor;
			}
		},
		removeHandler: function (event) {
			this.$emit("reduction", event.target);
			let targetZoomBox = event.target.closest(".cancle");
			let cancleImg = targetZoomBox.querySelector("img");
			let imgState2 = cancleImg.classList;
			imgState2.toggle("active");
			if (imgState2.contains("active")) {
				this.CancleImageValue = this.CancleImgActive;
				cancleImg.style.position = "relative";
				cancleImg.style.top = "4px";
				cancleImg.style.left = "3px";
			} else {
				this.CancleImageValue = this.CancleImg;
				cancleImg.style.position = "";
				cancleImg.style.top = "0";
				cancleImg.style.left = "0";
			}
		},
		settingHandler: function (event) {
			event;
			this.$store.state.settingModal = !this.$store.state.settingModal;
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.setting,
.zoomBox {
	user-select: none;
	padding: 10px 0px;
	.zoomWrap {
		cursor: pointer;
		margin-right: auto;
		justify-content: flex-end;

		div {
			width: 40px;
			height: 40px;
			box-shadow: 3px 3px 5px #0000001a;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 6px;
			margin-left: 10px;

			&:last-child {
				margin-right: 0;
			}
		}
	}
}
.grid-code {
	display: flex;
	align-items: center;
	flex-direction: column;
	.settingModalUl {
		height: auto;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		gap: 1em;

		.settingModalLi {
			width: calc((100% / 3) - 20px);

			.imgBox {
				width: 100%;
				position: relative;
				height: 100%;

				img {
					width: 100%;
					height: 100%;
				}
				.blackBox {
					background: #0000004b;
					position: absolute;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					transform: translate(-50%, -50%);
					display: flex;
					justify-content: center;
					align-items: center;
					transition: all 0.3s;
					cursor: pointer;
					&:hover {
						background: #00000096;
					}
					span {
						font-size: 58px;
						color: #ffffff;
					}
				}
			}
		}
	}
}
</style>
