<template>
	<div class="subNavigator mt-9 mb-9 ml-30">
		<span>{{ $store.state.parentMenu }}</span>
		<span class="active">{{ title }}</span>
	</div>
</template>

<script>
export default {
	name: "SubNavbar",
	watch: {
		$route() {
			this.title = this.$route.meta.title;
		},
	},
	data: function () {
		return {
			title: "",
		};
	},
};
</script>

<style lang="scss" scoped>
.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}
</style>
