import { fetchMasterYearList, fetchMasterList } from "@/api/index.js";

const masterStore = {
	namespaced: true,
	state: {
		masterList: [],
		masterCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_MASTER_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_MASTER_LIST: (state, data) => {
			state.masterList = data.items;
			state.masterCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_MASTER_YEAR_LIST: ({ commit }) => {
			// 마스터의 선택 가능한 기준연도 목록 조회
			return fetchMasterYearList()
				.then(({ data }) => {
					commit("SET_MASTER_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_MASTER_LIST: ({ commit }, info) => {
			// 마스터 목록 조회
			return fetchMasterList(info)
				.then(({ data }) => {
					commit("SET_MASTER_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default masterStore;
