<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조회연월</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field :name="'startYear'" :component="'yearSelect1'">
											<template v-slot:yearSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.rePlanManagementStore.yearList
													"
													:value="startYear"
													@change="startYearChange"
												/>
											</template>
										</field>
									</div>
									<span>~</span>
									<div class="selectBox ml-10">
										<field :name="'endYear'" :component="'yearSelect1'">
											<template v-slot:yearSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.rePlanManagementStore.yearList
													"
													:value="endYear"
													@change="endYearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					className="groupCell"
					gridName="rePlanManagement"
					title="중장기 RE 계획관리"
					:dataItems="$store.state.rePlanManagementStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:isAllDataEdit="true"
					@allDataSave="allDataSave"
					@allDataCancel="allDataCancel"
				/>
			</div>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import resize from "../../mixin/resize.js";

import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

export default {
	name: "RePlanManagement",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			startYear: "",
			endYear: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			showAlert: false,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "type",
					title: "구분",
					editable: false,
					minResizableWidth: 180,
					width: 180,
				},
			];
			const reverseYear = [...this.yearList].reverse();
			for (var i = 0; i <= reverseYear.length - 1; i++) {
				column.push({
					field: "y" + reverseYear[i],
					title: reverseYear[i] + "",
					minResizableWidth: 160,
					width: 160,
					resizable: false,
					className: "text-center",
					hidden: false,
				});
			}
			return column;
		},
		yearList() {
			return this.$store.state.rePlanManagementStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = new Date().getFullYear();
				this.startYear = value[value.length - 1];
				this.endYear = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created: async function () {
		this.$store.dispatch("rePlanManagementStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("rePlanManagementStore/FETCH_ORGANIZATION_YEAR_LIST");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		handleSubmit: async function () {
			this.searchData = "";
			this.searchData += "?start_year=" + this.startYear;
			this.searchData += "&end_year=" + this.endYear;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"rePlanManagementStore/FETCH_RE_PLAN_MANAGEMENT_LIST",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						console.log(this.columns);
						// 조회한 연도만 컬럼만 노출
						for (var i = 3; i < 3 + this.yearList.length; i++) {
							this.columns[i].hidden = true;
						}
						for (var j = this.startYear; j <= this.endYear; j++) {
							let index = this.columns.findIndex((p) => p.title === j + "");
							this.columns[index].hidden = false;
						}
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.DataTable.refreshData();
					}
				});
		},
		startYearChange(e) {
			this.startYear = e.target.value;
		},
		endYearChange(e) {
			this.endYear = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			this.fetchSearchList(4, this.workplaceValue.id);
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		allDataSave: async function (data) {
			console.log(data);
			this.$store
				.dispatch("rePlanManagementStore/EDIT_RE_PLAN_MANAGEMENT", data)
				.then(() => {
					this.showAlert = !this.showAlert;
					this.fetchGridData();
				});
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		clearSearchForm: function () {
			// 초기화
			this.renderKey = 0;
			this.$store.dispatch("rePlanManagementStore/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch(
				"rePlanManagementStore/FETCH_ORGANIZATION_YEAR_LIST"
			);
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
