<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>법인</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'corporation'"
											:style="{ maxWidth: '230px' }"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="corporationList"
													:value="corporationValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="corporationChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2" style="max-width: 318px; width: 100%">
							<div class="wrap">
								<label for=""><span>사용자그룹</span></label>
								<div class="lists" style="width: 100%">
									<div class="selectBox">
										<field
											:name="'userPermissions'"
											:style="{ maxWidth: '300px', width: '100%' }"
											:component="'permissionsSelect'"
										>
											<template v-slot:permissionsSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="permissionsList"
													:value="permissionsValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="permissionsChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="longBox">
							<div class="wrap d-flex align-items-center">
								<label for=""><span>사용자정보</span></label>
								<div class="searchInputBox d-flex align-items-center">
									<field
										:name="'search'"
										:style="{ width: '100%' }"
										:component="'codeInput'"
										:placeholder="'검색어를 입력해주세요'"
									>
										<template v-slot:codeInput="{ props }">
											<autocomplete
												v-bind="props"
												:data-items="searchCode"
												:value="searchValue"
												@change="autoChange"
											/>
										</template>
									</field>
									<img :src="searchicon" alt="돋보기" />
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="UserGrid"
					class="grid-code"
					title="사용자 권한 관리"
					gridName="user"
					insertButtonText="추가"
					popupButtonText="권한그룹관리"
					:dataItems="$store.state.userInfoStore.userList"
					:selectPermissions="permissionsDropDownList"
					:columns="columns"
					:newDataId="newDataId"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@isEditableChange="isEditableChange"
					@gridDialog="gridDialog"
				/>
			</div>
		</div>
	</div>
	<modal v-if="visibleDialog" :title="'권한 그룹 관리'" @close="gridDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code">
					<v-table
						ref="UserPermissionsGrid"
						class="grid-code"
						gridName="userPermissions"
						insertButtonText="권한 그룹 추가"
						:dataItems="$store.state.userInfoStore.userPermissionsList"
						:columns="userPermissionsColumns"
						:newDataId="newDataId"
						@insert="dialogInsert"
						@edit="dialogEdit"
						@remove="dialogRemove"
						@isEditableChange="isEditableChange"
					/>
				</div>
			</div>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import { AutoComplete } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";

import {
	insertUserInfo,
	editUserInfo,
	deleteUserInfo,
	insertUserPermissions,
	editUserPermissionse,
	deleteUserPermissions,
} from "@/api/index.js";

export default {
	name: "UserInfo",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		dropdownlist: DropDownList,
		autocomplete: AutoComplete,
		VTable,
		Modal,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			corporationList: [],
			corporationValue: {},
			permissionsList: [{ text: "전체" }],
			permissionsValue: { text: "전체" },
			permissionsDropDownList: [],
			searchValue: "",
			searchFormStates: false,
			visibleDialog: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "login_id",
					title: "사용자 ID",
					minResizableWidth: 160,
					width: 160,
					editable: true,
				},
				{
					field: "permission_name",
					title: "권한 그룹",
					cell: "dropdownPermissionCell",
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "employee_number",
					title: "사번",
					editable: true,
					minResizableWidth: 110,
					width: 110,
				},
				{
					field: "name",
					title: "이름",
					editable: true,
					minResizableWidth: 110,
					width: 110,
				},
				{
					field: "team",
					title: "팀명",
					editable: true,
					minResizableWidth: 110,
					width: 110,
				},
				{
					field: "position",
					title: "직급",
					editable: true,
					minResizableWidth: 110,
					width: 110,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
		userPermissionsColumns() {
			return [
				{ field: "no", title: "No", width: "50px", editable: false },
				{
					field: "name",
					title: "권한 그룹명",
					editable: true,
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "create_user",
					title: "등록자",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "create_date",
					title: "등록일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "update_user",
					title: "수정자",
					editable: false,
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "update_date",
					title: "수정일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 120,
					width: 120,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
		userPermissionsList() {
			return this.$store.state.userInfoStore.userPermissionsList;
		},
	},
	watch: {
		userPermissionsList(data) {
			this.permissionsList = [{ text: "전체" }];
			data.map((v) =>
				this.permissionsList.push({ id: v.permission_id, text: v.name })
			);
			this.permissionsValue = { text: "전체" };
			this.permissionsDropDownList = [];
			data.map((v) =>
				this.permissionsDropDownList.push({ id: v.permission_id, text: v.name })
			);
		},
	},
	created() {
		// 법인정보 SET
		this.$store
			.dispatch(
				"organizationStore/FETCH_ORGANIZATION_LIST",
				new Date().getFullYear()
			)
			.then((data) => {
				this.corporationList = [];
				data.items.map((v) =>
					this.corporationList.push({ id: v.org_id, text: v.org_name })
				);
				this.corporationValue = {
					id: data.items[0].org_id,
					text: data.items[0].org_name,
				};
			});
		// 사용자그룹 SET
		this.$store.dispatch("userInfoStore/FETCH_USER_PERMISSIONS_LIST");
		this.fetchGridData();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		async handleSubmit() {
			this.searchData = "";
			// this.searchData += "?permission_id=" + this.permissionsValue.id;
			// if (this.searchValue !== undefined) {
			// 	this.searchData += "&searchValue=" + this.searchValue;
			// }
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("userInfoStore/FETCH_USER_INFO_LIST", this.searchData)
				.then((data) => {
					if (this.$refs.UserGrid !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.UserGrid.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		insert: function (data) {
			console.log(data);
			insertUserInfo(data.data)
				.then((data) => {
					this.newDataId = data.data.id;
				})
				.catch((error) => console.log(error));
		},
		edit: function (data) {
			editUserInfo(data);
		},
		remove: function (id) {
			deleteUserInfo(id);
		},
		dialogInsert: function (data) {
			insertUserPermissions(data)
				.then((data) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		dialogEdit: function (data) {
			editUserPermissionse(data);
		},
		dialogRemove: function (id) {
			deleteUserPermissions(id);
		},
		corporationChange(e) {
			this.corporationValue = e.target.value;
		},
		permissionsChange(e) {
			this.permissionsValue = e.target.value;
		},
		gridDialog: function () {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.UserPermissionsGrid.cancelAll();
			}
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}
.selectBox {
	max-width: 250px;
	width: 100%;
}
.Codebar {
	padding: 16px 20px 5px 20px;
	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		.arrowTopBox {
			padding-left: 200px;
		}
	}
	.longBox {
		width: 400px !important;
	}
	.buttonBoxs {
		width: 200px;
	}
	.searchInputBox {
		position: relative;
		img {
			position: absolute;
			top: 10px;
			right: 15px;
		}
	}
}
</style>
