import {
	fetchEmissionUnitPriceYearList,
	fetchEmissionUnitPrice,
	insertEmissionUnitPrice,
	editEmissionUnitPrice,
	deleteEmissionUnitPrice,
} from "@/api/index.js";

const emissionUnitPriceStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_EMISSION_UNIT_PRICE_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_EMISSION_UNIT_PRICE: (state, data) => {
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_EMISSION_UNIT_PRICE_YEAR_LIST: ({ commit }) => {
			// 배출권 단가의 선택 가능한 기준연도 목록 조회
			return fetchEmissionUnitPriceYearList()
				.then(({ data }) => {
					commit("SET_EMISSION_UNIT_PRICE_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_EMISSION_UNIT_PRICE_LIST: ({ commit }, info) => {
			// 배출권 단가 관리 조회
			return fetchEmissionUnitPrice(info)
				.then(({ data }) => {
					commit("SET_EMISSION_UNIT_PRICE", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_EMISSION_UNIT_PRICE: ({ commit }, info) => {
			// 배출권 단가 관리 추가
			return insertEmissionUnitPrice(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_EMISSION_UNIT_PRICE: ({ commit }, info) => {
			// 배출권 단가 관리 수정
			return editEmissionUnitPrice(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_EMISSION_UNIT_PRICE: ({ commit }, info) => {
			// 배출권 단가 관리 삭제
			return deleteEmissionUnitPrice(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default emissionUnitPriceStore;
