<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조회연도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'year'"
											:style="{ maxWidth: '230px' }"
											:component="'yearSelect'"
										>
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.emissionUnitPriceStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="EmissionUnitPriceTable"
					title="배출권 단가 관리"
					gridName="emissionUnitPrice"
					:dataItems="$store.state.emissionUnitPriceStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:deleteDataCheck="true"
					insertButtonText="추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "emissionUnitPrice",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			searchYear: "",
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "trading_item",
					title: "거래종목",
					minResizableWidth: 120,
					width: 120,
				},
				{
					title: "월별 거래 종가(원)",

					children: [
						{
							field: "m1",
							title: "1월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m2",
							title: "2월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m3",
							title: "3월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m4",
							title: "4월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m5",
							title: "5월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m6",
							title: "6월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m7",
							title: "7월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m8",
							title: "8월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m9",
							title: "9월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m10",
							title: "10월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m11",
							title: "11월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "m12",
							title: "12월",
							minResizableWidth: 120,
							width: 120,
						},
					],
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
	},
	created() {
		this.$store.dispatch("emissionUnitPriceStore/SETTING_YEAR_LIST_INIT");
		this.$store
			.dispatch("emissionUnitPriceStore/FETCH_EMISSION_UNIT_PRICE_YEAR_LIST")
			.then((data) => {
				console.log(data);
				this.year = data.items[0];
				this.handleSubmit();
			});
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"emissionUnitPriceStore/FETCH_EMISSION_UNIT_PRICE_LIST",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.EmissionUnitPriceTable !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						} else {
							this.searchYear = data.items[0].year;
						}
						this.$refs.EmissionUnitPriceTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("emissionUnitPriceStore/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch(
				"emissionUnitPriceStore/FETCH_EMISSION_UNIT_PRICE_YEAR_LIST"
			);
		},
		insert: function (data) {
			let saveData = data.data;
			saveData.year = this.searchYear;
			console.log(data);
			this.$store
				.dispatch("emissionUnitPriceStore/INSERT_EMISSION_UNIT_PRICE", saveData)
				.then(() => {
					this.fetchGridData();
				});
		},
		edit: function (data) {
			this.$store
				.dispatch("emissionUnitPriceStore/EDIT_EMISSION_UNIT_PRICE", data)
				.then(() => {
					this.fetchGridData();
				});
		},
		remove: function (id) {
			this.$store.dispatch(
				"emissionUnitPriceStore/DELETE_EMISSION_UNIT_PRICE",
				id
			);
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
