<template>
	<td v-if="!dataItem.inEdit" :class="className">
		{{ dataItem[field] }}
	</td>
	<td
		v-else-if="
			columns &&
			columns.find((el) => el.field === field && el.editable === false)
		"
	>
		{{ dataItem[field] }}
	</td>
	<td v-else :class="className">
		<template v-if="(!dataItem.isNew && !disabled) || !data">{{
			dataItem[field]
		}}</template>
		<dropdownlist
			v-else
			@change="change"
			:value="value"
			:data-items="data"
			:text-field="'text'"
			:data-item-key="'id'"
			:style="{ height: '28px' }"
		></dropdownlist>
	</td>
</template>

<script>
import { DropDownList } from "@progress/kendo-vue-dropdowns";

export default {
	name: "DropDownCell",
	components: {
		dropdownlist: DropDownList,
	},
	props: {
		field: String,
		data: Array,
		dataItem: Object,
		dataItemId: String,
		format: String,
		className: String,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
		columns: Object,
		disabled: Boolean,
	},
	data: function () {
		return {
			value: {},
		};
	},
	created: function () {
		this.value = { id: this.dataItemId, text: this.dataItem[this.field] };
	},
	methods: {
		change: function (e) {
			this.value = e.value;
			this.$emit("change", e, e.target.value);
		},
	},
};
</script>

<style scoped>
td {
	vertical-align: middle !important;
}
</style>
