<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획기간</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="periodList"
													:value="periodValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="periodChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획년도</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
		:style="{ height: this.$store.state.resizeLast }"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					title="월별 BAU-할당량 관리"
					gridName="emissionBauMonth"
					:dataItems="$store.state.emissionBauMonthStore.dataList"
					:columns="columns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:isNoData="isNoData"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "EmissionBauMonth",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			yearList: [2021, 2022, 2023, 2024, 2025],
			year: "",
			orgYear: "",
			orgList: [],
			periodList: [{ id: "01", text: "3차" }],
			periodValue: { id: "01", text: "3차" },
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "measure_name",
					title: "단위",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
			];
			for (var i = 1; i < 13; i++) {
				column.push({
					field: "area",
					title: i + "월",
					children: [
						{
							field: "bau_" + i,
							title: "BAU",
							minResizableWidth: 100,
							cell: this.cellWithBackGround,
							width: 100,
						},
						{
							field: "quota_" + i,
							title: "할당량",
							minResizableWidth: 130,
							cell: this.cellWithBackGround,
							width: 130,
						},
						{
							field: "shortfall_" + i,
							title: "부족량",
							minResizableWidth: 130,
							cell: this.cellWithBackGround,
							width: 130,
						},
					],
				});
			}
			return column;
		},
	},
	watch: {
		orgYear(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.year = new Date().getFullYear();
		this.orgYear = new Date().getFullYear();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.orgYear)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.orgYear +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?period=" + this.periodValue.id;
			this.searchData += "&year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"emissionBauMonthStore/FETCH_EMISSION_BAU_MONTH",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						var totalBau1 = 0;
						var totalQuota1 = 0;
						var totalShortfall1 = 0;
						var totalBau2 = 0;
						var totalQuota2 = 0;
						var totalShortfall2 = 0;
						var totalBau3 = 0;
						var totalQuota3 = 0;
						var totalShortfall3 = 0;
						var totalBau4 = 0;
						var totalQuota4 = 0;
						var totalShortfall4 = 0;
						var totalBau5 = 0;
						var totalQuota5 = 0;
						var totalShortfall5 = 0;
						var totalBau6 = 0;
						var totalQuota6 = 0;
						var totalShortfall6 = 0;
						var totalBau7 = 0;
						var totalQuota7 = 0;
						var totalShortfall7 = 0;
						var totalBau8 = 0;
						var totalQuota8 = 0;
						var totalShortfall8 = 0;
						var totalBau9 = 0;
						var totalQuota9 = 0;
						var totalShortfall9 = 0;
						var totalBau10 = 0;
						var totalQuota10 = 0;
						var totalShortfall10 = 0;
						var totalBau11 = 0;
						var totalQuota11 = 0;
						var totalShortfall11 = 0;
						var totalBau12 = 0;
						var totalQuota12 = 0;
						var totalShortfall12 = 0;
						if (data.count < 1) {
							this.isNoData = true;
						} else {
							for (var idx in data.items) {
								totalBau1 += data.items[idx].bau_1;
								totalQuota1 += data.items[idx].quota_1;
								totalShortfall1 += data.items[idx].shortfall_1;
								totalBau2 += data.items[idx].bau_2;
								totalQuota2 += data.items[idx].quota_2;
								totalShortfall2 += data.items[idx].shortfall_2;
								totalBau3 += data.items[idx].bau_3;
								totalQuota3 += data.items[idx].quota_3;
								totalShortfall3 += data.items[idx].shortfall_3;
								totalBau4 += data.items[idx].bau_4;
								totalQuota4 += data.items[idx].quota_4;
								totalShortfall4 += data.items[idx].shortfall_4;
								totalBau5 += data.items[idx].bau_5;
								totalQuota5 += data.items[idx].quota_5;
								totalShortfall5 += data.items[idx].shortfall_5;
								totalBau6 += data.items[idx].bau_6;
								totalQuota6 += data.items[idx].quota_6;
								totalShortfall6 += data.items[idx].shortfall_6;
								totalBau7 += data.items[idx].bau_7;
								totalQuota7 += data.items[idx].quota_7;
								totalShortfall7 += data.items[idx].shortfall_7;
								totalBau8 += data.items[idx].bau_8;
								totalQuota8 += data.items[idx].quota_8;
								totalShortfall8 += data.items[idx].shortfall_8;
								totalBau9 += data.items[idx].bau_9;
								totalQuota9 += data.items[idx].quota_9;
								totalShortfall9 += data.items[idx].shortfall_9;
								totalBau10 += data.items[idx].bau_10;
								totalQuota10 += data.items[idx].quota_10;
								totalShortfall10 += data.items[idx].shortfall_10;
								totalBau11 += data.items[idx].bau_11;
								totalQuota11 += data.items[idx].quota_11;
								totalShortfall11 += data.items[idx].shortfall_11;
								totalBau12 += data.items[idx].bau_12;
								totalQuota12 += data.items[idx].quota_12;
								totalShortfall12 += data.items[idx].shortfall_12;
							}
							data.items.push({
								id: 0,
								workplace_name: "합계",
								bau_1: totalBau1,
								quota_1: totalQuota1,
								shortfall_1: totalShortfall1,
								bau_2: totalBau2,
								quota_2: totalQuota2,
								shortfall_2: totalShortfall2,
								bau_3: totalBau3,
								quota_3: totalQuota3,
								shortfall_3: totalShortfall3,
								bau_4: totalBau4,
								quota_4: totalQuota4,
								shortfall_4: totalShortfall4,
								bau_5: totalBau5,
								quota_5: totalQuota5,
								shortfall_5: totalShortfall5,
								bau_6: totalBau6,
								quota_6: totalQuota6,
								shortfall_6: totalShortfall6,
								bau_7: totalBau7,
								quota_7: totalQuota7,
								shortfall_7: totalShortfall7,
								bau_8: totalBau8,
								quota_8: totalQuota8,
								shortfall_8: totalShortfall8,
								bau_9: totalBau9,
								quota_9: totalQuota9,
								shortfall_9: totalShortfall9,
								bau_10: totalBau10,
								quota_10: totalQuota10,
								shortfall_10: totalShortfall10,
								bau_11: totalBau11,
								quota_11: totalQuota11,
								shortfall_11: totalShortfall11,
								bau_12: totalBau12,
								quota_12: totalQuota12,
								shortfall_12: totalShortfall12,
							});
						}
						this.$refs.DataTable.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		periodChange(e) {
			this.periodValue = e.target.value;
		},
		yearChange(event) {
			this.year = event.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.year = new Date().getFullYear();
			this.handleSubmit();
		},
		cellWithBackGround: function (h, tdElement, props, listeners) {
			const value = tdElement.children[0];
			listeners;
			if (parseInt(value) < 0) {
				return h(
					"td",
					{
						style: { color: "red" },
					},
					[value]
				);
			} else {
				return h(
					"td",
					{
						style: { color: "black" },
					},
					[value]
				);
			}
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
