import {
	fetchOrganizationYearList,
	fetchReductionBusinessItem,
	fetchReductionPlanManagement,
	editReductionPlanManagement,
} from "@/api/index.js";

const reductionPlanManagementStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		itemList: [],
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_REDUCTION_PLAN_MANAGEMENT: (state, data) => {
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_REDUCTION_BUSINESS_ITEM: (state, data) => {
			state.itemList = data.items;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_REDUCTION_PLAN_MANAGEMENT: ({ commit }, info) => {
			// 월별 감축계획 관리 조회
			return fetchReductionPlanManagement(info)
				.then(({ data }) => {
					commit("SET_REDUCTION_PLAN_MANAGEMENT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_REDUCTION_PLAN_MANAGEMENT: (info) => {
			// 월별 감축계획 관리 일괄 수정
			return editReductionPlanManagement(info.state.dataList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_REDUCTION_BUSINESS_ITEM: ({ commit }) => {
			// 감축 사업의 선택 가능한 감축 사업명 목록 조회
			return fetchReductionBusinessItem()
				.then(({ data }) => {
					commit("SET_REDUCTION_BUSINESS_ITEM", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default reductionPlanManagementStore;
