import {
	fetchEmissionActivityList,
	insertEmissionActivity,
	copyEmissionActivity,
	fetchEmissionList,
} from "@/api/index.js";

const emissionActivityStore = {
	namespaced: true,
	state: {
		emissionActivityList: [],
		emissionActivityCount: 0,
		activityDataList: [],
		activityDataCount: 0,
	},
	mutations: {
		SET_EMISSION_ACTIVITY_LIST: (state, emissionActivity) => {
			state.emissionActivityList = emissionActivity.items;
			state.emissionActivityCount = emissionActivity.count;
		},
		SET_EMISSION_LIST: (state, emissionData) => {
			state.activityDataList = emissionData.items;
			state.activityDataList.map((v) => (v.inEdit = true));
			state.activityDataCount = emissionData.count;
		},
		CHANGE_EMISSION: (state, emissionCodeNo) => {
			state.activityDataList.map((i) => {
				i.use_yn = false;
			});
			const emission_activity = state.emissionActivityList.filter(function (v) {
				return v.emission_code_no == emissionCodeNo;
			});
			state.activityDataList.map((i) => {
				if (
					emission_activity.findIndex(
						(no) => no.activity_code_no == i.code_no
					) != -1
				) {
					i.use_yn = true;
				} else {
					i.use_yn = false;
				}
			});
		},
	},
	actions: {
		FETCH_EMISSION_ACTIVITY_LIST: ({ commit }, year) => {
			// 배출활동 목록 조회
			return fetchEmissionActivityList(year)
				.then(({ data }) => {
					return commit("SET_EMISSION_ACTIVITY_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		INSERT_EMISSION_ACTIVITY: ({ dispatch }, updateData) => {
			// 배출활동 추가
			return insertEmissionActivity(updateData)
				.then(() => {
					return dispatch("FETCH_EMISSION_ACTIVITY_LIST", updateData.year);
				})
				.catch((error) => console.log(error));
		},
		COPY_EMISSION_ACTIVITY: ({ commit }) => {
			// 배출활동 추가
			return copyEmissionActivity()
				.then(({ data }) => {
					console.log(data);
					return commit("SET_EMISSION_ACTIVITY_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_EMISSION_LIST: ({ commit }) => {
			// 활동자료 목록 조회
			return fetchEmissionList()
				.then(({ data }) => {
					return commit("SET_EMISSION_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		EDIT_EMISSION: ({ commit }, emission_code_no) => {
			// 활동자료 사용여부 변경
			return commit("CHANGE_EMISSION", emission_code_no);
		},
	},
};

export default emissionActivityStore;
