<template>
	<td v-if="!dataItem.inEdit">
		{{ dataItem[field] }}
	</td>
	<td v-else>
		<k-input
			ref="nameInput"
			:value="dataItem[field]"
			@input="onInput"
			@change="change"
		></k-input>
	</td>
</template>

<script>
import { Input } from "@progress/kendo-vue-inputs";

export default {
	components: {
		"k-input": Input,
	},
	props: {
		field: String,
		dataItem: Object,
	},
	data: function () {
		return {
			getDataItem: this.dataItem,
		};
	},
	methods: {
		onInput: function (e) {
			this.getDataItem[this.field] = e.value;
		},
		change: function (e) {
			this.getDataItem[this.field] = e.value;
			this.$emit("change", e);
		},
	},
};
</script>
