<template>
	<fieldwrapper>
		<!-- kendo 라벨 , 인풋, error, hint, 셋팅  -->
		<div class="inputs d-flex k-form-field-wrap">
			<klabel
				:disabled="disabled"
				:editor-id="id"
				:editor-valid="valid"
				:optional="optional"
				>{{ title }}&nbsp;<span class="star" v-if="must !== 'N'">*</span>
			</klabel>
			<kinput
				v-if="mask == undefined"
				:valid="valid"
				:id="id"
				:value="value"
				:disabled="disabled"
				:placeholder="placeholder"
				:type="type"
				:class="{ active: showValidationMessage }"
				:style="{
					width: '100%',
					paddingLeft: '0px',
					fontFamily: 'AppleSDGothicNeoSB00',
					fontSize: '14px',
					height: '40px',
					color: '#6D6F7A',
				}"
				@input="handleChange"
				@blur="handleBlur"
				@focus="handleFocus"
			/>
			<masked-text-box
				v-else
				:mask="mask"
				:valid="valid"
				:id="id"
				:value="value"
				:disabled="disabled"
				:placeholder="placeholder"
				:type="type"
				:class="{ active: showValidationMessage }"
				:style="{
					width: '100%',
					paddingLeft: '0px',
					fontFamily: 'AppleSDGothicNeoSB00',
					fontSize: '14px',
					height: '40px',
					color: '#6D6F7A',
				}"
				@change="handleChange"
				@blur="handleBlur"
				@focus="handleFocus"
			/>
			<error v-if="showValidationMessage" class="errorBox">
				{{ validationMessage }}
			</error>
			<hint v-else>{{ hint }}</hint>
		</div>
	</fieldwrapper>
</template>
<script>
import { FieldWrapper } from "@progress/kendo-vue-form";
import { Error, Hint, Label } from "@progress/kendo-vue-labels";
import { Input, MaskedTextBox } from "@progress/kendo-vue-inputs";
export default {
	name: "InputCell",
	components: {
		klabel: Label,
		kinput: Input,
		MaskedTextBox,
		error: Error,
		fieldwrapper: FieldWrapper,
		hint: Hint,
	},
	props: {
		optional: Boolean,
		disabled: Boolean,
		placeholder: String,
		type: String,
		touched: Boolean,
		label: String,
		validationMessage: String,
		hint: String,
		id: String,
		valid: Boolean,
		title: String,
		must: String,
		value: {
			type: String,
			default: "",
		},
		mask: String,
	},

	computed: {
		showValidationMessage() {
			return this.$props.touched && this.$props.validationMessage;
		},
		showHint() {
			return !this.showValidationMessage && this.$props.hint;
		},
		hintId() {
			return this.showHint ? `${this.$props.id}_hint` : "";
		},
		errorId() {
			return this.showValidationMessage ? `${this.$props.id}_error` : "";
		},
	},
	emits: {
		change: null,
		blur: null,
		focus: null,
	},
	methods: {
		handleChange(e) {
			this.$emit("change", e);
		},
		handleBlur(e) {
			this.$emit("blur", e);
		},
		handleFocus(e) {
			this.$emit("focus", e);
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.inputsBox {
	.k-textbox {
		width: 100% !important;
	}

	.content.con2 {
		.inputs {
			span {
				width: calc(100% - 227px) !important;
			}
			.k-textbox {
				width: 100% !important;

				.k-input-inner {
					&::placeholder {
						color: #b2b5c5 !important;
					}
				}
			}
		}
	}

	.content {
		&.con1 {
			.inputs .k-textbox {
				width: calc(100% - 250px) !important;
			}
		}
	}
}

.inputs {
	height: 40px;
	align-items: center;
	position: relative;

	.k-label {
		color: #33343c !important;
	}

	.k-textbox {
		height: 100% !important;
		line-height: 40px;
		&.active {
			border: 1.5px solid #f37321 !important;
		}

		border: 1px solid #cccccc !important;
	}

	label {
		width: 220px;
		height: 40px;
		line-height: 40px;
		background: #f5f5f5;
		font-family: AppleSDGothicNeoSB00;
		font-size: 13px;
		color: #33343c;
		padding: 0px 20px;
		box-sizing: border-box;
		flex-shrink: 0;
		margin-right: 10px;

		.star {
			display: inline-block;
			color: #f37321;
			font-family: AppleSDGothicNeoEB00;
		}
	}

	span {
		display: inline-block;
		width: calc(100% - 250px) !important;
		// border: 1px solid #CCCCCC;
		border-radius: 4px;

		&:focus {
			border: 1px solid #6d6f7a !important;
		}

		input#firstName.k-input-inner {
			color: --bs-blue;

			&::placeholder {
				color: rgb(0, 38, 255) !important;
			}
		}
	}

	.errorBox {
		position: absolute;
		top: 42px;
		left: 233px;
		width: auto;
		background-color: rgba(243, 114, 33, 0.863);
		font-family: AppleSDGothicNeoR00;
		font-size: 12px;
		color: #f5e8e8;
		border-radius: 4px;
		padding: 5px 15px;
		z-index: 99;
	}
}
</style>
