<template>
	<sidebar v-if="$store.state.showSidebar" />
	<div
		class="mainWrap"
		:class="{
			change: $store.state.resize,
		}"
	>
		<main class="page-content active h-100" id="content">
			<navbar v-if="$store.state.showNavbar" />
			<tab v-if="$store.state.showTabs" />
			<sub-navbar v-if="$store.state.showSubNavbar" />
			<router-view v-slot="{ Component }">
				<suspense>
					<keep-alive :include="$store.state.cachedViews">
						<component :is="Component" :key="$route.path" />
					</keep-alive>
				</suspense>
			</router-view>
		</main>
	</div>
</template>

<script>
import "@/assets/scss/_index.scss";
import "@progress/kendo-theme-default/dist/all.css";

import Sidebar from "./components/common/sidebar/Sidebar.vue";
import Navbar from "./components/common/nav/Navbar.vue";
import Tab from "./components/common/tab/Tab.vue";
import SubNavbar from "./components/common/nav/SubNavbar.vue";

export default {
	name: "App",
	components: {
		Sidebar,
		Navbar,
		Tab,
		SubNavbar,
	},
};
</script>
