<template>
	<td v-if="!dataItem.inEdit">
		{{ dataItem[field] }}
	</td>
	<td v-else>
		<radiobutton
			:name="'group'"
			:value="dataItem.id"
			:checked="dataItem[field] === true"
			@change="handleChange"
		/>
	</td>
</template>

<script>
import { RadioButton } from "@progress/kendo-vue-inputs";

export default {
	components: {
		radiobutton: RadioButton,
	},
	props: {
		field: String,
		dataItem: Object,
	},
	data: function () {
		return {
			selectedValue: "",
		};
	},
	methods: {
		handleChange: function (e) {
			this.selectedValue = e.value;
			this.$emit("change", e, e.value);
		},
	},
};
</script>
