<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox">
							<div class="wrap">
								<label for=""><span>기준년도</span></label>
								<div class="lists">
									<div class="selectBox mr-2">
										<field
											:name="'year'"
											:style="{ maxWidth: '230px' }"
											:component="'yearSelect'"
										>
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.closingManagementStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:disabled="isDisable"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:disabled="isDisable"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="subContainer halfGrid">
				<div class="w-100 h-100">
					<v-table
						ref="ClosingManagementTable"
						title="마감 관리"
						gridName="closingManagement"
						:disabledState="true"
						:isAllDataEdit="true"
						@allDataSave="allDataSave"
						@allDataCancel="allDataCancel"
						:dataItems="$store.state.closingManagementStore.dataList"
						:columns="columns"
						:isNoData="isNoData"
					/>
				</div>
			</div>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

export default {
	name: "closingManagement",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			showAlert: false,
			searchFormStates: false,
			isDisable: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "sector_name",
					title: "부문",
					minResizableWidth: 150,
					width: 150,
					editable: false,
				},
				{
					field: "workplace_name",
					title: "사업장",
					minResizableWidth: 150,
					width: 150,
					editable: false,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					minResizableWidth: 150,
					width: 150,
					editable: false,
				},
			];
			for (var i = 1; i < 13; i++) {
				column.push({
					field: "m" + i,
					title: i + "월",
					cell: "checkboxCell",
					minResizableWidth: 120,
					width: 120,
				});
			}
			return column;
		},
		yearList() {
			return this.$store.state.closingManagementStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("closingManagementStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch(
			"closingManagementStore/FETCH_CLOSING_MANAGEMENT_YEAR_LIST"
		);
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
						// 케미칼 부문 일때만 사업장/생산팀 선택가능
						if (this.sectorValue.id === 68 || this.sectorValue.id === 137) {
							this.isDisable = false;
						} else {
							this.isDisable = true;
						}
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"closingManagementStore/FETCH_CLOSING_MANAGEMENT",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.ClosingManagementTable !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.ClosingManagementTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			// 케미칼 부문 일때만 사업장/생산팀 선택가능
			if (this.sectorValue.id === 68 || this.sectorValue.id === 137) {
				this.isDisable = false;
			} else {
				this.isDisable = true;
			}
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		allDataSave: async function (data) {
			console.log(data);
			this.$store
				.dispatch("closingManagementStore/EDIT_CLOSING_MANAGEMENT", data)
				.then(() => {
					this.showAlert = !this.showAlert;
					this.fetchGridData();
				});
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch(
				"closingManagementStore/FETCH_CLOSING_MANAGEMENT_YEAR_LIST"
			);
			this.fetchOrgList();
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
.selectBox {
	width: auto !important;
}
.d-flex.boxs {
	overflow: hidden;
	height: 58vh;
}

.subContainer {
	border-radius: 6px;
	padding-bottom: 35px;
	background: #fff;
	height: calc(100vh - 193px);
	overflow: auto;
	&.halfGrid {
		width: 100%;
		height: 100%;
	}
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.selHearders {
	width: 100%;
	justify-content: space-between;
	align-items: center;
	padding: 0px 20px 0px 0px;
	box-sizing: border-box;
}

.pt-10 {
	padding-top: 20px;
	&.active {
		height: calc(100% - 180px) !important;
	}
}
.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
