<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>배출활동</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'emission_code_no'"
											:style="{ maxWidth: '230px' }"
											:component="'emissionSelect'"
										>
											<template v-slot:emissionSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="
														$store.state.codeStore.emissionActivityCode
													"
													:value="emission_value"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="dropdownEmissionChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>활동자료</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'activity_code_no'"
											:style="{ maxWidth: '230px' }"
											:component="'activitySelect'"
										>
											<template v-slot:activitySelect="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="selectActivityData"
													:value="activity_value"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="dropdownActivityChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					v-if="$store.state.parameterStore.parameterList.length !== 0"
					ref="parameterGrid"
					gridName="parameter"
					:dataItems="$store.state.parameterStore.parameterList"
					:count="$store.state.parameterStore.parameterCount"
					:columns="parameterColumns"
					:newDataId="newDataId"
					:selectParameterGroup="this.dropdownParameterGroup"
					:showlimit="true"
					:pageable="true"
					insertButtonText="파라미터추가"
					popupButtonText="파라미터 그룹관리"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@limitChange="limitChange"
					@pageChange="pageChange"
					@gridDialog="gridDialog"
				/>
			</div>
		</div>
	</div>
	<modal v-if="visibleDialog" :title="'파라미터 그룹관리'" @close="gridDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code modal">
					<v-table
						v-if="$store.state.parameterStore.parameterGroupList.length !== 0"
						ref="parameterGroupGrid"
						gridName="parameterGroup"
						:dataItems="$store.state.parameterStore.parameterGroupList"
						:count="$store.state.parameterStore.parameterGroupCount"
						:columns="parameterGroupColumns"
						:newDataId="newDataId"
						insertButtonText="그룹추가"
						@insert="dialogInsert"
						@edit="dialogEdit"
						@remove="dialogRemove"
					/>
				</div>
			</div>
		</template>
	</modal>
</template>

<script>
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";

import {
	insertParameter,
	editParameter,
	deleteParameter,
	insertParameterGroup,
	editParameterGroup,
	deleteParameterGroup,
} from "@/api/index.js";

export default {
	name: "parameter",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		dropdownlist: DropDownList,
		VTable,
		Modal,
	},
	computed: {
		selectActivityData() {
			if ("id" in this.emission_value) {
				const activityDataList = [];
				this.$store.state.emissionActivityStore.emissionActivityList.map(
					(v) => {
						if (v.emission_code_no == this.emission_value.id) {
							activityDataList.push({
								id: v.activity_code_no,
								text: v.activity_code_name,
							});
						}
					}
				);
				return activityDataList;
			} else {
				return this.$store.state.codeStore.activityDataCode;
			}
		},
	},
	data: function () {
		return {
			newDataId: "0",
			page: 1,
			limit: 30,
			emission_value: { text: "전체" },
			activity_value: { text: "전체" },
			emission_code_no: null,
			activity_code_no: null,
			dropdownParameterGroup: [{ text: "전체" }],
			parameterColumns: [
				{
					field: "parameter_id",
					title: "파라미터 ID",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "emission_code_name",
					title: "배출활동",
					cell: "dropdownEmissionActivityCell",
					minResizableWidth: 180,
					width: 180,
				},
				{
					field: "activity_code_name",
					title: "활동자료",
					cell: "dropdownActivityDataCell",
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "group_name",
					title: "파라미터그룹",
					cell: "dropdownParameterGroupCell",
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "input_code_name",
					title: "입력구분",
					editable: false,
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "tier_name",
					title: "TIER구분",
					cell: "dropdownParameterCalculationCell",
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "parameter_value",
					title: "파라미터값",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "version",
					title: "버전",
					cell: "dropdownParameterVersionCell",
					minResizableWidth: 90,
					width: 90,
				},
				{
					field: "measure_name",
					cell: "dropdownMeasureCell",
					title: "단위",
					minResizableWidth: 115,
					width: 115,
				},
				{ field: "remarks", title: "비고", minResizableWidth: 150, width: 150 },
				{
					cell: "commandCell",
					minResizableWidth: 170,
					width: 170,
				},
			],
			parameterGroupColumns: [
				{
					field: "group_id",
					title: "그룹 ID",
					editable: this.isEditable,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "group_name",
					title: "그룹명",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "input_code_name",
					title: "입력구분",
					cell: "dropdownParameterGroupInputCell",
					minResizableWidth: 140,
					width: 140,
				},
				{
					field: "create_user",
					title: "등록자",
					editable: false,
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "create_date",
					title: "등록일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 140,
					width: 140,
				},
				{
					field: "update_user",
					title: "수정자",
					editable: false,
					minResizableWidth: 110,
					width: 110,
				},
				{
					field: "update_date",
					title: "수정일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 110,
					width: 110,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			],
			visibleDialog: false,
			searchFormStates: false,
		};
	},
	created: async function () {
		await this.$store.dispatch("codeStore/FETCH_CODE_LIST", "?order=code_name");
		this.$store.dispatch("codeStore/GET_CODE");
		this.$store.dispatch(
			"emissionActivityStore/FETCH_EMISSION_ACTIVITY_LIST",
			2022
		);
		this.$store
			.dispatch("parameterStore/FETCH_PARAMETER_GROUP_LIST")
			.then((data) => {
				for (let idx in data.items) {
					this.dropdownParameterGroup.push({
						id: data.items[idx].group_id,
						text: data.items[idx].group_name,
						input_code_no: data.items[idx].input_code_no,
						input_code_name: data.items[idx].input_code_name,
					});
				}
			})
			.catch((error) => console.log(error));
		this.fetchGridData();
	},
	methods: {
		insert: function (data) {
			return insertParameter(data)
				.then(({ data }) => {
					console.log(data.id);
					this.newDataId = data.id;
					return;
				})
				.catch((error) => console.log(error));
		},
		edit: function (data) {
			editParameter(data);
		},
		remove: function (id) {
			deleteParameter(id);
		},
		dialogInsert: function (data) {
			insertParameterGroup(data)
				.then((data) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		dialogEdit: function (data) {
			editParameterGroup(data);
		},
		dialogRemove: function (id) {
			deleteParameterGroup(id);
		},
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		gridDialog: function () {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.parameterGroupGrid.cancelAll();
			}
		},
		async handleSubmit(dataItem) {
			if ("emission_code_no" in dataItem) {
				if ("id" in dataItem.emission_code_no) {
					this.emission_code_no = dataItem.emission_code_no.id;
				} else {
					this.emission_code_no = null;
				}
			}
			if ("activity_code_no" in dataItem) {
				if ("id" in dataItem.activity_code_no) {
					this.activity_code_no = dataItem.activity_code_no.id;
				} else {
					this.activity_code_no = null;
				}
			}
			this.fetchGridData();
		},
		dropdownEmissionChange(event) {
			this.emission_value = event.value;
			console.log(this.emission_value);
		},
		dropdownActivityChange(event) {
			this.activity_value = event.value;
		},
		clearSearchForm() {
			this.emission_code_no = null;
			this.emission_value = { text: "전체" };
			this.activity_code_no = null;
			this.activity_value = { text: "전체" };
			this.fetchGridData();
		},
		fetchGridData() {
			const searchData = {
				page: this.page,
				limit: this.limit,
			};
			if (this.emission_code_no) {
				searchData.emission_code_no = this.emission_code_no;
			}
			if (this.activity_code_no) {
				searchData.activity_code_no = this.activity_code_no;
			}
			if (this.$refs.parameterGrid) {
				this.$store
					.dispatch("parameterStore/FETCH_PARAMETER_LIST", searchData)
					.then(() => this.$refs.parameterGrid.refreshData())
					.catch((error) => console.log(error));
			} else {
				this.$store.dispatch("parameterStore/FETCH_PARAMETER_LIST", searchData);
			}
		},
		limitChange(limit) {
			this.limit = limit;
			this.fetchGridData();
		},
		pageChange(page) {
			this.page = page;
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		.arrowTopBox {
			padding-left: 200px;
		}
	}
	.longBox {
		width: 400px !important;
	}
	.buttonBoxs {
		width: 200px;
	}
	.searchInputBox {
		position: relative;
		img {
			position: absolute;
			top: 10px;
			right: 15px;
		}
	}
}
</style>
