				},
				{
					field: "basic",
					title: "달성도(%)",
					editable: false,
					minResizableWidth: 150,
					width: 150,
				},

				{
					field: "area",
					title: "온실가스 감축량(tCO2-eq)",
					children: [
						{
							field: "area",
							title: "2019",
							children: [
								{
									field: "month1",
									title: "계획",
									minResizableWidth: 100,
									width: 100,
								},
								{
									field: "month2",
									title: "실적",
									minResizableWidth: 130,
									width: 130,
								},
							],
							editable: false,
							minWidth: 120,
						},
						{
							field: "area",
							title: "2020",
							children: [
								{
									field: "month3",
									title: "계획",
									minResizableWidth: 100,
									width: 100,
								},
								{
									field: "month4",
									title: "실적",
