							field: "month1",
							title: "RE 사용계획(kwh)",
							minResizableWidth: 130,
							width: 130,
						},
						{
							field: "month2",
							title: "RE 사용실적(kwh)",
							minResizableWidth: 130,
							width: 130,
						},
						{
							field: "month3",
							title: "달성도(%)",
							minResizableWidth: 130,
