							field: "month4",
							title: "온실가스 감축량 달성도(tCO2-eq)",
							minResizableWidth: 230,
							width: 230,
						},
					],
					editable: false,
					minWidth: 120,
				},
				{
					field: "area",
					title: "3월",
					children: [
						{
							field: "month1",
							title: "RE 사용계획(kwh)",
							minResizableWidth: 130,
							width: 130,
						},
						{
							field: "month2",
							title: "RE 사용실적(kwh)",
							minResizableWidth: 130,
							width: 130,
						},
						{
							field: "month3",
							title: "달성도(%)",
							minResizableWidth: 130,
							width: 130,
						},
						{
							field: "month4",
							title: "온실가스 감축량 달성도(tCO2-eq)",
							minResizableWidth: 230,
							width: 230,
						},
					],
					minWidth: 120,
					visibleDialog: false,
				},
			];
		},
	},

	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		setPercentage(percentage) {
			return Math.round(this.gridWidth / 100) * percentage;
		},
		toggleDialog() {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.TheTable.cancelAll();
			}
		},
		async handleSubmit(dataItem) {
			const searchData = {
				token: this.token,
			};
			if ("code" in dataItem) {
				searchData.code = dataItem.code;
			}
			if ("group" in dataItem) {
				if ("id" in dataItem.group) {
					searchData.group = dataItem.group.id;
				}
			}
			this.searchData = searchData;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("FETCH_CODE_LIST", this.searchData)
				.then(() => this.$refs.TheTable.refreshData())
				.catch((error) => console.log(error));
		},
		dropdownChange1(event) {
			this.group_value = event.value;
			if (this.group_value == "다년도") {
				this.pageChange = "다년도";
			} else if (this.group_value == "단년도") {
				this.pageChange = "단년도";
			}
		},
		dropdownChange2(event) {
			this.year_value = event.value;
		},
		dropdownChange3(event) {
			this.group_value1 = event.value;
		},
		dropdownChange4(event) {
			this.group_value2 = event.value;
		},
		dropdownChange5(event) {
			this.group_value3 = event.value;
		},
		dropdownChange6(event) {
			this.group_value4 = event.value;
		},
		dropdownChange7(event) {
			this.group_value5 = event.value;
		},
		dropdownChange8(event) {
			this.group_value8 = event.value;
		},
		autoChange(event) {
			this.code_value = event.target.value;
			// this.searchCode = this.filterData(event.target.value);
		},
		filterData(value) {
			const data = this.searchCode;
			const filter = {
				value: value,
				operator: "startswith",
				ignoreCase: true,
			};
			return value ? filterBy(data, filter) : data;
		},
		clearSearchForm() {
			this.searchData = {
				token: this.token,
			};
			this.code_value = "";
			this.code_group_value = {
				text: "전체",
			};
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
