<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준연도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.reductionPlanManagementStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>사업구분</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="typeList"
													:value="typeValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="typeChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>감축인정</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="isAdmitList"
													:value="isAdmitValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="isAdmitChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>감축사업명</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'group'"
											:style="{ maxWidth: '120px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="reductionList"
													:value="reductionValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="reductionChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					title="감축사업 등록"
					gridName="reductionPlanManagement"
					:dataItems="$store.state.reductionPlanManagementStore.dataList"
					:columns="columns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:isNoData="isNoData"
					:disabledState="true"
					:isAllDataEdit="true"
					@allDataSave="allDataSave"
					@allDataCancel="allDataCancel"
				/>
			</div>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

import { editReductionPlanManagement } from "@/api/index.js";

export default {
	name: "ReductionPlanManagement",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			typeList: [
				{ text: "전체" },
				{ id: "I", text: "내부" },
				{ id: "O", text: "외부" },
			],
			typeValue: { text: "전체" },
			isAdmitList: [{ text: "전체" }, { id: "1", text: "감축인정" }],
			isAdmitValue: { text: "전체" },
			reductionList: [{ text: "전체" }],
			reductionValue: { text: "전체" },
			showAlert: false,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "workplace_name",
					title: "사업장",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "type_name",
					title: "구분",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "item",
					title: "감축 사업명",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "is_admit",
					title: "감축 인정",
					minResizableWidth: 100,
					width: 100,
					cell: "checkboxCell",
					editable: false,
				},
				{
					field: "gas_reduction",
					title: "연간 감축량(계획)(tCO-eq)",
					resizable: false,
					minResizableWidth: 200,
					width: 200,
					editable: false,
				},
				{
					field: "perform_plan_date",
					title: "실적발생 예상연월",
					resizable: false,
					minResizableWidth: 200,
					width: 200,
					editable: false,
				},
				{
					field: "year",
					title: "연도",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "area",
					title: "월별 감축량(계획)(tCO-eq)",
					children: [],
				},
			];
			for (var i = 1; i < 13; i++) {
				column[8].children.push({
					field: "m" + i,
					title: i + "월",
					minResizableWidth: 120,
					width: 120,
					cell: "InputGridChangeCell",
				});
			}
			return column;
		},
		yearList() {
			return this.$store.state.reductionPlanManagementStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("reductionPlanManagementStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch(
			"reductionPlanManagementStore/FETCH_ORGANIZATION_YEAR_LIST"
		);
		this.fetchReductionList();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		fetchReductionList() {
			this.$store
				.dispatch("reductionPlanManagementStore/FETCH_REDUCTION_BUSINESS_ITEM")
				.then((data) => {
					this.reductionList = [{ text: "전체" }];
					data.items.map((v) =>
						this.reductionList.push({ id: v.reduction_id, text: v.item })
					);
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			if (this.typeValue.id !== undefined) {
				this.searchData += "&type=" + this.typeValue.id;
			}
			if (this.isAdmitValue.id !== undefined) {
				this.searchData += "&is_admit=" + true;
			}
			if (this.reductionValue.id !== undefined) {
				this.searchData += "&reduction_id=" + this.reductionValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"reductionPlanManagementStore/FETCH_REDUCTION_PLAN_MANAGEMENT",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.DataTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		typeChange(event) {
			this.typeValue = event.value;
		},
		isAdmitChange(event) {
			this.isAdmitValue = event.value;
		},
		reductionChange(event) {
			this.reductionValue = event.value;
		},
		allDataSave: async function (data) {
			let saveData = [];
			for (var idx in data) {
				let dataArry = {};
				dataArry.reduction_id = data[idx].reduction_id;
				if (data[idx].m1 !== "-") {
					dataArry.m1 = parseInt(data[idx].m1);
				}
				if (data[idx].m2 !== "-") {
					dataArry.m2 = parseInt(data[idx].m2);
				}
				if (data[idx].m3 !== "-") {
					dataArry.m3 = parseInt(data[idx].m3);
				}
				if (data[idx].m4 !== "-") {
					dataArry.m4 = parseInt(data[idx].m4);
				}
				if (data[idx].m5 !== "-") {
					dataArry.m5 = parseInt(data[idx].m5);
				}
				if (data[idx].m6 !== "-") {
					dataArry.m6 = parseInt(data[idx].m6);
				}
				if (data[idx].m7 !== "-") {
					dataArry.m7 = parseInt(data[idx].m7);
				}
				if (data[idx].m8 !== "-") {
					dataArry.m8 = parseInt(data[idx].m8);
				}
				if (data[idx].m9 !== "-") {
					dataArry.m9 = parseInt(data[idx].m9);
				}
				if (data[idx].m10 !== "-") {
					dataArry.m10 = parseInt(data[idx].m10);
				}
				if (data[idx].m11 !== "-") {
					dataArry.m11 = parseInt(data[idx].m11);
				}
				if (data[idx].m12 !== "-") {
					dataArry.m12 = parseInt(data[idx].m12);
				}
				saveData.push(dataArry);
			}
			editReductionPlanManagement(saveData).then(() => {
				this.showAlert = !this.showAlert;
				this.fetchGridData();
			});
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.typeValue = { text: "전체" };
			this.isAdmitValue = { text: "전체" };
			this.reductionValue = { text: "전체" };
			this.$store.dispatch(
				"reductionPlanManagementStore/FETCH_ORGANIZATION_YEAR_LIST"
			);
			this.fetchOrgList();
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
