				"티디아이",
				"울산1사업장",
				"울산2사업장",
				"울산3사업장",
				"중앙연구소",
				"소공 한화빌딩",
				"판교연구소",
				"가평 현등연구소",
				"폴리실리콘",
			],
			group_value2: "사업장 전체",
			group_value3: "생산팀 전체",
			group3: [
				"PE생산1팀",
				"PE생산2팀",
				"PE생산3팀",
				"W&C생산팀",
				"CA생산1팀",
				"CA생산2팀",
				"VCM생산1팀",
				"VCM생산2팀",
				"ECH생산팀",
				"PVC생산팀",
				"OA생산팀",
				"공통(여수)",
				"석유수지생산팀",
				"TDI생산팀",
				"PE생산팀",
				"VCM생산팀",
				"공통(울산1)",
				"PVC생산1팀",
				"PVC생산2팀",
				"공통(울산2)",
				"가소제생산팀",
				"중앙연구소",
				"한화빌딩",
				"판교연구소",
				"현등연구소",
				"폴리실리콘",
			],
			group4: ["가동 전", "가동 후"],
			group_value4: ["전체"],
			token: this.$cookie.getCookie("accesstoken"),
			searchicon,
			searchData: null,
			visibleDialog: false,
			searchFormStates: false,
		};
	},

	computed: {
		code_columns() {
			return [
				{
					field: "id",
					title: "사업장",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "teamName",
					title: "생산팀",
					minResizableWidth: 100,
					width: 100,
				},

				{
