					title: "감축아이템",
					resizable: false,
					className: "text-center",
					minResizableWidth: 400,
					width: 400,
				},
				{
					field: "month4",
					title: "구분",
					resizable: false,
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "month4",
					title: "월별 절감량 합계(tCO-eq)",
					resizable: false,
					className: "text-center",
					minResizableWidth: 220,
					width: 220,
				},
				{
					field: "area",
					title: "월별 절감량 (tCO-eq)",
					children: [
						{
							field: "months",
							title: "1월",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "month2",
							title: "2월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "3월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "4월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "5월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "6월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "7월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "8월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "9월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "10월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "11월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
						{
							field: "month2",
							title: "12월",
							minResizableWidth: 120,
							width: 120,
							resizable: false,
							className: "text-center",
						},
					],
				},
				{
					field: "files",
					title: "근거자료",
					cell: "FileFormCell",
					minResizableWidth: 200,
					width: 200,
				},
			];
		},
		code_columns2() {
			return [
				{
					field: "id",
					title: "사업장",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "teamName",
					title: "생산팀",
					minResizableWidth: 100,
					width: 100,
				},

				{
					field: "month3",
					title: "감축아이템",
					minResizableWidth: 400,
					width: 400,
				},
				{
					field: "month4",
					title: "구분",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "month4",
					title: "월별 절감량 합계(백만원)",
					minResizableWidth: 220,
					width: 220,
				},
				{
					field: "area",
					title: "월별 절감량 (백만원)",
					children: [
						{
							field: "months",
							title: "1/4분기",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "month2",
							title: "2/4분기",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "month2",
							title: "3/4분기",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "month2",
							title: "4/4분기",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "month2",
							title: "비고",
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "files",
							title: "근거자료",
							cell: "FileFormCell",
							minResizableWidth: 200,
							width: 200,
						},
					],
				},
			];
		},
	},

	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		setPercentage(percentage) {
			return Math.round(this.gridWidth / 100) * percentage;
		},
		toggleDialog() {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.TheTable.cancelAll();
			}
		},
		async handleSubmit(dataItem) {
			const searchData = {
				token: this.token,
			};
			if ("code" in dataItem) {
				searchData.code = dataItem.code;
			}
			if ("group" in dataItem) {
				if ("id" in dataItem.group) {
					searchData.group = dataItem.group.id;
				}
			}
			this.searchData = searchData;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("FETCH_CODE_LIST", this.searchData)
				.then(() => this.$refs.TheTable.refreshData())
				.catch((error) => console.log(error));
		},
		dropdownChange1(event) {
			this.day_value = event.value;
		},
		dropdownChange2(event) {
			this.year_value = event.value;
		},
		dropdownChange3(event) {
			this.group_group3 = event.value;
		},
		dropdownChange4(event) {
			this.group_value2 = event.value;
		},
		dropdownChange5(event) {
			this.group_value3 = event.value;
		},
		autoChange(event) {
			this.code_value = event.target.value;
			// this.searchCode = this.filterData(event.target.value);
		},
		filterData(value) {
			const data = this.searchCode;
			const filter = {
				value: value,
				operator: "startswith",
				ignoreCase: true,
			};
			return value ? filterBy(data, filter) : data;
		},
		clearSearchForm() {
			this.searchData = {
				token: this.token,
			};
			this.code_value = "";
			this.code_group_value = {
				text: "전체",
			};
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
