<template>
	<form-element>
		<!-- kendo form box-->
		<fieldset class="k-form-fieldset">
			<div class="grid-code form">
				<div class="forminfo d-flex justify-content-between">
					<!-- kendo form title-->
					<span>법인 정보</span>
					<!-- kendo form button box-->
					<div class="formButtonBox k-form-buttons ml-auto2">
						<basic-button
							class="inquiry"
							@click="addData"
							:type="'button'"
							title="부문 추가"
							:backgroundColor="'#FFFFFF'"
							:fontColor="'#F37321'"
							:borderColor="'#F37321'"
							:wsize="'120px'"
							>부문 추가</basic-button
						>
						<basic-button
							class="resets"
							@click="clear"
							:type="'button'"
							title="취소"
							:backgroundColor="'#F9F9F9'"
							:fontColor="'#5A5B68'"
							:borderColor="'#5A5B68'"
							:wsize="'90px'"
							>취소</basic-button
						>
						<kbutton
							class="submitbutton"
							:class="{ activesd: !kendoForm.allowSubmit }"
							style="cursor: pointer"
							type="submit"
							:disabled="!kendoForm.allowSubmit"
							>저장</kbutton
						>
						<basic-button
							class="paraGroup"
							@click="copyData"
							:type="'button'"
							title="전년도데이터복사"
							:backgroundColor="'#49A872'"
							:fontColor="'#FFFFFF'"
							:borderColor="''"
							:wsize="'130px'"
							>전년도 복사</basic-button
						>
					</div>
				</div>
				<!-- kendo form field 명칭 setting-->
				<div class="inputsBox">
					<div class="d-flex">
						<div class="content con1">
							<!-- kendo form field 법인명 setting-->
							<field
								:id="'corporate_name'"
								:name="'corporate_name'"
								:component="'myTemplate'"
								:disabled="true"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'법인명'"
									/>
								</template>
							</field>
							<!-- kendo form field 업종 setting-->
							<field
								:id="'industry'"
								:name="'industry'"
								:component="'myTemplate'"
								:disabled="true"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'업종(대표업종)'"
									/>
								</template>
							</field>
							<!-- kendo form field 소재지 setting-->
							<field
								:id="'address'"
								:name="'address'"
								:component="'myTemplate'"
								:validator="addressValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'소재지'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당부서 setting-->
							<field
								id="'department'"
								:name="'department'"
								:component="'myTemplate'"
								:validator="onerValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당부서'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 직급 setting-->
							<field
								:id="'position'"
								:name="'position'"
								:component="'myTemplate'"
								:validator="positionValueValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 직급'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 휴대폰 setting-->
							<field
								:id="'manager_mobile'"
								:name="'manager_mobile'"
								:component="'myTemplate'"
								:validator="mobileValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 휴대폰'"
										:mask="'000-0000-0000'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 주요 생산제품 또는 처리물질 setting-->
							<field
								:id="'product'"
								:name="'product'"
								:component="'myTemplate'"
								:validator="productValueValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'주요 생산제품 또는 처리물질'"
									/>
								</template>
							</field>
							<!-- kendo form field 종업원수 setting-->
							<field
								:id="'employees'"
								:name="'employees'"
								:component="'myTemplate'"
								:validator="employeesValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'상시 종업원수'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 당해년도 에너지 비용 setting-->
							<field
								:id="'energy_cost'"
								:name="'energy_cost'"
								:component="'myTemplate'"
								:validator="energyCostValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'당해년도 에너지 비용(백만원)'"
										class="telbox"
									/>
								</template>
							</field>
						</div>

						<div class="content con2">
							<!-- kendo form field 법인등록번호 setting-->
							<field
								:id="'corporate_no'"
								:name="'corporate_no'"
								:component="'myTemplate'"
								:disabled="true"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'법인등록번호'"
									/>
								</template>
							</field>
							<!-- kendo form field 대표자 setting-->
							<field
								:id="'representation'"
								:name="'representation'"
								:component="'myTemplate'"
								:validator="representationValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'대표자'"
									/>
								</template>
							</field>

							<!-- kendo form field 전화번호 setting-->
							<field
								:id="'phone'"
								:name="'phone'"
								:component="'myTemplate'"
								:validator="phoneValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:mask="'000-000-0000'"
										:title="'전화번호'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 setting-->
							<field
								:id="'manager'"
								:name="'manager'"
								:component="'myTemplate'"
								:validator="managerValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 전화번호 setting-->
							<field
								:id="'manager_phone'"
								:name="'manager_phone'"
								:component="'myTemplate'"
								:validator="phoneValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 전화번호'"
										:mask="'000-000-0000'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 이메일 setting-->
							<field
								:id="'manager_email'"
								:name="'manager_email'"
								:type="'email'"
								:component="'myTemplate'"
								:validator="emailValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 이메일'"
									/>
								</template>
							</field>
							<!-- kendo form field 처리량 setting-->
							<field
								:id="'throughput'"
								:name="'throughput'"
								:component="'myTemplate'"
								:validator="throughputValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'연간 생산량 또는 처리량(MT)'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 당해년도 매출액(백만원) setting'-->
							<field
								:id="'sales_amount'"
								:name="'sales_amount'"
								:component="'myTemplate'"
								:validator="totalPayValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'당해년도 매출액(백만원)'"
										class="telbox"
									/>
								</template>
							</field>
							<!-- kendo form field 자본금 setting'-->
							<field
								:id="'capital'"
								:name="'capital'"
								:component="'myTemplate'"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'자본금(백만원)'"
										class="telbox"
										:must="'N'"
									/>
								</template>
							</field>
						</div>
					</div>
					<!-- kendo form 필수항목 info'-->
					<div class="lastText">
						<p><span style="color: #f37321">*</span> 는 필수 항목입니다.</p>
					</div>
				</div>
			</div>
		</fieldset>
	</form-element>
	<modal
		v-if="visibleDialog"
		:message="'전년도의 데이터가 당해 연도로 복사됩니다. 변경하시겠습니까?'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<dialog-actions-bar>
				<kbutton @click="messageDialog">취소</kbutton>
				<kbutton @click="insertCopyData">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import { Field, FormElement } from "@progress/kendo-vue-form";
import { Button } from "@progress/kendo-vue-buttons";
import BasicButton from "../../components/common/button/BasicButton.vue";
import InputCell from "../../components/form/InputCell.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import NumericTextBoxCell from "../../components/form/NumericTextBoxCell.vue";

// 유효성 기능을 명시
import {
	addressValidator,
	onerValidator,
	positionValueValidator,
	mobileValidator,
	productValueValidator,
	employeesValidator,
	energyCostValidator,
	representationValidator,
	phoneValidator,
	managerValidator,
	emailValidator,
	throughputValidator,
	totalPayValidator,
} from "../../validator/Validator";

export default {
	name: "Corporation",
	components: {
		"form-element": FormElement,
		kbutton: Button,
		field: Field,
		BasicButton,
		InputCell,
		Modal,
		NumericTextBoxCell,
		DialogActionsBar,
	},
	data() {
		return {
			addressValidator,
			onerValidator,
			positionValueValidator,
			mobileValidator,
			productValueValidator,
			employeesValidator,
			energyCostValidator,
			representationValidator,
			phoneValidator,
			managerValidator,
			emailValidator,
			throughputValidator,
			totalPayValidator,
			visibleDialog: false,
		};
	},
	inject: {
		kendoForm: {
			default: {},
		},
	},
	computed: {
		org_id() {
			return this.$store.state.organizationStore.org_id;
		},
		depth() {
			return this.$store.state.organizationStore.depth;
		},
	},
	methods: {
		/* 취소 */
		clear() {
			// 폼초기화 함수
			this.kendoForm.onFormReset();
		},
		/* 부문 추가 */
		addData() {
			console.log("[addData]");
			this.$store.state.organizationStore.depth = this.depth + 1;
			this.$store.state.organizationStore.org_parent_id = this.org_id;
			this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_INIT");
		},
		/* 전년도 복사 */
		copyData() {
			if (this.$store.state.organizationStore.organizationList.length !== 0) {
				this.messageDialog();
			} else {
				this.insertCopyData();
			}
		},
		messageDialog() {
			this.visibleDialog = !this.visibleDialog;
		},
		insertCopyData() {
			this.$store
				.dispatch("organizationStore/COPY_ORGANIZATION")
				.then((data) => {
					console.log("[INSERT COPY DATA then ==>]");
					console.log(data);
					this.$store.dispatch("organizationStore/SETTING_DEPTH_INIT");
					this.$store.dispatch("organizationStore/SETTING_YEAR_LIST_INIT");
					this.$store.dispatch("organizationStore/SETTING_ORG_PARENT_INIT");
					this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_INIT");
					this.$store.dispatch(
						"organizationStore/FETCH_ORGANIZATION_YEAR_LIST"
					);
				})
				.catch((error) => console.log(error));
			this.messageDialog();
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.grid-code {
	&.form {
		padding: 20px;

		.k-input-inner {
			&::placeholder {
				color: #f37321 !important;
			}
		}

		.forminfo {
			height: 40px;

			span {
				font-family: AppleSDGothicNeoB00;
				font-size: 16px;
				color: #222222;
				display: flex;
				align-items: end;
			}

			.formButtonBox {
				button {
					margin-right: 0px;

					&:last-child {
						margin-right: 0px;
					}
				}
			}
		}

		.k-form-field {
			margin-top: 20px !important;

			.k-form-field-wrap {
				position: relative;

				.k-input {
					box-shadow: none !important;
				}

				.k-form-error {
					position: absolute;
					top: 0;
				}
			}
		}
	}

	.lastText {
		margin-top: 20px;
		margin-left: auto;
		font-size: 14px;
		font-family: AppleSDGothicNeoSB00;
		width: max-content;
	}

	.content {
		flex: 1;
	}
}
</style>
