import {
	fetchOrganizationYearList,
	fetchScopeList,
	insertScope,
	deleteScope,
	deleteScopeFile,
	fetchCodeList,
} from "@/api/index.js";

const scope3Store = {
	namespaced: true,
	state: {
		scopeList: [],
		scopeCount: 0,
		categoryList: [],
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_SCOPE_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.scopeList = data.items;
			state.scopeCount = data.count;
		},
		SET_CATEGORY_CODE_LIST: (state, data) => {
			state.categoryList = [];
			data.items.map((v) =>
				state.categoryList.push({ id: v.code_no, text: v.code_name })
			);
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_SCOPE_LIST: ({ commit }, info) => {
			// Scope3 실적 목록 조회
			return fetchScopeList(info)
				.then(({ data }) => {
					commit("SET_SCOPE_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_SCOPE: ({ commit }, info) => {
			// Scope3 추가 및 수정
			return insertScope(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_SCOPE: ({ commit }, id) => {
			// Scope3 삭제
			return deleteScope(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_SCOPE_FILE: ({ commit }, id) => {
			// Scope3의 첨부파일 삭제
			return deleteScopeFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_CATEGORY_CODE_LIST: ({ commit }, info) => {
			// 카테고리 코드 목록 조회
			return fetchCodeList(info)
				.then(({ data }) => {
					commit("SET_CATEGORY_CODE_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default scope3Store;
