<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획기간</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="periodList"
													:value="periodValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="periodChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code new2">
				<v-table
					ref="CarbonNeutralGrid"
					title="계획기간 배출목표 관리"
					gridName="emissionGoalYear"
					:rowspanNum="6"
					:rowspanNum2="3"
					:dataItems="$store.state.emissionGoalYearStore.emissionGoalList"
					:count="$store.state.emissionGoalYearStore.emissionGoalCount"
					:columns="columns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:isNoData="isNoData"
				/>
				<!-- <the-table
					v-if="this.inqryEmtrp.length !== 0"
					ref="TheTable"
					title="계획기간 배출목표 관리"
					:popupButtonText="'엑셀 다운'"
					:type="'anotherType'"
					:rowspanNum="6"
					:rowspanNum2="'typesd'"
					:dataItems="this.inqryEmtrp"
					:dropdownData="this.$store.state.dropdownCodeGroup"
					:columns="code_columns"
					actionName="CODE"
					:removeCheck="true"
				/> -->
			</div>
			<div class="grid-code new3">
				<v-table
					ref="EvidenceGrid"
					title="근거자료 등록"
					gridName="evidence"
					:dataItems="$store.state.activityManagementStore.evidenceList"
					:columns="evidenceColumns"
					:newDataId="newDataId"
					:deleteDataCheck="true"
					insertButtonText="추가"
					:isNoData="isEvidenceNoData"
					@insert="insertEvidence"
					@edit="editEvidence"
					@remove="removeEvidence"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "emissionGoalYear",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			isEvidenceNoData: false,
			year: "",
			orgList: [],
			periodList: [{ id: "01", text: "3차" }],
			periodValue: { id: "01", text: "3차" },
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			attchFile: null,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "workplace_name",
					title: "사업장",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "co2_eq_name",
					title: "합계",
					minResizableWidth: 120,
					width: 120,
					editable: false,
				},
				{
					field: "area",
					title: "계획기간 배출목표(tCO2-eq)",
					children: [
						{
							field: "co2_eq_2021",
							title: "2021",
							minResizableWidth: 120,
							width: 120,
							cell: this.cellWithBackGround,
						},
						{
							field: "co2_eq_2022",
							title: "2022",
							editable: false,
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "co2_eq_2023",
							title: "2023",
							editable: false,
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "co2_eq_2024",
							title: "2024",
							editable: false,
							minResizableWidth: 120,
							width: 120,
						},
						{
							field: "co2_eq_2025",
							title: "2025",
							editable: false,
							minResizableWidth: 120,
							width: 120,
						},
					],
				},
			];
		},
		evidenceColumns() {
			return [
				{
					field: "no",
					title: "No",
					editable: false,
					minResizableWidth: 55,
					width: 55,
				},
				{
					field: "description",
					title: "작성근거",
					minResizableWidth: 800,
					width: 800,
				},
				{
					field: "file_url",
					title: "사용량근거자료",
					cell: "FileFormCell",
					minResizableWidth: 190,
					width: 190,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
	},
	watch: {
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("activityManagementStore/SETTING_EVIDENCE_INIT");
		this.year = new Date().getFullYear();
		this.$nextTick(function () {
			this.fetchEvidence();
		});
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?period=" + this.periodValue.id;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"emissionGoalYearStore/FETCH_EMISSION_GOAL_YEAR",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.CarbonNeutralGrid !== null) {
						if (data.items.length < 1) {
							this.isNoData = true;
						}
						this.$refs.CarbonNeutralGrid.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		/* 근거자료 목록 */
		fetchEvidence() {
			var searchEvidenceData = "?route_name=emission_goal_year";
			this.$store
				.dispatch(
					"activityManagementStore/FETCH_EVIDENCE_LIST",
					searchEvidenceData
				)
				.then((data) => {
					if (data.count < 1) {
						this.isEvidenceNoData = true;
					}
					this.$refs.EvidenceGrid.refreshData();
				})
				.catch((error) => console.log(error));
		},
		periodChange(e) {
			this.periodValue = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.year = new Date().getFullYear();
		},
		insertEvidence: function (data) {
			const formData = new FormData();
			formData.append("route_name", "emission_goal_year");
			formData.append("description", data.data.description);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("activityManagementStore/INSERT_EVIDENCE", formData)
				.then(() => {
					this.fetchEvidence();
				});
		},
		editEvidence: function (data) {
			const formData = new FormData();
			formData.append("route_name", "emission_goal_year");
			formData.append("evidence_id", data.data.evidence_id);
			formData.append("description", data.data.description);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("activityManagementStore/INSERT_EVIDENCE", formData)
				.then(() => {
					this.fetchEvidence();
				});
		},
		removeEvidence: function (id) {
			this.$store.dispatch("activityManagementStore/DELETE_EVIDENCE", id);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"activityManagementStore/DELETE_EVIDENCE_FILE",
				e.evidence_id
			);
			this.fetchEvidence();
		},
		cellWithBackGround: function (h, tdElement, props, listeners) {
			console.log(tdElement, listeners);
			const product = props.dataItem.month1;
			if (product < 400) {
				return h(
					"td",
					{
						style: { color: "blue" },
					},
					[product]
				);
			}
			return h(
				"td",
				{
					style: { color: "red" },
				},
				[product]
				// [product, h("span", {}, " Maximum exceeded")]
			);
		},
	},
	mixins: [settingGrid()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
