<template>
	<td v-if="rowType === 'groupFooter'">
		{{ computedAggregates }}
	</td>
	<td v-else :class="className">
		{{ getNestedValue(field, dataItem) }}
	</td>
</template>

<script>
function nestedValue(fieldName, dataItem) {
	const path = fieldName.split(".");
	let data = dataItem;
	path.forEach((p) => {
		data = data ? data[p] : undefined;
	});

	return data;
}

export default {
	props: {
		field: String,
		dataItem: Object,
		rowType: String,
	},
	computed: {
		computedAggregates() {
			if ("aggregates" in this.dataItem) {
				if (this.field === "total") {
					return this.dataItem.aggregates.total.sum;
				} else if (this.field === "stationary_combustion") {
					return this.dataItem.aggregates.stationary_combustion.sum;
				} else if (this.field === "electricity") {
					return this.dataItem.aggregates.electricity.sum;
				} else if (this.field === "steam") {
					return this.dataItem.aggregates.steam.sum;
				} else if (this.field === "mobile_combustion") {
					return this.dataItem.aggregates.mobile_combustion.sum;
				} else if (this.field === "co2eq") {
					return this.dataItem.aggregates.co2eq.sum;
				} else if (this.field === "tj") {
					return this.dataItem.aggregates.tj.sum;
				} else if (this.field === "tj_period") {
					return this.dataItem.aggregates.tj_period.sum;
				} else if (this.field === "co2_eq") {
					return this.dataItem.aggregates.co2_eq.sum;
				} else if (this.field === "co2_eq_period") {
					return this.dataItem.aggregates.co2_eq_period.sum;
				} else if (this.field === "mt") {
					return this.dataItem.aggregates.mt.sum;
				} else if (this.field === "tj_basic_unit") {
					return this.dataItem.aggregates.tj_basic_unit.sum;
				} else if (this.field === "re_plan") {
					return this.dataItem.aggregates.re_plan.sum;
				} else if (this.field === "re_result") {
					return this.dataItem.aggregates.re_result.sum;
				} else if (this.field === "re_amount") {
					return this.dataItem.aggregates.re_amount.sum;
				} else if (
					this.field === "production_team_name" ||
					this.field === "business_part_name" ||
					this.field === "emission_activity"
				) {
					return "소계";
				}
			}
			return "";
		},
	},
	methods: {
		getNestedValue: nestedValue,
	},
};
</script>
