import { fetchParameterList, fetchParameterGroupList } from "@/api/index.js";

const parameterStore = {
	namespaced: true,
	state: {
		parameterList: [],
		parameterCount: 0,
		parameterGroupList: [],
		parameterGroupCount: 0,
		selectParameterGroup: [],
	},
	mutations: {
		SET_PARAMETER_LIST: (state, parameter) => {
			state.parameterList = parameter.items;
			state.parameterCount = parameter.count;
		},
		SET_PARAMETER_GROUP_LIST: (state, parameterGroup) => {
			state.parameterGroupList = parameterGroup.items;
			state.parameterGroupCount = parameterGroup.count;
			state.parameterGroupList.map((v) =>
				state.selectParameterGroup.push({
					id: v.group_id,
					text: v.group_name,
					input_code_no: v.input_code_no,
					input_code_name: v.input_code_name,
				})
			);
		},
	},
	actions: {
		FETCH_PARAMETER_LIST: ({ commit }, info) => {
			// 파라미터 목록 조회
			return fetchParameterList(info)
				.then(({ data }) => {
					commit("SET_PARAMETER_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_PARAMETER_GROUP_LIST: ({ commit }) => {
			// 파라미터 그룹 목록 조회
			return fetchParameterGroupList()
				.then(({ data }) => {
					commit("SET_PARAMETER_GROUP_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
	},
};

export default parameterStore;
