<template>
	<fieldwrapper style="position: relative">
		<klabel
			v-if="label"
			:editor-id="id"
			:editor-valid="valid"
			:disabled="disabled"
			:optional="optional"
			>{{ label }}</klabel
		>
		<img
			:src="name == 'username' ? password : ids"
			class="loginicons"
			v-if="loginibnput"
		/>
		<kinput
			:valid="valid"
			:id="id"
			:value="value"
			:disabled="disabled"
			:placeholder="placeholder"
			:type="type"
			:class="{ logininputstyle: loginibnput }"
			:style="{
				height: loginibnput ? '60px !important' : '',
			}"
			@input="handleChange"
			@blur="handleBlur"
			@focus="handleFocus"
		/>
		<error v-if="showValidationMessage">
			{{ validationMessage }}
		</error>
		<hint v-else>{{ hint }}</hint>
	</fieldwrapper>
</template>

<script>
import { FieldWrapper } from "@progress/kendo-vue-form";
import { Error, Hint, Label } from "@progress/kendo-vue-labels";
import { Input } from "@progress/kendo-vue-inputs";
import sideBarMainLogo from "@/assets/img/login/loginLogo.png";
import ids from "@/assets/img/login/id.png";
import password from "@/assets/img/login/password.png";
export default {
	name: "FormInput",
	props: {
		name: String,
		optional: Boolean,
		disabled: Boolean,
		placeholder: String,
		type: String,
		touched: Boolean,
		label: String,
		validationMessage: String,
		hint: String,
		id: String,
		valid: Boolean,
		loginibnput: Boolean,
		value: {
			type: String,
			default: "",
		},
	},
	data() {
		return {
			ids,
			password,
			sideBarMainLogo,
		};
	},
	components: {
		fieldwrapper: FieldWrapper,
		error: Error,
		hint: Hint,
		klabel: Label,
		kinput: Input,
	},
	computed: {
		showValidationMessage() {
			return this.$props.touched && this.$props.validationMessage;
		},
		showHint() {
			return !this.showValidationMessage && this.$props.hint;
		},
		hintId() {
			return this.showHint ? `${this.$props.id}_hint` : "";
		},
	},
	emits: {
		change: null,
		blur: null,
		focus: null,
	},
	methods: {
		handleChange(e) {
			this.$emit("change", e);
		},
		handleBlur(e) {
			this.$emit("blur", e);
		},
		handleFocus(e) {
			this.$emit("focus", e);
		},
	},
};
</script>
<style lang="scss">
.loginicons {
	position: absolute;
	top: 50%;
	left: 10px;
	transform: translateY(-50%);
	z-index: 99;
}

.logininputstyle {
	max-width: 400px;
	width: 100%;
	height: 40px !important;
	background-color: #ffffff 0% 0% no-repeat padding-box;
	box-shadow: 5px 5px 10px #0000001a;
	border: 1px solid #f9f9f9 !important;
	border-radius: 30px !important;

	input {
		width: 100%;
		height: 100%;
		padding-left: 35px !important;
		font-size: 18px;
		line-height: 18px;
		color: #b2b5c5;
	}
}
</style>
