<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준 년도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="$store.state.scope3Store.yearList"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="ScopeGrid"
					title="Scope3 실적 관리"
					gridName="scope"
					:dataItems="$store.state.scope3Store.scopeList"
					:columns="scopeColumns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					:selectWorkplace="workplaceDropDownList"
					:selectCategory="$store.state.scope3Store.categoryList"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="scopeColumns"
					:deleteDataCheck="true"
					insertButtonText="추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "Scoped3",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			newDataId: "0",
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [],
			productionTeamValue: {},
			workplaceDropDownList: [],
			attchFile: null,
			searchFormStates: false,
		};
	},
	computed: {
		scopeColumns() {
			return [
				{
					field: "no",
					title: "No",
					minResizableWidth: 55,
					width: 55,
					editable: false,
				},
				{
					field: "workplace_name",
					title: "사업장",
					cell: "dropdownWorkplaceCell",
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "category_name",
					title: "Category",
					cell: "dropdownCategoryCell",
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "emission_activity",
					title: "배출활동",
					minResizableWidth: 190,
					width: 190,
					editable: true,
				},
				{
					field: "measure_name",
					title: "단위",
					editable: false,
					minResizableWidth: 60,
					width: 60,
				},
				{
					field: "co2eq",
					title: "온실가스 배출량(tCO2eq)",
					minResizableWidth: 190,
					width: 190,
					editable: true,
				},
				{
					field: "remarks",
					title: "비고",
					minResizableWidth: 140,
					width: 140,
					editable: true,
				},
				{
					field: "file_url",
					title: "근거자료",
					cell: "FileFormCell",
					minResizableWidth: 210,
					width: 210,
					editable: false,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
		yearList() {
			return this.$store.state.scope3Store.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("scope3Store/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("scope3Store/FETCH_ORGANIZATION_YEAR_LIST");
		this.$store.dispatch("scope3Store/FETCH_CATEGORY_CODE_LIST", "?group=12");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceDropDownList = [];
						data.items.map((v) =>
							this.workplaceDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.workplaceValue = { text: "전체 사업장" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("scope3Store/FETCH_SCOPE_LIST", this.searchData)
				.then((data) => {
					if (this.$refs.ScopeGrid !== null) {
						var totalCo2 = 0;
						if (data.items.length < 1) {
							this.isNoData = true;
						} else {
							for (var idx in data.items) {
								totalCo2 += data.items[idx].co2eq;
							}
							data.items.push({
								id: 0,
								emission_activity: "합계",
								co2eq: totalCo2,
							});
						}
						this.$refs.ScopeGrid.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			this.workplaceDropDownList = [];
			this.workplaceDropDownList.push(event.value);
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("scope3Store/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch("scope3Store/FETCH_ORGANIZATION_YEAR_LIST");
			this.fetchOrgList();
			// this.year = this.yearList[0];
			// this.workplaceValue = { text: "전체 사업장" };
			// this.handleSubmit();
		},
		insert: function (data) {
			const formData = new FormData();
			formData.append("org_id", data.data.workplace_id);
			formData.append("category_no", data.data.category_no);
			formData.append("emission_activity", data.data.emission_activity);
			formData.append("co2eq", data.data.co2eq);
			formData.append("remarks", data.data.remarks);
			formData.append("file", this.attchFile);
			this.$store.dispatch("scope3Store/INSERT_SCOPE", formData).then(() => {
				// console.log(data.id);
				// this.newDataId = data.id;
				this.fetchGridData();
			});
		},
		edit: function (data) {
			const formData = new FormData();
			formData.append("scope3_performance_id", data.data.id);
			formData.append("org_id", data.data.workplace_id);
			formData.append("category_no", data.data.category_no);
			formData.append("emission_activity", data.data.emission_activity);
			formData.append("co2eq", data.data.co2eq);
			formData.append("remarks", data.data.remarks);
			formData.append("file", this.attchFile);
			this.$store.dispatch("scope3Store/INSERT_SCOPE", formData).then(() => {
				this.fetchGridData();
			});
		},
		remove: function (id) {
			this.$store.dispatch("scope3Store/DELETE_SCOPE", id);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"scope3Store/DELETE_SCOPE_FILE",
				e.scope_perform_id
			);
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
