<template>
	<nav class="nav-bar">
		<kbutton
			:class="'menu-btn'"
			:icon="'menu'"
			:fill-mode="'flat'"
			@click="handleNavEvent"
		/>
		<div class="logoBox"><img :src="mainLogo" alt="" /></div>
		<div class="d-flex">
			<div class="logBox">
				<p>
					<span class="textSmall">Tech그룹 </span
					><span class="textBold">홍길동</span> 님
				</p>
			</div>
			<div class="profile rounded-circle">
				<img :src="imgProfile" alt="Profile" />
			</div>
			<div class="logBox">
				<span class="log">로그아웃</span>
			</div>
		</div>
	</nav>
</template>

<script>
import resize from "../../../mixin/resize.js";

import { Button } from "@progress/kendo-vue-buttons";
import imgProfile from "@/assets/img/icon/useDefault.png";
import mainLogo from "@/assets/img/main/mainLogo.png";

export default {
	name: "Navbar",
	components: {
		kbutton: Button,
	},
	data() {
		return {
			imgProfile,
			mainLogo,
		};
	},

	methods: {
		async handleNavEvent() {
			const sidebar = document.getElementById("sidebar");
			const content = document.getElementById("content");

			sidebar.classList.toggle("active");
			content.classList.toggle("active");

			setTimeout(() => {
				this.handleResize();
				window.addEventListener("resize", function (event) {
					// 이벤트 강제 실행
					event;
					let newwidth = this.window.innerWidth; // eslint-disable-line no-unused-vars
					let newheight = window.innerHeight; // eslint-disable-line no-unused-vars
				});
				let resizeEvent = window.document.createEvent("UIEvents");
				resizeEvent.initUIEvent("resize", true, false, window, 0);
				window.dispatchEvent(resizeEvent);
			}, 400);
		},
	},

	mixins: [resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
.nav-bar {
	border-bottom: none;
	.logoBox {
		margin-right: auto;
		display: inline-block;
		width: 100px;
		height: 29px;
		margin-left: 23px;
		img {
			width: 100%;
			height: 100%;
		}
	}
	.profile {
		width: 30px !important;
		height: 30px !important;
		box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
		border-radius: 50%;
	}
	.logBox {
		display: flex;
		justify-content: center;
		align-items: center;
		.log {
			cursor: pointer;
			padding: 7px 16px;
			background: #ffffff;
			border-radius: 10px;
			font: normal normal normal 13px/20px AppleSDGothicNeoSB00;
			box-shadow: 3px 3px 5px rgb(0 0 0 / 5%);
		}
		p {
			font: normal normal normal 14px/30px AppleSDGothicNeoM00;
			margin-right: 6px;
			.textSmall {
				font: normal normal normal 13px/30px AppleSDGothicNeoM00;
				color: #616161;
			}
			.textBold {
				color: #33343c;
				font: normal normal normal 16px/30px AppleSDGothicNeoB00;
			}
		}
	}
}
</style>
