<template>
	<div class="signWrap">
		<div class="signcontent">
			<div class="form-content">
				<div class="logoBox">
					<img :src="sideBarMainLogo" alt="" />
				</div>
				<h4>Login</h4>
				<k-form @submit="handleSubmit">
					<formcontent />
				</k-form>
				<img :src="loginFooter" alt="" class="loginFooter" />
			</div>
		</div>
	</div>
</template>

<script>
import { Form } from "@progress/kendo-vue-form";
import FormContent from "../components/form/FormContent.vue";
import sideBarMainLogo from "@/assets/img/login/loginLogo.png";
import loginFooter from "@/assets/img/login/loginFooter.png";

export default {
	name: "SignIn",
	components: {
		"k-form": Form,
		formcontent: FormContent,
	},
	data() {
		return {
			loginFooter,
			sideBarMainLogo,
		};
	},
	beforeMount() {
		this.$store.state.showNavbar = false;
		this.$store.state.showSidebar = false;
		this.$store.state.showTabs = false;
		this.$store.state.showSubNavbar = false;
	},
	methods: {
		async handleSubmit(dataItem) {
			this.$store
				.dispatch("ACCOUNT_SIGNIN", dataItem)
				.then((data) => {
					console.log(data);
					this.$cookie.setCookie("accesstoken", data.access);
					this.$cookie.setCookie("refreshtoken", data.refresh);
					this.$router.push("/dashboard");
				})
				.catch((error) => console.log(error));
		},
	},
};
</script>

<style lang="scss" scoped>
.signWrap {
	width: 100%;
	height: 100%;
	background: #fff;
	position: relative;
	.signcontent {
		position: absolute;
		width: 600px;
		height: 550px;
		border-radius: 6px;
		transform: translate(-50%, -50%);
		border: 1px solid #eeeeee;
		top: 50%;
		left: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		.form-content {
			max-width: 400px;
			max-height: 458px;
			width: 100%;
			height: 100%;
			padding: 10px;
			display: flex;
			justify-content: center;
			flex-direction: column;
			text-align: center;
			.logoBox {
				margin: 0 auto;
			}
			form {
				height: auto !important;
			}
			h4 {
				margin-top: 18px;
				font-size: 25px;
				margin-bottom: 30px;
				font: normal normal bold 20px/38px Roboto;
				letter-spacing: 0px;
				color: #33343c;
			}
			.loginFooter {
				width: 103px;
				height: 30px;
				margin: 39px auto 0;
			}
		}
		.k-form-field .k-label {
			color: #0e0f1d;
		}
		.k-form-field .k-form-field-wrap span {
			border: 1px solid #f5f7fa;
		}
		.k-form-field .k-form-field-wrap input {
			height: 40px;
		}
	}
}
</style>
