<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준년도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="$store.state.bauStore.yearList"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조건</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'type'"
											:style="{ maxWidth: '230px' }"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="typeList"
													:value="typeValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="typeChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
		:style="{ height: this.$store.state.resizeLast }"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="bauTable"
					title="신규PJT 관리"
					gridName="newMngmn"
					:dataItems="$store.state.bauStore.bauList"
					:columns="columns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					:selectWorkplace="workplaceDropDownList"
					:selectProductionTeam="productionTeamDropDownList"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:deleteDataCheck="true"
					:datePickerFormat="'yyyy.MM'"
					insertButtonText="추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";

import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "newMngmn",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			typeList: [
				{ id: "01", text: "가동전" },
				{ id: "02", text: "가동후" },
			],
			typeValue: { id: "01", text: "가동전" },
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			workplaceDropDownList: [],
			productionTeamDropDownList: [],
			attchFile: null,
			y1: "",
			y2: "",
			y3: "",
			y4: "",
			y5: "",
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "workplace_name",
					title: "사업장",
					minResizableWidth: 190,
					width: 190,
					cell: "dropdownWorkplaceCell",
					disabled: true,
					editable: this.isEditable,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					cell: "dropdownProductionTeamCell",
					minResizableWidth: 190,
					width: 190,
					disabled: true,
					editable: this.isEditable,
				},
				{
					field: "pjt_name",
					title: "PJT 명칭",
					minResizableWidth: 110,
					width: 110,
					editable: this.isEditable,
				},
				{
					field: "operation_start",
					title: "PJT 가동개시(월)",
					cell: "datePickerCell",
					minResizableWidth: 225,
					width: 225,
				},
				{
					field: "pjt_description",
					title: "PJT 설명",
					minResizableWidth: 210,
					width: 210,
					editable: this.isEditable,
				},
				{
					title: "연간 총 예상 배출량(tCO2-eq)",
					minResizableWidth: 150,
					width: 150,
					children: [
						{
							field: "y1",
							title: this.y1,
							minResizableWidth: 100,
							width: 100,
							editable: this.isEditable,
						},
						{
							field: "y2",
							title: this.y2,
							minResizableWidth: 130,
							width: 130,
							resizable: false,
							className: "text-center",
							editable: this.isEditable,
						},
						{
							field: "y3",
							title: this.y3,
							minResizableWidth: 130,
							width: 130,
							resizable: false,
							className: "text-center",
							editable: this.isEditable,
						},
						{
							field: "y4",
							title: this.y4,
							minResizableWidth: 130,
							width: 130,
							resizable: false,
							className: "text-center",
							editable: this.isEditable,
						},
						{
							field: "y5",
							title: this.y5,
							minResizableWidth: 130,
							width: 130,
							resizable: false,
							className: "text-center",
							editable: this.isEditable,
						},
					],
				},
				{
					field: "file_url",
					title: "첨부자료",
					cell: "FileFormCell",
					minResizableWidth: 210,
					width: 210,
					editable: this.isEditable,
				},
				{
					cell: "commandCell",
					minResizableWidth: 160,
					width: 160,
				},
			];
		},
		yearList() {
			return this.$store.state.bauStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("bauStore/SETTING_INIT");
		this.$store.dispatch("bauStore/FETCH_ORGANIZATION_YEAR_LIST");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceDropDownList = [];
						data.items.map((v) =>
							this.workplaceDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamDropDownList = [];
						data.items.map((v) =>
							this.productionTeamDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			if (this.typeValue.id !== undefined) {
				this.searchData += "&type=" + this.typeValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("bauStore/FETCH_PJT_MANAGEMNET", this.searchData)
				.then((data) => {
					if (this.$refs.bauTable !== null) {
						this.y1 = String(this.year + 1);
						this.y2 = String(this.year + 2);
						this.y3 = String(this.year + 3);
						this.y4 = String(this.year + 4);
						this.y5 = String(this.year + 5);
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.bauTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			this.workplaceDropDownList = [];
			this.workplaceDropDownList.push(event.value);
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
			this.productionTeamDropDownList = [];
			this.productionTeamDropDownList.push(event.value);
		},
		typeChange(event) {
			this.typeValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("bauStore/SETTING_INIT");
			this.$store.dispatch("bauStore/FETCH_ORGANIZATION_YEAR_LIST");
		},
		insert: function (data) {
			const formData = new FormData();
			formData.append("workplace_id", data.data.workplace_id);
			formData.append("production_team_id", data.data.production_team_id);
			formData.append("pjt_name", data.data.pjt_name);
			formData.append(
				"operation_start",
				this.formatDate(data.data.operation_start)
			);
			formData.append("pjt_description", data.data.pjt_description);
			formData.append("y1", data.data.y1);
			formData.append("y2", data.data.y2);
			formData.append("y3", data.data.y3);
			formData.append("y4", data.data.y4);
			formData.append("y5", data.data.y5);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("bauStore/INSERT_PJT_MANAGEMNET", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		edit: function (data) {
			const formData = new FormData();
			formData.append("pjt_id", data.data.id);
			formData.append("pjt_name", data.data.pjt_name);
			formData.append(
				"operation_start",
				this.formatDate(data.data.operation_start)
			);
			formData.append("pjt_description", data.data.pjt_description);
			formData.append("y1", data.data.y1);
			formData.append("y2", data.data.y2);
			formData.append("y3", data.data.y3);
			formData.append("y4", data.data.y4);
			formData.append("y5", data.data.y5);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("bauStore/INSERT_PJT_MANAGEMNET", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		formatDate: function (date) {
			// 날짜 format하여 요청
			let month = date.getMonth() + 1;
			month = month >= 10 ? month : "0" + month;
			return date.getFullYear() + "" + month;
		},
		remove: function (id) {
			this.$store.dispatch("bauStore/DELETE_PJT_MANAGEMNET", id);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"bauStore/DELETE_PJT_MANAGEMNET_FILE",
				e.scope_perform_id
			);
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
