import {
	fetchCarbonNeutralYearList,
	fetchCarbonNeutralYear,
	editCarbonNeutralYear,
} from "@/api/index.js";

const carbonNeutralYearStore = {
	namespaced: true,
	state: {
		carbonNeutralList: [],
		carbonNeutralCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_CARBON_NEUTRAL_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_CARBON_NEUTRAL: (state, data) => {
			state.carbonNeutralList = data.items;
			state.carbonNeutralCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_CARBON_NEUTRAL_YEAR_LIST: ({ commit }) => {
			// 탄소중립 목표관리의 선택 가능한 기준연도 목록 조회
			return fetchCarbonNeutralYearList()
				.then(({ data }) => {
					commit("SET_CARBON_NEUTRAL_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_CARBON_NEUTRAL_YEAR: ({ commit }, info) => {
			// 연도별 탄소중립 목표관리 조회
			return fetchCarbonNeutralYear(info)
				.then(({ data }) => {
					commit("SET_CARBON_NEUTRAL", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_CARBON_NEUTRAL_YEAR: (info) => {
			console.log(info);
			// 연도별 탄소중립 목표관리 일괄 수정
			return editCarbonNeutralYear(info.state.carbonNeutralList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default carbonNeutralYearStore;
