import {
	fetchOrganizationList,
	fetchOrganizationYearList,
	copyOrganization,
	fetchOrganization,
	insertOrganizationDetail,
	editOrganizationDetail,
	deleteOrganizationDetail,
	fetchOrganizationCodeYearList,
	fetchOrganizationCode,
	insertOrganizationCode,
	editOrganizationCode,
	deleteOrganizationCode,
	copyOrganizationCode,
	fetchCodeList,
	fetchOrganizationSelect,
	fetchOrganizationMultiList,
	fetchOrganizationMultiCodeList,
} from "@/api/index.js";

const organizationStore = {
	namespaced: true,
	state: {
		organizationList: [
			{
				org_id: 1,
				org_name: " ",
				depth: 1,
				sub_menu: [
					{
						org_id: 2,
						org_name: " ",
						depth: 2,
						sub_menu: [
							{
								org_id: 3,
								org_name: " ",
								depth: 3,
								sub_menu: [
									{
										org_id: 4,
										org_name: " ",
										depth: 4,
										sub_menu: [
											{
												org_id: 5,
												org_name: " ",
												depth: 5,
												sub_menu: [],
											},
										],
									},
								],
							},
						],
					},
				],
			},
		],
		organizationCount: 0,
		organizationMultiList: [],
		organizationMultiCount: 0,
		organizationSelectList: [],
		organizationData: {},
		organizationDataBefor: {},
		organizationCodeList: [],
		organizationMultiCodeList: [],
		businessCodeList: [],
		factoryCodeList: [],
		facilityNameCodeList: [],
		facilitySizeCodeList: [],
		orgTypeList: [
			{ id: "01", text: "사업부" },
			{ id: "02", text: "공장" },
		],
		yearList: [],
		year: "",
		codeYearList: [],
		codeYear: "",
		org_parent_id: "",
		org_id: "",
		orgParentName: "",
		depth: 1,
		successMessage: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_ORGANIZATION_LIST: (state, data) => {
			state.organizationList = data.items;
			state.organizationCount = data.count;
		},
		SET_ORGANIZATION_MULTI_LIST: (state, data) => {
			state.organizationMultiList = data.items;
			state.organizationMultiCount = data.count;
		},
		SET_ORGANIZATION_SELECT_LIST: (state, data) => {
			state.organizationSelectList = [];
			data.items.map((v) =>
				state.organizationSelectList.push({ id: v.org_id, text: v.org_name })
			);
		},
		SET_ORGANIZATION_INFO: (state, data) => {
			data.factoryList =
				data.factory == null
					? null
					: {
							id: data.factory.org_code_id,
							text: data.factory.org_code_name,
							code: data.factory.org_code,
					  };
			data.businessPartList =
				data.business_part == null
					? null
					: {
							id: data.business_part.org_code_id,
							text: data.business_part.org_code_name,
					  };
			data.facilityNameCodeList =
				data.facility_name_code == null
					? null
					: {
							id: data.facility_name_code.code_no,
							text: data.facility_name_code.code_name,
					  };
			data.facilitySizeCodeList =
				data.facility_size_code == null
					? null
					: {
							id: data.facility_size_code.code_no,
							text: data.facility_size_code.code_name,
					  };
			// 배출시설 사업장-생산팀 SET
			state.orgParentName =
				data.corporate_parent_name == null
					? data.org_parent_name + "-" + data.corporate_name
					: data.corporate_parent_name;
			state.organizationData = data;
			console.log(state.organizationData);
		},
		SET_ORGANIZATION_CODE_YEAR_LIST: (state, data) => {
			state.codeYearList = data.items;
			state.codeYear = data.items[0];
		},
		SET_ORGANIZATION_CODE: (state, data) => {
			state.organizationCodeList = data.items;
			state.organizationCodeList.map((v) => (v.id = v.org_code_id));
		},
		SET_ORGANIZATION_MULTI_CODE: (state, data) => {
			state.organizationMultiCodeList = [];
			data.items.map((v) =>
				state.organizationMultiCodeList.push({
					id: v.org_code,
					text: v.org_code_name,
				})
			);
		},
		SET_BUSINESS_CODE: (state, data) => {
			data.items.map((v) =>
				state.businessCodeList.push({
					id: v.org_code_id,
					text: v.org_code_name,
				})
			);
		},
		SET_FACTORY_CODE: (state, data) => {
			data.items.map((v) =>
				state.factoryCodeList.push({
					id: v.org_code_id,
					text: v.org_code_name,
					code: v.org_code,
				})
			);
		},
		SET_FACILITY_NAME_CODE: (state, data) => {
			state.facilityNameCodeList = [];
			data.items.map((v) =>
				state.facilityNameCodeList.push({ id: v.code_no, text: v.code_name })
			);
		},
		SET_FACILITY_SIZE_CODE: (state, data) => {
			state.facilitySizeCodeList = [];
			data.items.map((v) =>
				state.facilitySizeCodeList.push({ id: v.code_no, text: v.code_name })
			);
		},
		SET_ORGANIZATION_INIT: (state) => {
			state.organizationData = {};
		},
		SET_ORG_INIT: (state) => {
			state.org_id = "";
		},
		SET_ORG_PARENT_INIT: (state) => {
			state.org_parent_id = "";
		},
		SET_DEPTH_INIT: (state) => {
			state.depth = 1;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_ORGANIZATION_LIST_INIT: (state) => {
			state.organizationList = [];
		},
		SET_ORGANIZATION_MULTI_LIST_INIT: (state) => {
			state.organizationMultiList = [];
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		SETTING_ORGANIZATION_INIT: ({ commit }) => {
			return commit("SET_ORGANIZATION_INIT");
		},
		SETTING_ORG_INIT: ({ commit }) => {
			return commit("SET_ORG_INIT");
		},
		SETTING_ORG_PARENT_INIT: ({ commit }) => {
			return commit("SET_ORG_PARENT_INIT");
		},
		SETTING_DEPTH_INIT: ({ commit }) => {
			return commit("SET_DEPTH_INIT");
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
		SETTING_ORGANIZATION_LIST_INIT: ({ commit }) => {
			return commit("SET_ORGANIZATION_LIST_INIT");
		},
		SETTING_ORGANIZATION_MULTI_LIST_INIT: ({ commit }) => {
			return commit("SET_ORGANIZATION_MULTI_LIST_INIT");
		},
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		COPY_ORGANIZATION: () => {
			// 전년도 조직 및 시설 데이터 복사
			return copyOrganization()
				.then(({ data }) => {
					// commit('FETCH_ORGANIZATION_YEAR_LIST', data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_LIST: ({ commit }, year) => {
			// 조직 및 시설 목록 조회
			return fetchOrganizationList(year)
				.then(({ data }) => {
					commit("SET_ORGANIZATION_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_MULTI_LIST: ({ commit }, info) => {
			// 기간의 조직 및 시설 목록 조회
			return fetchOrganizationMultiList(info)
				.then(({ data }) => {
					commit("SET_ORGANIZATION_MULTI_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_INFO: ({ commit }, info) => {
			// 조직 상세정보 조회
			return fetchOrganization(info)
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_INFO", data);
				})
				.catch((error) => console.log(error));
		},
		INSERT_ORGANIZATION_DETAIL: ({ commit }, info) => {
			// 메뉴 및 조직 상세정보 추가
			return insertOrganizationDetail(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_ORGANIZATION_DETAIL: ({ commit }, info) => {
			// 조직 상세정보 수정
			return editOrganizationDetail(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_ORGANIZATION_DETAIL: ({ commit }, info) => {
			// 메뉴 및 조직 상세정보 정보 삭제
			return deleteOrganizationDetail(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_CODE_YEAR_LIST: ({ commit }) => {
			// 조직코드의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationCodeYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_CODE_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_CODE_LIST: ({ commit }, info) => {
			// 조직 코드 목록 조회
			return fetchOrganizationCode(info)
				.then(({ data }) => {
					commit("SET_ORGANIZATION_CODE", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_MULTI_CODE_LIST: ({ commit }, info) => {
			// 기간의 조직 코드 목록 조회
			return fetchOrganizationMultiCodeList(info)
				.then(({ data }) => {
					commit("SET_ORGANIZATION_MULTI_CODE", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_BUSINESS_CODE_LIST: ({ commit }, info) => {
			// 사업부 코드 목록 조회
			return fetchOrganizationCode(info)
				.then(({ data }) => {
					commit("SET_BUSINESS_CODE", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_FACTORY_CODE_LIST: ({ commit }, info) => {
			// 공장 코드 목록 조회
			return fetchOrganizationCode(info)
				.then(({ data }) => {
					commit("SET_FACTORY_CODE", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_FACILITY_NAME_CODE_LIST: ({ commit }, info) => {
			// 배출시설 코드 목록 조회
			return fetchCodeList(info)
				.then(({ data }) => {
					commit("SET_FACILITY_NAME_CODE", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_FACILITY_SIZE_CODE_LIST: ({ commit }, info) => {
			// 배출시설규모 코드 목록 조회
			return fetchCodeList(info)
				.then(({ data }) => {
					commit("SET_FACILITY_SIZE_CODE", data);
				})
				.catch((error) => console.log(error));
		},
		INSERT_ORGANIZATION_CODE: ({ commit }, info) => {
			// 조직 코드 추가
			return insertOrganizationCode(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_ORGANIZATION_CODE: ({ commit }, info) => {
			// 조직 코드 수정
			return editOrganizationCode(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_ORGANIZATION_CODE: ({ commit }, info) => {
			// 조직 코드 삭제
			return deleteOrganizationCode(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		COPY_ORGANIZATION_CODE: () => {
			// 전년도 조직 코드 데이터 복사
			return copyOrganizationCode()
				.then(({ data }) => {
					// commit('FETCH_ORGANIZATION_CODE_LIST', data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ORGANIZATION_SELECT: ({ commit }, info) => {
			// 조직 및 시설 목록 조회
			return fetchOrganizationSelect(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					commit("SET_ORGANIZATION_SELECT_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
	},
	getters: {
		GET_ORGANIZATION_INIT: (state) => {
			return state.organizationData;
		},
	},
};

export default organizationStore;
