import {
	fetchCarbonNeutralYearList,
	fetchEmissionGoalMonth,
} from "@/api/index.js";

const emissionGoalMonthStore = {
	namespaced: true,
	state: {
		emissionGoalList: [],
		emissionGoalCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_CARBON_NEUTRAL_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_EMISSION_GOAL_MONTH: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.emissionGoalList = data.items;
			state.emissionGoalCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_CARBON_NEUTRAL_YEAR_LIST: ({ commit }) => {
			// 탄소중립 목표관리의 선택 가능한 기준연도 목록 조회
			return fetchCarbonNeutralYearList()
				.then(({ data }) => {
					commit("SET_CARBON_NEUTRAL_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_EMISSION_GOAL_MONTH: ({ commit }, info) => {
			// 월별 배출목표 조회
			return fetchEmissionGoalMonth(info)
				.then(({ data }) => {
					commit("SET_EMISSION_GOAL_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default emissionGoalMonthStore;
