<template>
	<Chart>
		<ChartTitle :text="title" />
		<ChartLegend :position="'bottom'" />
		<ChartSeries>
			<ChartSeriesItem
				:type="'area'"
				:name="'AHU'"
				:data-items="data[0].actual"
			/>
			<ChartSeriesItem
				:type="'area'"
				:name="'AHU Prediction'"
				:data-items="data[0].prediction"
			/>
		</ChartSeries>
	</Chart>
</template>

<script>
import {
	Chart,
	ChartLegend,
	ChartSeries,
	ChartSeriesItem,
	ChartTitle,
} from "@progress/kendo-vue-charts";
import "hammerjs";

export default {
	name: "AreaChart",
	props: {
		title: String,
		data: Object,
		categories: Array,
	},
	components: {
		Chart,
		ChartLegend,
		ChartSeries,
		ChartSeriesItem,
		ChartTitle,
	},
};
</script>
