<template>
	<Chart :pannable="true" :zoomable="true" :shared="true">
		<ChartTooltip :visible="true" :shared="true" />
		<ChartTitle :text="title" />
		<ChartLegend :position="legendPosition" />
		<ChartSeries>
			<ChartSeriesItem
				v-for="(item, index) in data"
				:name="item.name"
				:data-items="item.data"
				:type="item.type"
				:key="index"
				:line-style="lineStyle"
				:markers="markers"
			/>
		</ChartSeries>
		<ChartCategoryAxis>
			<ChartCategoryAxisItem :categories="categories" />
		</ChartCategoryAxis>
	</Chart>
</template>

<script>
import "hammerjs";
import {
	Chart,
	ChartLegend,
	ChartSeries,
	ChartSeriesItem,
	ChartTooltip,
	ChartTitle,
	ChartCategoryAxis,
	ChartCategoryAxisItem,
} from "@progress/kendo-vue-charts";

export default {
	name: "CommonChart",
	props: {
		title: String,
		legendPosition: {
			type: String,
			default: "bottom",
		},
		data: Object,
		categories: Array,
		lineStyle: String,
		markers: Object,
	},
	components: {
		Chart,
		ChartLegend,
		ChartSeries,
		ChartSeriesItem,
		ChartTooltip,
		ChartTitle,
		ChartCategoryAxis,
		ChartCategoryAxisItem,
	},
};
</script>
