import {
	fetchClosingManagementYearList,
	fetchClosingManagementList,
	editClosingManagement,
} from "@/api/index.js";

const closingManagementStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_CLOSING_MANAGEMENT_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_CLOSING_MANAGEMENT: (state, data) => {
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_CLOSING_MANAGEMENT_YEAR_LIST: ({ commit }) => {
			// 마감 관리의 선택 가능한 기준연도 목록 조회
			return fetchClosingManagementYearList()
				.then(({ data }) => {
					return commit("SET_CLOSING_MANAGEMENT_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_CLOSING_MANAGEMENT: ({ commit }, info) => {
			// 마감 목록 조회
			return fetchClosingManagementList(info)
				.then(({ data }) => {
					commit("SET_CLOSING_MANAGEMENT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_CLOSING_MANAGEMENT: (info) => {
			// 마감 일괄 수정
			return editClosingManagement(info.state.dataList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default closingManagementStore;
