<template>
	<td v-if="!dataItem.inEdit" :class="className">
		<checkbox :disabled="true" :default-checked="Boolean(dataItem[field])" />
	</td>
	<td v-else>
		<checkbox :default-checked="Boolean(dataItem[field])" @change="change" />
	</td>
</template>

<script>
import { Checkbox } from "@progress/kendo-vue-inputs";

export default {
	name: "CheckboxCell",
	components: {
		checkbox: Checkbox,
	},
	props: {
		field: String,
		dataItem: Object,
		className: String,
	},
	methods: {
		change(e) {
			this.$emit("change", e);
		},
	},
};
</script>

<style lang="scss" scoped>
input[type="checkbox"] {
	-webkit-appearance: none;
	position: relative;
	width: 14px;
	height: 14px;
	cursor: pointer;
	outline: none !important;
	border: 1px solid #33343c !important;
	border-radius: 2px;
	background: #fbfbfb;
}

input[type="checkbox"]::before {
	content: "\2713";
	font-weight: bolder;
	position: absolute;
	top: 60%;
	left: 50%;
	overflow: hidden;
	transform: scale(0) translate(-50%, -50%);
	line-height: 1;
}

input[type="checkbox"]:hover {
	border-color: #33343c;
}

input[type="checkbox"]:checked {
	background-color: rgb(73, 168, 114);
	border: none !important;
	color: white;
}

input[type="checkbox"]:checked::before {
	border-radius: 2px;
	transform: scale(1) translate(-50%, -50%);
}
</style>
