<template>
	<aside class="sidenav bg-white active" id="sidebar">
		<div class="wrapper">
			<div class="sidebar-logo">
				<img :src="sideBarMainLogo" alt="side_logo" />
			</div>
			<div
				class="sidebarList"
				v-for="(savedata, i) in categoryData['sidenavData']"
				:key="i"
			>
				<div
					class="sidenav-header"
					:class="$route.href == '/dashboard' && i == 0 ? 'active' : ''"
					@click="listOpen"
				>
					<img :src="listIconImg" />
					<h5>{{ savedata.title }}</h5>
					<div class="down-arrow" v-if="savedata.title !== 'Dashboard'">
						<span></span>
					</div>
				</div>
				<sidebar-list
					v-if="savedata.title !== 'Dashboard'"
					:sidertitle="savedata.title"
					:datata="savedata.sideBarList"
					:link="savedata.sideBarList"
					:thirdDepthDatas="savedata.sideBarList"
				/>
			</div>
			<div class="empty"></div>
			<img :src="sideBarBottomLogo" class="buttomLogo" alt="문양" />
		</div>
	</aside>
</template>

<script>
import resize from "../../../mixin/resize.js";

import SidebarList from "./SidebarList.vue";
import sideBarMainLogo from "@/assets/img/sidebar/sideLogo.png";
import sideBarBottomLogo from "@/assets/img/sidebar/sidebarLogo.png";
import listIconImg from "@/assets/img/sidebar/sidebarListImg.png";
import categoryData from "@/data/categoryData.json";

export default {
	name: "Sidebar",
	components: {
		SidebarList,
	},
	data: function () {
		return {
			listIconImg,
			sideBarMainLogo,
			categoryData,
			sideBarBottomLogo,
		};
	},
	watch: {
		$route() {
			this.setPath();
		},
	},
	mounted() {
		this.handleResize();
	},
	unmounted() {
		window.addEventListener("resize", this.handleResize);
	},
	methods: {
		listOpen: function (e) {
			let targets = e.target;
			let showTarget = targets.closest(".sidebarList").lastChild;
			targets
				.closest(".sidebarList")
				.querySelector(".sidenav-header")
				.classList.toggle("active");
			if (
				targets.innerHTML == "Dashboard" ||
				showTarget.classList == undefined
			) {
				location.href = "/dashboard";
				return false;
			} else {
				showTarget.classList.toggle("active");
			}
			setTimeout(() => {
				this.handleResize();
			}, 300);
		},
		setPath: function () {
			const sideNavData = categoryData.sidenavData;
			if (!this.$store.state.parentMenu) {
				for (let i in sideNavData) {
					const sideBarList = sideNavData[i].sideBarList;
					for (let j in sideBarList) {
						if (sideBarList[j].title == this.$route.meta.title) {
							this.$store.dispatch(
								"SET_PARENT_MENU",
								sideNavData[i].title + " > "
							);
							return;
						} else {
							if ("thirdDepth" in sideBarList[j]) {
								const thirdDepth = sideBarList[j].thirdDepth;
								for (let depth in thirdDepth) {
									if (thirdDepth[depth].title == this.$route.meta.title) {
										this.$store.dispatch(
											"SET_PARENT_MENU",
											sideNavData[i].title +
												" > " +
												sideBarList[j].title +
												" > "
										);
										return;
									}
								}
							}
						}
					}
				}
			}
		},
	},
	mixins: [resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.sidebar-logo {
	text-align: center;
	margin-bottom: 21px;

	img {
		width: 118px;
		height: 41px;
	}
}

aside.sidenav {
	// padding: 20px 10px 0px 20px;
	width: 230px;
	min-width: unset;
	border-right: none;

	.wrapper {
		position: relative;
		width: 100%;
		min-height: 100%;
		padding: 20px 10px 0px 20px;
	}

	.sidebarList {
		margin-bottom: 10px;
	}

	.sidenav-header {
		padding: 14px;
		height: 40px;
		position: relative;
		display: flex;
		font-size: 14px;
		line-height: 20px;
		background: $sidemenubg;
		border-radius: $borderradius;
		align-items: center;
		transition: all 0.3s;

		&.active {
			background: $sidemenubga;

			img {
				filter: brightness(2);
			}

			h5 {
				font: normal normal normal 14px/20px AppleSDGothicNeoB00;
				color: $sideActiveFontColor;
			}

			.down-arrow {
				transform: rotate(188deg);

				span {
					&::after {
						border-top: 1.5px solid $sideActiveFontColor;
						border-right: 1.5px solid $sideActiveFontColor;
						transform: rotate(127deg);
					}
				}
			}
		}

		img {
			width: 12px;
			height: 12px;
			margin-right: 14px;
		}

		h5 {
			color: $sidetext;
			font: normal normal normal 14px/20px AppleSDGothicNeoB00;
		}

		.down-arrow {
			position: absolute;
			right: 23px;
			top: 50%;
			width: 8px;
			height: 8px;
			transform: translateY(-88%);

			span {
				position: relative;

				&::after {
					position: absolute;
					display: inline-block;
					content: "";
					width: 8px;
					height: 8px;
					border-top: 1.5px solid $arrowColor;
					border-right: 1.5px solid $arrowColor;
					transform: rotate(135deg);
				}
			}
		}
	}

	.buttomLogo {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
	}

	.empty {
		height: 360px;
		width: 100%;
	}
}
</style>
