import {
	fetchOrganizationYearList,
	fetchPreQuota,
	editPreQuota,
} from "@/api/index.js";

const preQuotaStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_PRE_QUOTA: (state, data) => {
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_PRE_QUOTA: ({ commit }, info) => {
			// 사전할당량 관리 조회
			return fetchPreQuota(info)
				.then(({ data }) => {
					commit("SET_PRE_QUOTA", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_PRE_QUOTA: (info) => {
			// 사전할당량 관리 일괄 수정
			return editPreQuota(info.state.dataList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default preQuotaStore;
