<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form :reset="false" @searchFormStateA="searchFormStateA">
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준연도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'year'"
											:style="{ maxWidth: '120px' }"
											:component="'yearSelect'"
										>
											<template v-slot:yearSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="year"
													:style="{ width: 'auto' }"
													:value="year_value"
													@change="dropdownChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="subContainer halfGrid">
			<div class="d-flex selHearders">
				<div />
				<basic-button
					v-on:click="copyData"
					class="paraGroup"
					title="전년도데이터복사"
					:backgroundColor="'#49A872'"
					:fontColor="'#FFFFFF'"
					:borderColor="''"
					:wsize="'130px'"
					>전년도데이터복사</basic-button
				>
			</div>
			<div class="d-flex boxs">
				<div class="w-50 h-100">
					<v-table
						v-if="
							$store.state.emissionActivityStore.emissionActivityList.length !==
							0
						"
						ref="emissionGrid"
						title="배출활동 목록"
						gridName="emission"
						:dataItems="$store.state.emissionActivityStore.emissionActivityList"
						:count="$store.state.emissionActivityStore.emissionActivityCount"
						:columns="emission_activity_columns"
						:disabledState="true"
						:propsGroup="[{ field: 'emission_code_name' }]"
						@activityChange="activityChange"
						:type="'emissiong'"
					/>
				</div>
				<div class="dataChangeSaveBox" v-on:click="save">
					<img :src="IconSwitch" alt="적용 아이콘" v-if="!activeState" />
					<div class="activeWarp" v-else>
						<img :src="activeArrows" alt="적용 아이콘" class="saveArrows" />
					</div>
					<p class="dataChangeSaveBoxTitle">변경내용<br />저장</p>
				</div>
				<div class="w-50 h-100">
					<v-table
						v-if="
							$store.state.emissionActivityStore.activityDataList.length !== 0
						"
						ref="activityGrid"
						title="활동자료"
						:dataItems="$store.state.emissionActivityStore.activityDataList"
						:count="$store.state.emissionActivityStore.activityDataCount"
						:columns="activity_data_columns"
						:type="'emissiong'"
						:propsSort="[{ field: 'use_yn', dir: 'desc' }]"
						@activeChange="activeChange"
					/>
				</div>
			</div>
		</div>
	</div>
	<modal
		v-if="visibleDialog"
		:title="'알림'"
		:message="'당해 연도 데이터가 있는 경우 모두 초기화됩니다. 전년도 데이터로 변경하시겠습니까?'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="insertCopyData" class="button first">확인</kbutton>
				<kbutton @click="messageDialog" class="button second">취소</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import resize from "../../mixin/resize.js";

import IconSwitch from "@/assets/img/icon/switch.png";
import activeArrows from "@/assets/img/activeArrow.png";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import BasicButton from "../../components/common/button/BasicButton.vue";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

export default {
	name: "EmissionActivity",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		dropdownlist: DropDownList,
		BasicButton,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	computed: {
		activity_data_columns() {
			return [
				{ field: "code_name", title: "연료", editable: false },
				{ field: "code_id", title: "Code ID", editable: false },
				{
					field: "use_yn",
					title: "사용여부",
					cell: "activityCheckboxCell",
					width: "100px",
					editable: this.isEditable,
				},
			];
		},
	},
	data: function () {
		return {
			activeArrows,
			IconSwitch,
			activeState: false,
			searchFormStates: false,
			searchFormStates1: false,
			year_value: 2022,
			year: [2022, 2021, 2020, 2019, 2018, 2017, 2016, 2015, 2014, 2013, 2012],
			emission_code_no: 0,
			emission_activity_columns: [
				{ field: "activity_code_name", title: "연료" },
			],
			visibleDialog: false,
			isEditable: false,
		};
	},
	async created() {
		if (
			this.$store.state.emissionActivityStore.emissionActivityList.length == 0
		) {
			await this.$store.dispatch(
				"emissionActivityStore/FETCH_EMISSION_ACTIVITY_LIST",
				this.year_value
			);
		}
		if (this.$store.state.emissionActivityStore.activityDataList.length == 0) {
			await this.$store.dispatch("emissionActivityStore/FETCH_EMISSION_LIST");
		}
	},
	methods: {
		activeChange(data) {
			const emission_code_nos = [];
			this.$store.state.emissionActivityStore.emissionActivityList.map((i) => {
				if (i.emission_code_no == this.emission_code_no) {
					emission_code_nos.push(i.activity_code_no);
				}
			});
			const activity_code_nos = [];
			data.map((i) => {
				if (i.use_yn) activity_code_nos.push(i.code_no);
			});
			const activity_code_nos_length = emission_code_nos.filter((v) =>
				activity_code_nos.includes(v)
			).length;
			if (
				emission_code_nos.length == activity_code_nos_length &&
				emission_code_nos.length == activity_code_nos.length
			) {
				this.activeState = false;
			} else {
				this.activeState = true;
			}
		},
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		async handleSubmit(dataItem) {
			this.year_value = dataItem.year;
			await this.$store.dispatch(
				"emissionActivityStore/FETCH_EMISSION_ACTIVITY_LIST",
				this.year_value
			);
			this.$refs.emissionGrid.refreshData();
			this.refresh();
		},
		refresh() {
			this.emission_code_no =
				this.$store.state.emissionActivityStore.emissionActivityList[0].emission_code_no;
			this.activityChange(this.emission_code_no);
		},
		activityChange: async function (emission_code_no) {
			this.emission_code_no = emission_code_no;
			this.activeState = false;
			if (this.emission_code_no == 0) {
				this.isEditable = false;
			} else {
				this.isEditable = true;
			}
			await this.$store.dispatch(
				"emissionActivityStore/EDIT_EMISSION",
				emission_code_no
			);
			this.$refs.activityGrid.refreshData();
		},
		dropdownChange(event) {
			this.year_value = event.value;
		},
		copyData() {
			if (
				this.$store.state.emissionActivityStore.emissionActivityList.length !==
				0
			) {
				this.messageDialog();
			} else {
				this.insertCopyData();
			}
		},
		async insertCopyData() {
			await this.$store.dispatch(
				"emissionActivityStore/COPY_EMISSION_ACTIVITY"
			);
			this.messageDialog();
			this.$refs.emissionGrid.refreshData();
			this.refresh();
		},
		messageDialog() {
			this.visibleDialog = !this.visibleDialog;
		},
		async save() {
			const activity_code_nos = [];
			const data = this.$refs.activityGrid.returnData();
			data.map((i) => {
				if (i.use_yn) activity_code_nos.push(i.code_no);
			});
			const updateData = {
				year: this.year_value,
				emission_code_no: this.emission_code_no,
				activity_code_nos: activity_code_nos,
			};
			await this.$store.dispatch(
				"emissionActivityStore/INSERT_EMISSION_ACTIVITY",
				updateData
			);
			this.$refs.emissionGrid.refreshData();
		},
	},
	mixins: [resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/App.scss";
.k-grid-toolbar {
	padding: 15px 20px 10px 10px !important;
}

.d-flex.boxs {
	overflow: hidden;
	height: calc(100% - 50px);
}

.subContainer {
	border-radius: 6px;
	box-shadow: 5px 5px 10px rgb(34 34 34 / 10%);
	padding-bottom: 35px;
	background: #fff;
	height: 100%;
	overflow: hidden;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}
.Codebar {
	padding: 16px 20px 5px 20px;
	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		.arrowTopBox {
			padding-left: 200px;
		}
	}
	.longBox {
		width: 400px !important;
	}
	.buttonBoxs {
		width: 200px;
	}
	.searchInputBox {
		position: relative;
		img {
			position: absolute;
			top: 10px;
			right: 15px;
		}
	}
}
.selHearders {
	width: 100%;
	justify-content: space-between;
	align-items: center;
	padding: 0px 20px 0px 0px;
	box-sizing: border-box;
}

.pt-10 {
	padding-top: 20px;
}
</style>
