<template>
	<grid
		ref="grid"
		:style="{ width: '100%', height: '100%' }"
		:cell-render="customCellCheck ? 'customCell' : ''"
		:data-items="dataToShow"
		:edit-field="'inEdit'"
		:resizable="true"
		:sortable="true"
		:collapsedGroups="collapsedGroups"
		:group="group"
		:expand-field="'expanded'"
		:columns="columns"
		@itemchange="itemChange"
		@datastatechange="dataStateChange"
		@expandchange="expandChange"
		:class="{ emty: type == 'emty' }"
	>
		<template v-slot:customCell="{ props }">
			<custom-cell
				:gridName="gridName"
				:field="props.field"
				:expanded="props.expanded"
				:row-type="props.rowType"
				:level="props.level"
				:column-index="props.columnIndex"
				:columns-count="props.columnsCount"
				:data-item="props.dataItem"
				:class-name="props.className"
				@click="(e) => expandHandler(props.dataItem)"
			/>
		</template>

		<!-- 파라미터형식 드롭다운 -->
		<template v-slot:dropdownParameterTypeCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectParameterType"
				:data-item="props.dataItem"
				:data-item-id="props.dataItem.parameter_type"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddParameterTypeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 파라미터 드롭다운 -->
		<template v-slot:dropdownParameterValueCell="{ props }" class="gridSelect">
			<dropdown-cell
				v-if="props.dataItem.parameter_type == '01'"
				:data="$store.state.parameterStore.selectParameterGroup"
				:data-item="props.dataItem"
				:data-item-id="props.dataItem.parameter_value"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddParameterValueGroupChange(e, props.dataItem)"
			/>
			<dropdown-list-cell
				v-else-if="props.dataItem.parameter_type == '02'"
				:data="selectOperator"
				:data-item="props.dataItem"
				:field="props.field"
				@change="(e) => ddParameterValueOperatorChange(e, props.dataItem)"
			/>
			<inputCell
				v-else-if="props.dataItem.parameter_type == '03'"
				:data-item="props.dataItem"
				:field="props.field"
				@change="(e) => inputParameterValueChange(e, props.dataItem)"
			/>
		</template>
		<!-- 산정방법론등급 드롭다운 -->
		<template
			v-slot:dropdownCalculationGradeCell="{ props }"
			class="gridSelect"
		>
			<dropdown-cell
				:data="$store.state.codeStore.calculationGradeCode"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.cal_method_rate_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCalculationGradeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 적용산정등급 드롭다운 -->
		<template
			v-slot:dropdownCalculationApplyRateCell="{ props }"
			class="gridSelect"
		>
			<dropdown-cell
				:data="props.dataItem.history_cal_apply_rate"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.cal_apply_rate_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCalculationApplyRateChange(e, props.dataItem)"
			/>
		</template>
		<!-- 파라미터값 버전 드롭다운 -->
		<template
			v-slot:dropdownParameterVersionCell="{ props }"
			class="gridSelect"
		>
			<dropdown-list-cell
				:data="props.dataItem.history_parameter_version"
				:data-item="props.dataItem"
				:field="props.field"
				@change="(e) => ddParameterVersionChange(e, props.dataItem)"
			/>
		</template>
		<!-- 데이터 편집, 수정, 삭제, 취소 버튼 -->
		<template v-slot:commandCell="{ props }">
			<command-cell
				:data-item="props.dataItem"
				@edit="edit"
				@save="save"
				@remove="remove"
				@cancel="cancel"
			/>
		</template>

		<grid-toolbar :class="{ emissiong2: type == 'list' }">
			<div v-if="title" class="table_header row">
				<span class="tableTitle m-0 pr-0">{{ title }}</span>
			</div>
			<div v-if="selectCalculationGrade" class="k-command-cell">
				<!-- NOTE 배포를 위한 임시 스타일 수정 -->
				<dropdownlist
					:style="{ width: '150px !important', height: '32px' }"
					:data-items="selectCalculationGrade"
					:value="calculationGrade"
					@change="calculationGradeChange"
					:text-field="'text'"
					:data-item-key="'id'"
				/>
			</div>
			<basic-button
				v-if="insertGroupButtonText"
				@click="insertGroup"
				class="paraGroup"
				:backgroundColor="'#49A872'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				:wsize="'160px'"
				>{{ insertGroupButtonText }}</basic-button
			>
			<basic-button
				v-if="insertButtonText"
				@click="insert"
				:wsize="'130px'"
				class="paraAdd"
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				>{{ insertButtonText }}</basic-button
			>
		</grid-toolbar>

		<grid-norecords>데이터가 없습니다.</grid-norecords>
	</grid>
	<!-- 데이터 삭제 확인 메세지 팝업 -->
	<modal
		v-if="visibleDeleteDialog"
		:message="'해당 데이터를 삭제하시겠습니까?'"
		@close="messageDeleteDialog"
	>
		<template v-slot:actionBar>
			<dialog-actions-bar>
				<basic-button
					@click="messageDeleteDialog"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>취소</basic-button
				>
				<basic-button
					@click="deleteData"
					:wsize="'130px'"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>확인</basic-button
				>
			</dialog-actions-bar>
		</template>
	</modal>
	<!-- 파라미터 추가 시 산정식 확인 메세지 팝업 -->
	<modal
		v-if="visibleCheckDialog"
		:message="'산정식을 선택해주세요.'"
		@close="messageCheckDialog"
	>
		<template v-slot:actionBar>
			<dialog-actions-bar>
				<basic-button
					@click="messageCheckDialog"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>확인</basic-button
				>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import { process } from "@progress/kendo-data-query";
import { filterBy } from "@progress/kendo-data-query";
import { Grid, GridToolbar, GridNoRecords } from "@progress/kendo-vue-grid";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { DropDownList } from "@progress/kendo-vue-dropdowns";

import CustomCell from "./cell/CustomCell.vue";
import DropdownCell from "./cell/DropDownCell.vue";
import DropdownListCell from "./cell/DropDownListCell.vue";
import CommandCell from "./cell/CommandCell.vue";
import InputCell from "./cell/InputGridCell.vue";

import BasicButton from "../common/button/BasicButton.vue";
import Modal from "../modal/Modal.vue";

import { fetchParameter } from "@/api/index.js";

export default {
	name: "DetailTable",
	emits: ["insert", "edit", "remove"],
	props: {
		title: String,
		detailTitle: String,
		gridName: String, // 그리드 이름(데이터 처리를 위한 값)
		dataItem: Object, // 상위 행 데이터
		dataItems: Array, // 그리드 데이터
		columns: Array, // 그리드 컬럼
		customCellCheck: Boolean, // 커스텀 Cell 여부
		newDataId: Number, // 데이터 추가 시 전달 받을 id 값
		selectCalculationGrade: Array, // 산정등급코드
		deleteDataCheck: Boolean, // 데이터 삭제 여부 확인
		insertDataCheck: Boolean, // 데이터 추가 확인
		propsGroup: Array, // 그리드 그룹
		insertButtonText: String, // 추가 버튼 텍스트
		insertGroupButtonText: String, // 그룹 추가 버튼 텍스트
		navShow: Boolean, //툴바 표출 상태 명시
		type: String, //그룹 타입 명시
	},
	components: {
		Grid,
		"grid-toolbar": GridToolbar,
		"grid-norecords": GridNoRecords,
		dropdownlist: DropDownList,
		DialogActionsBar,
		CustomCell,
		DropdownCell,
		DropdownListCell,
		CommandCell,
		InputCell,
		BasicButton,
		Modal,
	},
	computed: {
		getFilteredData() {
			let result;
			if (this.gridName == "calculation") {
				result = filterBy(this.dataItems, {
					field: "calculation_id",
					operator: "eq",
					value: this.dataItem.calculation_id,
				});
			}
			return result;
		},
		dataToShow() {
			return process(this.updatedData, {
				group: this.group,
			});
		},
	},
	data: function () {
		return {
			highRankDataItemId: this.dataItem.calculation_id,
			collapsedGroups: [[]],
			gridData: [],
			updatedData: [],
			rowData: null,
			group: this.propsGroup,
			groupId: null,
			expandedItems: [],
			visibleDeleteDialog: false, // 삭제 여부 메세지 팝업값
			visibleCheckDialog: false, // 값 추가 시 그룹 데이터 확인 메세지 팝업값
			calculationGrade: this.selectCalculationGrade
				? this.selectCalculationGrade[this.selectCalculationGrade.length - 1]
				: "",
			selectParameterType: [
				{ id: "01", text: "파라미터 그룹" },
				{ id: "02", text: "산정식" },
				{ id: "03", text: "상수" },
			], // 파라미터 형식 드롭다운 값
			selectOperator: ["+", "-", "*", "/", "="], // 파라미터 값 연산자
		};
	},
	created: async function () {
		if (this.gridName == "calculationHistory") {
			// 산정식 변경이력 조회
			const info = {
				id: this.dataItem.calculation_id,
				version: this.dataItem.version,
			};
			await this.$store.dispatch(
				"calculationStore/FETCH_CALCULATION_PARAMETER_HISTORY_LIST",
				info
			);
			this.updatedData =
				this.$store.state.calculationStore.calculationParameterHistoryList;
		} else if (this.gridName == "calculation") {
			// 산정식 관리
			this.updatedData = this.filterCalculationParameterList(
				this.calculationGrade.id
			);
		} else if (this.gridName == "facilityEmission") {
			await this.$store.dispatch(
				"facilityEmissionStore/FETCH_FACILITY_EMISSION_PARAMETER_LIST",
				this.dataItem.fac_emi_id
			);
			this.updatedData =
				this.$store.state.facilityEmissionStore.facilityEmissionParameterList.filter(
					(v) => v.parameter_version == this.dataItem.cal_version
				);
		} else if (this.gridName == "activityHistory") {
			// 산정식 변경이력 조회
			console.log(this.dataItem);
			const info = {
				id: this.dataItem.activedata_id,
			};
			await this.$store.dispatch(
				"activityHistoryStore/FETCH_ACTIVITY_HISTORY_MONTH_LIST",
				info
			);
			this.updatedData =
				this.$store.state.activityHistoryStore.activityHistoryMonthList;
		} else {
			this.updatedData = this.getFilteredData;
		}
		this.getData();
		if (this.group) {
			this.collapsedGroups[0] = this.dataToShow.data.map((i) => i.value);
			if (this.gridName != "calculation") {
				this.groupId = this.collapsedGroups[0][0];
				this.collapsedGroups[0].shift();
			}
		}
	},
	methods: {
		filterCalculationParameterList: function (tier_no) {
			// 산정식 파라미터 목록 tier_no 필터
			return this.getFilteredData.filter((v) => v.tier_no == tier_no);
		},
		ddParameterTypeChange: function (e, dataItem) {
			// 파라미터 형식 드롭다운
			let index = this.gridData.findIndex((p) => p.id === dataItem.id);
			let updated = Object.assign({}, this.gridData[index], {
				parameter_type: e.target.value.id,
				parameter_type_label: e.target.value.text,
				parameter_value: "",
				parameter_value_label: "",
			});
			this.gridData.splice(index, 1, updated);
		},
		ddParameterValueGroupChange: function (e, dataItem) {
			// 파라미터 그룹 드롭다운
			let index = this.gridData.findIndex((p) => p.id === dataItem.id);
			let updated = Object.assign({}, this.gridData[index], {
				parameter_value: e.target.value.id,
				parameter_value_label: e.target.value.text,
				input_code_no: e.target.value.input_code_no,
				input_code_name: e.target.value.input_code_name,
			});
			this.gridData.splice(index, 1, updated);
		},
		ddParameterValueOperatorChange: function (e, dataItem) {
			// 연산자 드롭다운
			let index = this.gridData.findIndex((p) => p.id === dataItem.id);
			let updated = Object.assign({}, this.gridData[index], {
				parameter_value: e.target.value,
				parameter_value_label: e.target.value,
			});
			this.gridData.splice(index, 1, updated);
		},
		ddCalculationApplyRateChange: function (e, dataItem) {
			// 적용산정등급 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.cal_apply_rate_no = Number(e.target.value.id);
			item.cal_apply_rate_name = e.target.value.text;
			this.gridData = updatedData;
		},
		ddParameterVersionChange: function (e, dataItem) {
			// 파라미터값 버전 드롭다운
			const updatedData = this.gridData.slice();
			const item = this.update(updatedData, dataItem);
			item.parameter_version = e.target.value;
			const info = { id: dataItem.parameter_id, version: e.target.value };
			fetchParameter(info).then(({ data }) => {
				item.parameter_value = data.parameter_value;
				item.parameter_measure_no = data.parameter_measure_no;
				item.parameter_measure_name = data.parameter_measure_name;
				item.parameter_version = e.target.value;
				this.gridData = updatedData;
			});
		},
		inputParameterValueChange: function (dataItem) {
			// 상수 입력
			let index = this.gridData.findIndex((p) => p.id === dataItem.id);
			let updated = Object.assign({}, this.gridData[index], {
				parameter_value: String(dataItem.parameter_value_label),
				parameter_value_label: String(dataItem.parameter_value_label),
			});
			this.gridData.splice(index, 1, updated);
		},
		itemChange: function (e) {
			// 데이터 변경 시 실행
			if (e.dataItem.id) {
				let index = this.gridData.findIndex((p) => p.id === e.dataItem.id);
				let updated = Object.assign({}, this.gridData[index], {
					[e.field]: e.value,
				});
				this.gridData.splice(index, 1, updated);
			} else {
				e.dataItem[e.field] = e.value;
			}
		},
		insertGroup: function () {
			let id = 1;
			this.gridData.map((v) => {
				if (v.cal_param_group) {
					id = Math.max(parseInt(v.cal_param_group) + 1, id);
				}
			});
			const dataItem = {
				cal_param_group: id,
				cal_param_index: 0,
				inEdit: true,
			};
			if (this.gridName == "calculation") {
				dataItem.parameter_type = this.selectParameterType[0].id;
				dataItem.parameter_type_label = this.selectParameterType[0].text;
			}
			this.groupId = id;
			this.gridData.splice(0, 0, dataItem);
		},
		insert: function () {
			if (this.insertDataCheck && this.groupId == undefined) {
				this.messageCheckDialog();
			} else {
				const dataItem = { inEdit: true };
				if (this.gridName == "calculation") {
					dataItem.cal_param_group = this.groupId;
					dataItem.cal_param_index =
						this.gridData.filter((i) => i.cal_param_group == this.groupId)
							.length + 1;
					dataItem.parameter_type = this.selectParameterType[0].id;
					dataItem.parameter_type_label = this.selectParameterType[0].text;
				}
				this.gridData.push(dataItem);
			}
		},
		edit: function (e) {
			// 데이터 편집
			let index = this.gridData.findIndex((p) => p.id === e.dataItem.id);
			let updated = Object.assign({}, this.gridData[index], {
				inEdit: true,
				isNew: false,
			});
			this.gridData.splice(index, 1, updated);
		},
		save: async function (e) {
			// 데이터 저장
			let index = this.gridData.findIndex((p) => p.id === e.dataItem.id);
			let item = this.gridData[index];
			let updated = Object.assign(this.update(this.gridData.slice(), item), {
				inEdit: undefined,
				isNew: undefined,
			});
			let updateDataIndex = this.updatedData.findIndex(
				(p) => p.id === e.dataItem.id
			);
			let data = {};
			if ("id" in updated) {
				// 데이터 수정
				data = updated;
				if (this.gridName == "calculation") {
					data.calculation_id = this.highRankDataItemId;
					data.tier_no = this.calculationGrade.id;
					data.tier_name = this.calculationGrade.text;
				}
				this.$emit("edit", data);
			} else {
				// 데이터 추가
				data = updated;
				if (this.gridName == "calculation") {
					data.calculation_id = this.highRankDataItemId;
					data.tier_no = this.calculationGrade.id;
					data.tier_name = this.calculationGrade.text;
				}
				await this.$emit("insert", data);
				updated.id = this.newDataId;
			}
			this.gridData.splice(index, 1, updated);
			this.updatedData.splice(updateDataIndex, 1, updated);
		},
		update: function (data, item, remove) {
			// 데이터 업데이트
			let updated;
			if (item) {
				let itemid = item.id;
				let index = data.findIndex(
					(p) =>
						JSON.stringify({ ...p }) === JSON.stringify(item) ||
						(itemid && p.id === itemid)
				);
				if (index >= 0) {
					updated = Object.assign({}, item);
					data[index] = updated;
				} else {
					let id = 1;
					data.forEach((p) => {
						id = Math.max(p.id + 1, id);
					});
					updated = Object.assign({}, item, { id: id });
					data.unshift(updated);
					index = 0;
				}
				if (remove) {
					data = data.splice(index, 1);
				}
				return data[index];
			}
		},
		cancel: function (e) {
			// 데이터 편집 취소
			if (e.dataItem.id) {
				let index = this.gridData.findIndex((p) => p.id === e.dataItem.id);
				let updateDataIndex = this.updatedData.findIndex(
					(p) => p.id === e.dataItem.id
				);
				let updated = Object.assign(this.gridData[updateDataIndex], {
					inEdit: undefined,
				});
				this.updatedData.splice(index, 1, updated);
			} else {
				let index = this.updatedData.findIndex(
					(p) => JSON.stringify(e.dataItem) === JSON.stringify(p)
				);

				this.updatedData.splice(index, 1);
			}
		},
		cancelAll: function () {
			// 편집 상태 초기화
			let editedItems = this.updatedData.filter((p) => p.inEdit === true);
			if (editedItems.length) {
				editedItems.forEach((item) => {
					item.inEdit = undefined;
				});
			}
			this.getData();
		},
		remove(e) {
			// 데이터 삭제
			this.rowData = e;
			if (this.deleteDataCheck) {
				this.messageDeleteDialog();
			} else {
				this.deleteData();
			}
		},
		getData: function () {
			// 데이터 세팅
			this.gridData = process(this.updatedData, {}).data;
		},
		deleteData: async function () {
			// 데이터 삭제
			this.rowData.dataItem.inEdit = undefined;
			this.update(this.gridData, this.rowData.dataItem, true);
			this.update(this.updatedData, this.rowData.dataItem, true);
			await this.$emit("remove", this.rowData.dataItem.id);
			if (this.collapsedGroups[0].length == 0) {
				this.groupId = undefined;
			}
			if (this.visibleDeleteDialog) {
				this.messageDeleteDialog();
			}
		},
		refreshData: function (dataItem) {
			// 데이터 새로고침
			this.updatedData = dataItem;
			this.getData();
		},
		returnData: function () {
			if (this.gridData) {
				return this.gridData;
			} else {
				return false;
			}
		},
		createAppState: function (dataState) {
			this.group = dataState.group;
			this.take = dataState.take;
			this.skip = dataState.skip;
			this.getData();
		},
		dataStateChange: function (event) {
			this.createAppState(event.data);
		},
		expandChange: function (event) {
			// 그룹 확장
			event.dataItem[event.target.$props.expandField] = event.value;
			if (event.value) {
				this.groupId = event.dataItem.value;
				this.collapsedGroups[0] = this.dataToShow.data
					.filter((i) => i.value !== event.dataItem.value)
					.map((i) => i.value);
			} else {
				this.collapsedGroups = event.collapsedGroups;
			}
		},
		expandHandler: function (dataItem) {
			// 그룹 확장
			dataItem.expanded =
				dataItem.expanded === undefined ? false : !dataItem.expanded;
			if (dataItem.value) {
				this.groupId = dataItem.value;
				this.collapsedGroups[0] = this.dataToShow.data
					.filter((i) => i.value !== dataItem.value)
					.map((i) => i.value);
			}
		},
		messageDeleteDialog: function () {
			// 메세지 팝업 show, hide
			this.visibleDeleteDialog = !this.visibleDeleteDialog;
		},
		messageCheckDialog: function () {
			// 메세지 팝업 show, hide
			this.visibleCheckDialog = !this.visibleCheckDialog;
		},
		calculationGradeChange: function (event) {
			// 산정식 파라미터 산정등급코드 변경 시 그리드 데이터 변경
			this.calculationGrade = event.target.value;
			this.refreshData(
				this.filterCalculationParameterList(this.calculationGrade.id)
			);
		},
		facilityEmissionVersionChange: function (version) {
			// 조직 및 시설관리 배출활동 버전 변경 시 그리드 데이터 변경
			const data =
				this.$store.state.facilityEmissionStore.facilityEmissionParameterList.filter(
					(v) => v.parameter_version == version
				);
			data.map((v) => (v.inEdit = true));
			this.refreshData(data);
		},
	},
};
</script>

