<template>
	<div class="tapWrap">
		<ul class="tapWrapUl d-flex">
			<li
				v-for="tab in tabsView"
				:key="tab.path"
				class="tapWrapLi d-flex align-items-center"
				:class="isActive(tab) ? 'active' : ''"
			>
				<router-link
					:to="{ path: tab.path, query: tab.query, fullPath: tab.fullPath }"
				>
					<p>{{ tab.meta.title }}</p>
				</router-link>
				<div
					v-if="!isClose(tabsView)"
					class="close close1"
					@click.prevent.stop="closeTab(tab)"
				/>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: "Tab",
	props: {
		backgroundColor: String,
		fontColor: String,
	},
	computed: {
		tabsView() {
			return this.$store.state.tabsView;
		},
	},
	watch: {
		$route() {
			this.addTabs();
		},
	},
	mounted() {
		this.addTabs();
	},
	methods: {
		isActive(route) {
			return route.path === this.$route.path;
		},
		isClose(tabsView) {
			const length = tabsView.length;
			if (length > 1) {
				return false;
			} else {
				return true;
			}
		},
		addTabs() {
			const { name } = this.$route;
			if (name) {
				this.$store.dispatch("ADD_TAB_VIEW", this.$route);
				this.$store.dispatch("ADD_CACHED_VIEW", this.$route);
			}
			const tabsView = this.$store.state.tabsView;
			if (tabsView.length > 5) {
				this.closeTab(tabsView[0]);
			}
			return false;
		},
		closeTab(view) {
			this.$store.dispatch("DEL_TAB_VIEW", view).then(({ tabsView }) => {
				if (this.isActive(view)) {
					this.toLastView(tabsView, view);
				}
			});
			this.$store.dispatch("DEL_CACHED_VIEW", view);
		},
		toLastView(tabsView, view) {
			const latestView = tabsView.slice(-1)[0];
			if (latestView) {
				this.$router.push(latestView.fullPath);
			} else {
				if (view.name === "Dashboard") {
					this.$router.replace({ path: view.fullPath });
				} else {
					this.$router.push("/");
				}
			}
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.tapWrap {
	margin: 10px 20px 0px 20px;

	.tapWrapUl {
		width: auto;
		cursor: pointer;
		border-bottom: 1px solid $basicTapBorderColor;

		.tapWrapLi {
			border: 1px solid $basicTapBorderColor;
			border-bottom: none;

			&.active {
				background: $basicTapBackGroundColora;
				border: 1px solid $basicTapBorderColora;

				p {
					font: normal normal normal 13px/17px AppleSDGothicNeoSB00;
					color: $basicTapColora;

					&.close1 {
						&:after {
							color: #898c9b;
						}

						&:after {
							color: #b2b5c5;
							content: "\00d7";
							font-size: 14pt;
						}
					}
				}
			}

			background: $basicTapBackGroundColor;
			flex-shrink: 0;
			min-width: 110px;
			height: 31px;
			// justify-content: space-around;
			border-radius: 6px 6px 0 0;
			margin-right: 4px;
			box-sizing: border-box;
			padding: 6px 14px;

			&:last-child {
				margin-right: 0;
			}

			p {
				font: normal normal normal 13px/18px AppleSDGothicNeoM00;
				color: $basicTapColor;
			}

			.close {
				position: relative;
				display: inline-block;
				height: 32px;
				text-align: center;
				margin-left: 20px;

				&.close1 {
					&:after {
						color: #b2b5c5;
						content: "\00d7";
						font-size: 14pt;
					}
				}
			}
		}
	}
}
</style>
