<template>
	<div :class="{ activesOnMain: ExpansionOn == true }" class="h-100-108">
		<div class="searchbarWrap">
			<div class="searchWrap Codebar">
				<k-form @submit="handleSubmit">
					<search-form
						:reset="true"
						@clearSearchForm="clearSearchForm"
						@searchFormStateA="searchFormStateA"
					>
						<template v-slot:formdata>
							<field
								:id="'dataCheck'"
								:name="'dataCheck'"
								:data-items="readTypeList"
								:layout="'horizontal'"
								:component="'radioGroup'"
							>
								<template v-slot:radioGroup="{ props }">
									<formradiogroup
										v-bind="props"
										@change="(e) => checkData(e)"
										@blur="props.onBlur"
										@focus="props.onFocus"
									/>
								</template>
							</field>
							<div class="shortBox">
								<div class="wrap">
									<label for=""><span>기준년도</span></label>
									<div class="lists">
										<div class="selectBox mr-20">
											<field
												:name="'year'"
												:style="{ maxWidth: 'auto' }"
												:component="'yearSelect'"
											>
												<template v-slot:yearSelect="{ props }">
													<drop-down-list
														v-bind="props"
														:style="{ width: 'auto' }"
														:data-items="$store.state.greenGasStore.yearList"
														:value="year"
														@change="yearChange"
													/>
												</template>
											</field>
										</div>
									</div>
								</div>
							</div>
							<div class="shortBox mr-2">
								<div class="wrap">
									<label for=""><span>조직</span></label>
									<div class="lists">
										<div class="selectBox mr-10">
											<field
												:name="'sector'"
												:component="'searchTemplate'"
												:style="{ maxWidth: '230px' }"
											>
												<template v-slot:searchTemplate="{ props }">
													<drop-down-list
														v-bind="props"
														:style="{ width: '140px' }"
														:data-items="sectorList"
														:value="sectorValue"
														:text-field="'text'"
														:data-item-key="'id'"
														@change="sectorChange"
													/>
												</template>
											</field>
										</div>
										<div class="selectBox mr-10">
											<field :name="'workplace'" :component="'searchTemplate'">
												<template v-slot:searchTemplate="{ props }">
													<drop-down-list
														v-bind="props"
														:style="{ width: '140px' }"
														:data-items="workplaceList"
														:value="workplaceValue"
														:text-field="'text'"
														:data-item-key="'id'"
														@change="workplaceChange"
													/>
												</template>
											</field>
										</div>
										<div class="selectBox mr-10">
											<field
												:name="'productionTeam'"
												:component="'searchTemplate'"
											>
												<template v-slot:searchTemplate="{ props }">
													<drop-down-list
														v-bind="props"
														:style="{ width: '120px' }"
														:data-items="productionTeamList"
														:value="productionTeamValue"
														:text-field="'text'"
														:data-item-key="'id'"
														@change="productionTeamChange"
													/>
												</template>
											</field>
										</div>
									</div>
								</div>
							</div>
						</template>
					</search-form>
				</k-form>
			</div>
		</div>
		<div
			class="p-10t2"
			:class="{
				activesOn4: ExpansionOn == true,
				active: this.searchFormStates,
				change: this.$store.state.resize,
			}"
			:style="{ height: this.$store.state.startResize }"
		>
			<div class="p-10 m-0a">
				<div class="grid-code">
					<v-table
						ref="greenGasTable"
						gridName="greenGasFacilityEmission"
						:dataItems="$store.state.greenGasStore.greenGasList"
						:count="$store.state.greenGasStore.greenGasCount"
						:columns="columns"
						:isNoData="isNoData"
						excelDownloadButtonText="엑셀 다운로드"
						:excelColumns="columns"
						:zoomMode="true"
						@Expansions2="Expansions2"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import FormRadioGroup from "../../components/form/FormRadioGroup.vue";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "greenGasFacilityEmission",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		formradiogroup: FormRadioGroup,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			ExpansionOn: false,
			readTypeList: [
				{
					label: " 배출시설별 온실가스 종류별 배출량 조회",
					value: "01",
				},
				{
					label: " 배출시설별 월별 온실가스 배출량 조회",
					value: "02",
				},
			],
			readTypeValue: "01",
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "year",
					title: "연도",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 110,
					width: 110,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					editable: false,
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "facility_serial_no",
					title: "배출시설 일련번호",
					editable: false,
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "facility_code_name",
					title: "배출 시설명",
					editable: false,
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "corporate_name",
					title: "자체 시설명",
					editable: false,
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "emission_code_name",
					title: "배출 활동명",
					minResizableWidth: 140,
					width: 140,
					editable: false,
				},
				{
					field: "activity_code_name",
					title: "활동자료(연료 등)",
					minResizableWidth: 140,
					width: 140,
					editable: false,
				},
				{
					field: "scope_code_name",
					title: "Scope",
					minResizableWidth: 120,
					width: 120,
					editable: false,
				},
				{
					field: "measure_name",
					title: "단위",
					minResizableWidth: 120,
					width: 120,
					editable: false,
				},
				{
					field: "total",
					title: "합계(CO2-eq)",
					minResizableWidth: 110,
					width: 110,
					hidden: false,
				},
				{
					field: "co2",
					title: "CO2",
					minResizableWidth: 70,
					width: 70,
					hidden: false,
				},
				{
					field: "ch4",
					title: "CH4",
					minResizableWidth: 70,
					width: 70,
					hidden: false,
				},
				{
					field: "n20",
					title: "N2O",
					minResizableWidth: 70,
					width: 70,
				},
				{
					field: "hfcs",
					title: "HFCs",
					minResizableWidth: 70,
					width: 70,
					hidden: false,
				},
				{
					field: "pfcs",
					title: "PFCs",
					minResizableWidth: 70,
					width: 70,
					hidden: false,
				},
				{
					field: "sf6",
					title: "SF6",
					minResizableWidth: 70,
					width: 70,
					hidden: false,
				},
				{
					field: "total",
					title: "합계",
					minResizableWidth: 110,
					width: 110,
					hidden: true,
				},
				{
					field: "january",
					title: "1월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "february",
					title: "2월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "march",
					title: "3월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "april",
					title: "4월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "may",
					title: "5월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "jun",
					title: "6월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "july",
					title: "7월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "august",
					title: "8월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "september",
					title: "9월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "october",
					title: "10월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "november",
					title: "11월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
				{
					field: "december",
					title: "12월",
					minResizableWidth: 70,
					width: 70,
					hidden: true,
				},
			];
		},
		yearList() {
			return this.$store.state.greenGasStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		// this.$store.dispatch("greenGasStore/SETTING_INIT");
		this.$store.dispatch("greenGasStore/FETCH_ORGANIZATION_YEAR_LIST");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?read_type=" + this.readTypeValue;
			this.searchData += "&year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"greenGasStore/FETCH_GREEN_GAS_FACILITYE_MISSION",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.greenGasTable !== null) {
						// for (let idx in this.columns) {
						// 	this.columns[idx].hidden = false;
						// }
						if (this.readTypeValue == "01") {
							for (var i = 10; i < 17; i++) {
								this.columns[i].hidden = false;
							}
							for (var j = 17; j < this.columns.length; j++) {
								this.columns[j].hidden = true;
							}
						} else {
							for (var h = 10; h < 17; h++) {
								this.columns[h].hidden = true;
							}
							for (var k = 17; k < this.columns.length; k++) {
								this.columns[k].hidden = false;
							}
						}
						if (data.items.length < 1) {
							this.isNoData = true;
						}
						this.$refs.greenGasTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		checkData: function (e) {
			this.readTypeValue = e.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("greenGasStore/SETTING_INIT");
			this.$store.dispatch("greenGasStore/FETCH_ORGANIZATION_YEAR_LIST");
		},
		Expansions2() {
			this.ExpansionOn = !this.ExpansionOn;
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
