<template>
	<div
		style="
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			width: 100%;
			position: absolute;
		"
	>
		<loader v-show="isShow" type="converging-spinner" />
	</div>
	<Chart
		v-show="!isShow"
		:pannable="true"
		:zoomable="true"
		:shared="true"
		:style="{ maxwidth: '1350px', width: '100%', height: '100%' }"
	>
		<ChartTooltip :visible="true" :shared="true" />
		<ChartTitle :text="title" />
		<ChartLegend :position="legendPosition" />
		<ChartSeries>
			<ChartSeriesItem
				v-for="(item, index) in data"
				:name="item.name"
				:data-items="item.data"
				:type="item.type"
				:key="index"
				:line-style="lineStyle"
				:markers="markers"
				:stack="stack"
			/>
		</ChartSeries>
		<ChartCategoryAxis>
			<ChartCategoryAxisItem :categories="categories">
				<ChartCategoryAxisTitle :text="categoriesTitle" />
			</ChartCategoryAxisItem>
		</ChartCategoryAxis>
	</Chart>
</template>

<script>
import "hammerjs";
import { Loader } from "@progress/kendo-vue-indicators";
import {
	Chart,
	ChartLegend,
	ChartSeries,
	ChartSeriesItem,
	ChartTooltip,
	ChartTitle,
	ChartCategoryAxis,
	ChartCategoryAxisItem,
	ChartCategoryAxisTitle,
} from "@progress/kendo-vue-charts";

export default {
	name: "CommonChart",
	props: {
		title: String,
		legendPosition: {
			type: String,
			default: "bottom",
		},
		data: Object,
		categories: Array,
		categoriesTitle: String,
		lineStyle: String,
		markers: Object,
		stack: Boolean,
	},
	components: {
		Chart,
		ChartLegend,
		ChartSeries,
		ChartSeriesItem,
		ChartTooltip,
		ChartTitle,
		ChartCategoryAxis,
		ChartCategoryAxisItem,
		ChartCategoryAxisTitle,
		Loader,
	},
	computed: {
		isShow() {
			if (this.data === undefined) {
				return true;
			} else {
				return false;
			}
		},
	},
};
</script>
