export default {
	SET_ACCOUNT_SIGNIN: (state, token) => {
		state.accessToken = token["access"];
		state.refreshToken = token["refresh"];
	},
	SET_PARAMETER_GROUP: (state, parameterGroup) => {
		state.parameterGroup = parameterGroup;
	},
	SET_PARAMETER_VERSION: (state, parameterVersion) => {
		state.parameterVersion = parameterVersion;
	},
	SET_SUCCESS: (state, successMessage) => {
		state.successMessage = successMessage;
	},
	ADD_TAB_VIEW: (state, view) => {
		if (state.tabsView.some((tabView) => tabView.path === view.path)) return;
		state.tabsView.push(
			Object.assign({}, view, {
				title: view.name || "None",
			})
		);
	},
	ADD_CACHED_VIEW: (state, view) => {
		if (state.cachedViews.includes(view.name)) return;
		if (!view.meta.noCache) {
			state.cachedViews.push(view.name);
		}
	},
	DEL_TAB_VIEW: (state, view) => {
		for (const [i, tabView] of state.tabsView.entries()) {
			if (tabView.path === view.path) {
				state.tabsView.splice(i, 1);
				break;
			}
		}
	},
	DEL_CACHED_VIEW: (state, view) => {
		const index = state.cachedViews.indexOf(view.name);
		index > -1 && state.cachedViews.splice(index, 1);
	},
	SET_PARENT_MENU: (state, menu) => {
		state.parentMenu = menu;
	},
};
