import {
	fetchFacilityEmissionList,
	fetchFacilityEmissionParameterList,
} from "@/api/index.js";

const facilityEmissionStore = {
	namespaced: true,
	state: {
		facilityEmissionList: [],
		facilityEmissionCount: 0,
		facilityEmissionParameterList: [],
		facilityEmissionParameterCount: 0,
	},
	mutations: {
		SET_FACILITY_EMISSION_LIST: (state, facilityEmission) => {
			state.facilityEmissionList = facilityEmission.items;
			state.facilityEmissionList.map((v) => {
				v.disposal_date = new Date(v.disposal_date);
			});
			state.facilityEmissionCount = facilityEmission.count;
		},
		SET_FACILITY_EMISSION_PARAMETER_LIST: (
			state,
			facilityEmissionParameter
		) => {
			state.facilityEmissionParameterList = facilityEmissionParameter.items;
			state.facilityEmissionParameterCount = facilityEmissionParameter.count;
		},
	},
	actions: {
		FETCH_FACILITY_EMISSION_LIST: ({ commit }, id) => {
			// 시설 배출활동 목록 조회
			return fetchFacilityEmissionList(id)
				.then(({ data }) => {
					return commit("SET_FACILITY_EMISSION_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_FACILITY_EMISSION_PARAMETER_LIST: ({ commit }, fac_emi_id) => {
			// 시설 배출활동 파라미터 목록 조회
			return fetchFacilityEmissionParameterList(fac_emi_id)
				.then(({ data }) => {
					return commit("SET_FACILITY_EMISSION_PARAMETER_LIST", data);
				})
				.catch((error) => console.log(error));
		},
	},
};

export default facilityEmissionStore;
