const resize = () => {
	return {
		mounted() {
			this.handleResize();
			window.addEventListener("resize", this.handleResize);
		},
		unmounted() {
			window.addEventListener("resize", this.handleResize);
		},
		methods: {
			handleResize: function () {
				this.$nextTick(function () {
					if (document.getElementById("searchForm")) {
						setTimeout(() => {
							let searchForm = document.getElementById("searchForm");
							let sclientHeight =
								searchForm.closest(".searchWrap ").clientHeight;
							console.log(sclientHeight);
							if (sclientHeight == 50) {
								this.$store.state.modalResizeBle2[0] =
									"calc(100% - 24px) !important";
							}
							if (sclientHeight > 110) {
								this.$store.state.modalResizeBle2[0] =
									"calc(100% - 26px) !important";
							}
							if (sclientHeight > 140 || sclientHeight > 145) {
								this.$store.state.resize = true;
								this.$store.state.modalResizeBle[0] =
									"calc(100% - 24px) !important";
								this.$store.state.resizeble[0] = "calc(100% - 113px)";
								this.$store.state.startResize = "calc(100% - 161px)";
								this.$store.state.modalResizeBle2[0] =
									"calc(100%  - 26px) !important";
							} else {
								this.$store.state.resize = false;
								this.$store.state.newReize = "";
								this.$store.state.startResize = "";
							}
							if (sclientHeight > 160) {
								this.$store.state.modalResizeBle[0] =
									"calc(100% - -76px) !important";
								this.$store.state.modalResizeBle2[0] =
									"calc(100% - -24px) !important";

								this.$store.state.resizeble2[0] = "calc(100% - 163px)";
							}
							if (sclientHeight >= 195 && sclientHeight < 211) {
								this.$store.state.startResize = "calc(100% - 209px)";

								this.$store.state.resizeble[0] = "calc(100% - 162px)";
								this.$store.state.modalResizeBle[0] =
									"calc(100% + 27px) !important";
							}
							if (sclientHeight >= 211) {
								this.$store.state.newReize = "calc(100vh - 372px)";
								this.$store.state.modalResizeBle2[0] =
									"calc(100% + 77px) !important";
								this.$store.state.resizeble2[0] = "calc(100% - 214px)";
								this.$store.state.resizeLast = "calc(100vh - 370px)";
							} else {
								this.$store.state.resizeLast = "";
							}
							if (sclientHeight >= 245 && sclientHeight < 261) {
								this.$store.state.modalResizeBle2[0] =
									"calc(100% + 75px) !important";
								this.$store.state.resizeble[0] = "calc(100% - 213px)";
								this.$store.state.modalResizeBle[0] =
									"calc(100% + 77px) !important";
							}
							if (sclientHeight >= 261) {
								this.$store.state.resizeLast = "calc(100vh - 424px)";
								this.$store.state.modalResizeBle[0] =
									"calc(100% + 127px) !important";
								this.$store.state.modalResizeBle2[0] =
									"calc(100% + 126px) !important";
								this.$store.state.resizeble[0] = "calc(100% - 264px)";
								this.$store.state.resizeble2[0] = "calc(100% - 264px)";
							}
						}, 250);
					}
				});
			},
		},
	};
};

export default resize;
