<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획기간</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="periodList"
													:value="periodValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="periodChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획년도</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
		:style="{ height: this.$store.state.resizeLast }"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					title="계획기간 BAU-할당량 관리"
					gridName="emissionBauYear"
					:dataItems="$store.state.emissionBauYearStore.dataList"
					:columns="columns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:isNoData="isNoData"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "EmissionBauYear",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			yearList: ["전체", 2021, 2022, 2023, 2024, 2025],
			year: "전체",
			orgYear: "",
			orgList: [],
			periodList: [{ id: "01", text: "3차" }],
			periodValue: { id: "01", text: "3차" },
			sectorList: [],
			sectorValue: {},
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "measure_name",
					title: "단위",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
			];
			if (this.periodValue.id === "01") {
				for (var i = 2021; i < 2026; i++) {
					column.push({
						field: "area",
						title: i + "",
						children: [
							{
								field: "bau_" + i,
								title: "BAU",
								minResizableWidth: 100,
								cell: this.cellWithBackGround,
								width: 100,
							},
							{
								field: "quota_" + i,
								title: "할당량",
								minResizableWidth: 130,
								cell: this.cellWithBackGround,
								width: 130,
							},
							{
								field: "shortfall_" + i,
								title: "부족량",
								minResizableWidth: 130,
								cell: this.cellWithBackGround,
								width: 130,
							},
						],
						hidden: false,
					});
				}
			}
			return column;
		},
	},
	watch: {
		orgYear(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.orgYear = new Date().getFullYear();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.orgYear)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.orgYear +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					// 부문 SET
					this.sectorList = [];
					data.items.map((v) =>
						this.sectorList.push({ id: v.org_id, text: v.org_name })
					);
					this.sectorValue = {
						id: data.items[0].org_id,
						text: data.items[0].org_name,
					};
					if (this.renderKey == 0) {
						this.renderKey++;
						this.handleSubmit();
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?period=" + this.periodValue.id;
			if (this.year !== "전체") {
				this.searchData += "&year=" + this.year;
			}
			this.searchData += "&sector_id=" + this.sectorValue.id;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"emissionBauYearStore/FETCH_EMISSION_BAU_YEAR",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						var totalBau2021 = 0;
						var totalQuota2021 = 0;
						var totalShortfall2021 = 0;
						var totalBau2022 = 0;
						var totalQuota2022 = 0;
						var totalShortfall2022 = 0;
						var totalBau2023 = 0;
						var totalQuota2023 = 0;
						var totalShortfall2023 = 0;
						var totalBau2024 = 0;
						var totalQuota2024 = 0;
						var totalShortfall2024 = 0;
						var totalBau2025 = 0;
						var totalQuota2025 = 0;
						var totalShortfall2025 = 0;
						// 조회한 연도만 컬럼만 노출
						if (this.year === "전체") {
							for (var i = 2; i < 7; i++) {
								this.columns[i].hidden = false;
							}
						} else {
							for (var j = 2; j < 7; j++) {
								this.columns[j].hidden = true;
							}
							let index = this.columns.findIndex(
								(p) => p.title === this.year + ""
							);
							this.columns[index].hidden = false;
						}
						if (data.count < 1) {
							this.isNoData = true;
						} else {
							for (var idx in data.items) {
								totalBau2021 += data.items[idx].bau_2021;
								totalQuota2021 += data.items[idx].quota_2021;
								totalShortfall2021 += data.items[idx].shortfall_2021;
								totalBau2022 += data.items[idx].bau_2022;
								totalQuota2022 += data.items[idx].quota_2022;
								totalShortfall2022 += data.items[idx].shortfall_2022;
								totalBau2023 += data.items[idx].bau_2023;
								totalQuota2023 += data.items[idx].quota_2023;
								totalShortfall2023 += data.items[idx].shortfall_2023;
								totalBau2024 += data.items[idx].bau_2024;
								totalQuota2024 += data.items[idx].quota_2024;
								totalShortfall2024 += data.items[idx].shortfall_2024;
								totalBau2025 += data.items[idx].bau_2025;
								totalQuota2025 += data.items[idx].quota_2025;
								totalShortfall2025 += data.items[idx].shortfall_2025;
							}
							data.items.push({
								id: 0,
								workplace_name: "합계",
								bau_2021: totalBau2021,
								quota_2021: totalQuota2021,
								shortfall_2021: totalShortfall2021,
								bau_2022: totalBau2022,
								quota_2022: totalQuota2022,
								shortfall_2022: totalShortfall2022,
								bau_2023: totalBau2023,
								quota_2023: totalQuota2023,
								shortfall_2023: totalShortfall2023,
								bau_2024: totalBau2024,
								quota_2024: totalQuota2024,
								shortfall_2024: totalShortfall2024,
								bau_2025: totalBau2025,
								quota_2025: totalQuota2025,
								shortfall_2025: totalShortfall2025,
							});
						}
						this.$refs.DataTable.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		periodChange(e) {
			this.periodValue = e.target.value;
		},
		yearChange(event) {
			this.year = event.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.year = "전체";
			this.handleSubmit();
		},
		cellWithBackGround: function (h, tdElement, props, listeners) {
			const value = tdElement.children[0];
			listeners;
			if (parseInt(value) < 0) {
				return h(
					"td",
					{
						style: { color: "red" },
					},
					[value]
				);
			} else {
				return h(
					"td",
					{
						style: { color: "black" },
					},
					[value]
				);
			}
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
