<template>
	<div v-if="type == 'organization'">
		<!-- subbar 시작 -->
		<div>
			<!-- subbar 기준년도 셀렉트-->
			<p class="basicYear">기준 연도</p>
			<td class="k-command-cell selectBox">
				<drop-down-list
					:data-items="$store.state.organizationStore.yearList"
					:value="$store.state.organizationStore.year"
					@change="change"
				>
				</drop-down-list>
			</td>
		</div>
		<!-- kendo treeview box-->
		<treeview :animation-expand-effects="effects" :key="renderKey">
			<li
				v-for="item1depth in orgList"
				:key="item1depth.index"
				class="subtitlebox"
			>
				<div
					class="box active"
					@click="setData($event, item1depth.depth, item1depth.org_id)"
				>
					<span></span
					><span class="subtext main1 title">{{ item1depth.org_name }}</span>
				</div>
				<ul v-if="item1depth.sub_menu.length > 0">
					<li v-for="item2depth in item1depth.sub_menu" :key="item2depth.index">
						<div
							class="box"
							@click="setData($event, item2depth.depth, item2depth.org_id)"
						>
							<span></span
							><span class="subtext main2">{{ item2depth.org_name }}</span>
						</div>
						<ul v-if="item2depth.sub_menu.length > 0">
							<li
								v-for="item3depth in item2depth.sub_menu"
								:key="item3depth.index"
							>
								<div
									class="box"
									@click="setData($event, item3depth.depth, item3depth.org_id)"
								>
									<span></span
									><span class="subtext main3">{{ item3depth.org_name }}</span>
								</div>
								<ul v-if="item3depth.sub_menu.length > 0">
									<li
										v-for="item4depth in item3depth.sub_menu"
										:key="item4depth.index"
									>
										<div
											class="box"
											@click="
												setData($event, item4depth.depth, item4depth.org_id)
											"
										>
											<span></span
											><span class="subtext main4">{{
												item4depth.org_name
											}}</span>
										</div>

										<ul v-if="item4depth.sub_menu.length > 0">
											<li
												v-for="item5depth in item4depth.sub_menu"
												:key="item5depth.index"
											>
												<div
													class="box"
													@click="
														setData($event, item5depth.depth, item5depth.org_id)
													"
												>
													<span class="subBoxLines"></span
													><span class="subtext main5">{{
														item5depth.org_name
													}}</span>
												</div>
											</li>
										</ul>
									</li>
								</ul>
							</li>
						</ul>
					</li>
				</ul>
			</li>
		</treeview>
	</div>
	<div v-if="type == 'menu'">
		<!-- subbar 시작 -->
		<div>
			<!-- subbar 기준년도 셀렉트-->
			<p class="basicYear">메뉴 선택</p>
		</div>
		<!-- kendo treeview box-->
		<treeview :animation-expand-effects="effects" :key="renderKey">
			<li
				v-for="item1depth in menuList"
				:key="item1depth.index"
				class="subtitlebox"
			>
				<div class="box active">
					<span></span
					><span class="subtext main title active">{{
						item1depth.menu_name
					}}</span>
				</div>
				<ul v-if="item1depth.sub_menu.length > 0" class="uls">
					<li v-for="item2depth in item1depth.sub_menu" :key="item2depth.index">
						<div class="box2" @click="setMenuData($event, item2depth.menu_id)">
							<span class="subBoxLines"></span
							><span class="subtext main">{{ item2depth.menu_name }}</span>
						</div>
						<ul v-if="item2depth.sub_menu.length > 0" class="uls">
							<li
								v-for="item3depth in item2depth.sub_menu"
								:key="item3depth.index"
							>
								<div
									class="box2"
									@click="setMenuData($event, item3depth.menu_id)"
								>
									<span class="subBoxLines"></span
									><span class="subtext mad">{{ item3depth.menu_name }}</span>
								</div>
							</li>
						</ul>
					</li>
				</ul>
			</li>
		</treeview>
	</div>
</template>

<script>
import { TreeView } from "@progress/kendo-treeview-vue-wrapper";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
export default {
	name: "Subbar",
	components: {
		treeview: TreeView,
		DropDownList,
	},
	props: {
		type: String,
	},
	data() {
		return {
			animationeffect: "expand:vertical",
			opacity: true,
			renderKey: 0,
			state: false,
			name: 0,
		};
	},

	computed: {
		//subBar effects

		effects() {
			var effect = this.animationeffect + " " + (this.opacity ? "fadeIn" : "");
			return effect;
		},
		yearList() {
			return this.$store.state.organizationStore.yearList;
		},
		orgList() {
			return this.$store.state.organizationStore.organizationList;
		},
		menuList() {
			return this.$store.state.menuStore.menuList;
		},
	},
	watch: {
		orgList() {
			this.renderKey += 1;
		},
		menuList() {
			this.renderKey += 1;
		},
	},

	methods: {
		//subBar Arrow toggle color;
		setData(event, depth, org_id) {
			this.$store.state.organizationStore.depth = depth;
			this.$store.state.organizationStore.org_id = org_id;
			// 하위조직 생성 후 조직클릭하여 넘어갔을 경우
			if (this.$store.state.organizationStore.org_parent_id !== "") {
				this.$store.dispatch("organizationStore/SETTING_ORG_PARENT_INIT");
				this.$store.dispatch("organizationStore/FETCH_ORGANIZATION_INFO", {
					org_id: org_id,
				});
			}

			let targets = event.target;
			let t1 = document.querySelectorAll(".box");

			for (let i = 0; i < t1.length; i++) {
				t1[i].classList.remove("active");
			}

			if (targets.closest(".k-item").querySelector(".main1")) {
				targets
					.closest(".k-item")
					.querySelector(".box")
					.classList.add("active");
			}
			if (targets.closest(".k-item").querySelector(".main2")) {
				targets
					.closest(".k-item")
					.querySelector(".box")
					.classList.add("active");
			}
			if (targets.closest(".k-item").querySelector(".main3")) {
				targets
					.closest(".k-item")
					.querySelector(".box")
					.classList.add("active");
			}
			if (targets.closest(".k-item").querySelector(".main4")) {
				targets
					.closest(".k-item")
					.querySelector(".box")
					.classList.add("active");
			}
			if (targets.closest(".k-item").querySelector(".main5")) {
				targets
					.closest(".k-item")
					.querySelector(".box")
					.classList.add("active");
			}
		},
		setMenuData(event, menu_id) {
			let box2 = document.querySelectorAll(".box2");
			for (let i = 0; i < box2.length; i++) {
				box2[i].classList.remove("active");
			}
			event.target.closest(".box2").classList.add("active");
			this.$store.state.menuStore.menu_id = menu_id;
		},
		change(e) {
			console.log(e);
			this.$store.dispatch("organizationStore/SETTING_DEPTH_INIT");
			this.$store.state.organizationStore.year = e.target.value;
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/App.scss";

.subtextEtc {
	white-space: break-spaces;
}
.subtext {
	white-space: break-spaces;
}
</style>
