<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar" style="height: auto">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>코드 그룹</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect'"
										>
											<template v-slot:codeGroupSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="selectCodeGroup"
													:value="codeGroupValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="dropdownChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="longBox">
							<div class="wrap d-flex align-items-center">
								<label for=""><span>검색어</span></label>
								<div class="searchInputBox d-flex align-items-center">
									<field
										:name="'code'"
										:style="{ width: '100%' }"
										:component="'codeInput'"
										:placeholder="'코드 ID 또는 코드명을 입력해주세요.'"
									>
										<template v-slot:codeInput="{ props }">
											<autocomplete
												v-bind="props"
												:data-items="searchCode"
												:value="codeValue"
												@change="autoChange"
												style="padding-left: 10px"
											/>
										</template>
									</field>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					v-if="$store.state.codeStore.codeList.length !== 0"
					ref="CodeGrid"
					title="코드 목록"
					gridName="code"
					:dataItems="$store.state.codeStore.codeList"
					:count="$store.state.codeStore.codeCount"
					:propsSort="[
						{ field: 'group_name', dir: 'asc' },
						{ field: 'code_id', dir: 'asc' },
					]"
					:columns="codeColumns"
					:newDataId="newDataId"
					:selectCodeGroup="selectCodeGroup"
					:deleteDataCheck="true"
					insertButtonText="코드추가"
					popupButtonText="코드그룹관리"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@isEditableChange="isEditableChange"
					@gridDialog="gridDialog"
				/>
			</div>
		</div>
	</div>
	<modal v-if="visibleDialog" :title="'그룹코드관리'" @close="gridDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code">
					<v-table
						v-if="$store.state.codeStore.codeGroup.length !== 0"
						ref="CodeGroupGrid"
						title="그룹코드 목록"
						gridName="codeGroup"
						:dataItems="$store.state.codeStore.codeGroup"
						:count="$store.state.codeStore.codeGroupCount"
						:columns="codeGroupColumns"
						:newDataId="newDataId"
						insertButtonText="그룹추가"
						@insert="dialogInsert"
						@edit="dialogEdit"
						@remove="dialogRemove"
						@isEditableChange="isEditableChange"
					/>
				</div>
			</div>
		</template>
	</modal>
</template>
<script>
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import { AutoComplete } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";

import {
	insertCode,
	editCode,
	deleteCode,
	insertCodeGroup,
	editCodeGroup,
	deleteCodeGroup,
} from "@/api/index.js";

export default {
	name: "code",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		dropdownlist: DropDownList,
		autocomplete: AutoComplete,
		VTable,
		Modal,
	},
	data: function () {
		return {
			newDataId: "0",
			year: [2022, 2021, 2020, 2019, 2018, 2017, 2016, 2015, 2014, 2013, 2012],
			year_value: 2022,
			searchData: null,
			codeValue: "",
			codeGroupValue: { text: "전체" },
			selectCodeGroup: [{ text: "전체" }],
			codeGroupColumns: [
				{
					field: "group_id",
					editable: false,
					title: "그룹ID",
					minResizableWidth: 80,
					width: 80,
				},
				{
					field: "group_name",
					title: "그룹명",
					minResizableWidth: 180,
					width: 180,
				},
				{
					field: "create_user",
					editable: false,
					title: "등록자",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "create_date",
					editable: false,
					title: "등록일",
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 100,
					width: 100,
				},

				{
					field: "update_user",
					editable: false,
					title: "수정자",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "update_date",
					editable: false,
					title: "수정일",
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 100,
					width: 100,
				},
				{ cell: "commandCell", minResizableWidth: 130, width: 130 },
			],
			visibleDialog: false,
			searchFormStates: false,
		};
	},
	computed: {
		searchCode() {
			const searchcode = [];
			for (let code in this.$store.state.codeStore.codeList) {
				searchcode.push(this.$store.state.codeStore.codeList[code].code_id);
				searchcode.push(this.$store.state.codeStore.codeList[code].code_name);
			}
			return searchcode;
		},
		codeColumns() {
			return [
				{
					field: "group_name",
					title: "코드 그룹",
					cell: "dropdownCodeGroupCell",
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "code_id",
					title: "코드 ID",
					editable: this.isEditable,
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "code_name",
					title: "코드명",
					minResizableWidth: 180,
					width: 180,
				},
				{
					field: "use_yn",
					title: "사용여부",
					cell: "checkboxCell",
					minResizableWidth: 105,
					width: 105,
				},
				{
					field: "create_user",
					title: "등록자",
					minResizableWidth: 140,
					width: 140,
					editable: false,
				},
				{
					field: "create_date",
					title: "등록일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "update_user",
					title: "수정자",
					minResizableWidth: 130,
					width: 130,
					editable: false,
				},
				{
					field: "update_date",
					title: "수정일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 130,
					width: 130,
				},
				{ cell: "commandCell", minResizableWidth: 170, width: 170 },
			];
		},
	},
	created: async function () {
		this.$store.dispatch("codeStore/FETCH_CODE_LIST");
		await this.$store.dispatch("codeStore/FETCH_CODE_GROUP_LIST");
		this.getCodeGroupData();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		insert: function (data) {
			insertCode(data)
				.then(({ data }) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		edit: function (data) {
			console.log(data);
			editCode(data);
		},
		remove: function (id) {
			deleteCode(id);
		},
		dialogInsert: function (data) {
			insertCodeGroup(data)
				.then(({ data }) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		dialogEdit: function (data) {
			editCodeGroup(data);
		},
		dialogRemove: function (id) {
			deleteCodeGroup(id);
		},
		getCodeGroupData() {
			for (let group in this.$store.state.codeStore.codeGroup) {
				this.selectCodeGroup.push({
					id: this.$store.state.codeStore.codeGroup[group].group_id,
					text: this.$store.state.codeStore.codeGroup[group].group_name,
				});
			}
		},
		gridDialog: function () {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.CodeGroupGrid.cancelAll();
			}
		},
		handleSubmit: async function (dataItem) {
			this.searchData = "";
			if ("group" in dataItem) {
				if ("id" in dataItem.group) {
					this.searchData += "?group=" + String(dataItem.group.id);
				}
			}
			if ("code" in dataItem) {
				this.searchData +=
					`${Object.keys(dataItem).length >= 2 ? "&" : "?"}code=` +
					dataItem.code;
			}
			this.fetchGridData();
		},
		fetchGridData: async function () {
			await this.$store.dispatch("codeStore/FETCH_CODE_LIST", this.searchData);
			this.$refs.CodeGrid.refreshData();
		},
		dropdownChange: function (event) {
			this.codeGroupValue = event.value;
		},
		autoChange: function (event) {
			this.codeValue = event.target.value;
		},
		clearSearchForm: function () {
			this.searchData = null;
			this.codeValue = "";
			this.codeGroupValue = { text: "전체" };
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}
.longBox {
	width: 52% !important;
	.searchInputBox {
		input {
			padding-left: 20px !important;
		}
	}
}
.Codebar {
	padding: 16px 20px 5px 20px;
	height: 100px;
	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}
	.buttonBoxs {
		width: 200px;
	}
}
@media (max-width: 1019px) {
	.longBox {
		width: 80% !important;
	}
}
</style>
