import {
	fetchUserInfo,
	insertUserInfo,
	editUserInfo,
	deleteUserInfo,
	fetchUserPermissions,
	insertUserPermissions,
	editUserPermissionse,
	deleteUserPermissions,
} from "@/api/index.js";

const userInfoStore = {
	namespaced: true,
	state: {
		userList: [],
		userCount: 0,
		userPermissionsList: [],
		userPermissionsCount: 0,
	},
	mutations: {
		SET_USER_INFO_LIST: (state, data) => {
			state.userList = data.items;
			state.userList.map((v) => {
				v.id = v.user_id;
			});
			state.userCount = data.count;
		},
		SET_USER_PERMISSIONS: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.userPermissionsList = data.items;
			state.userPermissionsCount = data.count;
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_USER_INFO_LIST: ({ commit }, info) => {
			// 사용자 권한 목록 조회
			return fetchUserInfo(info)
				.then(({ data }) => {
					commit("SET_USER_INFO_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_USER_INFO: ({ commit }, info) => {
			// 사용자 권한 추가
			return insertUserInfo(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_USER_INFO: ({ commit }, info) => {
			// 사용자 권한 수정
			return editUserInfo(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_USER_INFO: ({ commit }, id) => {
			// 사용자 권한 삭제
			return deleteUserInfo(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_USER_PERMISSIONS_LIST: ({ commit }) => {
			// 권한 그룹 목록 조회
			return fetchUserPermissions()
				.then(({ data }) => {
					commit("SET_USER_PERMISSIONS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_USER_PERMISSIONS: ({ commit }, info) => {
			// 권한 그룹 추가
			return insertUserPermissions(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_USER_PERMISSIONS: ({ commit }, info) => {
			// 권한 그룹 수정
			return editUserPermissionse(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_USER_PERMISSIONS: ({ commit }, info) => {
			// 권한 그룹 삭제
			return deleteUserPermissions(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
	},
};

export default userInfoStore;
