<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조회연도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.exEmissionTradingStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="dataGrid"
					title="외부 배출권거래 관리"
					gridName="exEmissionTrading"
					:dataItems="$store.state.exEmissionTradingStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					:selectEmissionTradingType="typeDropDownList"
					:selectEmissionTradingMode="modeDropDownList"
					:selectEmissionPrice="emissionPriceDropDownList"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:deleteDataCheck="true"
					:datePickerFormat="'yyyy.MM.dd'"
					insertButtonText="추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "ExEmissionTrading",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			newDataId: "0",
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			typeDropDownList: [
				{ id: "S", text: "매도" },
				{ id: "B", text: "매수" },
			],
			modeDropDownList: [
				{ id: "I", text: "장내" },
				{ id: "E", text: "장외" },
			],
			emissionPriceDropDownList: [],
			attchFile: null,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "trading_date",
					title: "거래일자",
					minResizableWidth: 260,
					width: 260,
					cell: "datePickerCell",
				},
				{
					field: "type_name",
					title: "구분",
					cell: "dropdownEmissionTradingTypeCell",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "mode_name",
					title: "거래방식",
					cell: "dropdownEmissionTradingModeCell",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "emission_price_name",
					title: "거래종목",
					cell: "dropdownEmissionPriceCell",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "seller",
					title: "거래처명",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "registration_number",
					title: "거래처(사업자통번호)",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "volume",
					title: "거래량(ton)",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "unit_price",
					title: "거래단가(ton)",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "price",
					title: "거래금액(억원)",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "remarks",
					title: "비고",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "file_url",
					title: "근거자료",
					cell: "FileFormCell",
					minResizableWidth: 210,
					width: 210,
					editable: false,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
		yearList() {
			return this.$store.state.exEmissionTradingStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchEmissionUnitPriceList();
			}
		},
	},
	created() {
		this.$store.dispatch("exEmissionTradingStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch(
			"exEmissionTradingStore/FETCH_EX_EMISSION_TRADING_YEAR_LIST"
		);
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 배출권 거래종목 목록 */
		fetchEmissionUnitPriceList() {
			let searchData = "";
			searchData += "?year=" + this.year;
			this.$store
				.dispatch(
					"emissionUnitPriceStore/FETCH_EMISSION_UNIT_PRICE_LIST",
					searchData
				)
				.then((data) => {
					this.emissionPriceDropDownList = [];
					data.items.map((v) => {
						this.emissionPriceDropDownList.push({
							id: v.id,
							text: v.trading_item,
						});
					});
					if (this.renderKey == 0) {
						this.renderKey++;
						this.handleSubmit();
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"exEmissionTradingStore/FETCH_EX_EMISSION_TRADING_LIST",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.dataGrid !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.dataGrid.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("exEmissionTradingStore/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch(
				"exEmissionTradingStore/FETCH_EX_EMISSION_TRADING_YEAR_LIST"
			);
		},
		insert: function (data) {
			const formData = new FormData();
			formData.append("trading_date", this.formatDate(data.data.trading_date));
			formData.append("type", data.data.type);
			formData.append("mode", data.data.mode);
			formData.append("emission_price_id", data.data.emission_price_id);
			formData.append("seller", data.data.seller);
			formData.append("registration_number", data.data.registration_number);
			formData.append("volume", data.data.volume);
			formData.append("unit_price", data.data.unit_price);
			formData.append("price", data.data.price);
			formData.append("remarks", data.data.remarks);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("exEmissionTradingStore/INSERT_EX_EMISSION_TRADING", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		edit: function (data) {
			const formData = new FormData();
			formData.append("ex_trading_id", data.data.ex_trading_id);
			formData.append("trading_date", this.formatDate(data.data.trading_date));
			formData.append("type", data.data.type);
			formData.append("mode", data.data.mode);
			formData.append("emission_price_id", data.data.emission_price_id);
			formData.append("seller", data.data.seller);
			formData.append("registration_number", data.data.registration_number);
			formData.append("volume", data.data.volume);
			formData.append("unit_price", data.data.unit_price);
			formData.append("price", data.data.price);
			formData.append("remarks", data.data.remarks);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("exEmissionTradingStore/INSERT_EX_EMISSION_TRADING", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		formatDate: function (date) {
			// 날짜 format하여 요청
			let month = date.getMonth() + 1;
			let day = date.getDate();
			month = month >= 10 ? month : "0" + month;
			day = day >= 10 ? day : "0" + day;
			return date.getFullYear() + "-" + month + "-" + day;
		},
		remove: function (id) {
			this.$store.dispatch(
				"exEmissionTradingStore/DELETE_EX_EMISSION_TRADING",
				id
			);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"exEmissionTradingStore/DELETE_EX_EMISSION_TRADING_FILE",
				e.ex_trading_id
			);
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
