import {
	fetchOrganizationYearList,
	fetchCompanyEmissionManagement,
	editCompanyEmissionManagement,
	fetchCompanyCarriedManagement,
} from "@/api/index.js";

const companyEmissionManagementStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		carrieList: [],
		carrieCount: 0,
		yearList: [],
		startYear: "",
		endYear: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.startYear = data.items[data.items.length - 1];
			state.endYear = data.items[0];
		},
		SET_COMPANY_EMISSION_MANAGEMENT: (state, data) => {
			var rowSpan = 0;
			data.items.map((v) => (v.measure = v.measure_name));
			for (var idx in data.items) {
				rowSpan++;
				if (rowSpan % 10 !== 1) {
					data.items[idx].sector_name = "";
					data.items[idx].workplace_name = "";
				}
			}
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_COMPANY_CARRIED_MANAGEMENT: (state, data) => {
			state.carrieList = data.items;
			state.carrieList.map((v) => (v.id = v.cp_carried_id));
			state.carrieCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.startYear = "";
			state.endYear = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_COMPANY_EMISSION_MANAGEMENT: ({ commit }, info) => {
			// 전사 배출권 총괄 관리 조회
			return fetchCompanyEmissionManagement(info)
				.then(({ data }) => {
					commit("SET_COMPANY_EMISSION_MANAGEMENT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_COMPANY_EMISSION_MANAGEMENT: (info) => {
			// 전사 배출권 총괄 관리 일괄 수정
			return editCompanyEmissionManagement(info.state.dataList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_COMPANY_CARRIED_MANAGEMENT: ({ commit }, info) => {
			// 전사 배출권 차입/이월 목록 조회
			return fetchCompanyCarriedManagement(info)
				.then(({ data }) => {
					commit("SET_COMPANY_CARRIED_MANAGEMENT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default companyEmissionManagementStore;
