import {
	fetchOrganizationYearList,
	fetchBusinessPerformanceList,
	insertBusinessPerformance,
	insertBusinessPerformanceFile,
	deleteBusinessPerformanceFile,
	fetchBusinessPerformanceMonthList,
} from "@/api/index.js";

const bsnprInqryStore = {
	namespaced: true,
	state: {
		businessPerformanceList: [],
		businessPerformanceCount: 0,
		businessPerformanceMonthList: [],
		businessPerformanceMonthCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_BUSINESS_PERFORMANCE_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].id = parseInt(idx) + 1;
			}
			state.businessPerformanceList = data.items;
			state.businessPerformanceCount = data.count;
		},
		SET_BUSINESS_PERFORMANCE_MONTH_LIST: (state, data) => {
			var rowSpan = 0;
			for (var idx in data.items) {
				rowSpan++;
				if (rowSpan % 6 !== 1) {
					data.items[idx].business_part_name = "";
					data.items[idx].unit = "";
				}
			}
			state.businessPerformanceMonthList = data.items;
			state.businessPerformanceMonthCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_BUSINESS_PERFORMANCE: ({ commit }, info) => {
			// 사업부 성과 관리 조회
			return fetchBusinessPerformanceList(info)
				.then(({ data }) => {
					commit("SET_BUSINESS_PERFORMANCE_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_BUSINESS_PERFORMANCE: ({ commit }, info) => {
			// 사업부/사업장 성과 관리 추가 및 수정
			return insertBusinessPerformance(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_BUSINESS_PERFORMANCE_FILE: ({ commit }, info) => {
			return insertBusinessPerformanceFile(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_BUSINESS_PERFORMANCE_FILE: ({ commit }, id) => {
			// 사업부/사업장 성과 관리 증빙자료 삭제
			return deleteBusinessPerformanceFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_BUSINESS_PERFORMANCE_MONTH: ({ commit }, info) => {
			// 사업부 성과 조회
			return fetchBusinessPerformanceMonthList(info)
				.then(({ data }) => {
					commit("SET_BUSINESS_PERFORMANCE_MONTH_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default bsnprInqryStore;
