import {
	fetchCalculationList,
	fetchCalculationParameterList,
	fetchCalculationTierList,
	fetchCalculationHistoryList,
	fetchCalculationParameterHistoryList,
} from "@/api/index.js";

const calculationStore = {
	namespaced: true,
	state: {
		calculationList: [],
		calculationCount: 0,
		calculationParameterList: [],
		calculationParameterCount: 0,
		calculationTierList: [],
		calculationTierCount: 0,
		calculationHistoryList: [],
		calculationHistoryCount: 0,
		calculationParameterHistoryList: [],
		calculationParameterHistoryCount: 0,
	},
	mutations: {
		SET_CALCULATION_LIST: (state, calculation) => {
			state.calculationList = calculation.items;
			state.calculationCount = calculation.count;
		},
		SET_CALCULATION_PARAMETER_LIST: (state, calculationParameter) => {
			state.calculationParameterList = calculationParameter.items;
			state.calculationParameterCount = calculationParameter.count;
		},
		SET_CALCULATION_TIER_LIST: (state, calculationTier) => {
			state.calculationTierList = calculationTier.items;
			state.calculationTierCount = calculationTier.count;
		},
		SET_EDIT_CALCULATION_TIER_LIST: (state) => {
			state.calculationTierList.map((v) => (v.inEdit = true));
		},
		SET_CALCULATION_HISTORY_LIST: (state, calculationHistory) => {
			state.calculationHistoryList = calculationHistory.items;
			state.calculationHistoryList.map((v) => {
				v.inEdit = true;
				v.use_status = false;
			});
			state.calculationHistoryCount = calculationHistory.count;
		},
		SET_CALCULATION_PARAMETER_HISTORY_LIST: (
			state,
			calculationParameterHistory
		) => {
			state.calculationParameterHistoryList = calculationParameterHistory.items;
			state.calculationParameterHistoryCount =
				calculationParameterHistory.count;
		},
	},
	actions: {
		FETCH_CALCULATION_LIST: ({ commit }) => {
			// 산정식 목록 조회
			return fetchCalculationList()
				.then(({ data }) => {
					return commit("SET_CALCULATION_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_CALCULATION_PARAMETER_LIST: ({ commit }) => {
			// 산정식 파라미터 목록 조회
			return fetchCalculationParameterList()
				.then(({ data }) => {
					return commit("SET_CALCULATION_PARAMETER_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_CALCULATION_TIER_LIST: ({ commit }) => {
			// 산정식 등급 목록 조회
			return fetchCalculationTierList()
				.then(({ data }) => {
					return commit("SET_CALCULATION_TIER_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		INEDIT_CALCULATION_TIER_LIST: ({ commit }) => {
			commit("SET_EDIT_CALCULATION_TIER_LIST");
		},
		FETCH_CALCULATION_HISTORY_LIST: ({ commit }, id) => {
			// 산정식 이력 목록 조회
			return fetchCalculationHistoryList(id)
				.then(({ data }) => {
					return commit("SET_CALCULATION_HISTORY_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_CALCULATION_PARAMETER_HISTORY_LIST: ({ commit }, info) => {
			// 산정식 이력 목록 조회
			return fetchCalculationParameterHistoryList(info)
				.then(({ data }) => {
					return commit("SET_CALCULATION_PARAMETER_HISTORY_LIST", data);
				})
				.catch((error) => console.log(error));
		},
	},
};

export default calculationStore;
