<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>구분</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="typeList"
													:value="typeValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="typeChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조회기간</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="startYear"
													@change="startYearChange"
												/>
											</template>
										</field>
									</div>
									<span v-if="typeValue.id === '01'">~</span>
									<div
										v-if="typeValue.id === '01'"
										class="selectBox mr-10 ml-10"
									>
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="endYear"
													@change="endYearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>원단위 기준</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="buYearList"
													:value="buStartYear"
													@change="buStartYearChange"
												/>
											</template>
										</field>
									</div>
									<span>~</span>
									<div class="selectBox ml-10">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="buYearList"
													:value="buEndYear"
													@change="buEndYearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
		:style="{ height: this.$store.state.resizeLast }"
	>
		<div class="p-10 m-0a">
			<div v-if="isTypeYear" class="grid-code">
				<v-table
					ref="SlsexFcltsYearGrid"
					title="기존시설 예상배출량 조회"
					gridName="slsexFcltsYear"
					:dataItems="$store.state.slsexFcltsStore.facilityYearList"
					:columns="yearColumns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="yearColumns"
					:isNoData="isNoData"
					:isAllDataEdit="true"
					@allDataSave="allDataSave"
					@allDataCancel="allDataCancel"
				/>
			</div>
			<div v-else class="grid-code">
				<v-table
					ref="SlsexFcltsMonthGrid"
					title="기존시설 예상배출량 조회"
					gridName="slsexFcltsMonth"
					:dataItems="$store.state.slsexFcltsStore.facilityMonthList"
					:columns="monthColumns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="monthColumns"
					:isNoData="isNoData"
				/>
			</div>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";
import monthData from "../../data/hsrpMngmnDummyData.json";

export default {
	name: "slsexFclts",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			monthData: monthData,
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			typeList: [
				{ id: "01", text: "다년도" },
				{ id: "02", text: "단년도" },
			],
			typeValue: { id: "01", text: "다년도" },
			yearList: [],
			startYear: "",
			endYear: "",
			buYearList: [],
			buStartYear: "",
			buEndYear: "-",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			attchFile: null,
			showAlert: false,
			isTypeYear: true,
			searchFormStates: false,
		};
	},
	computed: {
		yearColumns() {
			let column = [
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 80,
					width: 80,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					title: "기준연도",
					minResizableWidth: 120,
					width: 120,
					children: [
						{
							field: "basic_unit",
							title: "배출 원단위(tCO2-eq/톤)",
							minResizableWidth: 200,
							width: 200,
							editable: false,
						},
					],
				},
			];
			for (var i = this.startYear; i <= this.endYear; i++) {
				if (i === this.today.getFullYear() + 1) {
					column.push({
						title: i + "",
						minResizableWidth: 120,
						width: 120,
						children: [
							{
								field: "mt_" + i,
								title: "생산 계획(톤)",
								minResizableWidth: 130,
								width: 130,
								editable: false,
							},
							{
								field: "co2_eq_" + i,
								title: "예상 매출량(tCO2-eq)",
								minResizableWidth: 200,
								width: 200,
								resizable: false,
								className: "text-center",
								editable: false,
							},
						],
					});
				} else {
					column.push({
						title: i + "",
						minResizableWidth: 120,
						width: 120,
						children: [
							{
								field: "co2_eq_" + i,
								title: "예상 매출량(tCO2-eq)",
								minResizableWidth: 200,
								width: 200,
								resizable: false,
								className: "text-center",
								editable: true,
							},
						],
					});
				}
			}
			return column;
		},
		monthColumns() {
			let column = [
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "area",
					title: "기준연도",
					editable: false,
					minResizableWidth: 120,
					width: 120,
					children: [
						{
							field: "basic_unit",
							title: "배출 원단위(tCO2-eq/톤)",
							minResizableWidth: 200,
							width: 200,
						},
					],
				},
			];
			for (var i = 1; i < 13; i++) {
				column.push({
					title: i + "월",
					editable: false,
					minResizableWidth: 120,
					width: 120,
					children: [
						{
							field: "mt_" + i,
							title: "생산 계획(톤)",
							minResizableWidth: 130,
							width: 130,
						},
						{
							field: "co2_eq_" + i,
							title: "예상 매출량(tCO2-eq)",
							minResizableWidth: 200,
							width: 200,
							resizable: false,
							className: "text-center",
						},
					],
				});
			}
			return column;
		},
		today() {
			return new Date();
		},
	},
	watch: {
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		// 조회기간
		for (
			var i = this.today.getFullYear() + 1;
			i < this.today.getFullYear() + 30;
			i++
		) {
			this.yearList.push(i);
		}
		this.startYear = this.yearList[0];
		this.endYear = this.yearList[2];
		// 원단위기준
		for (var j = 2015; j < this.today.getFullYear() - 1; j++) {
			this.buYearList.push(j);
		}
		this.buStartYear = this.today.getFullYear() - 1;
		this.$store
			.dispatch(
				"organizationStore/FETCH_ORGANIZATION_LIST",
				this.today.getFullYear()
			)
			.then((data) => {
				this.orgList = data.items;
			});
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.today.getFullYear() +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?start_year=" + this.startYear;
			if (this.typeValue.id == "01") {
				this.searchData += "&end_year=" + this.endYear;
			}
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.searchData += "&bu_start_year=" + this.buStartYear;
			if (this.buEndYear !== "-") {
				this.searchData += "&bu_end_year=" + this.buEndYear;
			}
			if (this.typeValue.id == "01") {
				this.fetchGridDataYear();
			} else {
				this.fetchGridDataMonth();
			}
		},
		fetchGridDataYear() {
			this.$store
				.dispatch("slsexFcltsStore/FETCH_BAU_FACILITY_YEAR", this.searchData)
				.then((data) => {
					this.isTypeYear = true;
					if (this.$refs.SlsexFcltsYearGrid !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.SlsexFcltsYearGrid.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		fetchGridDataMonth() {
			this.$store
				.dispatch("slsexFcltsStore/FETCH_BAU_FACILITY_MONTH", this.searchData)
				.then((data) => {
					this.isTypeYear = false;
					if (this.$refs.SlsexFcltsMonthGrid !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.SlsexFcltsMonthGrid.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		typeChange(e) {
			this.typeValue = e.target.value;
		},
		startYearChange(e) {
			this.startYear = e.target.value;
		},
		endYearChange(e) {
			this.endYear = e.target.value;
		},
		buStartYearChange(e) {
			this.buStartYear = e.target.value;
		},
		buEndYearChange(e) {
			this.buEndYear = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
		},
		openDialog() {
			this.messageDialog();
		},
		allDataSave: async function (data) {
			this.$store
				.dispatch("slsexFcltsStore/EDIT_BAU_FACILITY_YEAR", data)
				.then(() => {
					this.showAlert = !this.showAlert;
					this.fetchGridDataYear();
				});
		},
		allDataCancel: async function () {
			this.fetchGridDataYear();
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
