<template>
	<td v-if="!dataItem.inEdit">
		<datepicker
			:style="{ width: '230px' }"
			:disabled="true"
			:default-value="dataItem[field]"
			:format="format"
		/>
	</td>
	<td v-else>
		<datepicker
			:style="{ width: '230px' }"
			:default-value="dataItem[field]"
			:format="format"
			@change="change"
		/>
	</td>
</template>

<script>
import { DatePicker } from "@progress/kendo-vue-dateinputs";

export default {
	name: "DatePickerCell",
	components: {
		datepicker: DatePicker,
	},
	props: {
		field: String,
		dataItem: Object,
		format: String,
	},
	methods: {
		change: function (e) {
			this.$emit("change", e, e.target.value);
		},
	},
};
</script>
