<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준연도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.sectorEmissionStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					title="타 부문 배출량 관리"
					gridName="otherPartEmissionManagement"
					:dataItems="$store.state.sectorEmissionStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:deleteDataCheck="true"
					insertButtonText="추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

export default {
	name: "otherPartEmissionManagement",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
	},
	data: function () {
		return {
			renderKey: 0,
			newDataId: "0",
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			attchFile: null,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "year",
					title: "연도",
					minResizableWidth: 150,
					width: 150,
					editable: false,
				},
				{
					field: "sector_name",
					title: "부문",
					minResizableWidth: 150,
					width: 150,
					editable: false,
				},
				{
					field: "measure_name",
					title: "단위",
					minResizableWidth: 120,
					width: 120,
					editable: false,
				},
				{
					field: "total",
					title: "합계",
					minResizableWidth: 150,
					width: 150,
					editable: false,
				},
			];
			for (var i = 1; i < 13; i++) {
				column.push({
					field: "m" + i,
					title: i + "월",
					minResizableWidth: 180,
					width: 180,
				});
			}
			column.push({
				field: "file_url",
				title: "근거자료",
				cell: "FileFormCell",
				minResizableWidth: 210,
				width: 210,
				editable: false,
			});
			column.push({
				cell: "commandCell",
				minResizableWidth: 160,
				width: 160,
			});
			return column;
		},
		yearList() {
			return this.$store.state.sectorEmissionStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("sectorEmissionStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("sectorEmissionStore/FETCH_ORGANIZATION_YEAR_LIST");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					// 부문 SET
					this.sectorList = [];
					data.items.map((v) =>
						this.sectorList.push({ id: v.org_id, text: v.org_name })
					);
					this.sectorValue = {
						id: data.items[0].org_id,
						text: data.items[0].org_name,
					};
					this.$store.state.sectorEmissionStore.sectorValue =
						data.items[0].org_id;
					this.$store.state.sectorEmissionStore.sectorName =
						data.items[0].org_name;
					if (this.renderKey == 0) {
						this.renderKey++;
						this.handleSubmit();
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"sectorEmissionStore/FETCH_SECTOR_EMISSION_LIST",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.DataTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
			this.$store.state.sectorEmissionStore.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.$store.state.sectorEmissionStore.sectorValue = event.value.id;
			this.$store.state.sectorEmissionStore.sectorName = event.value.text;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("sectorEmissionStore/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch("sectorEmissionStore/FETCH_ORGANIZATION_YEAR_LIST");
			this.fetchOrgList();
			// this.year = this.yearList[0];
			// this.workplaceValue = { text: "전체 사업장" };
			// this.handleSubmit();
		},
		insert: function (data) {
			const formData = new FormData();
			formData.append(
				"sector_id",
				this.$store.state.sectorEmissionStore.sectorValue
			);
			formData.append("year", data.data.year);
			formData.append("m1", data.data.m1);
			formData.append("m2", data.data.m2);
			formData.append("m3", data.data.m3);
			formData.append("m4", data.data.m4);
			formData.append("m5", data.data.m5);
			formData.append("m6", data.data.m6);
			formData.append("m7", data.data.m7);
			formData.append("m8", data.data.m8);
			formData.append("m9", data.data.m9);
			formData.append("m10", data.data.m10);
			formData.append("m11", data.data.m11);
			formData.append("m12", data.data.m12);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("sectorEmissionStore/INSERT_SECTOR_EMISSION", formData)
				.then(() => {
					// console.log(data.id);
					// this.newDataId = data.id;
					this.fetchGridData();
				});
		},
		edit: function (data) {
			const formData = new FormData();
			formData.append("sector_emission_id", data.data.id);
			formData.append("sector_id", data.data.sector_id);
			formData.append("year", data.data.year);
			formData.append("m1", data.data.m1);
			formData.append("m2", data.data.m2);
			formData.append("m3", data.data.m3);
			formData.append("m4", data.data.m4);
			formData.append("m5", data.data.m5);
			formData.append("m6", data.data.m6);
			formData.append("m7", data.data.m7);
			formData.append("m8", data.data.m8);
			formData.append("m9", data.data.m9);
			formData.append("m10", data.data.m10);
			formData.append("m11", data.data.m11);
			formData.append("m12", data.data.m12);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("sectorEmissionStore/INSERT_SECTOR_EMISSION", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		remove: function (id) {
			this.$store.dispatch("sectorEmissionStore/DELETE_SECTOR_EMISSION", id);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"sectorEmissionStore/DELETE_SECTOR_EMISSION_FILE",
				e.sector_emission_id
			);
			this.fetchGridData();
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
