<template>
	<td v-if="dataItem.inEdit && !isTotal">
		<k-input
			ref="nameInput"
			:value="dataItem[field]"
			@input="onInput"
			@change="change"
		></k-input>
	</td>
	<td v-else>
		{{ dataItem[field] }}
	</td>
</template>

<script>
import { Input } from "@progress/kendo-vue-inputs";

export default {
	components: {
		"k-input": Input,
	},
	props: {
		field: String,
		dataItem: Object,
		gridName: String,
	},
	computed: {
		isTotal() {
			if (this.dataItem.method_name === "계") {
				return true;
			} else {
				return false;
			}
		},
	},
	data: function () {
		return {
			getDataItem: this.dataItem,
		};
	},
	methods: {
		onInput: function (e) {
			this.getDataItem[this.field] = e.value;
		},
		change: function (e) {
			this.getDataItem[this.field] = e.value;
			this.$emit("change", e.value);
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/App.scss";
.font-color-red {
	color: red;
}
</style>
