import {
	fetchActivityYearList,
	fetchActivityManagementList,
	editActivityManagement,
	fetchEvidenceList,
	insertEvidence,
	deleteEvidence,
	deleteEvidenceFile,
} from "@/api/index.js";

const activityManagementStore = {
	namespaced: true,
	state: {
		activityManagementList: [],
		activityManagementCount: 0,
		evidenceList: [],
		evidenceCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ACTIVITY_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_ACTIVITY_MANAGEMENT_LIST: (state, data) => {
			const monthList = [
				"january",
				"february",
				"march",
				"april",
				"may",
				"jun",
				"july",
				"august",
				"september",
				"october",
				"november",
				"december",
			];
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
				data.items[idx].total_confirm = 0;
				for (var month in monthList) {
					data.items[idx].total_confirm += parseFloat(
						data.items[idx][monthList[month] + "_confirm"] == null
							? 0
							: data.items[idx][monthList[month] + "_confirm"]
					);
				}
			}
			state.activityManagementList = data.items;
			state.activityManagementCount = data.count;
		},
		SET_EVIDENCE_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.evidenceList = data.items;
			state.evidenceCount = data.count;
			state.evidenceList.map((v) => (v.id = v.evidence_id));
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_EVIDENCE_INIT: (state) => {
			state.evidenceList = [];
			state.evidenceCount = 0;
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ACTIVITY_YEAR_LIST: ({ commit }) => {
			// 선택 가능한 기준연도 목록 조회
			return fetchActivityYearList()
				.then(({ data }) => {
					commit("SET_ACTIVITY_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ACTIVITY_MANAGEMENT_LIST: ({ commit }, info) => {
			// 활동량 관리 목록 조회
			return fetchActivityManagementList(info)
				.then(({ data }) => {
					commit("SET_ACTIVITY_MANAGEMENT_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_ACTIVITY_MANAGEMENT: ({ commit }, info) => {
			// 활동량 관리 일괄 수정
			return editActivityManagement(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_EVIDENCE_LIST: ({ commit }, info) => {
			// 근거 자료 목록 조회
			return fetchEvidenceList(info)
				.then(({ data }) => {
					commit("SET_EVIDENCE_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_EVIDENCE: ({ commit }, info) => {
			// 근거 자료 추가 및 수정
			return insertEvidence(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_EVIDENCE: ({ commit }, id) => {
			// 근거 자료 삭제
			return deleteEvidence(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_EVIDENCE_FILE: ({ commit }, id) => {
			// 근거 자료의 첨부파일 삭제
			return deleteEvidenceFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
		SETTING_EVIDENCE_INIT: ({ commit }) => {
			return commit("SET_EVIDENCE_INIT");
		},
	},
};

export default activityManagementStore;
