import { fetchActivityYearList, fetchActivityMonthList } from "@/api/index.js";

const activityMonthStore = {
	namespaced: true,
	state: {
		activityMonthList: [],
		activityMonthCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ACTIVITY_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_ACTIVITY_MONTH_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
				data.items[idx].id = parseInt(idx) + 1;
			}
			state.activityMonthList = data.items;
			state.activityMonthCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ACTIVITY_YEAR_LIST: ({ commit }) => {
			// 선택 가능한 기준연도 목록 조회
			return fetchActivityYearList()
				.then(({ data }) => {
					commit("SET_ACTIVITY_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ACTIVITY_MONTH_LIST: ({ commit }, info) => {
			// 월별 사용량 조회
			return fetchActivityMonthList(info)
				.then(({ data }) => {
					commit("SET_ACTIVITY_MONTH_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default activityMonthStore;
