import {
	fetchOrganizationYearList,
	fetchEnergyStatistics,
	fetchEnergyYear,
	fetchEnergyMonth,
	fetchEnergySameMonth,
	fetchEnergyFacilityEmissionList,
	fetchEnergyBasicUnit,
	fetchEnergyBasicUnitPeriod,
} from "@/api/index.js";

const energyStore = {
	namespaced: true,
	state: {
		energyUsageList: [],
		energyUsageChart: [],
		energyYearList: [],
		energyYearChart: [],
		energyMonthList: [],
		energyMonthChart: [],
		energySameMonthList: [],
		energySameMonthChart: [],
		energyBasicUnitList: [],
		energyBasicUnitChart: [],
		energyBasicUnitPeriodList: [],
		energyBasicUnitPeriodChart: [],
		energyList: [],
		energyCount: 0,
		yearList: [],
		year: "",
		startYear: "",
		endYear: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
			state.startYear = data.items[data.items.length - 1];
			state.endYear = data.items[0];
		},
		SET_ENERGY_STATISTICS: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.energyUsageList = data.grid;
			state.energyUsageChart = data.chart;
		},
		SET_ENERGY_YEAR: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.energyYearList = data.grid;
			state.energyYearChart = data.chart;
		},
		SET_ENERGY_MONTH: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.energyMonthList = data.grid;
			state.energyMonthChart = data.chart;
		},
		SET_ENERGY_SAME_MONTH: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.energySameMonthList = data.grid;
			state.energySameMonthChart = data.chart;
		},
		SET_ENERGY_BASIC_UNIT: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.energyBasicUnitList = data.grid;
			state.energyBasicUnitChart = data.chart;
		},
		SET_ENERGY_BASIC_UNIT_PERIOD: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.energyBasicUnitPeriodList = data.grid;
			state.energyBasicUnitPeriodChart = data.chart;
		},
		SET_ENERGY_GRID: (state, data) => {
			state.energyList = data.items;
			state.energyCount = data.count;
		},
		SET_INIT: (state) => {
			// state.energyUsageList = [];
			// state.energyUsageChart = [];
			// state.energyYearList = [];
			// state.energyYearChart = [];
			// state.energyMonthList = [];
			// state.energyMonthChart = [];
			// state.energySameMonthList = [];
			// state.energySameMonthChart = [];
			// state.energyBasicUnitList = [];
			// state.energyBasicUnitChart = [];
			// state.energyBasicUnitPeriodList = [];
			// state.energyBasicUnitPeriodChart = [];
			state.energyList = [];
			state.energyCount = 0;
			state.year = "";
			state.startYear = "";
			state.endYear = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_STATISTICS: ({ commit }, info) => {
			// 에너지 통계 조회
			return fetchEnergyStatistics(info)
				.then(({ data }) => {
					commit("SET_ENERGY_STATISTICS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_YEAR: ({ commit }, info) => {
			// 연도별 에너지 사용량 추이 조회
			return fetchEnergyYear(info)
				.then(({ data }) => {
					commit("SET_ENERGY_YEAR", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_MONTH: ({ commit }, info) => {
			// 월별 에너지 사용량 추이 조회
			return fetchEnergyMonth(info)
				.then(({ data }) => {
					commit("SET_ENERGY_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_SAME_MONTH: ({ commit }, info) => {
			// 동월 에너지 사용량 추이 조회
			return fetchEnergySameMonth(info)
				.then(({ data }) => {
					commit("SET_ENERGY_SAME_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_FACILITY_EMISSION_LIST: ({ commit }, info) => {
			// 배출시설별 에너지 사용량 조회
			return fetchEnergyFacilityEmissionList(info)
				.then(({ data }) => {
					commit("SET_ENERGY_GRID", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_BASIC_UNIT: ({ commit }, info) => {
			// 에너지 원단위 조회
			return fetchEnergyBasicUnit(info)
				.then(({ data }) => {
					commit("SET_ENERGY_BASIC_UNIT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_BASIC_UNIT_PERIOD: ({ commit }, info) => {
			// 에너지 원단위 추이 조회
			return fetchEnergyBasicUnitPeriod(info)
				.then(({ data }) => {
					commit("SET_ENERGY_BASIC_UNIT_PERIOD", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_INIT: ({ commit }) => {
			return commit("SET_INIT");
		},
	},
};

export default energyStore;
