import {
	fetchExEmissionTradingYearList,
	fetchExEmissionTrading,
	insertExEmissionTrading,
	deleteExEmissionTrading,
	deleteExEmissionTradingFile,
} from "@/api/index.js";

const exEmissionTradingStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_EX_EMISSION_TRADING_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_EX_EMISSION_TRADING_LIST: (state, data) => {
			state.dataList = data.items;
			state.dataList.map((v) => {
				v.trading_date = new Date(v.trading_date);
			});
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_EX_EMISSION_TRADING_YEAR_LIST: ({ commit }) => {
			// 내부 배출권거래의 선택 가능한 기준연도 목록 조회
			return fetchExEmissionTradingYearList()
				.then(({ data }) => {
					return commit("SET_EX_EMISSION_TRADING_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_EX_EMISSION_TRADING_LIST: ({ commit }, info) => {
			// 내부 배출권거래 관리
			return fetchExEmissionTrading(info)
				.then(({ data }) => {
					commit("SET_EX_EMISSION_TRADING_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_EX_EMISSION_TRADING: ({ commit }, info) => {
			// 내부 배출권거래 관리 추가 및 수정
			return insertExEmissionTrading(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_EX_EMISSION_TRADING: ({ commit }, id) => {
			// 내부 배출권거래 관리 삭제
			return deleteExEmissionTrading(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_EX_EMISSION_TRADING_FILE: ({ commit }, id) => {
			// 내부 배출권거래 관리 근거자료 삭제
			return deleteExEmissionTradingFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default exEmissionTradingStore;
