import {
	fetchOrganizationYearList,
	fetchBauBasicUnit,
	fetchPjtManagemnet,
	insertPjtManagemnet,
	deletePjtManagemnet,
	deletePjtManagemnetFile,
	fetchBauMal,
} from "@/api/index.js";

const bauStore = {
	namespaced: true,
	state: {
		bauList: [],
		bauCount: 0,
		yearList: [],
		year: "",
		startYear: "",
		endYear: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
			state.startYear = data.items[data.items.length - 1];
			state.endYear = data.items[0];
		},
		SET_BAU_LIST: (state, data) => {
			state.bauList = data.items;
			state.bauCount = data.count;
		},
		SET_BAU_PJT_LIST: (state, data) => {
			state.bauList = data.items;
			state.bauList.map((v) => {
				v.operation_start = new Date(
					v.operation_start.substring(0, 4) +
						"-" +
						v.operation_start.substring(4, 6)
				);
			});
			state.bauCount = data.count;
		},
		SET_INIT: (state) => {
			state.bauList = [];
			state.bauCount = 0;
			state.yearList = [];
			state.year = "";
			state.startYear = "";
			state.endYear = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_BAU_BASIC_UNIT: ({ commit }, info) => {
			// 원단위 배출량 조회
			return fetchBauBasicUnit(info)
				.then(({ data }) => {
					commit("SET_BAU_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_PJT_MANAGEMNET: ({ commit }, info) => {
			// 신규 PJT 관리
			return fetchPjtManagemnet(info)
				.then(({ data }) => {
					commit("SET_BAU_PJT_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_PJT_MANAGEMNET: ({ commit }, info) => {
			// 신규 PJT 관리 추가 및 수정
			return insertPjtManagemnet(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_PJT_MANAGEMNET: ({ commit }, id) => {
			// 신규 PJT 관리 삭제
			return deletePjtManagemnet(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_PJT_MANAGEMNET_FILE: ({ commit }, id) => {
			// 신규 PJT 관리 첨부파일 삭제
			return deletePjtManagemnetFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_BAU_MAL: ({ commit }, info) => {
			// 중장기 예상배출량 조회
			return fetchBauMal(info)
				.then(({ data }) => {
					commit("SET_BAU_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_INIT: ({ commit }) => {
			return commit("SET_INIT");
		},
	},
};

export default bauStore;
