import {
	fetchActivitySAPList,
	fetchActivityDataList,
	fetchActivityDataParcList,
	fetchActivityMasterList,
} from "@/api/index.js";

const activityInterfaceStore = {
	namespaced: true,
	state: {
		interfaceSapList: [],
		activityInterfaceSapCount: 0,
		activityDataList: [],
		activityDataCount: 0,
		interfaceDataParcList: [],
		activityInterfaceDataParcCount: 0,
		interfaceMasterList: [],
		interfaceMasterCount: 0,
	},
	mutations: {
		SET_INTERFACE_SAP_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
				data.items[idx].id = parseInt(idx) + 1;
			}
			state.interfaceSapList = data.items;
			state.activityInterfaceSapCount = data.count;
		},
		SET_INTERFACEDATA_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
				data.items[idx].id = parseInt(idx) + 1;
			}
			state.activityDataList = data.items;
			state.activityDataCount = data.count;
		},
		SET_INTERFACE_DATAPARC_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
				data.items[idx].id = parseInt(idx) + 1;
			}
			state.interfaceDataParcList = data.items;
			state.activityInterfaceDataParcCount = data.count;
		},
		SET_INTERFACE_MASTER_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = data.items[idx].master_code;
				data.items[idx].id = data.items[idx].master_code;
			}
			state.interfaceMasterList = data.items;
			state.interfaceMasterCount = data.count;
		},
	},
	actions: {
		FETCH_INTERFACE_LIST: ({ commit }, info) => {
			// 월별 SAP사용량 조회
			return fetchActivitySAPList(info)
				.then(({ data }) => {
					commit("SET_INTERFACE_SAP_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_INTERFACEDATA_LIST: ({ commit }, info) => {
			// 월별 전체사용량 조회
			return fetchActivityDataList(info)
				.then(({ data }) => {
					commit("SET_INTERFACEDATA_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_INTERFACE_DATAPARC_LIST: ({ commit }, info) => {
			// 월별 DataParc사용량 조회
			return fetchActivityDataParcList(info)
				.then(({ data }) => {
					commit("SET_INTERFACE_DATAPARC_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_INTERFACE_MASTER_LIST: ({ commit }, info) => {
			// 인터페이스 마스터 조회
			return fetchActivityMasterList(info)
				.then(({ data }) => {
					commit("SET_INTERFACE_MASTER_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
	},
};

export default activityInterfaceStore;
