<template>
	<div class="content1">
		<!-- 서브바 컨포넌트 -->
		<div class="subbar mr-20">
			<Subbar :type="'organization'" />
		</div>
		<!-- kendo form 컨포넌트 설정 -->
		<div class="form-content">
			<k-form class="h-100" @submit="saveData" v-if="currentDepth == 0">
				<corporation />
			</k-form>
			<k-form
				class="h-100"
				@submit="saveData"
				:initial-values="
					$store.getters['organizationStore/GET_ORGANIZATION_INIT']
				"
				v-if="currentDepth == 1"
			>
				<corporation ref="kendoForm" />
				<!-- 법인 정보 -->
			</k-form>
			<k-form
				class="h-100"
				@submit="saveData"
				:initial-values="
					$store.getters['organizationStore/GET_ORGANIZATION_INIT']
				"
				v-if="currentDepth == 2"
			>
				<sector @deleteData="deleteData" ref="kendoForm" />
				<!-- 부문 정보 -->
			</k-form>
			<k-form
				class="h-100"
				@submit="saveData"
				:initial-values="
					$store.getters['organizationStore/GET_ORGANIZATION_INIT']
				"
				v-if="currentDepth == 3"
			>
				<workplace @deleteData="deleteData" ref="kendoForm" />
				<!-- 사업장 정보 -->
			</k-form>
			<k-form
				class="h-100"
				@submit="saveData"
				:initial-values="
					$store.getters['organizationStore/GET_ORGANIZATION_INIT']
				"
				v-if="currentDepth == 4"
			>
				<production-team @deleteData="deleteData" ref="kendoForm" />
				<!-- 생산팀 정보 -->
			</k-form>
			<k-form
				class="h-100"
				@submit="saveData"
				:initial-values="
					$store.getters['organizationStore/GET_ORGANIZATION_INIT']
				"
				v-if="currentDepth == 5"
			>
				<facility-emission
					:org_id="org_id"
					@deleteData="deleteData"
					ref="kendoForm"
				/>
				<!-- 배출시설 정보 -->
			</k-form>
		</div>
	</div>
	<modal
		v-if="showConfirm"
		:title="'알림'"
		:message="'저장하시겠습니까?'"
		:type="'alert'"
		@close="messageDialog('confirm')"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="saveDataCallback" class="button first">확인</kbutton>
				<kbutton @click="messageDialog('confirm')" class="button second"
					>취소</kbutton
				>
			</dialog-actions-bar>
		</template>
	</modal>
	<modal
		v-if="showDelete"
		:title="'알림'"
		:message="'삭제하시겠습니까?'"
		:type="'alert'"
		@close="messageDialog('delete')"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="deleteDataCallback" class="button first">확인</kbutton>
				<kbutton @click="messageDialog('delete')" class="button second"
					>취소</kbutton
				>
			</dialog-actions-bar>
		</template>
	</modal>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="alertMessage"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog('alert')" class="button first"
					>확인</kbutton
				>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import "@progress/kendo-ui";
import { Form } from "@progress/kendo-vue-form";
import Subbar from "../../components/common/sidebar/Subbar.vue";
import Corporation from "./Corporation.vue";
import Sector from "./Sector.vue";
import Workplace from "./Workplace.vue";
import ProductionTeam from "./ProductionTeam.vue";
import FacilityEmission from "./FacilityEmission.vue";
import Modal from "../../components/modal/Modal.vue";
import { Button } from "@progress/kendo-vue-buttons";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";

import {
	insertOrganizationDetail,
	editOrganizationDetail,
} from "@/api/index.js";
export default {
	name: "organization",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		Subbar,
		Corporation,
		Sector,
		Workplace,
		ProductionTeam,
		FacilityEmission,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			nowState: true,
			infoData: {},
			currentDepth: 0,
			dataItem: null,
			showConfirm: false,
			showDelete: false,
			showAlert: false,
			alertMessage: "",
		};
	},
	computed: {
		year() {
			return this.$store.state.organizationStore.year;
		},
		org_id() {
			return this.$store.state.organizationStore.org_id;
		},
		depth() {
			return this.$store.state.organizationStore.depth;
		},
		organizationData() {
			return this.$store.state.organizationStore.organizationData;
		},
		orgParentId() {
			return this.$store.state.organizationStore.org_parent_id;
		},
	},
	watch: {
		year() {
			console.log("[watch year]");
			this.fetchList();
		},
		org_id() {
			console.log("[watch org_id]");
			this.fetchData();
		},
		organizationData(value) {
			if (value.org_id !== undefined || this.orgParentId !== "") {
				console.log("[change depth data ==>]");
				this.currentDepth = this.depth;
				this.$nextTick(function () {
					if (this.$refs.kendoForm !== undefined) this.$refs.kendoForm.clear();
				});
			}
		},
	},
	created() {
		console.log("[MAIN CREATED]");
		this.$store.dispatch("organizationStore/SETTING_DEPTH_INIT");
		this.$store.dispatch("organizationStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_INIT");
		this.$store.dispatch("organizationStore/SETTING_ORG_PARENT_INIT");
		this.$store.dispatch("organizationStore/SETTING_ORG_INIT");
		this.$store.dispatch("organizationStore/FETCH_ORGANIZATION_YEAR_LIST");
	},
	methods: {
		/* 조직 트리 목록 */
		fetchList() {
			console.log("[fetchList]");
			if (!this.year) {
				return;
			}
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then(() => {
					this.$store.state.organizationStore.org_id =
						this.$store.state.organizationStore.organizationList[0].org_id;
				});
		},
		/* 상세 */
		fetchData() {
			// this.currentDepth = 0;
			this.$store.dispatch("organizationStore/SETTING_ORG_PARENT_INIT");
			this.$store.dispatch("organizationStore/FETCH_ORGANIZATION_INFO", {
				org_id: this.org_id,
			});
		},
		saveData(dataItem) {
			this.dataItem = dataItem;
			this.showConfirm = true;
		},
		/* submit */
		async saveDataCallback() {
			this.showConfirm = false;
			this.infoData = this.dataItem;
			// 생산팀 사업부/공장 SET
			if (this.depth == 4) {
				this.infoData.factory = this.dataItem.factoryList.id;
				this.infoData.business_part = this.dataItem.businessPartList.id;
			} else if (this.depth == 5) {
				this.infoData.facility_name_code =
					this.dataItem.facilityNameCodeList.id;
				this.infoData.facility_size_code =
					this.dataItem.facilitySizeCodeList.id;
			}

			if (this.$store.state.organizationStore.org_parent_id !== "") {
				this.insertData();
			} else {
				this.editData();
			}
		},
		/* 등록 */
		insertData() {
			this.infoData.org_parent_id =
				this.$store.state.organizationStore.org_parent_id;
			this.infoData.year = this.$store.state.organizationStore.year;
			this.infoData.depth = this.$store.state.organizationStore.depth;
			insertOrganizationDetail(this.infoData)
				.then((res) => {
					this.$store.dispatch(
						"organizationStore/FETCH_ORGANIZATION_LIST",
						this.year
					);
					this.$store.state.organizationStore.org_id = res.data.org_id;
					this.alertMessage = "등록되었습니다.";
					this.showAlert = !this.showAlert;
				})
				.catch((error) => {
					console.log(error);
					this.alertMessage = "처리실패! 시스템 관리자에게 문의해주세요.";
					this.showAlert = !this.showAlert;
				});
		},
		/* 수정 */
		editData() {
			editOrganizationDetail(this.infoData)
				.then((res) => {
					this.$store.dispatch(
						"organizationStore/FETCH_ORGANIZATION_LIST",
						this.year
					);
					this.$store.dispatch("organizationStore/FETCH_ORGANIZATION_INFO", {
						org_id: res.data.org_id,
					});
					this.alertMessage = "정상처리 되었습니다.";
					this.showAlert = !this.showAlert;
				})
				.catch((error) => {
					console.log(error);
					this.alertMessage = "처리실패! 시스템 관리자에게 문의해주세요.";
					this.showAlert = !this.showAlert;
				});
		},
		deleteData() {
			this.showDelete = true;
		},
		/* 삭제 */
		deleteDataCallback() {
			this.showDelete = false;
			this.$store
				.dispatch("organizationStore/DELETE_ORGANIZATION_DETAIL", {
					org_id: this.org_id,
				})
				.then(() => {
					this.$store.dispatch(
						"organizationStore/FETCH_ORGANIZATION_LIST",
						this.year
					);
					this.$store.state.organizationStore.org_id =
						this.organizationData.org_parent_id;
					this.$store.state.organizationStore.depth = this.depth - 1;
					this.alertMessage = "정상처리 되었습니다.";
					this.showAlert = !this.showAlert;
				})
				.catch((error) => {
					console.log(error);
					this.alertMessage = "처리실패! 시스템 관리자에게 문의해주세요.";
					this.showAlert = !this.showAlert;
				});
		},
		messageDialog(type) {
			if (type === "alert") {
				this.showAlert = !this.showAlert;
			} else if (type === "confirm") {
				this.showConfirm = !this.showConfirm;
			} else if (type === "delete") {
				this.showDelete = !this.showDelete;
			}
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
.form-content {
	all: initial;
	width: calc(100% - 231px) !important;
	.formWraps {
		height: 100%;
	}
}

.content1 {
	height: calc(100vh - 179px) !important;
}
</style>
