<template>
	<div class="d-flex kendoRadio">
		<radiobutton
			:name="'group1'"
			:value="'first'"
			:checked="selectedValue === 'first'"
			@change="handleChange"
			:label="firstTitle"
			class="radioBox mr-20"
		/>
		<radiobutton
			:name="'group1'"
			:value="'second'"
			:checked="selectedValue === 'second'"
			@change="handleChange"
			:label="secondTitle"
		/>
	</div>
</template>
<script>
import { RadioButton } from "@progress/kendo-vue-inputs";

export default {
	components: {
		radiobutton: RadioButton,
	},
	props: {
		firstTitle: String,
		secondTitle: String,
	},
	data: function () {
		return {
			selectedValue: "first",
		};
	},
	methods: {
		handleChange: function (e) {
			let state = (this.selectedValue = e.value);
			this.$emit("radioSelect", state);
		},
	},
};
</script>
<style lang="scss">
@import "@/assets/scss/_index.scss";
.kendoRadio {
	margin-right: 38px;
	&.active {
		display: none !important;
	}
}
label {
	font-family: AppleSDGothicNeoSB00;
	font-size: 14px;
	color: #33343c;
}
.k-radio {
	border-color: none !important;
	background: #dddddd;
}
.k-radio:checked {
	color: white;
	border-color: #51ce87;
	border: 4.95px solid #51ce87 !important;
	background-color: #fff;
}
</style>
