<template>
	<form-element id="searchForm">
		<fieldset class="d-flex">
			<div class="d-flex align-items-center formwarp">
				<slot name="formdata" />
			</div>
			<div class="buttonBoxs">
				<BasicButton
					v-if="reset"
					:type="'button'"
					:backgroundColor="'#F9F9F9'"
					:fontColor="'#5A5B68'"
					:borderColor="'#5A5B68'"
					@click="clear"
					>초기화</BasicButton
				>
				<BasicButton
					:type="'submit'"
					:backgroundColor="'#F37321'"
					:fontColor="'#FFFFFF'"
					class="sidebutton"
					:class="{ single: reset == false }"
					>조회</BasicButton
				>
			</div>
		</fieldset>
	</form-element>
	<div class="arrowTopBox" @click="actives">
		<img :src="upArrow" class="upArrow" />
	</div>
</template>

<script>
import resize from "../../mixin/resize.js";

import dataicon from "@/assets/img/icon/cals.png";
import searchicon from "@/assets/img/icon/search.png";
import upArrow from "@/assets/img/icon/upArrow.png";

import { FormElement } from "@progress/kendo-vue-form";
import BasicButton from "../common/button/BasicButton.vue";

export default {
	name: "SearchForm",
	emits: ["clearSearchForm", "searchFormStateA"],
	props: {
		reset: Boolean,
	},
	components: {
		"form-element": FormElement,
		BasicButton,
	},
	data: function () {
		return {
			dataicon,
			upArrow,
			searchicon,
			searchFormState: false,
		};
	},
	inject: {
		kendoForm: { default: {} },
	},

	methods: {
		actives: function (e) {
			this.handleResize();
			let targets = e.target;
			let showTarget = document.getElementById("searchForm");
			showTarget.classList.toggle("active");
			targets.closest(".searchWrap").classList.toggle("active");
			targets
				.closest(".searchWrap")
				.querySelector(".arrowTopBox img")
				.classList.toggle("active");
			if (document.querySelector(".kendoRadio")) {
				document.querySelector(".kendoRadio").classList.toggle("active");
			}
			this.$emit("searchFormStateA");
		},
		clear: function () {
			this.kendoForm.onFormReset();
			this.$emit("clearSearchForm");
		},
	},
	mixins: [resize()],
};
</script>

<style lang="scss" scoped>
.formwarp {
	flex-wrap: wrap;
	width: calc(100% - 100px);
}

.searchForm {
	padding: 16px 10px 5px 10px;

	&.active {
		padding: 0;
		border-radius: none;
	}
}
.buttonBoxs {
	width: 200px;
	margin-left: auto;
	align-self: flex-start;
	white-space: nowrap;
}
.single {
	float: right;
}
@media (max-width: 1300px) {
	// .buttonBoxs {
	// 	margin-bottom: 10px;
	// }
}
</style>
