<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획년도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.reductionBusinessStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '120px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					title="감축사업 등록"
					gridName="reductionBusiness"
					:dataItems="$store.state.reductionBusinessStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:disabledState="true"
					:deleteDataCheck="true"
					insertButtonText="추가"
					popupButtonText="방법론 관리"
					:datePickerFormat="'yyyy.MM'"
					:selectWorkplace="workplaceDropDownList"
					:selectProductionTeam="productionTeamDropDownList"
					:selectReductionBusinessType="typeDropDownList"
					:selectEnergyCode="energyCodeDropDownList"
					:selectActivityCode="activityCodeDropDownList"
					:selectRationalizationCode="rationalizationCodeDropDownList"
					:selectInvestmentCode="investmentCodeDropDownList"
					:selectReductionStatus="statusDropDownList"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
					@gridDialog="toggleDialog"
				/>
			</div>
		</div>
	</div>
	<modal v-if="visibleDialog" :title="'방법론 관리'" @close="toggleDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code">
					<v-table
						ref="DataMethodologyTable"
						class="grid-code"
						gridName="methodologyManagement"
						insertButtonText="추가"
						:dataItems="$store.state.reductionBusinessStore.methodologyList"
						:columns="methodologyColumns"
						:isNoData="isMethodologyNoData"
						:newDataId="newDataId"
						@insert="dialogInsert"
						@edit="dialogEdit"
						@remove="dialogRemove"
						@isEditableChange="isEditableChange"
					/>
				</div>
			</div>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import resize from "../../mixin/resize.js";

import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import {
	insertMethodologyManagement,
	editMethodologyManagement,
	deleteMethodologyManagement,
} from "@/api/index.js";

export default {
	name: "ReductionBusiness",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
	},
	data: function () {
		return {
			renderKey: 0,
			newDataId: "0",
			searchicon,
			searchData: null,
			isNoData: false,
			isMethodologyNoData: false,
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			workplaceDropDownList: [],
			productionTeamDropDownList: [],
			typeDropDownList: [
				{ id: "I", text: "내부" },
				{ id: "O", text: "외부" },
			],
			energyCodeDropDownList: [],
			activityCodeDropDownList: [],
			rationalizationCodeDropDownList: [],
			investmentCodeDropDownList: [],
			statusDropDownList: [
				{ id: "U", text: "미등록" },
				{ id: "R", text: "등록" },
				{ id: "A", text: "승인" },
			],
			attchFile: null,
			visibleDialog: false,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "workplace_name",
					title: "사업장",
					minResizableWidth: 100,
					width: 100,
					cell: "dropdownWorkplaceCell",
				},
				{
					field: "production_team_name",
					title: "생산팀",
					minResizableWidth: 100,
					width: 100,
					cell: "dropdownProductionTeamCell",
				},
				{
					field: "type_name",
					title: "구분",
					minResizableWidth: 100,
					width: 100,
					cell: "dropdownReductionBusinessTypeCell",
				},
				{
					field: "item",
					title: "감축 사업명",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "energy_code_name",
					title: "에너지원",
					minResizableWidth: 130,
					width: 130,
					cell: "dropdownEnergyCodeCell",
				},
				{
					field: "activity_code_name",
					title: "감축활동 분류",
					minResizableWidth: 150,
					width: 150,
					cell: "dropdownActivityCodeCell",
				},
				{
					field: "rationalization_code_name",
					title: "합리화 유형",
					minResizableWidth: 130,
					width: 130,
					cell: "dropdownRationalizationCodeCell",
				},
				{
					field: "investment_code_name",
					title: "투자/관리",
					minResizableWidth: 100,
					width: 100,
					cell: "dropdownInvestmentCodeCell",
				},
				{
					title: "외부감축 방법론",
					children: [
						{
							field: "field",
							title: "분야",
							minResizableWidth: 180,
							width: 180,
						},
						{
							field: "methodology",
							title: "방법론",
							minResizableWidth: 450,
							width: 450,
						},
					],
				},
				{
					field: "status_name",
					title: "상태",
					minResizableWidth: 100,
					width: 100,
					cell: "dropdownReductionStatusCell",
				},
				{
					field: "is_admit",
					title: "감축인정",
					minResizableWidth: 130,
					width: 130,
					cell: "checkboxCell",
				},
				{
					field: "plan_year",
					title: "계획년도",
					minResizableWidth: 130,
					width: 130,
					editable: false,
				},
				{
					field: "perform_plan_date",
					title: "실적발생 예상연월",
					minResizableWidth: 200,
					width: 200,
					cell: "datePickerCell",
				},
				{
					field: "investment_amount",
					title: "투자액(백만원)",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "effect_amount",
					title: "효과금액(백만원)",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "effect_evidence",
					title: "효과금액 산출근거(단가)",
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "annual_saving_amount",
					title: "연간 절감 금액(백만원)",
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "pre_gas_reduction",
					title: "사업 전 온실가스 배출량(tCO-eq)",
					minResizableWidth: 255,
					width: 255,
				},
				{
					field: "after_gas_reduction",
					title: "감축사업(계획) 온실가스 배출량(tCO-eq)",
					minResizableWidth: 255,
					width: 255,
				},
				{
					field: "gas_reduction",
					title: "연간 온실가스 절감량(tCO-eq)",
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "file_url",
					title: "근거자료",
					cell: "FileFormCell",
					minResizableWidth: 210,
					width: 210,
					editable: false,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
		methodologyColumns() {
			return [
				{
					field: "no",
					title: "No",
					minResizableWidth: 80,
					width: 80,
					editable: false,
				},
				{
					field: "field",
					title: "방법론 분야",
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "methodology",
					title: "방법론명",
					minResizableWidth: 350,
					width: 350,
				},
				{
					field: "unique_code",
					title: "방법론 고유번호",
					minResizableWidth: 180,
					width: 180,
				},
				{ cell: "commandCell", minResizableWidth: 170, width: 170 },
			];
		},
		yearList() {
			return this.$store.state.reductionBusinessStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("reductionBusinessStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("reductionBusinessStore/FETCH_ORGANIZATION_YEAR_LIST");
		// 공통코드 에너지원
		this.$store
			.dispatch("codeStore/FETCH_CODE_LIST", "?group=15")
			.then((data) => {
				this.energyCodeDropDownList = [];
				data.items.map((v) =>
					this.energyCodeDropDownList.push({ id: v.code_no, text: v.code_name })
				);
			});
		// 공통코드 감축활동 분류
		this.$store
			.dispatch("codeStore/FETCH_CODE_LIST", "?group=16")
			.then((data) => {
				this.activityCodeDropDownList = [];
				data.items.map((v) =>
					this.activityCodeDropDownList.push({
						id: v.code_no,
						text: v.code_name,
					})
				);
			});
		// 공통코드 합리화 유형
		this.$store
			.dispatch("codeStore/FETCH_CODE_LIST", "?group=17")
			.then((data) => {
				this.rationalizationCodeDropDownList = [];
				data.items.map((v) =>
					this.rationalizationCodeDropDownList.push({
						id: v.code_no,
						text: v.code_name,
					})
				);
			});
		// 공통코드 투자/관리
		this.$store
			.dispatch("codeStore/FETCH_CODE_LIST", "?group=18")
			.then((data) => {
				this.investmentCodeDropDownList = [];
				data.items.map((v) =>
					this.investmentCodeDropDownList.push({
						id: v.code_no,
						text: v.code_name,
					})
				);
			});
		// 방법론 관리 목록 조회
		this.$store
			.dispatch("reductionBusinessStore/FETCH_METHODOLOGY_LIST")
			.then((data) => {
				if (data.count < 1) {
					this.isMethodologyNoData = true;
				}
			});
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			// this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceDropDownList = [];
						data.items.map((v) =>
							this.workplaceDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamDropDownList = [];
						data.items.map((v) =>
							this.productionTeamDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"reductionBusinessStore/FETCH_REDUCTION_BUSINESS_LIST",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						if (data.items.length < 1) {
							this.isNoData = true;
						}
						this.$refs.DataTable.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
			this.$store.state.reductionBusinessStore.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
			this.productionTeamDropDownList = [];
			this.productionTeamDropDownList.push(event.value);
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("reductionBusinessStore/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch(
				"reductionBusinessStore/FETCH_ORGANIZATION_YEAR_LIST"
			);
			this.fetchOrgList();
		},
		insert: function (data) {
			const formData = new FormData();
			formData.append("production_team_code", data.data.production_team_code);
			formData.append("type", data.data.type);
			formData.append("item", data.data.item);
			formData.append("energy_code_no", data.data.energy_code_no);
			formData.append("activity_code_no", data.data.activity_code_no);
			formData.append(
				"rationalization_code_no",
				data.data.rationalization_code_no
			);
			formData.append("investment_code_no", data.data.investment_code_no);
			formData.append("method_id", data.data.method_id);
			formData.append("status", data.data.status);
			formData.append("is_admit", data.data.is_admit);
			formData.append("plan_year", data.data.plan_year);
			formData.append("perform_plan_year", data.data.perform_plan_year);
			formData.append("perform_plan_month", data.data.perform_plan_month);
			formData.append("investment_amount", data.data.investment_amount);
			formData.append("effect_amount", data.data.effect_amount);
			formData.append("effect_evidence", data.data.effect_evidence);
			formData.append("annual_saving_amount", data.data.annual_saving_amount);
			formData.append("pre_gas_reduction", data.data.pre_gas_reduction);
			formData.append("after_gas_reduction", data.data.after_gas_reduction);
			formData.append("gas_reduction", data.data.gas_reduction);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("reductionBusinessStore/INSERT_REDUCTION_BUSINESS", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		edit: function (data) {
			const formData = new FormData();
			formData.append("reduction_id", data.data.reduction_id);
			formData.append("production_team_code", data.data.production_team_code);
			formData.append("type", data.data.type);
			formData.append("item", data.data.item);
			formData.append("energy_code_no", data.data.energy_code_no);
			formData.append("activity_code_no", data.data.activity_code_no);
			formData.append(
				"rationalization_code_no",
				data.data.rationalization_code_no
			);
			formData.append("investment_code_no", data.data.investment_code_no);
			formData.append("method_id", data.data.method_id);
			formData.append("status", data.data.status);
			formData.append("is_admit", data.data.is_admit);
			formData.append("plan_year", data.data.plan_year);
			formData.append("perform_plan_year", data.data.perform_plan_year);
			formData.append("perform_plan_month", data.data.perform_plan_month);
			formData.append("investment_amount", data.data.investment_amount);
			formData.append("effect_amount", data.data.effect_amount);
			formData.append("effect_evidence", data.data.effect_evidence);
			formData.append("annual_saving_amount", data.data.annual_saving_amount);
			formData.append("pre_gas_reduction", data.data.pre_gas_reduction);
			formData.append("after_gas_reduction", data.data.after_gas_reduction);
			formData.append("gas_reduction", data.data.gas_reduction);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("reductionBusinessStore/INSERT_REDUCTION_BUSINESS", formData)
				.then(() => {
					this.fetchGridData();
				});
		},
		remove: function (id) {
			this.$store.dispatch(
				"reductionBusinessStore/DELETE_REDUCTION_BUSINESS",
				id
			);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"reductionBusinessStore/DELETE_REDUCTION_BUSINESS_FILE",
				e.reduction_id
			);
			this.fetchGridData();
		},
		dialogInsert: function (data) {
			insertMethodologyManagement(data)
				.then((data) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		dialogEdit: function (data) {
			editMethodologyManagement(data);
		},
		dialogRemove: function (id) {
			deleteMethodologyManagement(id);
		},
		toggleDialog() {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.DataMethodologyTable.cancelAll();
			}
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

