<template>
	<Chart :donut-center-render="'donutCenterRenderer'">
		<ChartTooltip :format="'{0}%'" />
		<ChartTitle :text="title" />
		<template v-slot:donutCenterRenderer>
			<span>{{ donutTitle }}</span>
		</template>
		<ChartSeries>
			<ChartSeriesItem
				:type="'donut'"
				:data-items="dataItems"
				:category-field="'name'"
				:field="'data'"
			>
				<ChartSeriesLabels
					:color="'#fff'"
					:background="'none'"
					:content="labelContent"
				/>
			</ChartSeriesItem>
		</ChartSeries>
		<ChartLegend :visible="true" :position="'bottom'" />
	</Chart>
</template>

<script>
import "hammerjs";
import {
	Chart,
	ChartLegend,
	ChartSeries,
	ChartSeriesItem,
	ChartTooltip,
	ChartTitle,
	ChartSeriesLabels,
} from "@progress/kendo-vue-charts";

const labelContent = (e) => e.category;

export default {
	name: "DonutChart",
	props: {
		title: String,
		data: Object,
		donutTitle: String,
	},
	components: {
		Chart,
		ChartLegend,
		ChartSeries,
		ChartSeriesItem,
		ChartTooltip,
		ChartTitle,
		ChartSeriesLabels,
	},
	data: function () {
		return {
			dataItems: this.data,
			labelContent: labelContent,
		};
	},
};
</script>

