<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-20">
							<div class="wrap">
								<label for=""><span>기준년도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="$store.state.emissionStore.yearList"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="EmissionGrid"
					title="고유배출계수 (Tier3) 관리"
					gridName="emission"
					:dataItems="$store.state.emissionStore.emissionList"
					:count="$store.state.emissionStore.emissionCount"
					:columns="emissionColumns"
					:isNoData="isNoData"
					:newDataId="newDataId"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="emissionColumns"
					:isAllDataEdit="true"
					@allDataSave="allDataSave"
					@allDataCancel="allDataCancel"
					@saveFile="saveFile"
					@removeFile="removeFile"
				/>
			</div>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import resize from "../../mixin/resize.js";

import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

export default {
	name: "emission",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [],
			productionTeamValue: {},
			showAlert: false,
			searchFormStates: false,
		};
	},
	computed: {
		emissionColumns() {
			return [
				{
					field: "no",
					title: "No",
					editable: false,
					minResizableWidth: 45,
					width: 45,
				},
				{
					field: "business_place_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					editable: false,
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "facility_serial_no",
					title: "일련번호",
					editable: false,
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "facility_code_name",
					title: "대표시설",
					editable: false,
					minResizableWidth: 135,
					width: 135,
				},
				{
					field: "corporate_name",
					title: "자제시설",
					editable: false,
					minResizableWidth: 180,
					width: 180,
				},
				{
					field: "emission_code_name",
					title: "배출활동",
					editable: false,
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "activity_code_name",
					title: "활동자료",
					minResizableWidth: 130,
					width: 130,
					editable: false,
				},
				{
					field: "co2",
					title: "CO2",
					minResizableWidth: 120,
					width: 120,
					type: "numeric",
				},
				{
					field: "ch4",
					title: "CH4",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "n2o",
					title: "N2O",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "net_calolific_value",
					title: "순발열량",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "total_calolific_value",
					title: "총발열량",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "oxidation_coefficient",
					title: "산화계수",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "file_url",
					title: "첨부파일",
					cell: "FileFormCell",
					minResizableWidth: 230,
					width: 230,
					editable: false,
				},
			];
		},
		yearList() {
			return this.$store.state.emissionStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("emissionStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("emissionStore/FETCH_EMISSION_YEAR_LIST");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("emissionStore/FETCH_EMISSION_LIST", this.searchData)
				.then((data) => {
					if (this.$refs.EmissionGrid !== null) {
						if (data.items.length < 1) {
							this.isNoData = true;
						}
						this.$refs.EmissionGrid.refreshData();
					}
				});
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.$store.dispatch("emissionStore/FETCH_EMISSION_YEAR_LIST");
			this.fetchOrgList();
		},
		allDataSave: async function (data) {
			this.$store
				.dispatch("emissionStore/EDIT_FACILITY_PARAMETER", data)
				.then(() => {
					this.showAlert = !this.showAlert;
					this.fetchGridData();
				});
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		saveFile: async function (e, file) {
			const formData = new FormData();
			formData.append("file", file);
			const fileInfo = {
				id: e.fac_tier3_param_id,
				file: formData,
			};
			await this.$store.dispatch(
				"emissionStore/INSERT_EMISSION_FILE",
				fileInfo
			);
			this.fetchGridData();
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"emissionStore/DELETE_EMISSION_FILE",
				e.fac_tier3_param_id
			);
			this.fetchGridData();
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
