<template>
	<grid
		ref="grid"
		:class="className"
		:style="{ height: '100%' }"
		:data-items="gridData"
		:detail="detailDataItems ? 'detailGrid' : ''"
		:edit-field="'inEdit'"
		:resizable="true"
		:sortable="{ allowUnsort: true, mode: 'multiple' }"
		:collapsedGroups="collapsedGroups"
		:groupable="
			aggregates ? { enabled: false, footer: 'visible' } : { enabled: false }
		"
		:group="group"
		:sort="sort"
		:take="take"
		:skip="skip"
		:page-size="limit"
		:pageable="pageable"
		:total="total"
		:cell-render="cellRender"
		:expand-field="'expanded'"
		:columns="columns"
		style="width: 100%"
		@itemchange="itemChange"
		@datastatechange="dataStateChange"
		@expandchange="expandChange"
		@sortchange="sortChangeHandler"
		@pagechange="pageChangeHandler"
