		<!-- 코드그룹 드롭다운 -->
		<template v-slot:dropdownCodeGroupCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectCodeGroup"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.group_id)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCodeGroupChange(e, props.dataItem)"
			/>
		</template>
		<!-- 파라미터그룹 드롭다운 -->
		<template v-slot:dropdownParameterGroupCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectParameterGroup"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.group_id)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddParameterGroupChange(e, props.dataItem)"
			/>
