				:field="props.field"
				:disabled="true"
				@change="(e) => ddEmissionActivityChange(e, props.dataItem)"
			/>
		</template>
		<!-- 활동자료 드롭다운 -->
		<template v-slot:dropdownActivityDataCell="{ props }" class="gridSelect">
			<dropdown-cell
