				:field="props.field"
				:disabled="true"
				@change="(e) => ddUsageCalorificChange(e, props.dataItem)"
			/>
		</template>
		<!-- 배출계수등급 드롭다운 -->
		<template
			v-slot:dropdownEmissionCoefficientCell="{ props }"
