			/>
		</template>
		<!-- 카테고리 드롭다운 -->
		<template v-slot:dropdownCategoryCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectCategory"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.category_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCategoryChange(e, props.dataItem)"
			/>
		</template>
		<!-- 에너지원 목록 드롭다운 -->
		<template v-slot:dropdownEnergyCodeCell="{ props }" class="gridSelect">
