				:data="selectActivityCode"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.activity_code_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddActivityCodeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 합리화 유형 목록 드롭다운 -->
		<template
			v-slot:dropdownRationalizationCodeCell="{ props }"
			class="gridSelect"
		>
			<dropdown-cell
				:data="selectRationalizationCode"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.rationalization_code_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddRationalizationCodeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 투자/관리 목록 드롭다운 -->
		<template v-slot:dropdownInvestmentCodeCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectInvestmentCode"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.investment_code_no)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddInvestmentCodeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 감축사업 상태 코드 목록 드롭다운 -->
		<template v-slot:dropdownReductionStatusCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectReductionStatus"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.status)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddReductionStatusChange(e, props.dataItem)"
			/>
		</template>
		<!-- 내부/외부 목록 드롭다운 -->
		<template
			v-slot:dropdownReductionBusinessTypeCell="{ props }"
			class="gridSelect"
		>
			<dropdown-cell
				:data="selectReductionBusinessType"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.type)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddReductionBusinessTypeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 매도/매수 목록 드롭다운 -->
		<template
			v-slot:dropdownEmissionTradingTypeCell="{ props }"
			class="gridSelect"
		>
			<dropdown-cell
				:data="selectEmissionTradingType"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.type)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddEmissionTradingTypeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 거래방식 목록 드롭다운 -->
		<template
			v-slot:dropdownEmissionTradingModeCell="{ props }"
			class="gridSelect"
		>
			<dropdown-cell
				:data="selectEmissionTradingMode"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.mode)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddEmissionTradingModeChange(e, props.dataItem)"
			/>
		</template>
		<!-- 종목 목록 드롭다운 -->
		<template v-slot:dropdownEmissionPriceCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectEmissionPrice"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.type)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddEmissionPriceChange(e, props.dataItem)"
			/>
		</template>
		<!-- 계획년도 목록 드롭다운 -->
		<template v-slot:dropdownYearCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectYear"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.year)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddYearChange(e, props.dataItem)"
			/>
		</template>
		<!-- 적용연도 목록 드롭다운 -->
		<template v-slot:dropdownCarriedYearCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectCarriedYear"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.carried_year)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCarriedYearChange(e, props.dataItem)"
			/>
		</template>
		<!-- 매도자 목록 드롭다운 -->
