				:field="props.field"
				:disabled="true"
				@change="(e) => ddBuyerChange(e, props.dataItem)"
			/>
		</template>
		<!-- 이월/차입 목록 드롭다운 -->
		<template v-slot:dropdownCarriedType="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectCarriedType"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.type)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddCarriedTypeChange(e, props.dataItem)"
			/>
