		<template v-slot:dropdownWorkplaceCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectWorkplace"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.workplace_id)"
				:field="props.field"
				:columns="columns"
				:disabled="true"
				:gridName="gridName"
				@change="(e) => ddWorkplaceChange(e, props.dataItem)"
			/>
		</template>
		<!-- 생산팀 목록 드롭다운 -->
		<template v-slot:dropdownProductionTeamCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectProductionTeam"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.production_Team_id)"
				:field="props.field"
				:columns="columns"
				:disabled="true"
				:gridName="gridName"
				@change="(e) => ddProductionTeamChange(e, props.dataItem)"
			/>
