		<template v-slot:dropdownFacilityCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectFacility"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.facility_id)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddFacilityChange(e, props.dataItem)"
			/>
		</template>
		<!--인터페이스 시스템 목록 드롭다운 -->
		<template v-slot:dropdownInterfacesysCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectInterfacesys"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.interface_sys)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddInterfacesysChange(e, props.dataItem)"
			/>
		</template>
