		<template v-slot:dropdownPermissionCell="{ props }" class="gridSelect">
			<dropdown-cell
				:data="selectPermissions"
				:data-item="props.dataItem"
				:data-item-id="String(props.dataItem.permission_id)"
				:field="props.field"
				:disabled="true"
				@change="(e) => ddPermissionsChange(e, props.dataItem)"
			/>
		</template>
		<!-- 날짜 선택 -->
		<template v-slot:datePickerCell="{ props }" class="gridSelect">
			<datepicker-cell
				:data-item="props.dataItem"
				:field="props.field"
				:format="datePickerFormat"
				@change="(e) => datePickerHandler(e, props.dataItem)"
			/>
		</template>
