		<template v-slot:checkboxCell="{ props }">
			<checkbox-cell
				:data-item="props.dataItem"
				:columns="columns"
				:field="props.field"
				:disabledState="disabledState"
				@change="props.onChange"
				@blur="props.onBlur"
				@focus="props.onFocus"
			/>
		</template>
