			<checkbox-cell
				:data-item="props.dataItem"
				:columns="columns"
				:field="props.field"
				:disabledState="disabledState"
				:isChangeEvent="true"
				@change="(e) => activityUseynChange(e, props.dataItem)"
				@blur="props.onBlur"
				@focus="props.onFocus"
			/>
		</template>
		<!-- 배출활동 선택 라디오 버튼 -->
		<template v-slot:emissionActivitySelectCell="{ props }">
			<td>
				<radiobutton
					:name="'group'"
					:value="props.dataItem.use_yn"
					:checked="props.dataItem.use_yn === true"
					@change="(e) => radioEmissionActivityHandler(props.dataItem)"
				/>
			</td>
		</template>
		<!-- 라디오버튼 -->
		<template v-slot:radioButtonCell="{ props }">
			<radio-button-cell
				:data-item="props.dataItem"
				:field="props.field"
				@change="(e) => radioActiveHandler(e, props.dataItem)"
				@blur="props.onBlur"
				@focus="props.onFocus"
			/>
		</template>
		<!-- 데이터 편집, 수정, 삭제, 취소 버튼 -->
		<template v-slot:commandCell="{ props }">
			<command-cell
				:data-item="props.dataItem"
				@edit="edit"
				@save="save"
				@remove="remove"
				@cancel="cancel"
			/>
		</template>
		<!-- 증빙자료, 파일업로드 버튼 -->
		<template v-slot:FileFormCell="{ props }">
			<file-form-cell
				:data-item="props.dataItem"
				:gridName="gridName"
				@edit="edit"
				@save="saveFile"
				@remove="removeFile"
			/>
		</template>
		<!-- 수정된 값 font-color change -->
		<template v-slot:InputGridChangeCell="{ props }">
			<input-grid-change-cell
				:data-item="props.dataItem"
				:gridName="gridName"
				:field="props.field"
				@change="props.onChange"
				@blur="props.onBlur"
				@focus="props.onFocus"
			/>
		</template>
		<!-- 합계 cell -->
		<template v-slot:TotalSumCell="{ props }">
			<total-sum-cell
				:data-item="props.dataItem"
				:gridName="gridName"
				:field="props.field"
				@change="totalChange"
				@blur="props.onBlur"
				@focus="props.onFocus"
			/>
		</template>
		<!-- td Style change -->
		<template v-slot:StyleCell="{ props }">
			<style-cell
				:data-item="props.dataItem"
				:gridName="gridName"
				:field="props.field"
			/>
		</template>
		<template v-slot:cellmodals="{ props }">
			<cellmodal :data-item="props.dataItem" @clickEvent="clickEvent" />
		</template>
		<!-- 디테일 데이터 그리드 -->
		<template v-slot:detailGrid="{ props }">
			<detail-grid
				ref="detailGrid"
				:title="detailTitle"
				:gridName="gridName"
				:dataItem="props.dataItem"
				:dataItems="detailDataItems"
				:columns="detailColumns"
				:customCellCheck="customCellCheck"
				:propsGroup="detailGroup"
				:newDataId="newDetailDataId"
				:selectCalculationGrade="selectCalculationGrade"
				:insertGroupButtonText="detailInsertGroupButtonText"
				:insertButtonText="detailInsertButtonText"
				:insertDataCheck="insertDataCheck"
				@insert="detailInsert"
				@edit="detailEdit"
				@remove="detailRemove"
				:navShow="navShow"
				:type="type"
			/>
		</template>
		<template v-slot:rowspans1="{ props }">
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-if="props.field === 'id' && props.dataItem.id % 2 !== 0"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="
					props.field === 'sector_name' && props.dataItem.sector_name % 2 !== 0
				"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="
					props.field === 'workplace_name' &&
					props.dataItem.workplace_name % 2 !== 0
				"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="
					props.field === 'measure_name' &&
					props.dataItem.measure_name % 2 !== 0
				"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="props.field === 're_plan_mal_total'"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="props.field === 're_plan_method_total'"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="
					props.field === 'business_part_name' &&
					props.dataItem.business_part_name % 2 !== 0
				"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="props.field === 'unit' && props.dataItem.unit % 2 !== 0"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum ? rowspanNum : 6"
				v-else-if="props.field === 'area' && props.dataItem.area % 2 !== 0"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				:rowspan="rowspanNum2 ? rowspanNum2 : rowspanNum"
				v-else-if="
					props.field === 'production_team_name' &&
					props.dataItem.production_team_name % 2 !== 0
				"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
			<td
				v-else-if="
					props.field !== 'id' &&
					props.field !== 'sector_name' &&
					props.field !== 'workplace_name' &&
					props.field !== 'measure_name' &&
					props.field !== 're_plan_mal_total' &&
					props.field !== 're_plan_method_total' &&
					props.field !== 'business_part_name' &&
					props.field !== 'unit' &&
					props.field !== 'area' &&
					props.field !== 'production_team_name'
				"
				:class="props.className"
			>
				{{ getNestedValue(props.field, props.dataItem) }}
			</td>
		</template>
		<grid-toolbar :class="{ emissiong: type == 'emissiong' || '' }">
			<div v-if="title" class="table_header row">
				<span class="tableTitle m-0 pr-0">{{ title }}</span>
				<span v-if="count" class="pl-6">
					<span class="tableTitleSub"
						>(전체 <span class="Num">{{ total }}</span
						>건)</span
					>
				</span>
			</div>
			<div v-if="yearText" class="table_header row" style="padding-left: 25px">
				<span class="tableTitle m-0 pr-0 mr-2">기준 연도</span>
				<dropdownlist
					:data-items="yearDataItems"
					:value="yearValue"
					@change="changeYear"
					style="border: 1px solid rgb(214 214 214)"
				/>
			</div>
			<!-- limit 드롭다운 -->
			<div v-if="showlimit" class="k-command-cell">
				<dropdownlist
					:data-items="limitData"
					:value="limit"
					@change="limitChange"
				/>
			</div>
			<!-- 배출량 재산정 버튼 -->
			<basic-button
				v-if="emsprAdjstButtonText"
				:wsize="'130px'"
				class="mr-1"
				:backgroundColor="'rgb(243, 115, 33)'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				>{{ emsprAdjstButtonText }}</basic-button
			>

			<!-- 전력배출계수 버튼 -->
			<basic-button
				v-if="pwremCfcntButtonText"
				:wsize="'160px'"
				class="mr-1"
				:backgroundColor="'rgb(243, 115, 33)'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				>{{ pwremCfcntButtonText }}</basic-button
			>

			<!-- 활성화 버튼 -->
			<basic-button
				v-if="activeDataButtonText"
				@click="activeData"
				:wsize="'130px'"
				class="paraGroup"
				:backgroundColor="'#49A872'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				>{{ activeDataButtonText }}</basic-button
			>
			<!-- 팝업 버튼 -->
			<basic-button
				v-if="popupButtonText"
				@click="gridDialog"
				class="paraGroup"
				:backgroundColor="'#49A872'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				:wsize="'130px'"
				>{{ popupButtonText }}</basic-button
			>
			<!-- 엑셀 업로드 버튼 -->
			<basic-button
				v-if="excelUploadButtonText"
				@click="importExcel"
				class="exclGroup"
				:backgroundColor="'#FFFFFF'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				:wsize="'140px'"
				>{{ excelUploadButtonText }} <img :src="downloadicon" alt=""
			/></basic-button>
			<!-- 엑셀 다운 버튼 -->
			<basic-button
				v-if="excelDownloadButtonText"
				@click="exportExcel"
				class="exclGroup"
				:backgroundColor="'#FFFFFF'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				:wsize="'140px'"
				><img :src="downloadicon" alt="" /> {{ excelDownloadButtonText }}
			</basic-button>
			<!-- 전년도 복사 버튼 -->
			<basic-button
				v-if="copyButtonText"
				@click="copyData"
				:wsize="'130px'"
				class="paraAdd"
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				>{{ copyButtonText }}</basic-button
			>
			<!-- 추가 버튼 -->
			<basic-button
				v-if="insertButtonText"
				@click="insert"
				:wsize="'120px'"
				class="paraAdd"
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				>{{ insertButtonText }}</basic-button
			>
			<!-- 전체 저장 버튼 -->
			<basic-button
				v-if="saveButtonText"
				@click="allDataSave"
				:wsize="'130px'"
				class="paraAdd"
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				>{{ saveButtonText }}</basic-button
			>
			<!-- zoombutton 추가 -->
			<ZoomButton
				v-if="zoomMode"
				@Expansion="ExpansionGrid"
				@reduction="reductions"
				:zoomModeType="zoomModeType"
			/>
			<!-- 전체 권한 설정/취소 -->
			<basic-button
				v-if="checkedAllButtonText"
				@click="checkedAll"
				class="paraGroup"
				:backgroundColor="!isEditMode ? 'gray' : '#49A872'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				:wsize="'150px'"
				:disabled="!isEditMode"
				>{{ checkedAllButtonText }}</basic-button
			>
			<!-- 전체 편집 버튼 -->
			<command-all-cell
				v-if="isAllDataEdit"
				:data-item="dataItems"
				:isEditMode="isEditMode"
				@edit="allDataEdit"
				@save="allDataEditSave"
				@cancel="allDataCancel"
			/>
			<!-- 편집 -->
			<basic-button
				v-if="addButtonText4"
				@click="toggleDialog"
				class="paraGroup"
				:backgroundColor="'#49A872'"
				:fontColor="'#FFFFFF'"
				:borderColor="''"
				:wsize="'100px'"
				>{{ addButtonText4 }}</basic-button
			>
			<!-- 취소 -->
			<basic-button
				v-if="addButtonText5"
				@click="insert"
				:wsize="'100px'"
				class="paraAdd"
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				>{{ addButtonText5 }}</basic-button
			>
		</grid-toolbar>

		<template v-slot:footerCell="{ props }">
			<footer-cell
				:data-item="props.dataItem"
				:field="props.field"
				:row-type="props.rowType"
			/>
		</template>

		<grid-norecords>
			<loader
				v-if="!isNoData"
				type="converging-spinner"
				style="position: relative"
			/>
			<span v-else>데이터가 없습니다.</span>
		</grid-norecords>
		<!-- <grid-norecords>데이터가 없습니다.</grid-norecords> -->
	</grid>
	<!-- 데이터 저장 확인 메세지 팝업 -->
	<modal
		v-if="visibleConfirmDialog"
		:title="'알림'"
		:message="'변경 내용을 저장하시겠습니까?'"
		:type="'alert'"
		@close="messageDialog('confirm')"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="allDataSaveCallback" class="button first"
					>확인</kbutton
				>
				<kbutton @click="messageDialog('confirm')" class="button second"
					>취소</kbutton
				>
			</dialog-actions-bar>
		</template>
	</modal>
	<!-- 데이터 삭제 확인 메세지 팝업 -->
	<modal
		v-if="visibleDialog"
		:title="'알림'"
		:message="'해당 데이터를 삭제하시겠습니까?'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
				<kbutton @click="deleteData('confirm')" class="button second"
					>취소</kbutton
				>
				<!-- <basic-button
					@click="messageDialog"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>취소</basic-button
				>
				<basic-button
					@click="deleteData"
					:wsize="'130px'"
					class="paraAdd"
					:backgroundColor="'#ffffff'"
					:fontColor="'#49A872'"
					:borderColor="'#49A872'"
					>확인</basic-button
				> -->
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import { process } from "@progress/kendo-data-query";
import { Grid, GridToolbar, GridNoRecords } from "@progress/kendo-vue-grid";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import { RadioButton } from "@progress/kendo-vue-inputs";
import { saveExcel } from "@progress/kendo-vue-excel-export";
import { Loader } from "@progress/kendo-vue-indicators";
import { Button } from "@progress/kendo-vue-buttons";

import DetailGrid from "./DetailTable.vue";
import DropdownCell from "./cell/DropDownCell.vue";
import DropdownListCell from "./cell/DropDownListCell.vue";
import CheckboxCell from "./cell/CheckboxCell.vue";
import DatepickerCell from "./cell/DatePickerCell.vue";
import CommandCell from "./cell/CommandCell.vue";
import CommandAllCell from "./cell/CommandAllCell.vue";
import RadioButtonCell from "./cell/RadioButtonCell.vue";
import FileFormCell from "./cell/FileFormCell.vue";
import FooterCell from "./cell/FooterCell.vue";
import InputGridChangeCell from "./cell/InputGridChangeCell.vue";
import TotalSumCell from "./cell/TotalSumCell.vue";
import StyleCell from "./cell/StyleCell.vue";
import BasicButton from "../common/button/BasicButton.vue";
import Modal from "../modal/Modal.vue";
import downloadicon from "@/assets/img/downloadicon.png";
import ZoomButton from "../../components/common/button/ZoomButton";
import cellmodal from "./cell/cellClickModal.vue";

import { fetchParameterVersion } from "../../api/index.js";

export default {
	name: "VTable",
	emits: [
		"insert",
		"edit",
		"remove",
		"detailInsert",
		"detailEdit",
		"detailRemove",
		"isEditableChange",
		"allDataSave",
		"allDataCancel",
		"activityChange",
		"gridDialog",
		"activeData",
		"copyData",
		"changeYear",
		"refreshData",
		"exportExcel",
		"importExcel",
		"saveFile",
		"removeFile",
		"Expansions2",
		"limitChange",
		"pageChange",
		"activeChange",
		"clickEvents",
	],
	props: {
		title: String,
		detailTitle: String,
		gridName: String, // 그리드 이름(데이터 처리를 위한 값)
		className: String,
		dataItems: Array, // 그리드 데이터
		count: Number, // 그리드 데이터 합계
		detailDataItems: Array, // 디테일 그리드 데이터
		columns: Array, // 그리드 컬럼
		detailColumns: Array, // 디테일 그리드 컬럼
		showlimit: Boolean, // limit 여부
		pageable: Boolean, // 페이지네이션 사용여부
		aggregates: Array, // 집계
		customExpandCheck: Boolean, // 커스텀 그룹 확장 체크
		customCellCheck: Boolean, // 커스텀 Cell 여부
		newDataId: String, // 데이터 추가 시 전달 받을 id 값
		newDetailDataId: Number, // 데이터 추가시 전달 받을 id 값(디테일 그리드)
		selectCodeGroup: Array, // 코드그룹
		selectParameterGroup: Array, // 파라미터그룹
		selectoperatingBoundary: Array, // 운영경계구분코드
		selectoperatingBoundaryLowRank: Array, // 운영경계구분코드의 하위 경계코드
		selectCalculationGrade: Array, // 산정등급코드
		selectOrgType: Array, // 조직구분
		selectWorkplace: Array, // 사업장 목록
		selectCarriedType: Array, // 이월/차입 목록
		selectSector: Array, // 부문 목록
		selectProductionTeam: Array, // 생산팀 목록
		selectFacility: Array, // 배출활동 목록
		selectInterfacesys: Array, // 인터페이스 시스템 목록
		selectPermissions: Array, // 권한 그룹 목록
		selectCategory: Array, // Scope3 Category 코드
		selectEnergyCode: Array, // 에너지원 코드
		selectActivityCode: Array, // 감축활동 분류 코드
		selectRationalizationCode: Array, // 합리화 유형 코드
		selectInvestmentCode: Array, // 투자/관리 코드
		selectReductionStatus: Array, // 감축사업 상태 코드
		selectReductionBusinessType: Array, // 내부/외부 코드 목록
		selectEmissionTradingType: Array, // 매도/매수 코드 목록
		selectEmissionTradingMode: Array, // 거래방식 코드 목록
		selectEmissionPrice: Array, // 종목 코드 목록
		selectSeller: Array, // 매도자 코드 목록
		selectYear: Array, // 계획년도 목록
		selectCarriedYear: Array, // 적용연도 코드 목록
		selectBuyer: Array, // 매수자 코드 목록
		propsGroup: Array, // 그리드 그룹
		detailGroup: Array, // 디테일 그리드 그룹
		propsSort: Array, // 정렬
		openGroupName: String, // 그리드 그룹 적용 시 펼칠 그룹명
		deleteDataCheck: Boolean, // 데이터 삭제 여부 확인
		insertDataCheck: Boolean, // 데이터 추가 확인
		insertButtonText: String, // 추가 버튼 텍스트
		copyButtonText: String, // 전년도 복사 버튼 텍스트
		saveButtonText: String, // 전체 권한 설정/취소 버튼 텍스트
		checkedAllButtonText: String, // 저장 버튼 텍스트
		popupButtonText: String, // 팝업 버튼 텍스트
		activeDataButtonText: String, // 전체 저장 버튼 텍스트
		detailInsertGroupButtonText: String, // 디테일 그리드 그룹 추가 버튼 텍스트
		detailInsertButtonText: String, // 디테일 그리드 추가 버튼 텍스트
		yearDataItems: Array, // 기준연도 그리드 데이터
		yearValue: String,
		yearText: String,
		facilitySizeCodeId: Number, // 배출시설 규모 ID
		excelUploadButtonText: String, // 엑셀 업로드 버튼 텍스트
		excelDownloadButtonText: String, // 엑셀 다운로드 버튼 텍스트
		excelColumns: Array, // 엑셀 컬럼
		zoomMode: Boolean, // 확대축소 버튼
		zoomModeType: String, // 확대축소 버튼 상태
		isAllDataEdit: Boolean, // 전체 편집 버튼 여부
		type: String, //그룹 타입 명시
		navShow: Boolean, //툴바 표출 상태 명시
		isNoData: Boolean,
		rowspanNum: Number,
		rowspanNum2: String,
		emsprAdjstButtonText: String, //배출량 산정 버튼 추가
		pwremCfcntButtonText: String, //전력배출계수 적용 관리 버튼 추가
		disabledState: Boolean,
		addButtonText4: String,
		addButtonText5: String,
		datePickerFormat: String,
	},
	watch: {
		newDataId: function (value) {
			if (value) {
				this.gridDataProcess[this.refreshIndex].id = value;
				this.updatedData[this.refreshIndex].id = value;
			}
		},
	},
	components: {
		Grid,
		"grid-toolbar": GridToolbar,
		"grid-norecords": GridNoRecords,
		DialogActionsBar,
		radiobutton: RadioButton,
		DetailGrid,
		DropdownCell,
		DropdownListCell,
		DatepickerCell,
		CheckboxCell,
		CommandCell,
		CommandAllCell,
		RadioButtonCell,
		FileFormCell,
		FooterCell,
		BasicButton,
		Modal,
		dropdownlist: DropDownList,
		ZoomButton,
		InputGridChangeCell,
		TotalSumCell,
		StyleCell,
		Loader,
		kbutton: Button,
		cellmodal: cellmodal,
	},
	computed: {
		gridDataProcess() {
			let gridDataProcess = [];
			if ("data" in this.gridData) {
				gridDataProcess = this.gridData.data;
			} else {
				gridDataProcess = this.gridData;
			}
			return gridDataProcess;
		},
	},
	data: function () {
		return {
			collapsedGroups: [[]],
			gridPageable: { pageSizes: true },
			gridData: [],
			updatedData: [],
			allSaveChangeData: [],
			rowData: null,
			activeRow: {},
			skip: 0,
			take: 30,
			total: this.count,
			limit: 30,
			limitData: [30, 40, 50, 60, 70],
			group: this.propsGroup,
			sort: this.propsSort ? this.propsSort : [],
			expandedItems: [],
			visibleConfirmDialog: false,
			visibleDialog: false,
			downloadicon,
			isEditMode: false,
			fac_emi_id: 0, // 배출활동선택 활성화를 위한 id값
			cellRender: "",
			refreshIndex: 0,
		};
	},
	created: function () {
		if (this.aggregates) {
			this.cellRender = "footerCell";
		} else if (this.rowspanNum) {
			this.cellRender = "rowspans1";
		} else {
			this.cellRender = "";
		}
		this.updatedData = this.dataItems;
		let no = 1;
		this.updatedData.forEach((p) => {
			if (isNaN(p.no) == false) {
				no = Math.max(parseInt(p.no) + 1, no);
			}
		});
		if (this.aggregates) {
			this.group.map((group) => (group.aggregates = this.aggregates));
		}
		this.getData();
		if (this.group && this.aggregates == undefined) {
			this.collapsedGroups[0] = this.gridData.data.map((i) => i.value);
		}
	},
	methods: {
		selectCalculation(emission_code_no) {
			const emissionList = [];
			this.$store.state.calculationStore.calculationList.map((v) => {
				if (v.emission_code_no == emission_code_no) {
					emissionList.push({ id: v.calculation_id, text: v.calculation_name });
				}
			});
			return emissionList;
		},
		selectActivityData(emission_code_no) {
			const activityDataList = [];
			this.$store.state.emissionActivityStore.emissionActivityList.map((v) => {
				if (v.emission_code_no == emission_code_no)
					if (v.activity_code_no != null || v.activity_code_name != null)
						activityDataList.push({
							id: v.activity_code_no,
							text: v.activity_code_name,
						});
			});
			return activityDataList;
		},
