		datePickerHandler: function (e, dataItem) {
			// 날짜 선택
			const updatedData = this.gridDataProcess.slice();
			const item = this.update(updatedData, dataItem);
			if (this.gridName == "facilityEmission") {
				item.disposal_date = e.value;
				if (new Date() > e.value) {
					item.state = "폐기";
				} else {
					item.state = "진행중";
				}
			} else if (this.gridName == "newMngmn") {
				item.operation_start = e.value;
			} else if (
				this.gridName == "emissionTrading" ||
				this.gridName == "exEmissionTrading"
			) {
				item.trading_date = e.value;
			} else if (this.gridName == "reductionBusiness") {
				item.perform_plan_date = e.value;
			}
			this.gridData = updatedData;
		},
		radioEmissionActivityHandler: function (dataItem) {
			// TODO 배출활동선택 API 개발 필요
			this.fac_emi_id = dataItem.fac_emi_id;
		},
		radioActiveHandler: function (e, dataItem) {
			// 활성화할 산정식 선택
			this.activeRow = {
				id: dataItem.calculation_id,
				version: dataItem.version,
			};
		},
		activityUseynChange: function (e, dataItem) {
			if (typeof e === "boolean") {
				// 배출활동 목록 선택
				const updatedData = this.gridDataProcess.slice();
				const item = this.update(updatedData, dataItem);
				item.use_yn = e;
				this.gridData = updatedData;
				this.$emit("activeChange", this.gridDataProcess);
			}
		},
		totalChange: function () {
			// 월별 RE 실적 관리 소계 계산
			if (this.gridName === "rePerformanceMonth") {
				var totalReResult = 0;
				var totalReAmount = 0;
				for (let idx in this.gridDataProcess) {
					for (let jdx in this.gridDataProcess[idx].items) {
						if (
							this.gridDataProcess[idx].items[jdx].method_name !== "계" &&
							this.gridDataProcess[idx].items[jdx].re_result !== null
						) {
							totalReResult += parseInt(
								this.gridDataProcess[idx].items[jdx].re_result
							);
						}
						if (
							this.gridDataProcess[idx].items[jdx].method_name !== "계" &&
							this.gridDataProcess[idx].items[jdx].re_amount !== null
						) {
							totalReAmount += parseInt(
								this.gridDataProcess[idx].items[jdx].re_amount
							);
						}
					}
					this.gridDataProcess[idx].items[5].re_result = totalReResult;
					this.gridDataProcess[idx].items[5].re_amount = totalReAmount;
					totalReResult = 0;
					totalReAmount = 0;
				}
			}
		},
		allSaveChange: function (dataItem) {
			let isData = false;
			for (let v = 0; v < this.allSaveChangeData.length; v++) {
				if (this.allSaveChangeData[v].id == dataItem.id) {
					this.allSaveChangeData[v] = dataItem;
					isData = true;
					return false;
				}
			}
			if (!isData) {
				this.allSaveChangeData.push(dataItem);
			}
		},
		activeData: function () {
			// 활성화할 산정식 데이터 전달
			this.$emit("activeData", this.activeRow);
		},
		itemChange: function (e) {
			// 데이터 변경 시 실행
			if (e.dataItem !== undefined) {
				if (e.dataItem.id) {
					let index = this.gridDataProcess.findIndex(
						(p) => p.id === e.dataItem.id
					);
					let updated = Object.assign({}, this.gridDataProcess[index], {
						[e.field]: e.value,
					});
					this.gridDataProcess.splice(index, 1, updated);
				} else {
					e.dataItem[e.field] = e.value;
				}
			}
		},
		insert: function () {
			const dataItem = { inEdit: true, isNew: true };
			// 자동증가 ID값 설정
			let id = 1;
			let no = 1;
			// 코드 목록 및 코드 그룹 관련 데이터 세팅
			if (
				this.gridName == "code" ||
				this.gridName == "codeGroup" ||
				this.gridName == "organizationCode" ||
				this.gridName == "parameterGroup" ||
				this.gridName == "userPermissions" ||
				this.gridName == "companyCarriedManagement"
			) {
				dataItem.create_user = "admin";
				dataItem.update_user = "admin";
				dataItem.create_date = new Date();
				dataItem.update_date = new Date();
			}
			if (this.gridName == "code") {
				dataItem.use_yn = true;
				this.updatedData.map((p) => {
					if (isNaN(p.code_id) == false) {
						id = Math.max(parseInt(p.code_id) + 1, id);
					}
				});
				dataItem.code_id = id;
			}
			if (this.gridName == "codeGroup" || this.gridName == "parameterGroup") {
				this.updatedData.map((p) => {
					if (isNaN(p.group_id) == false) {
						id = Math.max(parseInt(p.group_id) + 1, id);
					}
				});
				dataItem.group_id = id;
			}
			if (this.gridName == "parameter") {
				dataItem.version = 1;
			}
			if (this.gridName == "calculation") {
				dataItem.version = 1;
				dataItem.use_yn = true;
				dataItem.update_user = "admin";
				dataItem.update_date = new Date();
			}
			if (this.gridName == "organizationCode") {
				this.updatedData.map((p) => {
					if (isNaN(p.org_code_id) == false) {
						id = Math.max(parseInt(p.org_code_id) + 1, id);
					}
				});
				dataItem.org_code_id = id;
				if (this.yearText) {
					dataItem.year = this.yearValue;
				}
			}
			if (this.gridName == "companyCarriedManagement") {
				this.updatedData.map((p) => {
					if (isNaN(p.cp_carried_id) == false) {
						id = Math.max(parseInt(p.cp_carried_id) + 1, id);
					}
				});
				dataItem.cp_carried_id = id;
			}
			if (this.gridName == "facilityEmission") {
				dataItem.cal_version = 1;
				dataItem.disposal_date = new Date();
			} else if (this.gridName == "newMngmn") {
				dataItem.operation_start = new Date();
			} else if (
				this.gridName == "emissionTrading" ||
				this.gridName == "exEmissionTrading"
			) {
				dataItem.trading_date = new Date();
			} else if (this.gridName == "reductionBusiness") {
				dataItem.perform_plan_date = new Date();
			}
			if (this.gridName == "evidence") {
				this.updatedData.map((p) => {
					if (isNaN(p.evidence_id) == false) {
						id = Math.max(parseInt(p.evidence_id) + 1, id);
					}
				});
				dataItem.evidence_id = id;
			}
			if (this.gridName == "scope") {
				dataItem.measure_name = "ton";
			}
			if (this.gridName == "reductionBusiness") {
				dataItem.plan_year = this.$store.state.reductionBusinessStore.year;
			}
			if (this.gridName == "otherPartEmissionManagement") {
				dataItem.year = this.$store.state.sectorEmissionStore.year;
				dataItem.sector_name = this.$store.state.sectorEmissionStore.sectorName;
			}
			// index를 찾기 위한 number 추가
			this.updatedData.forEach((p) => {
				if (isNaN(p.no) == false) {
					no = Math.max(parseInt(p.no) + 1, no);
				}
			});
			dataItem.no = no;
			this.gridDataProcess.splice(0, 0, dataItem);
			/* if (this.aggregates == undefined) {
				this.gridDataProcess.splice(0, 0, dataItem);
			} else {
				this.gridDataProcess[0].items.splice(0, 0, dataItem);
			} */
			// 데이터 추가/편집 구분
			this.$emit("isEditableChange", true);
		},
		edit: function (e) {
			if (this.gridName == "newMngmn") {
				// 한 행씩 편집 가능
				this.cancelAll();
			}

			// 데이터 편집
			let index = this.gridDataProcess.findIndex((p) => p.id === e.dataItem.id);
			let updated = Object.assign({}, this.gridDataProcess[index], {
				inEdit: true,
				isNew: false,
			});
			this.gridDataProcess.splice(index, 1, updated);
			// 데이터 추가/편집 구분
			if (this.gridName == "newMngmn") {
				if (e.dataItem.operation_start > new Date()) {
					this.$emit("isEditableChange", true);
				} else {
					this.$emit("isEditableChange", false);
				}
			} else {
				this.$emit("isEditableChange", false);
			}
		},
		allDataEdit: function () {
			this.isEditMode = true;
			if (this.propsGroup) {
				for (let idx in this.gridDataProcess) {
					for (let item in this.gridDataProcess[idx].items) {
						this.gridDataProcess[idx].items[item].inEdit = true;
						this.gridDataProcess[idx].items[item].isNew = false;
					}
				}
			} else {
				for (let idx in this.gridDataProcess) {
					let updated = Object.assign({}, this.gridDataProcess[idx], {
						inEdit: true,
						isNew: false,
					});
					this.gridDataProcess.splice(idx, 1, updated);
				}
			}
		},
		save: function (e) {
			let index = 0;
			// 데이터 저장
			if (e.dataItem.id) {
				index = this.gridDataProcess.findIndex((p) => p.id === e.dataItem.id);
			} else {
				index = this.gridDataProcess.findIndex((p) => p.no === e.dataItem.no);
			}
			let item = this.gridDataProcess[index];
			let updated = Object.assign(
				this.update(this.gridDataProcess.slice(), item),
				{ inEdit: undefined, isNew: undefined }
			);
			let updateDataIndex = this.updatedData.findIndex(
				(p) => p.id === e.dataItem.id
			);
			const data = {};
			if ("id" in updated) {
				// 데이터 수정
				data.id = updated.id;
				data.data = updated;
				updated.update_date = new Date();
				if (this.gridName == "calculation") {
					updated.version += 1;
				}
				this.$emit("edit", data);
				if (this.gridName == "facilityEmission") {
					data.data.disposal_date = new Date(data.data.disposal_date);
					if (this.$refs.detailGrid && this.$refs.detailGrid.returnData()) {
						this.$emit("detailEdit", {
							id: e.dataItem.fac_emi_id,
							data: this.$refs.detailGrid.returnData(),
						});
					}
				}
			} else {
				// 데이터 추가
				data.data = updated;
				this.$emit("insert", data);
				if (this.gridName == "facilityEmission") {
					data.data.disposal_date = new Date(data.data.disposal_date);
					if (this.$refs.detailGrid && this.$refs.detailGrid.returnData()) {
						this.$emit("detailEdit", {
							id: e.dataItem.fac_emi_id,
							data: this.$refs.detailGrid.returnData(),
						});
					}
				}
				// updated.id = this.newDataId;
				this.total += 1;
			}
			this.refreshIndex = updateDataIndex;
			this.gridDataProcess.splice(index, 1, updated);
			this.updatedData.splice(updateDataIndex, 1, updated);
		},
		update: function (data, item, remove) {
			// 데이터 업데이트
			let updated;
			if (item) {
				let itemid = item.id;
				let index = data.findIndex(
					(p) =>
						JSON.stringify({ ...p }) === JSON.stringify(item) ||
						(itemid && p.id === itemid)
				);
				if (index >= 0) {
					updated = Object.assign({}, item);
					data[index] = updated;
				} else {
					let id = 1;
					data.forEach((p) => {
						id = Math.max(p.id + 1, id);
					});
					updated = Object.assign({}, item, { id: id });
					data.unshift(updated);
					index = 0;
				}
				if (remove) {
					data = data.splice(index, 1);
				}
				return data[index];
			}
		},
		cancel: function (e) {
			// 데이터 편집 취소
			if (e.dataItem.id) {
				let index = this.gridDataProcess.findIndex(
					(p) => p.id === e.dataItem.id
				);
				let updateDataIndex = this.updatedData.findIndex(
					(p) => p.id === e.dataItem.id
				);
				let updated = Object.assign(this.updatedData[updateDataIndex], {
					inEdit: undefined,
				});
				this.gridDataProcess.splice(index, 1, updated);
			} else {
				let index = this.gridDataProcess.findIndex(
					(p) => JSON.stringify(e.dataItem) === JSON.stringify(p)
				);
				this.gridDataProcess.splice(index, 1);
			}
			if (this.gridName == "facilityEmission") {
				this.$refs.detailGrid.cancelAll();
			}
		},
		cancelAll: function () {
			// 편집 상태 초기화
			let editedItems = this.updatedData.filter((p) => p.inEdit === true);
			if (editedItems.length) {
				editedItems.forEach((item) => {
					item.inEdit = undefined;
				});
			}
			this.getData();
		},
		remove: function (e) {
			this.rowData = e;
			if (this.deleteDataCheck) {
				this.messageDialog();
			} else {
				this.deleteData();
			}
		},
		allDataSave: function () {
			// 전체 데이터 저장
			if (this.gridName == "calculationTier") {
				this.$emit("allDataSave", this.allSaveChangeData);
			} else {
				this.$emit("allDataSave", this.updatedData);
			}
		},
		allDataEditSave: function () {
			this.messageDialog("confirm");
		},
		allDataSaveCallback: function () {
			this.messageDialog("confirm");
			if (this.gridName == "management") {
				for (let idx in this.gridDataProcess) {
					let updated = Object.assign({}, this.gridDataProcess[idx], {
						inEdit: undefined,
						isNew: undefined,
					});
					this.gridDataProcess.splice(idx, 1, updated);
				}
			}
			this.isEditMode = false;
			// 전체 데이터 저장
			this.$emit("allDataSave", this.updatedData);
		},
		allDataCancel: function () {
			// 전체 데이터 취소
			this.isEditMode = false;
			this.$emit("allDataCancel");
		},
		getData: function () {
			// 데이터 세팅
			this.gridData = process(this.updatedData, {
				group: this.group,
				sort: this.sort,
			});
		},
		deleteData: function () {
			// 데이터 삭제
			this.update(this.gridDataProcess, this.rowData.dataItem, true);
			this.update(this.updatedData, this.rowData.dataItem, true);
			this.$emit("remove", this.rowData.dataItem.id);
			if (this.visibleDialog) {
				this.messageDialog();
			}
		},
		returnData: function () {
			// 데이터 반환
			return this.gridDataProcess;
		},
		createAppState: function (dataState) {
			this.group = dataState.group;
			this.take = dataState.take;
			this.skip = dataState.skip;
			this.sort = dataState.sort;
			this.getData();
		},
		dataStateChange: function (event) {
			this.createAppState(event.data);
		},
		expandChange: function (event) {
			// 그룹 확장
			event.dataItem[event.target.$props.expandField] = event.value;
			if (event.value && this.propsGroup) {
				this.collapsedGroups[0] = this.gridData.data
					.filter((i) => i.value !== event.dataItem.value)
					.map((i) => i.value);
				if (this.gridName == "emission") {
					// 배출활동 목록 클릭 시 활동자료 사용여부 조회
					const emission_code_no = event.dataItem.items[0].emission_code_no;
					this.$emit("activityChange", emission_code_no);
				}
			} else {
				this.collapsedGroups = event.collapsedGroups;
				if (this.gridName == "emission") {
					this.$emit("activityChange", 0);
				}
			}
		},
		customExpandChange: function (dataItem) {
			// 커스텀 그룹 확장
			this.gridDataProcess.forEach((el) => (el.expanded = false));
			dataItem.expanded =
				dataItem.expanded === undefined ? true : !dataItem.expanded;
		},
		refreshData: function () {
			// 데이터 새로고침
			if (this.isAllDataEdit) {
				this.isEditMode = false;
			}
			this.total = this.count;
			this.updatedData = this.dataItems;
			this.getData();
		},
		sortChangeHandler: function (e) {
			this.sort = e.sort;
			this.refreshData();
		},
		messageDialog: function (type) {
			// 메세지 팝업 show, hide
			if (type === "confirm") {
				this.visibleConfirmDialog = !this.visibleConfirmDialog;
			} else {
				this.visibleDialog = !this.visibleDialog;
			}
		},
		gridDialog: function () {
			this.$emit("gridDialog");
		},
		detailInsert: function (data) {
			// 디테일 그리드 추가 데이터 전달
			this.$emit("detailInsert", data);
		},
		detailEdit: function (data) {
			// 디테일 그리드 수정 데이터 전달
			this.$emit("detailEdit", data);
		},
		detailRemove: function (id) {
			// 디테일 그리드 삭제 ID 전달
			this.$emit("detailRemove", id);
		},
		copyData: function () {
			this.$emit("copyData");
		},
		changeYear: function (e) {
			this.$emit("changeYear", e);
		},
		exportExcel() {
			// 엑셀 다운로드
			saveExcel({
				data: this.dataItems,
				fileName: this.title,
				columns: this.excelColumns,
			});
		},
		importExcel(e, file) {
			// 엑셀 업로드
			this.$emit("importExcel", e, file);
		},
		saveFile(e, file) {
			this.isEditMode = false;
			// 파일 업로드
			this.$emit("saveFile", e, file);
		},
		removeFile(e) {
			// 파일 삭제
			this.$emit("removeFile", e);
		},
		ExpansionGrid(e) {
			this.$emit("Expansions2", e);
		},
		reductions(data) {
			console.log(data, "2");
		},
		getNestedValue: function (fieldName, dataItem) {
			const path = fieldName.split(".");
			let data = dataItem;

			path.forEach((a) => {
				data = data ? data[a] : undefined;
			});

			return data;
		},
		limitChange(event) {
			// limit 설정
			this.skip = 0;
			this.take = event.target.value;
			this.limit = event.target.value;
			this.$emit("limitChange", event.target.value);
		},
		pageChangeHandler: function (event) {
			// 페이지네이션
			this.skip = event.page.skip;
			this.take = event.page.take;
			this.$emit("pageChange", this.skip / this.take + 1);
		},
		checkedAll: function () {
			var count = 0;
			var dataSize = this.gridDataProcess.length * 4;
			if (this.gridName === "menuPermissions") {
				for (let idx in this.gridDataProcess) {
					if (this.gridDataProcess[idx].p_create) {
						count++;
					}
					if (this.gridDataProcess[idx].p_modify) {
						count++;
					}
					if (this.gridDataProcess[idx].p_delete) {
						count++;
					}
					if (this.gridDataProcess[idx].p_read) {
						count++;
					}
				}
				if (count === dataSize) {
					for (let idx in this.gridDataProcess) {
						this.gridDataProcess[idx].p_create = false;
						this.gridDataProcess[idx].p_modify = false;
						this.gridDataProcess[idx].p_delete = false;
						this.gridDataProcess[idx].p_read = false;
					}
				} else {
					for (let idx in this.gridDataProcess) {
						this.gridDataProcess[idx].p_create = true;
						this.gridDataProcess[idx].p_modify = true;
						this.gridDataProcess[idx].p_delete = true;
						this.gridDataProcess[idx].p_read = true;
					}
				}
			}
		},
		clickEvent: function (e) {
			this.$emit("clickEvents", e);
		},
	},
};
</script>

