<template>
	<td v-if="!dataItem.inEdit" class="k-command-cell" :class="className">
		{{ dataItem[field] }}
	</td>
	<td v-else class="k-command-cell">
		<template v-if="!data">{{ dataItem[field] }}</template>
		<dropdownlist
			v-else
			:style="{ height: '28px' }"
			@change="change"
			:value="dataItem[field]"
			:data-items="data"
		></dropdownlist>
	</td>
</template>
<script>
import { DropDownList } from "@progress/kendo-vue-dropdowns";

export default {
	components: {
		dropdownlist: DropDownList,
	},
	props: {
		field: String,
		data: Array,
		dataItem: Object,
		format: String,
		className: String,
		columnIndex: Number,
		columnsCount: Number,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
	},
	methods: {
		change(e) {
			this.$emit("change", e, e.target.value);
		},
	},
};
</script>

<style scoped>
td {
	vertical-align: middle !important;
}
</style>
