<template>
	<basic-button
		v-if="!isEditMode"
		:wsize="'90px'"
		class="paraAdd"
		:backgroundColor="'#ffffff'"
		:fontColor="'#49A872'"
		:borderColor="'#49A872'"
		:type="'button'"
		@click="editHandler"
		>편집</basic-button
	>
	<basic-button
		v-if="isEditMode"
		:wsize="'130px'"
		class="paraAdd"
		:backgroundColor="'#ffffff'"
		:fontColor="'#49A872'"
		:borderColor="'#49A872'"
		:type="'button'"
		@click="addUpdateHandler"
		>저장</basic-button
	>
	<basic-button
		v-if="isEditMode"
		:wsize="'130px'"
		class="resets"
		:backgroundColor="'#F9F9F9'"
		:fontColor="'#5A5B68'"
		:borderColor="'#5A5B68'"
		:type="'button'"
		@click="cancelDiscardHandler"
		>취소</basic-button
	>
</template>

<script>
import BasicButton from "../../common/button/BasicButton.vue";

export default {
	name: "CommandAllCell",
	components: {
		BasicButton,
	},
	props: {
		field: String,
		dataItem: Object,
		format: String,
		className: String,
		columnIndex: Number,
		columnsCount: Number,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
		isEditMode: Boolean,
	},
	methods: {
		editHandler: function () {
			this.$emit("edit", { dataItem: this.dataItem });
		},
		addUpdateHandler: function () {
			this.$emit("save", { dataItem: this.dataItem });
		},
		cancelDiscardHandler: function () {
			this.$emit("cancel", { dataItem: this.dataItem });
		},
	},
};
</script>

