import { fetchOrganizationYearList, fetchTeamReport } from "@/api/index.js";

const teamReportStore = {
	namespaced: true,
	state: {
		teamrEportList: [],
		teamrEportCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_TEAMR_EPORT: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.teamrEportList = data.items;
			state.teamrEportCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_TEAMR_EPORT: ({ commit }, info) => {
			// 사내보고 관리 목록 조회
			return fetchTeamReport(info)
				.then(({ data }) => {
					commit("SET_TEAMR_EPORT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default teamReportStore;
