<template>
	<!-- <td v-if="!dataItem['inEdit']" class="k-command-cell newbox"> -->
	<td class="k-command-cell newbox">
		<div v-if="dataItem.id !== 0" class="d-flex k-form-field newfild">
			<div
				v-if="
					beforeSave ||
					(dataItem.file_url !== null &&
						dataItem.file_url !== undefined &&
						dataItem.file_url !== '')
				"
				class="d-flex align-items-center aa"
			>
				<label class="k-label k-form-label filesButton"></label>
				<kbutton
					class="kbutton"
					:type="'file'"
					:style="{
						width: '100px',
						height: '30px',
						border: '1px solid #CCCCCC',
						color: '#49A872',
						fontfamily: 'AppleSDGothicNeoSB00',
						fontsize: '12px',
						display: 'inline-block',
						textalign: 'left',
						cursor: 'pointer',
					}"
				>
					<a
						href="#"
						class="filnput"
						style="display: inline-block; margin-rignt: auto"
						@click.prevent="
							downloadFile({
								url: dataItem.file_url,
								label: dataItem.file_name,
							})
						"
					>
						증빙자료...
					</a>
				</kbutton>
				<div class="xicons" @click="removeFile">
					<span><img :src="xicons" alt="삭제" /></span>
				</div>
			</div>

			<div>
				<localization
					@click="eventtt"
					:language="'bg-BG'"
					style="display: block"
					:class="{ active: dataItem.file_url ? '' : true }"
				>
					<intl :locale="'bg'" style="display: block">
						<upload
							:files="files"
							:batch="false"
							:multiple="false"
							:class="'p-0'"
							:with-credentials="false"
							:disabled="isShow"
							:save-url="'https://demos.telerik.com/kendo-ui/service-v4/upload/save'"
							:remove-url="'https://demos.telerik.com/kendo-ui/service-v4/upload/remove'"
							@add="onAdd"
							@remove="onRemove"
							@progress="onProgress"
							@statuschange="onStatusChange"
							class="files"
						>
							파일 업로드
						</upload>
					</intl>
				</localization>
			</div>
		</div>
	</td>
</template>

<script>
import xicons from "@/assets/img/icon/xx.png";

import { Button } from "@progress/kendo-vue-buttons";
import { Upload } from "@progress/kendo-vue-upload";
import {
	IntlProvider,
	LocalizationProvider,
	loadMessages,
} from "@progress/kendo-vue-intl";
import {} from "@progress/kendo-vue-intl";
import axios from "axios";

const bgMessages = {
	upload: {
		select: "파일 업로드",
	},
};
loadMessages(bgMessages, "bg-BG");
export default {
	name: "FileFormCell",
	xicons,
	components: {
		kbutton: Button,
		upload: Upload,
		intl: IntlProvider,
		localization: LocalizationProvider,
	},
	props: {
		field: String,
		dataItem: Object,
		format: String,
		className: String,
		columnIndex: Number,
		columnsCount: Number,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
		gridName: String,
	},
	computed: {
		isShow() {
			if (this.dataItem["inEdit"]) {
				if (
					this.gridName === "bsnprInqry" &&
					this.dataItem.perform_id === null
				) {
					return true;
				} else if (
					this.gridName === "wrkprMngmn" &&
					this.dataItem.perform_id === null
				) {
					return true;
				} else if (
					this.gridName === "rePerformanceMonth" &&
					this.dataItem.re_m_plan_id === null
				) {
					return true;
				} else {
					return false;
				}
			} else {
				return true;
			}
		},
		file_url() {
			return this.dataItem.file_url;
		},
	},
	watch: {
		file_url(value) {
			if (value === undefined) {
				this.files = [];
				this.beforeSave = false;
			}
		},
	},
	data() {
		return {
			xicons,
			addOneValidFile: false,
			success: false,
			files: [],
			xiconImage: "../",
			beforeSave: false,
		};
	},
	methods: {
		eventtt: (e) => {
			e.stopPropagation();
			let etargets = e.target;
			let etarget = e.target.offsetParent.firstChild;
			if (etargets.innerHTML == "파일 업로드") {
				etarget.click();
			} else {
				return false;
			}
		},
		onAdd(event) {
			this.files = event.newState;
			this.importFile();
		},
		onRemove(event) {
			this.files = event.newState;
		},
		onProgress(event) {
			this.files = event.newState;
		},
		onStatusChange(event) {
			this.files = event.newState;
		},
		async downloadFile({ url, label }) {
			// 저장 전 업로드한 파일 다운로드 불가
			if (!this.dataItem.isNew) {
				const response = await axios.get(url, { responseType: "blob" });
				const blob = new Blob([response.data], { type: "application/pdf" });
				const link = document.createElement("a");
				link.href = URL.createObjectURL(blob);
				link.download = label;
				link.click();
				URL.revokeObjectURL(link.href);
			}
		},
		importFile() {
			// 저장 전 업로드한 파일 화면 표시
			this.beforeSave = true;
			// 파일 서버에 저장
			this.$emit("save", this.dataItem, this.files[0].getRawFile());
		},
		removeFile() {
			// 저장 전 업로드한 파일 화면에서 삭제/서버 삭제
			this.beforeSave = false;
			if (this.dataItem.isNew) {
				this.files = [];
			} else {
				this.$emit("remove", this.dataItem);
			}
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

button[data-v-01c0e216] {
	display: inline-block !important;
	margin-left: auto !important;
	background: #fff;
	cursor: pointer;
}

.newbox button {
	vertical-align: middle;
	display: inline-block;
	text-align: left;
}

.filebox {
	justify-content: center;
	cursor: pointer;
}
.k-dropzone::deep .k-button-md {
	padding: 0 !important;
}
.newfild {
	justify-content: center;
	align-items: center !important;
	width: 194px;
	margin: 0 auto;
}

.files {
	min-width: initial !important;
	width: auto !important;
	margin-left: 10px;
}

.aa {
	position: relative;

	.xicons {
		cursor: pointer;
		color: gray;
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
	}
}
.active {
	margin-left: auto !important;
}
</style>
