<template>
	<td v-if="dataItem.inEdit && !isCloseDisable">
		<k-input
			ref="nameInput"
			:value="dataItem[field]"
			:style="styleObject"
			:disabled="isCloseDisable"
			@input="onInput"
			@change="change"
		></k-input>
	</td>
	<td v-else :style="tdStyleObject">
		{{ dataItem[field] }}
	</td>
</template>

<script>
import { Input } from "@progress/kendo-vue-inputs";

export default {
	components: {
		"k-input": Input,
	},
	props: {
		field: String,
		dataItem: Object,
		gridName: String,
	},
	data: function () {
		return {
			getDataItem: this.dataItem,
			styleObject: {},
			tdStyleObject: {},
		};
	},
	computed: {
		isCloseDisable() {
			if (this.gridName == "management") {
				if (this.field === "total_confirm") {
					return true;
				} else if (this.field === "january_confirm") {
					return this.getDataItem.january_closing_yn !== null
						? this.getDataItem.january_closing_yn
						: false;
				} else if (this.field === "february_confirm") {
					return this.getDataItem.february_closing_yn !== null
						? this.getDataItem.february_closing_yn
						: false;
				} else if (this.field === "march_confirm") {
					return this.getDataItem.march_closing_yn !== null
						? this.getDataItem.march_closing_yn
						: false;
				} else if (this.field === "april_confirm") {
					return this.getDataItem.april_closing_yn !== null
						? this.getDataItem.april_closing_yn
						: false;
				} else if (this.field === "may_confirm") {
					return this.getDataItem.may_closing_yn !== null
						? this.getDataItem.may_closing_yn
						: false;
				} else if (this.field === "jun_confirm") {
					return this.getDataItem.jun_closing_yn !== null
						? this.getDataItem.jun_closing_yn
						: false;
				} else if (this.field === "july_confirm") {
					return this.getDataItem.july_closing_yn !== null
						? this.getDataItem.july_closing_yn
						: false;
				} else if (this.field === "august_confirm") {
					return this.getDataItem.august_closing_yn !== null
						? this.getDataItem.august_closing_yn
						: false;
				} else if (this.field === "september_confirm") {
					return this.getDataItem.september_closing_yn !== null
						? this.getDataItem.september_closing_yn
						: false;
				} else if (this.field === "october_confirm") {
					return this.getDataItem.october_closing_yn !== null
						? this.getDataItem.october_closing_yn
						: false;
				} else if (this.field === "november_confirm") {
					return this.getDataItem.november_closing_yn !== null
						? this.getDataItem.november_closing_yn
						: false;
				} else if (this.field === "december_confirm") {
					return this.getDataItem.december_closing_yn !== null
						? this.getDataItem.december_closing_yn
						: false;
				} else {
					return false;
				}
			} else if (this.gridName == "companyEmissionManagement") {
				if (
					this.dataItem.type_name === "배출권 거래" ||
					this.dataItem.type_name === "배출권 제출" ||
					this.dataItem.type_name === "배출권 보유"
				) {
					return false;
				} else {
					return true;
				}
			} else if (this.gridName == "reductionPlanManagement") {
				if (this.getDataItem[this.field] !== "-") {
					return false;
				} else {
					return true;
				}
			} else {
				return false;
			}
		},
	},
	created() {
		this.setStyle();
	},
	methods: {
		setStyle() {
			if (this.gridName === "companyEmissionManagement") {
				var y2021 = this.getDataItem.y2021;
				var y2022 = this.getDataItem.y2022;
				var y2023 = this.getDataItem.y2023;
				var y2024 = this.getDataItem.y2024;
				var y2025 = this.getDataItem.y2025;
				if (this.dataItem.type_name === "차입/이월") {
					if (parseInt(y2021) < 0) {
						this.tdStyleObject = { color: "red" };
					} else {
						this.tdStyleObject = { color: "blue" };
					}
					if (parseInt(y2022) < 0) {
						this.tdStyleObject = { color: "red" };
					} else {
						this.tdStyleObject = { color: "blue" };
					}
					if (parseInt(y2023) < 0) {
						this.tdStyleObject = { color: "red" };
					} else {
						this.tdStyleObject = { color: "blue" };
					}
					if (parseInt(y2024) < 0) {
						this.tdStyleObject = { color: "red" };
					} else {
						this.tdStyleObject = { color: "blue" };
					}
					if (parseInt(y2025) < 0) {
						this.tdStyleObject = { color: "red" };
					} else {
						this.tdStyleObject = { color: "blue" };
					}
				}
			}
		},
		onInput: function (e) {
			this.getDataItem[this.field] = e.value;
		},
		change: function (e) {
			if (this.gridName == "management") {
				this.styleObject = { color: "red" };
			}
			this.getDataItem[this.field] = e.value;
			this.$emit("change", e);
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/App.scss";
.font-color-red {
	color: red;
}
</style>
