import {
	fetchCodeList,
	fetchCodeGroup,
	fetchOperationBoundaryLowRank,
} from "@/api/index.js";

const codeStore = {
	namespaced: true,
	state: {
		codeList: [],
		codeCount: 0,
		codeGroup: [],
		codeGroupCount: 0,
		emissionActivityCode: [], // 배출활동코드
		activityDataCode: [], // 활동자료코드
		calculationGradeCode: [], // 산정등급코드
		parameterInputCode: [], // 파라미터입력구분코드
		measureCode: [], // 단위코드
		operatingBoundaryCode: [], // 운영경계구분코드
		operationBoundaryLowRankCode: [], // 운영경계구분코드의 하위 경계 코드
	},
	mutations: {
		SET_CODE: (state) => {
			state.emissionActivityCode = [{ text: "전체" }];
			state.activityDataCode = [{ text: "전체" }];
			state.calculationGradeCode = [{ text: "전체" }];
			state.parameterInputCode = [{ text: "전체" }];
			state.measureCode = [{ text: "전체" }];
			state.operatingBoundaryCode = [{ text: "전체" }];

			// 배출활동코드
			state.codeList.map((v) => {
				if (v.group_id == "03")
					state.emissionActivityCode.push({ id: v.id, text: v.code_name });
			});
			// 활동자료코드
			state.codeList.map((v) => {
				if (v.group_id == "05")
					state.activityDataCode.push({ id: v.id, text: v.code_name });
			});
			// 활동자료코드
			state.codeList.map((v) => {
				if (v.group_id == "10")
					state.calculationGradeCode.push({ id: v.id, text: v.code_name });
			});
			// 파라미터입력구분코드
			state.codeList.map((v) => {
				if (v.group_id == "11")
					state.parameterInputCode.push({ id: v.id, text: v.code_name });
			});
			// 단위코드
			state.codeList.map((v) => {
				if (v.group_id == "06")
					state.measureCode.push({ id: v.id, text: v.code_name });
			});
			// 운영경계구분코드
			state.codeList.map((v) => {
				if (v.group_id == "02")
					state.operatingBoundaryCode.push({ id: v.id, text: v.code_name });
			});
		},
		SET_CODE_LIST: (state, code) => {
			state.codeList = code.items;
			state.codeCount = code.count;
		},
		SET_CODE_GROUP: (state, codeGroup) => {
			state.codeGroup = codeGroup.items;
			state.codeGroupCount = codeGroup.count;
		},
		SET_OPERATION_BOUNDARY_LOW_RANK: (state, operationBoundaryCode) => {
			operationBoundaryCode.items.map((v) =>
				state.operationBoundaryLowRankCode.push({ id: v.id, text: v.code_name })
			);
		},
	},
	actions: {
		GET_CODE: ({ commit }) => {
			// 특정 코드 값 가져오기
			return commit("SET_CODE");
		},
		FETCH_CODE_LIST: ({ commit }, info) => {
			// 코드 목록 조회
			return fetchCodeList(info)
				.then(({ data }) => {
					commit("SET_CODE_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_CODE_GROUP_LIST: ({ commit }, info) => {
			// 코드 그룹 조회
			return fetchCodeGroup(info)
				.then(({ data }) => {
					commit("SET_CODE_GROUP", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_OPERATION_BOUNDARY_LOW_RANK: ({ commit }) => {
			// 운영경계구분코드의 하위 경계 코드 목록 조회
			return fetchOperationBoundaryLowRank()
				.then(({ data }) => {
					commit("SET_OPERATION_BOUNDARY_LOW_RANK", data);
				})
				.catch((error) => console.log(error));
		},
	},
};

export default codeStore;
