import {
	fetchOrganizationYearList,
	fetchFinancialPerformanceManagement,
	editFinancialPerformanceManagement,
	fetchFinancialPerformanceYear,
} from "@/api/index.js";

const financialPerformanceStore = {
	namespaced: true,
	state: {
		financialPerformanceList: [],
		financialPerformanceCount: 0,
		financialPerformanceYearList: [],
		financialPerformanceYearCount: 0,
		yearList: [],
		year: "",
		startYear: "",
		endYear: "",
		successMessage: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
			state.startYear = data.items[data.items.length - 1];
			state.endYear = data.items[0];
		},
		SET_FINANCIAL_PERFORMANCE_MANAGEMENT_LIST: (state, data) => {
			state.financialPerformanceList = data.items;
			state.financialPerformanceCount = data.count;
		},
		SET_FINANCIAL_PERFORMANCE_YEAR_LIST: (state, data) => {
			state.financialPerformanceYearList = data.items;
			state.financialPerformanceYearCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
			state.startYear = "";
			state.endYear = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_FINANCIAL_PERFORMANCE_MANAGEMENT_LIST: ({ commit }, info) => {
			// 재무실적 관리 조회
			return fetchFinancialPerformanceManagement(info)
				.then(({ data }) => {
					commit("SET_FINANCIAL_PERFORMANCE_MANAGEMENT_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_FINANCIAL_PERFORMANCE_MANAGEMENT: (info) => {
			// 재무실적 관리 일괄 수정
			return editFinancialPerformanceManagement(
				info.state.financialPerformanceList
			)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_FINANCIAL_PERFORMANCE_YEAR: ({ commit }, info) => {
			// 재무실적 조회
			return fetchFinancialPerformanceYear(info)
				.then(({ data }) => {
					commit("SET_FINANCIAL_PERFORMANCE_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default financialPerformanceStore;
