<template>
	<div class="sideBarListBox">
		<ul class="nav flex-column bg-white mb-0" v-if="sidertitle">
			<li
				class="nav-item"
				v-for="(datatas, i) in datata"
				:key="i"
				@click="thirdDepthDataShow"
			>
				<sidebar-list-item
					:sidertitle="sidertitle"
					:text="datata[i].title"
					:to="{ name: datata[i].linkTo }"
					:thirdDepthData="datata[i].thirdDepth"
				>
				</sidebar-list-item>
			</li>
		</ul>
	</div>
</template>
<script>
import SidebarListItem from "./SidebarListItem.vue";
import iconBarDashboard from "@/assets/img/sidebar/sidebarListIcon.svg";
import iconBarGrid from "@/assets/img/ic_bar_grid.png";
import iconBarProfile from "@/assets/img/ic_bar_profile.png";

export default {
	name: "SidebarList",
	props: {
		sidertitle: String,
		datata: Array,
		link: Array,
		thirdDepthDatas: Array,
	},
	components: {
		SidebarListItem,
	},
	data: function () {
		return {
			iconBarDashboard,
			iconBarGrid,
			iconBarProfile,
		};
	},
	methods: {
		thirdDepthDataShow(e) {
			let targeted = e.target;
			let targets = targeted.closest(".nav-item");
			let $this = targets.querySelector(".thirdDepth");
			let $this2 = targets.querySelector(".twoDepth");
			let childrentwoDepth = document.querySelectorAll(".twoDepth");
			let childrenTargets = document.querySelectorAll(".thirdDepth");

			for (let i = 0; i < childrentwoDepth.length; i++) {
				for (let i = 0; i < childrentwoDepth.length; i++) {
					childrentwoDepth[i].classList.remove("active");
				}
				$this2.classList.add("active");
			}

			if ($this) {
				for (let i = 0; i < childrenTargets.length; i++) {
					for (let i = 0; i < childrenTargets.length; i++) {
						childrenTargets[i].classList.remove("active");
					}
					$this.classList.add("active");
				}
			} else {
				return false;
			}
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.sideBarListBox {
	display: none;

	&.active {
		display: block;
		background: $sidemenubga;
	}
}
</style>
