import {
	fetchOrganizationYearList,
	fetchRePerformanceMonth,
	editRePerformanceMonth,
	insertRePerformanceMonthFile,
	deleteRePerformanceMonthFile,
} from "@/api/index.js";

const rePerformanceMonthStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
		successMessage: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_RE_PERFORMANCE_MONTH_LIST: (state, data) => {
			for (var idx in data.items) {
				if (data.items[idx].method_name === "계") {
					data.items[idx].id = 0;
				}
			}
			// var rowSpan = 0;
			// for (var idx in data.items) {
			// 	rowSpan++;
			// 	if (rowSpan % 6 !== 1) {
			// 		data.items[idx].workplace_name = "";
			// 		data.items[idx].production_team_name = "";
			// 	}
			// }
			console.log(data.items);
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					commit("SET_ORGANIZATION_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_RE_PERFORMANCE_MONTH_LIST: ({ commit }, info) => {
			// 월별 RE 실적 관리
			return fetchRePerformanceMonth(info)
				.then(({ data }) => {
					commit("SET_RE_PERFORMANCE_MONTH_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_RE_PERFORMANCE_MONTH: (info) => {
			// 월별 RE 실적 관리 추가 및 수정
			return editRePerformanceMonth(info.state.dataList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_RE_PERFORMANCE_MONTH_FILE: ({ commit }, info) => {
			// 월별 RE 실적 증빙 자료 추가
			return insertRePerformanceMonthFile(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_RE_PERFORMANCE_MONTH_FILE: ({ commit }, id) => {
			// 월별 RE 실적 증빙 자료 삭제
			return deleteRePerformanceMonthFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default rePerformanceMonthStore;
