<template>
	<form-element>
		<!-- kendo form box-->
		<fieldset class="k-form-fieldset">
			<div class="grid-code form">
				<div class="forminfo d-flex justify-content-between">
					<!-- kendo form title-->
					<span>생산팀 정보</span>
					<!-- kendo form button box-->
					<div class="formButtonBox k-form-buttons ml-auto2">
						<basic-button
							class="inquiry"
							@click="openCodeModal"
							:type="'button'"
							title="조직코드관리"
							:backgroundColor="'#FFFFFF'"
							:fontColor="'#F37321'"
							:borderColor="'#F37321'"
							:wsize="'120px'"
							>조직코드관리</basic-button
						>
						<basic-button
							v-if="org_parent_id == ''"
							class="inquiry"
							@click="addData"
							:type="'button'"
							title="시설 추가"
							:backgroundColor="'#FFFFFF'"
							:fontColor="'#F37321'"
							:borderColor="'#F37321'"
							:wsize="'120px'"
							>시설 추가</basic-button
						>
						<basic-button
							v-if="org_parent_id == ''"
							class="resets"
							@click="deleteData"
							:type="'button'"
							title="삭제"
							:backgroundColor="'#F9F9F9'"
							:fontColor="'#5A5B68'"
							:borderColor="'#5A5B68'"
							:wsize="'90px'"
							>삭제</basic-button
						>
						<basic-button
							class="resets"
							@click="clear"
							:type="'button'"
							title="취소"
							:backgroundColor="'#F9F9F9'"
							:fontColor="'#5A5B68'"
							:borderColor="'#5A5B68'"
							:wsize="'90px'"
							>취소</basic-button
						>
						<kbutton
							class="submitbutton"
							type="submit"
							:disabled="!kendoForm.allowSubmit"
							:class="{ activesd: !kendoForm.allowSubmit }"
							>저장</kbutton
						>
					</div>
				</div>
				<div class="inputsBox">
					<div class="d-flex">
						<div class="content con1">
							<!-- kendo form field 명칭 setting-->
							<field
								:name="'corporate_name'"
								:component="'myTemplate'"
								:label="'corporate_name'"
								:validator="nameValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.corporate_name"
										:title="'명칭'"
									/>
								</template>
							</field>
							<!-- kendo form field 공장 정보 setting-->
							<field
								:name="'factoryList'"
								:component="'myTemplate'"
								:id="'factoryList'"
								:label="'공장 정보'"
								:data="$store.state.organizationStore.factoryCodeList"
								:validator="factoryValueValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<form-drop-down-cell
										v-bind="props"
										@change="changeFactory"
										@blur="props.onBlur"
										@focus="props.onFocus"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 직급 setting-->
							<field
								:name="'position'"
								:component="'myTemplate'"
								:label="'position'"
								:validator="positionValueValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 직급'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 휴대폰setting-->
							<field
								:name="'manager_mobile'"
								:component="'myTemplate'"
								:label="'manager_mobile'"
								:validator="mobileValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 휴대폰'"
										:mask="'000-0000-0000'"
									/>
								</template>
							</field>
							<!-- kendo form field 할당 비율setting-->
							<field
								v-if="isYusuFactory"
								:name="'factory_rate_yusu1'"
								:component="'myTemplate'"
								:label="'factory_rate_yusu1'"
								:validator="factoryRateYusu1Validator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'여수 1공장 배출량 할당 비율(%)'"
										class="telbox"
									/>
								</template>
							</field>
						</div>

						<div class="content con2">
							<!-- kendo form field 사업부 정보setting-->
							<field
								:name="'businessPartList'"
								:component="'myTemplate'"
								:id="'businessPartList'"
								:label="'사업부 정보'"
								:data="$store.state.organizationStore.businessCodeList"
								:validator="businessPartValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<form-drop-down-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 setting-->
							<field
								:name="'manager'"
								:component="'myTemplate'"
								:label="'manager'"
								:validator="managerValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 전화번호setting-->
							<field
								:name="'manager_phone'"
								:component="'myTemplate'"
								:label="'manager_phone'"
								:validator="phoneValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 전화번호'"
										:mask="'000-000-0000'"
									/>
								</template>
							</field>
							<!-- kendo form field 담당자 이메일setting-->
							<field
								:name="'manager_email'"
								:type="'email'"
								:component="'myTemplate'"
								:label="'manager_email'"
								:validator="emailValidator"
							>
								<template v-slot:myTemplate="{ props }">
									<input-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'담당자 이메일'"
									/>
								</template>
							</field>
							<!-- kendo form field 할당 비율setting-->
							<field
								v-if="isYusuFactory"
								:name="'factory_rate_yusu2'"
								:component="'myTemplate'"
								:label="'factory_rate_yusu2'"
								:validator="factoryRateYusu1Validator"
							>
								<template v-slot:myTemplate="{ props }">
									<numeric-text-box-cell
										v-bind="props"
										@change="props.onChange"
										@blur="props.onBlur"
										@focus="props.onFocus"
										:title="'여수 2공장 배출량 할당 비율(%)'"
										class="telbox"
									/>
								</template>
							</field>
						</div>
					</div>
					<!-- kendo form 필수항목 info'-->
					<div class="lastText">
						<p><span style="color: #f37321">*</span> 는 필수 항목입니다.</p>
					</div>
				</div>
			</div>
		</fieldset>
	</form-element>
	<modal v-if="visibleDialog" :title="'조직코드관리'" @close="toggleDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code">
					<v-table
						ref="codeGrid"
						class="grid-code"
						gridName="organizationCode"
						copyButtonText="전년도 복사"
						insertButtonText="조직코드추가"
						:dataItems="$store.state.organizationStore.organizationCodeList"
						:columns="organizationGroupColumns"
						:newDataId="newDataId"
						:selectOrgType="$store.state.organizationStore.orgTypeList"
						:yearText="'기준연도'"
						:yearDataItems="$store.state.organizationStore.codeYearList"
						:yearValue="String($store.state.organizationStore.codeYear)"
						@insert="dialogInsert"
						@edit="dialogEdit"
						@remove="dialogRemove"
						@copyData="dialogCopyData"
						@changeYear="changeYear"
						@isEditableChange="isEditableChange"
					/>
				</div>
			</div>
		</template>
	</modal>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'처리 실패! 입력정보를 확인해주세요.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import { Field, FormElement } from "@progress/kendo-vue-form";
import { Button } from "@progress/kendo-vue-buttons";
import BasicButton from "../../components/common/button/BasicButton.vue";
import InputCell from "../../components/form/InputCell.vue";
import FormDropDownCell from "../../components/form/FormDropDownCell.vue";
import NumericTextBoxCell from "../../components/form/NumericTextBoxCell.vue";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";

// 유효성 기능을 명시
import {
	nameValidator,
	factoryValueValidator,
	positionValueValidator,
	mobileValidator,
	factoryRateYusu1Validator,
	businessPartValidator,
	managerValidator,
	phoneValidator,
	emailValidator,
} from "../../validator/Validator";

import {
	insertOrganizationCode,
	editOrganizationCode,
	deleteOrganizationCode,
} from "@/api/index.js";

export default {
	name: "ProductionTeam",
	emits: ["deleteData"],
	components: {
		"form-element": FormElement,
		kbutton: Button,
		field: Field,
		BasicButton,
		InputCell,
		FormDropDownCell,
		NumericTextBoxCell,
		VTable,
		Modal,
	},
	data() {
		return {
			nameValidator,
			factoryValueValidator,
			positionValueValidator,
			mobileValidator,
			factoryRateYusu1Validator,
			businessPartValidator,
			managerValidator,
			phoneValidator,
			emailValidator,
			visibleDialog: false,
			organizationGroupColumns: [
				{
					field: "org_code",
					title: "조직코드",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "org_type_label",
					title: "조직구분",
					cell: "dropdownOrgTypeCell",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "org_code_name",
					title: "조직코드명",
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "create_user",
					title: "등록자",
					editable: false,
					minResizableWidth: 70,
					width: 70,
				},
				{
					field: "create_date",
					title: "등록일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "update_user",
					title: "수정자",
					editable: false,
					minResizableWidth: 70,
					width: 70,
				},
				{
					field: "update_date",
					title: "수정일",
					editable: false,
					format: "{0:yyyy.MM.dd}",
					minResizableWidth: 150,
					width: 150,
				},
				{ cell: "commandCell", minResizableWidth: 170, width: 170 },
			],
			searchCodeData: "",
			isYusuFactory: false,
		};
	},
	inject: {
		kendoForm: {
			default: {},
		},
	},
	computed: {
		org_parent_id() {
			return this.$store.state.organizationStore.org_parent_id;
		},
		year() {
			return this.$store.state.organizationStore.year;
		},
		codeYear() {
			return this.$store.state.organizationStore.codeYear;
		},
		org_id() {
			return this.$store.state.organizationStore.org_id;
		},
		depth() {
			return this.$store.state.organizationStore.depth;
		},
	},
	watch: {
		codeYear() {
			console.log("[watch codeYear]");
			this.fetchCodeList();
		},
	},
	created: function () {
		this.$store.dispatch("organizationStore/FETCH_ORGANIZATION_CODE_YEAR_LIST");
		if (this.org_parent_id == "") {
			if (
				this.$store.state.organizationStore.organizationData.factory.org_code ==
				"026"
			) {
				this.isYusuFactory = true;
			}
		}
	},
	methods: {
		/* 취소 */
		clear() {
			// 폼초기화 함수
			this.kendoForm.onFormReset();
		},
		/* 부문 추가 */
		addData() {
			console.log("[addData]");
			this.$store.state.organizationStore.depth = this.depth + 1;
			this.$store.state.organizationStore.org_parent_id = this.org_id;
			this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_INIT");
		},
		deleteData(dataItem) {
			this.$emit("deleteData", dataItem);
		},
		toggleDialog() {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.codeGrid.cancelAll();
			}
		},
		fetchCodeList() {
			this.searchCodeData = "";
			this.searchCodeData += "?year=" + this.codeYear;
			this.$store.dispatch(
				"organizationStore/FETCH_ORGANIZATION_CODE_LIST",
				this.searchCodeData
			);
			this.$store.dispatch(
				"organizationStore/FETCH_BUSINESS_CODE_LIST",
				this.searchCodeData + "&org_type=01"
			); // 01: 사업부
			this.$store.dispatch(
				"organizationStore/FETCH_FACTORY_CODE_LIST",
				this.searchCodeData + "&org_type=02"
			); // 02: 공장
		},
		openCodeModal() {
			this.visibleDialog = true;
		},
		dialogInsert: function (data) {
			insertOrganizationCode(data)
				.then((data) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		dialogEdit: function (data) {
			console.log(data);
			editOrganizationCode(data);
		},
		dialogRemove: function (id) {
			deleteOrganizationCode(id);
		},
		changeYear(e) {
			console.log(e);
			this.$store.dispatch("organizationStore/SETTING_DEPTH_INIT");
			this.$store.state.organizationStore.codeYear = e.target.value;
		},
		dialogCopyData() {
			this.$store
				.dispatch("organizationStore/COPY_ORGANIZATION_CODE")
				.then((data) => {
					console.log("[INSERT COPY CODE DATA then ==>]");
					console.log(data);
					this.$store.dispatch(
						"organizationStore/FETCH_ORGANIZATION_CODE_YEAR_LIST"
					);
					this.fetchCodeList();
				})
				.catch((error) => console.log(error));
		},
		changeFactory() {
			// 여수1공장+여수2공장 선택시에만 노출
			if (this.kendoForm.valueGetter("factoryList").code == "026") {
				this.isYusuFactory = true;
			} else {
				this.isYusuFactory = false;
			}
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.grid-code {
	&.form {
		padding: 20px;

		.k-input-inner {
			&::placeholder {
				color: #f37321 !important;
			}
		}

		.forminfo {
			height: 40px;

			span {
				font-family: AppleSDGothicNeoB00;
				font-size: 16px;
				color: #222222;
				display: flex;
				align-items: end;
			}

			.formButtonBox {
				button {
					margin-right: 0px;

					&:last-child {
						margin-right: 0px;
					}
				}
			}
		}

		.k-form-field {
			margin-top: 20px !important;

			.k-form-field-wrap {
				position: relative;

				.k-input {
					box-shadow: none !important;
				}

				.k-form-error {
					position: absolute;
					top: 0;
				}
			}
		}
	}

	.lastText {
		margin-top: 20px;
		margin-left: auto;
		font-size: 14px;
		font-family: AppleSDGothicNeoSB00;
		width: max-content;
	}

	.content {
		flex: 1;
	}
}
</style>
