<template>
	<Chart
		:series-defaults-type="'column'"
		:style="{ width: '100%', height: '100%' }"
	>
		<ChartLegend :position="'bottom'" :orientation="'horizontal'" />
		<ChartCategoryAxis>
			<ChartCategoryAxisItem :categories="data[0].categories">
				<ChartCategoryAxisTitle />
			</ChartCategoryAxisItem>
		</ChartCategoryAxis>
		<ChartValueAxis>
			<ChartValueAxisItem :name="'RevenueAxis'" :labels="valueAxisLabels">
				<ChartValueAxisTitle :text="title ? title : false" />
				<ChartValueAxisLabels :format="'{0:C}M'" />
			</ChartValueAxisItem>
		</ChartValueAxis>
		<ChartSeries>
			<ChartSeriesItem
				:name="data[0].seriesItemName[0]"
				:stack="{ type: 'Number' }"
				:data-items="data[0].firstSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[1]"
				:data-items="data[0].secondSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[2]"
				:data-items="data[0].thirdSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[3]"
				:data-items="data[0].forSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[4]"
				:data-items="data[0].fiveSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[5]"
				:data-items="data[0].sixSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[6]"
				:data-items="data[0].sevenSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[7]"
				:data-items="data[0].eightSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[8]"
				:data-items="data[0].nineSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[9]"
				:data-items="data[0].tenSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[10]"
				:data-items="data[0].elSeries"
			/>
			<ChartSeriesItem
				:name="data[0].seriesItemName[11]"
				:data-items="data[0].twoelSeries"
			/>
		</ChartSeries>
	</Chart>
</template>
<script>
import {
	ChartLegend,
	ChartValueAxisTitle,
	Chart,
	ChartSeries,
	ChartSeriesItem,
	ChartCategoryAxis,
	ChartCategoryAxisItem,
	ChartValueAxis,
	ChartValueAxisItem,
} from "@progress/kendo-vue-charts";

export default {
	props: {
		data: Object,
		seriesItemName: Object,
		title: String,
	},
	components: {
		ChartValueAxisTitle,
		Chart,
		ChartSeries,
		ChartSeriesItem,
		ChartCategoryAxis,
		ChartCategoryAxisItem,
		ChartValueAxisItem,
		ChartValueAxis,
		ChartLegend,
	},

	data: function () {
		return {
			valueAxisLabels: {
				padding: 3,
				font: "12px Arial, sans-serif",
			},
		};
	},
};
</script>
