<template>
	<td v-if="!dataItem.inEdit" :class="className">
		<checkbox
			:disabled="this.disabledState"
			:checked="Boolean(dataItem[field])"
		/>
	</td>
	<td
		v-else-if="
			columns &&
			columns.find((el) => el.field === field && el.editable !== true)
		"
	>
		<checkbox :disabled="true" :checked="Boolean(dataItem[field])" />
	</td>
	<td v-else :class="className">
		<checkbox
			v-if="isChangeEvent"
			:checked="Boolean(dataItem[field])"
			@change="change"
			:disabled="state"
		/>
		<checkbox v-else :checked="Boolean(dataItem[field])" :disabled="state" />
	</td>
</template>

<script>
import { Checkbox } from "@progress/kendo-vue-inputs";

export default {
	name: "CheckboxCell",
	props: {
		columns: Array,
		field: String,
		dataItem: Object,
		className: String,
		disabledState: Boolean,
		isChangeEvent: Boolean,
	},
	components: {
		checkbox: Checkbox,
	},
	data() {
		return {
			active: false,
			state: this.disableds,
		};
	},
	methods: {
		change(e) {
			this.$emit("change", e.value);
		},
	},
};
</script>

<style lang="scss" scoped>
input[type="checkbox"] {
	-webkit-appearance: none;
	position: relative;
	width: 14px;
	height: 14px;
	cursor: pointer;
	outline: none !important;
	border: 1px solid #33343c !important;
	border-radius: 2px;
	background: #fbfbfb;
}

input[type="checkbox"]::before {
	content: "\2713";
	font-weight: bolder;
	position: absolute;
	top: 50%;
	left: 50%;
	overflow: hidden;
	transform: scale(0) translate(-50%, -50%);
	line-height: 1;
}

input[type="checkbox"]:hover {
	border-color: #33343c;
}

input[type="checkbox"]:checked {
	background-color: rgb(73, 168, 114);
	border: none !important;
	color: white;
}

input[type="checkbox"]:checked::before {
	border-radius: 2px;
	transform: scale(1) translate(-50%, -50%);
}
</style>
