<template>
	<router-link class="nav-link p-0 twoDepth" :to="to" @click="setParent()">
		<div class="activeBox d-flex" />
		<span class="twoDepthTitle">{{ text }}</span>
	</router-link>
	<ul class="thirdDepth mt-10" v-if="thirdDepthData">
		<li
			class="thirdDepthList"
			v-for="(a, i) in thirdDepthData"
			:key="i"
			@click="thirdDepthDataShow2"
		>
			<router-link
				class="nav-link p-0"
				:to="{ name: thirdDepthData[i].linkTo }"
				@click="setParent(thirdDepthData[i].title)"
			>
				<p class="thirdDepthListP">
					<b class="middot" style="font-size: 50px">&middot;</b
					>{{ thirdDepthData[i].title }}
				</p>
			</router-link>
		</li>
	</ul>
</template>

<script>
export default {
	name: "SidebarListItem",
	props: {
		sidertitle: String,
		text: String,
		visibleDialogsss: Boolean,
		Boolean,
		to: {
			type: [Object, String],
			required: true,
		},
		thirdDepthData: Array,
	},
	data: function () {
		return {
			menu: "",
		};
	},
	methods: {
		setParent: function (depthMenu) {
			if (depthMenu) {
				this.menu = this.sidertitle + " > " + this.text + " > ";
			} else {
				if (this.thirdDepthData) {
					this.menu = this.sidertitle + " > " + this.text + " > ";
				} else {
					this.menu = this.sidertitle + " > ";
				}
			}
			this.$store.dispatch("SET_PARENT_MENU", this.menu);
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.activeBox {
	background: #eeeeee;
	width: 10px;
	height: 16px;
	margin-right: 15px;
	border-radius: 5px;
}

.nav-item {
	.twoDepth {
		&.active {
			.twoDepthTitle {
				color: #377a54 !important;
			}

			.activeBox {
				background: #51ce87;
			}
		}
	}
}

.thirdDepth {
	width: 149px;
	display: none;

	&:hover {
		.twoDepthTitle {
			color: #377a54 !important;
		}
	}

	&.active {
		display: block;
	}

	.thirdDepthList {
		margin-bottom: 6px;

		&:last-child {
			margin-bottom: 0;
		}

		.thirdDepthListP {
			display: flex;
			align-items: center;
			letter-spacing: -0.3px;
			.middot {
				display: inline-block;
				font-size: 46px;
				margin-right: 6px;
			}

			&:hover {
				p {
					color: #377a54 !important;
				}
			}
		}
	}
}
</style>
