<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form :reset="false" @searchFormStateA="searchFormStateA">
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>산정식</span></label>
								<div class="lists">
									<div class="selectBox">
										<field
											:name="'calculation'"
											:style="{ maxWidth: '200px' }"
											:component="'calculationSelect'"
										>
											<template v-slot:calculationSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:data-items="selectCalculation"
													:value="calculation"
													@change="dropdownChange"
													:text-field="'text'"
													:data-item-key="'id'"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					v-if="
						$store.state.calculationStore.calculationHistoryList.length !== 0 ||
						this.isNoData == true
					"
					ref="calculationHistoryGrid"
					gridName="calculationHistory"
					:dataItems="$store.state.calculationStore.calculationHistoryList"
					:count="$store.state.calculationStore.calculationHistoryCount"
					:detailDataItems="true"
					:columns="calculationHistoryColumns"
					:detailColumns="calculationParameterColumns"
					:detailGroup="[{ field: 'tier_name' }]"
					activeDataButtonText="저장"
					@activeData="activeData"
					:navShow="true"
					:type="'emty'"
					:isNoData="isNoData"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import resize from "../../mixin/resize.js";
import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";

import { editCalculationParameterVersionActive } from "@/api/index.js";

export default {
	name: "calculationHistory",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		dropdownlist: DropDownList,
		VTable,
	},
	data: function () {
		return {
			calculation: null,
			selectCalculation: [],
			calculationHistoryColumns: [
				{
					field: "calculation_name",
					title: "산정식명",
					editable: false,
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "emission_code_name",
					title: "배출활동",
					editable: false,
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "scope_code_name",
					title: "Scope",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "boundary_code_name",
					title: "경계",
					editable: false,
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "use_yn",
					title: "사용여부",
					cell: "radioButtonCell",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "remarks",
					title: "비고",
					editable: false,
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "version",
					title: "버전",
					editable: false,
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "update_date",
					title: "최종변경일",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "update_user",
					title: "변경자",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
			],
			// NOTE 배포를 위한 임시 가로사이즈 수정
			calculationParameterColumns: [
				{
					field: "tier_name",
					title: "Tier",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "calculation_formula",
					title: "산정식",
					minResizableWidth: 595,
					width: 595,
				},
			],
			searchFormStates: false,
			searchFormStates1: false,
			isNoData: false,
		};
	},
	created: async function () {
		// 산정식 목록 조회
		await this.$store.dispatch("calculationStore/FETCH_CALCULATION_LIST");
		await this.$store.state.calculationStore.calculationList.map((v) =>
			this.selectCalculation.push({
				id: v.calculation_id,
				text: v.calculation_name,
			})
		);
		this.calculation = this.selectCalculation[0];
		// 산정식 이력 목록 조회
		await this.$store.dispatch(
			"calculationStore/FETCH_CALCULATION_HISTORY_LIST",
			this.calculation.id
		);
		if (this.$store.state.calculationStore.calculationHistoryList.length == 0) {
			this.isNoData = true;
		}
		// 산정식 파라미터 이력 목록 조회
		// this.$store.dispatch(
		// 	"calculationStore/FETCH_CALCULATION_PARAMETER_HISTORY_LIST"
		// );
		// 코드 값 조회
		await this.$store.dispatch("codeStore/FETCH_CODE_LIST");
		this.$store.dispatch("codeStore/GET_CODE");
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		dropdownChange: function (event) {
			this.calculation = event.value;
		},
		handleSubmit: async function (dataItem) {
			await this.$store.dispatch(
				"calculationStore/FETCH_CALCULATION_HISTORY_LIST",
				dataItem.calculation.id
			);
			this.$refs.calculationHistoryGrid.refreshData();
		},
		activeData: function (data) {
			editCalculationParameterVersionActive(data);
		},
	},
	mixins: [resize(), settingGrid()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px; // height: 100px;
	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}
	.buttonBoxs {
		width: 200px;
	}
	.searchInputBox {
		position: relative;
		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
