<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획기간</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="periodList"
													:value="periodValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="periodChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>계획년도</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field :name="'startYear'" :component="'yearSelect1'">
											<template v-slot:yearSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="startYear"
													@change="startYearChange"
												/>
											</template>
										</field>
									</div>
									<span>~</span>
									<div class="selectBox ml-10">
										<field :name="'endYear'" :component="'yearSelect1'">
											<template v-slot:yearSelect1="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="yearList"
													:value="endYear"
													@change="endYearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
		:style="{ height: this.$store.state.resizeLast }"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<v-table
					ref="DataTable"
					className="groupCell"
					gridName="companyEmissionManagement"
					title="전사 배출권 총괄 관리"
					:rowspanNum="10"
					:dataItems="$store.state.companyEmissionManagementStore.dataList"
					:columns="columns"
					:isNoData="isNoData"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="columns"
					:isAllDataEdit="true"
					@allDataSave="allDataSave"
					@allDataCancel="allDataCancel"
					@clickEvents="clickEvents"
				/>
			</div>
		</div>
	</div>
	<modal v-if="visibleDialog" :title="'차입/이월관리'" @close="toggleDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code">
					<v-table
						ref="DataCarriedTable"
						class="grid-code"
						gridName="companyCarriedManagement"
						insertButtonText="추가"
						:dataItems="$store.state.companyEmissionManagementStore.carrieList"
						:columns="carrieColumns"
						:isNoData="isCarrieNoData"
						:newDataId="newDataId"
						:selectYear="yearDropDownList"
						:selectCarriedYear="yearDropDownList"
						:selectSector="sectorDropDownList"
						:selectWorkplace="workplaceDropDownList"
						:selectCarriedType="typeDropDownList"
						@insert="dialogInsert"
						@edit="dialogEdit"
						@remove="dialogRemove"
						@isEditableChange="isEditableChange"
					/>
				</div>
			</div>
		</template>
	</modal>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import resize from "../../mixin/resize.js";

import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";
import {
	editCompanyEmissionManagement,
	insertCompanyCarriedManagement,
	editCompanyCarriedManagement,
	deleteCompanyCarriedManagement,
} from "@/api/index.js";

export default {
	name: "CompanyEmissionManagement",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			isCarrieNoData: false,
			yearList: [2021, 2022, 2023, 2024, 2025],
			startYear: "",
			endYear: "",
			orgYear: "",
			orgList: [],
			periodList: [{ id: "01", text: "3차" }],
			periodValue: { id: "01", text: "3차" },
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			yearDropDownList: [],
			workplaceDropDownList: [],
			sectorDropDownList: [],
			typeDropDownList: [
				{ id: "1", text: "차입" },
				{ id: "2", text: "이월" },
			],
			workplaceCode: "",
			showAlert: false,
			visibleDialog: false,
			searchFormStates: false,
		};
	},
	computed: {
		columns() {
			let column = [
				{
					field: "sector_name",
					title: "부문",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "type_name",
					title: "구분(톤)",
					minResizableWidth: 210,
					width: 210,
					cell: "cellmodals",
				},
				{
					field: "measure",
					title: "단위",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
			];
			const reverseYear = [...this.yearList];
			for (var i = 0; i <= reverseYear.length - 1; i++) {
				column.push({
					field: "y" + reverseYear[i],
					title: reverseYear[i] + "",
					minResizableWidth: 160,
					width: 160,
					cell: "InputGridChangeCell",
					hidden: false,
				});
				// {
				// 	field: "month1",
				// 	title: "2020",
				// 	minResizableWidth: 100,
				// 	width: 100,
				// 	cell: this.cellWithBackGround,
				// },
			}
			return column;
		},
		carrieColumns() {
			return [
				{
					field: "year",
					title: "계획년도",
					cell: "dropdownYearCell",
					minResizableWidth: 120,
					width: 120,
				},
				{
					field: "sector_name",
					title: "부문",
					cell: "dropdownSectorCell",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "workplace_name",
					title: "사업장",
					cell: "dropdownWorkplaceCell",
					minResizableWidth: 140,
					width: 140,
				},
				{
					field: "carried_year",
					title: "적용연도",
					cell: "dropdownCarriedYearCell",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "type_name",
					title: "구분",
					cell: "dropdownCarriedType",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "value",
					title: "차입/이월량(tCO2-eq)",
					minResizableWidth: 170,
					width: 170,
				},
				{
					field: "remarks",
					title: "비고",
					minResizableWidth: 210,
					width: 210,
				},
				{ cell: "commandCell", minResizableWidth: 170, width: 170 },
			];
		},
	},
	watch: {
		orgYear(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.yearDropDownList = [];
		this.yearList.map((v) =>
			this.yearDropDownList.push({
				id: v,
				text: v,
			})
		);
		this.year = new Date().getFullYear();
		this.startYear = new Date().getFullYear();
		this.endYear = new Date().getFullYear();
		this.orgYear = new Date().getFullYear();
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.orgYear)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.orgYear +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorDropDownList = [];
						data.items.map((v) =>
							this.sectorDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceDropDownList = [];
						data.items.map((v) =>
							this.workplaceDropDownList.push({
								id: v.org_id,
								text: v.org_name,
							})
						);
						this.workplaceValue = { text: "전체 사업장" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?period=" + this.periodValue.id;
			this.searchData += "&start_year=" + this.startYear;
			this.searchData += "&end_year=" + this.endYear;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"companyEmissionManagementStore/FETCH_COMPANY_EMISSION_MANAGEMENT",
					this.searchData
				)
				.then((data) => {
					if (this.$refs.DataTable !== null) {
						// 조회한 연도만 컬럼만 노출
						for (var i = 4; i < 4 + this.yearList.length; i++) {
							this.columns[i].hidden = true;
						}
						for (var j = this.startYear; j <= this.endYear; j++) {
							let index = this.columns.findIndex((p) => p.title === j + "");
							this.columns[index].hidden = false;
						}
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.DataTable.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		periodChange(e) {
			this.periodValue = e.target.value;
		},
		startYearChange(event) {
			this.startYear = event.value;
		},
		endYearChange(event) {
			this.endYear = event.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
		},
		allDataSave: async function (data) {
			let index = 0;
			var saveData = [];
			index = data.findIndex((p) => p.type_name === "배출권 거래");
			saveData.push(data[index]);
			index = data.findIndex((p) => p.type_name === "배출권 제출");
			saveData.push(data[index]);
			index = data.findIndex((p) => p.type_name === "배출권 보유");
			saveData.push(data[index]);
			editCompanyEmissionManagement(saveData).then(() => {
				this.showAlert = !this.showAlert;
				this.fetchGridData();
			});
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		clearSearchForm() {
			this.startYear = new Date().getFullYear();
			this.endYear = new Date().getFullYear();
			this.year = new Date().getFullYear();
			this.workplaceValue = { text: "전체 사업장" };
			this.handleSubmit();
		},
		clickEvents(data) {
			if (data.dataItem.type_name == "차입/이월") {
				this.workplaceCode = data.dataItem.workplace_code;
				var searchData = "";
				searchData += "?workplace_code=" + data.dataItem.workplace_code;
				this.$store
					.dispatch(
						"companyEmissionManagementStore/FETCH_COMPANY_CARRIED_MANAGEMENT",
						searchData
					)
					.then((data) => {
						if (data.count < 1) {
							this.isCarrieNoData = true;
						}
						this.visibleDialog = true;
					});
			}
		},
		dialogInsert: function (data) {
			data.data.workplace_code = this.workplaceCode;
			insertCompanyCarriedManagement(data)
				.then((data) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		dialogEdit: function (data) {
			editCompanyCarriedManagement(data);
		},
		dialogRemove: function (id) {
			deleteCompanyCarriedManagement(id);
		},
		cellWithBackGround: function (h, tdElement, props, listeners) {
			const product = props.dataItem.month1;
			listeners;
			if (product < 400) {
				return h(
					"td",
					{
						style: { color: "blue" },
					},
					[product]
				);
			}
			return h(
				"td",
				{
					style: { color: "red" },
				},
				[product]
			);
		},
		toggleDialog() {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.DataCarriedTable.cancelAll();
			}
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
