import {
	fetchOrganizationYearList,
	fetchEnergyUsePlan,
	editEnergyUsePlan,
} from "@/api/index.js";

const energyUsePlanStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_ENERGY_USE_PLAN: (state, data) => {
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_ENERGY_USE_PLAN: ({ commit }, info) => {
			// 월별 에너지 사용계획 관리 조회
			return fetchEnergyUsePlan(info)
				.then(({ data }) => {
					commit("SET_ENERGY_USE_PLAN", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_ENERGY_USE_PLAN: (info) => {
			// 월별 에너지 사용계획 관리 일괄 수정
			return editEnergyUsePlan(info.state.dataList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default energyUsePlanStore;
