<template>
	<div class="searchbarWrap content1" style="padding: 0">
		<div class="subbar mr-20"><Subbar :type="'menu'" /></div>
		<div class="form-content searchWrap Codebar">
			<k-form @submit="handleSubmit" class="h-100">
				<div class="h-100">
					<div class="subContainer halfGrid h-100" style="width: 100%">
						<div class="w-100 h-100">
							<v-table
								ref="MenuPermissionsTable"
								title="메뉴 권한 관리"
								gridName="menuPermissions"
								:disabledState="true"
								:isAllDataEdit="true"
								@allDataSave="allDataSave"
								@allDataCancel="allDataCancel"
								:dataItems="$store.state.menuStore.menuPermissionsList"
								:columns="columns"
								:isNoData="isNoData"
								checkedAllButtonText="전체 권한 설정/취소"
							/>
						</div>
					</div>
				</div>
			</k-form>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import { Form } from "@progress/kendo-vue-form";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";
import Subbar from "../../components/common/sidebar/Subbar.vue";

export default {
	name: "MenuPermissions",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		VTable,
		Modal,
		DialogActionsBar,
		kbutton: Button,
		Subbar,
	},
	data: function () {
		return {
			searchData: null,
			isNoData: false,
			showAlert: false,
		};
	},
	computed: {
		columns() {
			return [
				{
					field: "permission_name",
					title: "권한그룹",
					minResizableWidth: 100,
					width: 100,
					editable: false,
				},
				{
					field: "p_create",
					title: "추가",
					cell: "checkboxCell",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "p_modify",
					title: "수정",
					cell: "checkboxCell",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "p_delete",
					title: "삭제",
					cell: "checkboxCell",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "p_read",
					title: "조회",
					cell: "checkboxCell",
					minResizableWidth: 100,
					width: 100,
				},
			];
		},
		menu_id() {
			return this.$store.state.menuStore.menu_id;
		},
	},
	watch: {
		menu_id() {
			this.fetchGridData();
		},
	},
	created() {
		this.$store.dispatch("menuStore/FETCH_MENU_LIST").then((data) => {
			this.$store.state.menuStore.menu_id = data.items[0].sub_menu[0].menu_id;
		});
	},
	methods: {
		fetchGridData() {
			this.searchData = "";
			this.searchData += "?menu_id=" + this.menu_id;
			this.$store
				.dispatch("menuStore/FETCH_MENU_PERMISSIONS", this.searchData)
				.then((data) => {
					if (this.$refs.MenuPermissionsTable !== null) {
						if (data.count < 1) {
							this.isNoData = true;
						}
						this.$refs.MenuPermissionsTable.refreshData();
					}
				});
		},
		allDataSave: async function (data) {
			console.log(data);
			this.$store.dispatch("menuStore/EDIT_MENU_PERMISSIONS", data).then(() => {
				this.showAlert = !this.showAlert;
				this.fetchGridData();
			});
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/App.scss";

.form-content {
	all: initial;
	width: calc(100% - 231px) !important;
	height: 100%;
	.formWraps {
		height: 100%;
	}
}

.content1 {
	height: calc(100vh - 179px) !important;
}
.d-flex.boxs {
	overflow: hidden;
	height: 58vh;
}

.subContainer {
	border-radius: 6px;
	padding-bottom: 35px;
	background: #fff;
	height: calc(100vh - 193px);
	overflow: hidden;
	&.halfGrid {
		box-shadow: none !important;
	}
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.selHearders {
	width: 100%;
	justify-content: space-between;
	align-items: center;
	padding: 0px 20px 0px 0px;
	box-sizing: border-box;
}

.pt-10 {
	padding-top: 20px;
}
</style>
