import {
	fetchMasterYearList,
	fetchFacilityParameterList,
	editFacilityParameter,
	insertFacilityParameterFile,
	deleteFacilityParameterFile,
} from "@/api/index.js";

const emissionStore = {
	namespaced: true,
	state: {
		emissionList: [],
		emissionCount: 0,
		yearList: [],
		year: "",
		successMessage: "",
	},
	mutations: {
		SET_EMISSION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_EMISSION_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.emissionList = data.items;
			state.emissionCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_EMISSION_YEAR_LIST: ({ commit }) => {
			// 선택 가능한 기준연도 목록 조회
			return fetchMasterYearList()
				.then(({ data }) => {
					commit("SET_EMISSION_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_EMISSION_LIST: ({ commit }, info) => {
			// 마스터 목록 조회
			return fetchFacilityParameterList(info)
				.then(({ data }) => {
					commit("SET_EMISSION_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		EDIT_FACILITY_PARAMETER: (info) => {
			// 시설 고유배출계수(Tier3) 일괄 수정
			return editFacilityParameter(info.state.emissionList)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_EMISSION_FILE: ({ commit }, info) => {
			return insertFacilityParameterFile(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_EMISSION_FILE: ({ commit }, id) => {
			return deleteFacilityParameterFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default emissionStore;
