import { fetchOrganizationYearList, fetchQuotaMonth } from "@/api/index.js";

const quotaMonthStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_QUOTA_MONTH: (state, data) => {
			var rowSpan = 0;
			for (var idx in data.items) {
				rowSpan++;
				if (rowSpan % 5 !== 1) {
					data.items[idx].sector_name = "";
					data.items[idx].workplace_name = "";
					data.items[idx].production_team_name = "";
					data.items[idx].measure_name = "";
				}
			}
			state.dataList = data.items;
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_QUOTA_MONTH: ({ commit }, info) => {
			// 연/월 할당량 조회
			return fetchQuotaMonth(info)
				.then(({ data }) => {
					commit("SET_QUOTA_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default quotaMonthStore;
