import {
	fetchOrganizationYearList,
	fetchSectorEmissionList,
	insertSectorEmission,
	deleteSectorEmission,
	deleteSectorEmissionFile,
} from "@/api/index.js";

const sectorEmissionStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		yearList: [],
		year: "",
		sectorName: "",
		sectorValue: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_SECTOR_EMISSION_LIST: (state, data) => {
			state.dataList = data.items;
			state.dataList.map((v) => {
				v.id = v.sector_emission_id;
			});
			state.dataCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_SECTOR_EMISSION_LIST: ({ commit }, info) => {
			// 타 부문 배출량 목록 조회
			return fetchSectorEmissionList(info)
				.then(({ data }) => {
					commit("SET_SECTOR_EMISSION_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_SECTOR_EMISSION: ({ commit }, info) => {
			// 타 부문 배출량 추가 및 수정
			return insertSectorEmission(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_SECTOR_EMISSION: ({ commit }, id) => {
			// 타 부문 배출량 삭제
			return deleteSectorEmission(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_SECTOR_EMISSION_FILE: ({ commit }, id) => {
			// 타 부문 배출량의 첨부파일 삭제
			return deleteSectorEmissionFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default sectorEmissionStore;
