<template>
	<td v-if="dataItem.id == 0"></td>
	<td v-else-if="!dataItem['inEdit']" class="k-command-cell" style="padding: 0">
		<kbutton
			:theme-color="'primary'"
			class="k-grid-edit-command Add"
			@click="editHandler"
		>
			편집
		</kbutton>
		<kbutton class="k-grid-remove-command del" @click="removeHandler">
			삭제
		</kbutton>
	</td>
	<td v-else class="k-command-cell">
		<kbutton class="k-grid-edit-command Add" @click="addUpdateHandler">
			{{ dataItem.id ? "수정" : "저장" }}
		</kbutton>
		<kbutton class="k-grid-remove-command cancel" @click="cancelDiscardHandler">
			취소
		</kbutton>
	</td>
</template>

<script>
import { Button } from "@progress/kendo-vue-buttons";
export default {
	name: "FileFormCell",
	components: {
		kbutton: Button,
	},
	props: {
		field: String,
		dataItem: Object,
		format: String,
		className: String,
		columnIndex: Number,
		columnsCount: Number,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
	},
	methods: {
		editHandler: function () {
			this.$emit("edit", { dataItem: this.dataItem });
		},
		removeHandler: function () {
			this.$emit("remove", { dataItem: this.dataItem });
		},
		addUpdateHandler: function () {
			this.$emit("save", { dataItem: this.dataItem });
		},
		cancelDiscardHandler: function () {
			this.$emit("cancel", { dataItem: this.dataItem });
		},
	},
};
</script>

