<template>
	<td class="k-command-cell">
		<div v-if="dataItem.type_name == '차입/이월'">
			<span class="k-grid-edit-command Add" style="cursor: pointer">
				{{ dataItem.type_name }}
			</span>
			<BasicButton
				:backgroundColor="'#ffffff'"
				:fontColor="'#49A872'"
				:borderColor="'#49A872'"
				class="ml-10 cell"
				:wsize="'70px'"
				@click="clickEvent"
				>차입 이월</BasicButton
			>
		</div>
		<div v-else>
			<span class="k-grid-edit-command Add">
				{{ dataItem.type_name }}
			</span>
		</div>
	</td>
</template>

<script>
import BasicButton from "../../common/button/BasicButton.vue";

export default {
	name: "FileFormCell",
	components: {
		BasicButton,
	},
	props: {
		field: String,
		dataItem: Object,
		format: String,
		className: String,
		columnIndex: Number,
		columnsCount: Number,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
	},
	methods: {
		clickEvent: function () {
			this.$emit("clickEvent", { dataItem: this.dataItem });
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
button.cell {
	p {
		font-size: 11px !important;
	}
}
</style>
