import {
	fetchCarbonNeutralYearList,
	fetchEmissionGoalYear,
} from "@/api/index.js";

const emissionGoalYearStore = {
	namespaced: true,
	state: {
		emissionGoalList: [],
		emissionGoalCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_CARBON_NEUTRAL_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_EMISSION_GOAL_YEAR: (state, data) => {
			var rowSpan = 0;
			for (var idx in data.items) {
				rowSpan++;
				if (rowSpan % 6 !== 1) {
					data.items[idx].workplace_name = "";
				}
				if (rowSpan % 3 !== 1) {
					data.items[idx].production_team_name = "";
				}
			}
			state.emissionGoalList = data.items;
			state.emissionGoalCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_CARBON_NEUTRAL_YEAR_LIST: ({ commit }) => {
			// 탄소중립 목표관리의 선택 가능한 기준연도 목록 조회
			return fetchCarbonNeutralYearList()
				.then(({ data }) => {
					commit("SET_CARBON_NEUTRAL_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_EMISSION_GOAL_YEAR: ({ commit }, info) => {
			// 계획기간 배출목표 조회
			return fetchEmissionGoalYear(info)
				.then(({ data }) => {
					commit("SET_EMISSION_GOAL_YEAR", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default emissionGoalYearStore;
