import {
	fetchOrganizationYearList,
	fetchGreenGasBasic,
	fetchGreenGasYear,
	fetchGreenGasMonth,
	fetchGreenGasSameMonth,
	fetchGreenGasScope,
	fetchGreenGasScopePeriod,
	fetchGreenGasBasicUnit,
	fetchGreenGasBasicUnitPeriod,
	fetchGreenGasProduct,
	fetchGreenGasFacilityEmission,
} from "@/api/index.js";

const greenGasStore = {
	namespaced: true,
	state: {
		greenGasBasicList: [],
		greenGasBasicChart: [],
		greenGasYearList: [],
		greenGasYearChart: [],
		greenGasMonthList: [],
		greenGasMonthChart: [],
		greenGasSameMonthList: [],
		greenGasSameMonthChart: [],
		greenGasScopeList: [],
		greenGasScopeChart: [],
		greenGasBasicUnitList: [],
		greenGasBasicUnitChart: [],
		greenGasBasicUnitPeriodList: [],
		greenGasBasicUnitPeriodChart: [],
		greenGasProductList: [],
		greenGasProductChart: [],
		greenGasList: [],
		greenGasCount: 0,
		yearList: [],
		year: "",
		startYear: "",
		endYear: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
			state.startYear = data.items[data.items.length - 1];
			state.endYear = data.items[0];
		},
		SET_GREEN_GAS: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasBasicList = data.grid;
			state.greenGasBasicChart = data.chart;
		},
		SET_GREEN_GAS_YEAR: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasYearList = data.grid;
			state.greenGasYearChart = data.chart;
		},
		SET_GREEN_GAS_MONTH: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasMonthList = data.grid;
			state.greenGasMonthChart = data.chart;
		},
		SET_GREEN_GAS_SAME_MONTH: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasSameMonthList = data.grid;
			state.greenGasSameMonthChart = data.chart;
		},
		SET_GREEN_GAS_SCOPE: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasScopeList = data.grid;
			state.greenGasScopeChart = data.chart;
		},
		SET_GREEN_GAS_BASIC_UNIT: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasBasicUnitList = data.grid;
			state.greenGasBasicUnitChart = data.chart;
		},
		SET_GREEN_GAS_BASIC_UNIT_PERIOD: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasBasicUnitPeriodList = data.grid;
			state.greenGasBasicUnitPeriodChart = data.chart;
		},
		SET_GREEN_GAS_PRODUCT: (state, data) => {
			for (var idx in data.grid) {
				data.grid[idx].no = parseInt(idx) + 1;
			}
			state.greenGasProductList = data.grid;
			state.greenGasProductChart = data.chart;
		},
		SET_GREEN_GAS_GRID: (state, data) => {
			state.greenGasList = data.items;
			state.greenGasCount = data.count;
		},
		SET_INIT: (state) => {
			state.greenGasList = [];
			state.greenGasChart = [];
			state.yearList = [];
			state.year = "";
			state.startYear = "";
			state.endYear = "";
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 조직 및 시설의 선택 가능한 기준연도 목록 조회
			return fetchOrganizationYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_BASIC: ({ commit }, info) => {
			// 온실가스 배출량 조회
			return fetchGreenGasBasic(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_YEAR: ({ commit }, info) => {
			// 연도별 온실가스 배출량 추이 조회
			return fetchGreenGasYear(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_YEAR", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_MONTH: ({ commit }, info) => {
			// 월별 온실가스 배출량 추이 조회
			return fetchGreenGasMonth(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_SAME_MONTH: ({ commit }, info) => {
			// 동월 월별 온실가스 배출량 추이 조회
			return fetchGreenGasSameMonth(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_SAME_MONTH", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_SCOPE: ({ commit }, info) => {
			// Scope별 온실가스 배출량 조회
			return fetchGreenGasScope(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_SCOPE", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_SCOPE_PERIOD: ({ commit }, info) => {
			// Scope별 온실가스 배출량 추이 조회
			return fetchGreenGasScopePeriod(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_SCOPE", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_BASIC_UNIT: ({ commit }, info) => {
			// 온실가스 원단위 조회
			return fetchGreenGasBasicUnit(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_BASIC_UNIT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_BASIC_UNIT_PERIOD: ({ commit }, info) => {
			// 온실가스 원단위 추이 조회
			return fetchGreenGasBasicUnitPeriod(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_BASIC_UNIT_PERIOD", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_PRODUCT: ({ commit }, info) => {
			// 제품 온실가스 원단위 추이 조회
			return fetchGreenGasProduct(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_PRODUCT", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_GREEN_GAS_FACILITYE_MISSION: ({ commit }, info) => {
			// 배출시설별 온실가스 배출량 조회
			return fetchGreenGasFacilityEmission(info)
				.then(({ data }) => {
					commit("SET_GREEN_GAS_GRID", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_INIT: ({ commit }) => {
			return commit("SET_INIT");
		},
	},
};

export default greenGasStore;
