<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>구분</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'codeGroupSelect'">
											<template v-slot:codeGroupSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="typeList"
													:value="typeValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="typeChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>기준년도</span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'year'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="
														$store.state.activityManagementStore.yearList
													"
													:value="year"
													@change="yearChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span>조직</span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'sector'"
											:component="'searchTemplate'"
											:style="{ maxWidth: '230px' }"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="sectorList"
													:value="sectorValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="sectorChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'workplace'" :component="'searchTemplate'">
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="workplaceList"
													:value="workplaceValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="workplaceChange"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field
											:name="'productionTeam'"
											:component="'searchTemplate'"
										>
											<template v-slot:searchTemplate="{ props }">
												<drop-down-list
													v-bind="props"
													:style="{ width: '130px' }"
													:data-items="productionTeamList"
													:value="productionTeamValue"
													:text-field="'text'"
													:data-item-key="'id'"
													@change="productionTeamChange"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code new2">
				<v-table
					ref="ManagementGrid"
					title="월별 활동량 조회"
					gridName="management"
					:dataItems="
						$store.state.activityManagementStore.activityManagementList
					"
					:count="$store.state.activityManagementStore.activityManagementCount"
					:columns="managementColumns"
					excelDownloadButtonText="엑셀 다운로드"
					:excelColumns="managementColumns"
					:isNoData="isNoData"
					:isAllDataEdit="true"
					@allDataSave="allDataSave"
					@allDataCancel="allDataCancel"
				/>
			</div>
			<div class="grid-code new3">
				<v-table
					ref="EvidenceGrid"
					title="근거자료 등록"
					gridName="evidence"
					:dataItems="$store.state.activityManagementStore.evidenceList"
					:columns="evidenceColumns"
					:newDataId="newDataId"
					:deleteDataCheck="true"
					insertButtonText="추가"
					:isNoData="isEvidenceNoData"
					@insert="insertEvidence"
					@edit="editEvidence"
					@remove="removeEvidence"
					@saveFile="saveFile"
					@removeFile="removeFile"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
	<modal
		v-if="showAlert"
		:title="'알림'"
		:message="'정상처리 되었습니다.'"
		:type="'alert'"
		@close="messageDialog"
	>
		<template v-slot:actionBar>
			<div class="code-modal"></div>
			<dialog-actions-bar>
				<kbutton @click="messageDialog" class="button first">확인</kbutton>
			</dialog-actions-bar>
		</template>
	</modal>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";
import resize from "../../mixin/resize.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";
import { DialogActionsBar } from "@progress/kendo-vue-dialogs";
import { Button } from "@progress/kendo-vue-buttons";

export default {
	name: "activityManagement",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		DropDownList,
		VTable,
		Modal,
		kbutton: Button,
		DialogActionsBar,
	},
	data: function () {
		return {
			renderKey: 0,
			searchicon,
			searchData: null,
			isNoData: false,
			isEvidenceNoData: false,
			typeList: [
				{ id: "01", text: "월마감" },
				{ id: "02", text: "명세서" },
				{ id: "03", text: "확정" },
			],
			typeValue: { id: "01", text: "월마감" },
			year: "",
			orgList: [],
			sectorList: [],
			sectorValue: {},
			workplaceList: [{ text: "전체 사업장" }],
			workplaceValue: { text: "전체 사업장" },
			productionTeamList: [{ text: "전체 생산팀" }],
			productionTeamValue: { text: "전체 생산팀" },
			monthList: [
				"january",
				"february",
				"march",
				"april",
				"may",
				"jun",
				"july",
				"august",
				"september",
				"october",
				"november",
				"december",
			],
			attchFile: null,
			showAlert: false,
			searchFormStates: false,
		};
	},
	computed: {
		managementColumns() {
			return [
				{
					field: "no",
					title: "No",
					editable: false,
					minResizableWidth: 55,
					width: 55,
				},
				{
					field: "workplace_name",
					title: "사업장",
					editable: false,
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "production_team_name",
					title: "생산팀",
					editable: false,
					minResizableWidth: 110,
					width: 110,
				},
				{
					field: "emission_type",
					title: "배출유형",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "facility_name",
					title: "배출시설명",
					editable: false,
					minResizableWidth: 230,
					width: 230,
				},
				{
					field: "emission_code_name",
					title: "배출활동",
					editable: false,
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "activity_code_name",
					title: "연료",
					editable: false,
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "measure_name",
					title: "단위",
					editable: false,
					minResizableWidth: 120,
					width: 120,
				},
				{
					title: "합계",
					children: [
						{
							field: "total_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "total_confirm",
							title: "확정",
							minResizableWidth: 150,
							width: 150,
							cell: "InputGridChangeCell",
							disable: true,
							editable: false,
						},
					],
				},
				{
					title: "1월",
					hidden: true,
					children: [
						{
							field: "january_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "january_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "2월",
					hidden: true,
					children: [
						{
							field: "february_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "february_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "3월",
					hidden: true,
					children: [
						{
							field: "march_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "march_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "4월",
					hidden: true,
					children: [
						{
							field: "april_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "april_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "5월",
					hidden: true,
					children: [
						{
							field: "may_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "may_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "6월",
					hidden: true,
					children: [
						{
							field: "jun_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "jun_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "7월",
					hidden: true,
					children: [
						{
							field: "july_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "july_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "8월",
					hidden: true,
					children: [
						{
							field: "august_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "august_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "9월",
					hidden: true,
					children: [
						{
							field: "september_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "september_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "10월",
					hidden: true,
					children: [
						{
							field: "october_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "october_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "11월",
					hidden: true,
					children: [
						{
							field: "november_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "november_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					title: "12월",
					hidden: true,
					children: [
						{
							field: "december_latest",
							title: "최신",
							minResizableWidth: 120,
							width: 120,
							editable: false,
						},
						{
							field: "december_confirm",
							title: "확정",
							cell: "InputGridChangeCell",
							minResizableWidth: 150,
							width: 150,
							editable: true,
						},
					],
				},
				{
					field: "source",
					title: "출처",
					editable: false,
					minResizableWidth: 150,
					width: 150,
				},
			];
		},
		evidenceColumns() {
			return [
				{
					field: "no",
					title: "No",
					minResizableWidth: 55,
					width: 55,
					editable: false,
				},
				{
					field: "description",
					title: "작성근거",
					minResizableWidth: 870,
					width: 870,
				},
				{
					field: "file_url",
					title: "사용량근거자료",
					cell: "FileFormCell",
					editable: false,
					minResizableWidth: 190,
					width: 190,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			];
		},
		yearList() {
			return this.$store.state.activityManagementStore.yearList;
		},
	},
	watch: {
		yearList(value) {
			if (value.length !== 0) {
				this.year = value[0];
			}
		},
		year(value) {
			if (value.length !== 0) {
				this.fetchOrgList();
			}
		},
		orgList(value) {
			if (value.length !== 0) {
				this.fetchSearchList(2, value[0].org_id);
			}
		},
	},
	created() {
		this.$store.dispatch("activityManagementStore/SETTING_YEAR_LIST_INIT");
		this.$store.dispatch("activityManagementStore/SETTING_EVIDENCE_INIT");
		this.$store.dispatch("activityManagementStore/FETCH_ACTIVITY_YEAR_LIST");
		this.$nextTick(function () {
			this.fetchEvidence();
		});
	},
	methods: {
		searchFormStateA() {
			this.searchFormStates = !this.searchFormStates;
			this.handleResize();
		},
		/* 조직 목록 */
		fetchOrgList() {
			// this.$store.dispatch("organizationStore/SETTING_ORGANIZATION_LIST_INIT");
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_LIST", this.year)
				.then((data) => {
					this.orgList = data.items;
				});
		},
		/* 검색영역 dropdown 목록 */
		fetchSearchList(depth, org_parent_id) {
			var searchOrgData =
				"?year=" +
				this.year +
				"&depth=" +
				depth +
				"&org_parent_id=" +
				org_parent_id;
			this.$store
				.dispatch("organizationStore/FETCH_ORGANIZATION_SELECT", searchOrgData)
				.then((data) => {
					if (depth == 2) {
						// 부문 SET
						this.sectorList = [];
						data.items.map((v) =>
							this.sectorList.push({ id: v.org_id, text: v.org_name })
						);
						this.sectorValue = {
							id: data.items[0].org_id,
							text: data.items[0].org_name,
						};
						this.fetchSearchList(3, data.items[0].org_id);
					} else if (depth == 3) {
						// 사업장 SET
						this.workplaceList = [{ text: "전체 사업장" }];
						data.items.map((v) =>
							this.workplaceList.push({ id: v.org_id, text: v.org_name })
						);
						this.workplaceValue = { text: "전체 사업장" };
						this.fetchSearchList(4, data.items[0].org_id);
					} else if (depth == 4) {
						// 생산팀 SET
						this.productionTeamList = [{ text: "전체 생산팀" }];
						data.items.map((v) =>
							this.productionTeamList.push({ id: v.org_id, text: v.org_name })
						);
						this.productionTeamValue = { text: "전체 생산팀" };
						if (this.renderKey == 0) {
							this.renderKey++;
							this.handleSubmit();
						}
					}
				});
		},
		async handleSubmit() {
			this.searchData = "";
			this.searchData += "?year=" + this.year;
			this.searchData += "&type=" + this.typeValue.id;
			this.searchData += "&sector_id=" + this.sectorValue.id;
			if (this.workplaceValue.id !== undefined) {
				this.searchData += "&workplace_id=" + this.workplaceValue.id;
			}
			if (this.productionTeamValue.id !== undefined) {
				this.searchData += "&production_team_id=" + this.productionTeamValue.id;
			}
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch(
					"activityManagementStore/FETCH_ACTIVITY_MANAGEMENT_LIST",
					this.searchData
				)
				.then((data) => {
					if (this.year < new Date().getFullYear) {
						// 조회한 월 컬럼만 노출
						for (var i = 9; i < 21; i++) {
							this.managementColumns[i].hidden = true;
						}
						for (var idx in this.monthList) {
							if (!this.setHidden(this.monthList[idx] + "_latest")) {
								this.managementColumns[9 + parseInt(idx)].hidden = false;
							}
						}
					} else {
						for (let i = 9; i < 21; i++) {
							this.managementColumns[i].hidden = false;
						}
					}
					if (this.$refs.ManagementGrid !== null) {
						if (data.items.length < 1) {
							this.isNoData = true;
						}
						this.$refs.ManagementGrid.refreshData();
					}
				})
				.catch((error) => console.log(error));
		},
		/* 근거자료 목록 */
		fetchEvidence() {
			var searchEvidenceData = "?route_name=activity_management";
			this.$store
				.dispatch(
					"activityManagementStore/FETCH_EVIDENCE_LIST",
					searchEvidenceData
				)
				.then((data) => {
					if (data.count < 1) {
						this.isEvidenceNoData = true;
					}
					this.$refs.EvidenceGrid.refreshData();
				})
				.catch((error) => console.log(error));
		},
		setHidden(key) {
			const list =
				this.$store.state.activityManagementStore.activityManagementList;
			for (let idx in list) {
				if (list[idx][key] !== null) {
					return false;
				}
			}
			return true;
		},
		typeChange(e) {
			this.typeValue = e.target.value;
		},
		yearChange(e) {
			this.year = e.target.value;
		},
		sectorChange(event) {
			this.sectorValue = event.value;
			this.fetchSearchList(3, this.sectorValue.id);
		},
		workplaceChange(event) {
			this.workplaceValue = event.value;
			if (this.workplaceValue.id !== undefined) {
				this.fetchSearchList(4, this.workplaceValue.id);
			}
		},
		productionTeamChange(event) {
			this.productionTeamValue = event.value;
		},
		clearSearchForm() {
			this.renderKey = 0;
			this.typeValue = { id: "01", text: "월마감" };
			this.$store.dispatch("activityManagementStore/SETTING_YEAR_LIST_INIT");
			this.$store.dispatch("activityManagementStore/FETCH_ACTIVITY_YEAR_LIST");
			this.fetchOrgList();
		},
		allDataSave: async function (data) {
			const info = { type: this.typeValue.id, data: data };
			this.$store
				.dispatch("activityManagementStore/EDIT_ACTIVITY_MANAGEMENT", info)
				.then(() => {
					const dataList =
						this.$store.state.activityManagementStore.activityManagementList;
					for (var idx in dataList) {
						dataList[idx].total_confirm = 0;
						for (var month in this.monthList) {
							dataList[idx].total_confirm += parseFloat(
								dataList[idx][this.monthList[month] + "_confirm"] == null
									? 0
									: dataList[idx][this.monthList[month] + "_confirm"]
							);
						}
					}
					this.$refs.ManagementGrid.refreshData();
					this.showAlert = !this.showAlert;
					// this.fetchGridData();
				});
		},
		allDataCancel: async function () {
			this.fetchGridData();
		},
		insertEvidence: function (data) {
			const formData = new FormData();
			formData.append("route_name", "activity_management");
			formData.append("description", data.data.description);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("activityManagementStore/INSERT_EVIDENCE", formData)
				.then(() => {
					this.fetchEvidence();
				});
		},
		editEvidence: function (data) {
			const formData = new FormData();
			formData.append("route_name", "activity_management");
			formData.append("evidence_id", data.data.evidence_id);
			formData.append("description", data.data.description);
			formData.append("file", this.attchFile);
			this.$store
				.dispatch("activityManagementStore/INSERT_EVIDENCE", formData)
				.then(() => {
					this.fetchEvidence();
				});
		},
		removeEvidence: function (id) {
			this.$store.dispatch("activityManagementStore/DELETE_EVIDENCE", id);
		},
		saveFile: async function (e, file) {
			this.attchFile = file;
		},
		removeFile: async function (e) {
			await this.$store.dispatch(
				"activityManagementStore/DELETE_EVIDENCE_FILE",
				e.evidence_id
			);
			this.fetchEvidence();
		},
		messageDialog() {
			this.showAlert = !this.showAlert;
		},
	},
	mixins: [settingGrid(), resize()],
};
</script>

