import {
	fetchReductionBusinessYearList,
	fetchReductionBusinessList,
	insertReductionBusiness,
	deleteReductionBusiness,
	deleteReductionBusinessFile,
	fetchMethodologyManagement,
	fetchMethodologyFieldList,
	fetchMethodologyList,
} from "@/api/index.js";

const reductionBusinessStore = {
	namespaced: true,
	state: {
		dataList: [],
		dataCount: 0,
		methodologyList: [],
		methodologyCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ORGANIZATION_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_REDUCTION_BUSINESS_LIST: (state, data) => {
			state.dataList = data.items;
			state.dataList.map((v) => {
				v.id = v.reduction_id;
				v.perform_plan_date = new Date(v.perform_plan_date);
			});
			state.dataCount = data.count;
		},
		SET_METHODOLOGY_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.methodologyList = data.items;
			state.methodologyCount = data.count;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
		SET_SUCCESS: (state, successMessage) => {
			state.successMessage = successMessage;
		},
	},
	actions: {
		FETCH_ORGANIZATION_YEAR_LIST: ({ commit }) => {
			// 감축 사업의 선택 가능한 계획연도 목록 조회
			return fetchReductionBusinessYearList()
				.then(({ data }) => {
					return commit("SET_ORGANIZATION_YEAR_LIST", data);
				})
				.catch((error) => console.log(error));
		},
		FETCH_REDUCTION_BUSINESS_LIST: ({ commit }, info) => {
			// 감축 사업 조회
			return fetchReductionBusinessList(info)
				.then(({ data }) => {
					commit("SET_REDUCTION_BUSINESS_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		INSERT_REDUCTION_BUSINESS: ({ commit }, info) => {
			// 감축 사업 추가 및 수정
			return insertReductionBusiness(info)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_REDUCTION_BUSINESS: ({ commit }, id) => {
			// 감축 사업 삭제
			return deleteReductionBusiness(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		DELETE_REDUCTION_BUSINESS_FILE: ({ commit }, id) => {
			// 감축 사업 첨부파일 삭제
			return deleteReductionBusinessFile(id)
				.then(({ data }) => {
					commit("SET_SUCCESS", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_METHODOLOGY_LIST: ({ commit }) => {
			// 방법론 관리 조회
			return fetchMethodologyManagement()
				.then(({ data }) => {
					commit("SET_METHODOLOGY_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_METHODOLOGY_FIELD: () => {
			// 방법론 분야 목록 조회
			return fetchMethodologyFieldList()
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_METHODOLOGY: (info) => {
			// 방법론 목록 조회
			return fetchMethodologyList(info)
				.then(({ data }) => {
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default reductionBusinessStore;
