<template>
	<td v-if="renderCell" :class="className">
		<b>{{ getNestedValue(field, dataItem) }}</b>
	</td>
	<td
		v-else-if="renderArrow"
		key="'g' + columnIndex"
		:class="'k-group-cell'"
	></td>
	<td
		v-else-if="columnIndex <= level"
		key="g-colspan"
		:colSpan="columnsCount - columnIndex"
	>
		<p class="k-reset">
			<a
				@click="onClick"
				tabIndex="-1"
				href="#"
				:class="expanded ? 'k-i-collapse k-icon' : 'k-i-expand k-icon'"
			/>
			{{ groupData }}
		</p>
	</td>
</template>

<script>
function nestedValue(fieldName, dataItem) {
	const path = fieldName.split(".");
	let data = dataItem;
	path.forEach((p) => {
		data = data ? data[p] : "";
	});

	return data;
}

export default {
	emits: {
		click: null,
	},
	props: {
		gridName: String,
		field: String,
		dataItem: Object,
		format: String,
		className: String,
		columnIndex: Number,
		columnsCount: Number,
		rowType: String,
		level: Number,
		expanded: Boolean,
		editor: String,
	},
	computed: {
		renderArrow() {
			var returnValue =
				this.columnIndex === undefined ||
				this.level === undefined ||
				this.columnIndex < this.level ||
				this.columnsCount === undefined ||
				this.rowType !== "groupHeader" ||
				this.dataItem[this.field] === undefined;
			return returnValue && this.dataItem[this.field];
		},
		renderCell() {
			return this.field !== undefined && this.rowType !== "groupHeader";
		},
		groupData() {
			let str = "";
			const items = this.dataItem.items;
			if (this.gridName == "calculation") {
				items.map((v) => (str += v.parameter_value_label));
			} else if (this.gridName == "facilityEmission") {
				items.map((v) => (str += v.parameter_type_value_label));
			}
			return str;
		},
	},
	methods: {
		onClick(e) {
			// @ts-ignore
			this.$emit("click", e);
		},
		getNestedValue: nestedValue,
	},
};
</script>
