import {
	fetchActivityYearList,
	fetchActivityHistoryList,
	fetchActivityHistoryMonthList,
} from "@/api/index.js";

const activityHistoryStore = {
	namespaced: true,
	state: {
		activityHistoryList: [],
		activityHistoryMonthList: [],
		activityHistoryMonthCount: 0,
		yearList: [],
		year: "",
	},
	mutations: {
		SET_ACTIVITY_YEAR_LIST: (state, data) => {
			state.yearList = data.items;
			state.year = data.items[0];
		},
		SET_ACTIVITY_HISTORY_LIST: (state, data) => {
			for (var idx in data) {
				data[idx].no = parseInt(idx) + 1;
			}
			state.activityHistoryList = data;
		},
		SET_ACTIVITY_HISTORY_MONTH_LIST: (state, data) => {
			for (var idx in data.items) {
				data.items[idx].no = parseInt(idx) + 1;
			}
			state.activityHistoryMonthList = data;
		},
		SET_YEAR_LIST_INIT: (state) => {
			state.yearList = [];
			state.year = "";
		},
	},
	actions: {
		FETCH_ACTIVITY_YEAR_LIST: ({ commit }) => {
			// 선택 가능한 기준연도 목록 조회
			return fetchActivityYearList()
				.then(({ data }) => {
					commit("SET_ACTIVITY_YEAR_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ACTIVITY_HISTORY_LIST: ({ commit }, info) => {
			// 활동량 이력 조회
			return fetchActivityHistoryList(info)
				.then(({ data }) => {
					commit("SET_ACTIVITY_HISTORY_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		FETCH_ACTIVITY_HISTORY_MONTH_LIST: ({ commit }, info) => {
			// 월별 활동량 이력 조회
			return fetchActivityHistoryMonthList(info)
				.then(({ data }) => {
					commit("SET_ACTIVITY_HISTORY_MONTH_LIST", data);
					return data;
				})
				.catch((error) => console.log(error));
		},
		SETTING_YEAR_LIST_INIT: ({ commit }) => {
			return commit("SET_YEAR_LIST_INIT");
		},
	},
};

export default activityHistoryStore;
