<template>
	<div class="h-100" :class="{ activesOnMain2: ExpansionOn2 == true }">
		<form-element class="h-302">
			<!-- kendo form box-->
			<fieldset class="k-form-fieldset">
				<div class="grid-code form">
					<div class="forminfo d-flex justify-content-between">
						<!-- kendo form title-->
						<span>배출시설 정보</span>
						<!-- kendo form button box-->
						<div class="formButtonBox k-form-buttons ml-auto2">
							<basic-button
								v-if="org_parent_id == ''"
								class="resets"
								@click="deleteData"
								:type="'button'"
								title="삭제"
								:backgroundColor="'#F9F9F9'"
								:fontColor="'#5A5B68'"
								:borderColor="'#5A5B68'"
								:wsize="'90px'"
								>삭제</basic-button
							>
							<basic-button
								class="resets"
								@click="clear"
								:type="'button'"
								title="취소"
								:backgroundColor="'#F9F9F9'"
								:fontColor="'#5A5B68'"
								:borderColor="'#5A5B68'"
								:wsize="'90px'"
								>취소</basic-button
							>
							<kbutton
								class="submitbutton"
								style="cursor: pointer"
								type="submit"
								:disabled="!kendoForm.allowSubmit"
								:class="{ activesd: !kendoForm.allowSubmit }"
								>저장</kbutton
							>
						</div>
					</div>
					<div class="inputsBox">
						<div class="d-flex">
							<div class="content con1">
								<!-- kendo form field 기준 연도 setting-->
								<field
									:name="'year'"
									:component="'myTemplate'"
									:label="'year'"
									:value="String(this.year)"
									:disabled="true"
								>
									<template v-slot:myTemplate="{ props }">
										<input-cell
											v-bind="props"
											@change="props.onChange"
											@blur="props.onBlur"
											@focus="props.onFocus"
											:title="'기준 연도'"
											:must="'N'"
										/>
									</template>
								</field>
								<!-- kendo form field 대표 시설명 setting-->
								<field
									:name="'facilityNameCodeList'"
									:component="'myTemplate'"
									:id="'facilityNameCodeList'"
									:label="'대표 시설명'"
									:data="$store.state.organizationStore.facilityNameCodeList"
									:validator="facilityNameValidator"
								>
									<template v-slot:myTemplate="{ props }">
										<form-drop-down-cell
											v-bind="props"
											@change="props.onChange"
											@blur="props.onBlur"
											@focus="props.onFocus"
										/>
									</template>
								</field>
								<!-- kendo form field 배출시설 일련번호 setting-->
								<field
									:name="'facility_serial_no'"
									:component="'myTemplate'"
									:label="'facility_serial_no'"
									:validator="facilitySerialNoValidator"
								>
									<template v-slot:myTemplate="{ props }">
										<input-cell
											v-bind="props"
											@change="props.onChange"
											@blur="props.onBlur"
											@focus="props.onFocus"
											:title="'배출시설 일련번호'"
										/>
									</template>
								</field>
							</div>

							<div class="content con2">
								<!-- kendo form field 사업장-생산팀 setting-->
								<field
									:name="'corporate_parent_name'"
									:component="'myTemplate'"
									:value="$store.state.organizationStore.orgParentName"
									:label="'corporate_parent_name'"
									:disabled="true"
								>
									<template v-slot:myTemplate="{ props }">
										<input-cell
											v-bind="props"
											@change="props.onChange"
											@blur="props.onBlur"
											@focus="props.onFocus"
											:title="'사업장-생산팀'"
										/>
									</template>
								</field>
								<!-- kendo form field 자체 시설명 setting-->
								<field
									:name="'corporate_name'"
									:component="'myTemplate'"
									:label="'corporate_name'"
									:validator="corporateNameValidator"
								>
									<template v-slot:myTemplate="{ props }">
										<input-cell
											v-bind="props"
											@change="props.onChange"
											@blur="props.onBlur"
											@focus="props.onFocus"
											:title="'자체 시설명'"
										/>
									</template>
								</field>
								<!-- kendo form field 배출시설 규모 setting-->
								<field
									:name="'facilitySizeCodeList'"
									:component="'myTemplate'"
									:id="'facilitySizeCodeList'"
									:label="'배출시설 규모'"
									:data="$store.state.organizationStore.facilitySizeCodeList"
									:validator="facilitySizeValidator"
								>
									<template v-slot:myTemplate="{ props }">
										<form-drop-down-cell
											v-bind="props"
											@change="(e) => facilitySizeCodeList(e)"
											@blur="props.onBlur"
											@focus="props.onFocus"
										/>
									</template>
								</field>
							</div>
						</div>
						<!-- kendo form 필수항목 info'-->
						<div class="lastText">
							<p><span style="color: #f37321">*</span> 는 필수 항목입니다.</p>
						</div>
					</div>
				</div>
			</fieldset>
		</form-element>
		<!-- 그리드영역 -->

		<div class="p-10n" :class="{ activesOn3: ExpansionOn2 == true }">
			<div class="w-100 h-100">
				<div
					class="grid-code"
					:style="{
						height: ExpansionOn2 == true ? '100% !important' : '100%',
					}"
				>
					<v-table
						v-if="
							$store.state.facilityEmissionStore.facilityEmissionList.length !==
							0
						"
						ref="facilityEmissionGrid"
						title="배출활동"
						detailTitle="배출량 산정 파라미터"
						gridName="facilityEmission"
						:dataItems="$store.state.facilityEmissionStore.facilityEmissionList"
						:count="$store.state.facilityEmissionStore.calculationCount"
						:detailDataItems="true"
						:columns="facilityEmissionColumns"
						:detailColumns="facilityEmissionParameterColumns"
						:detailGroup="[
							{
								field: 'cal_param_group',
							},
						]"
						:customExpandCheck="true"
						:customCellCheck="true"
						:newDataId="newDataId"
						:newDetailDataId="newDetailDataId"
						:deleteDataCheck="true"
						:insertDataCheck="true"
						insertButtonText="배출활동추가"
						:facilitySizeCodeId="facilitySizeCodeId"
						:datePickerFormat="'yyyy.MM.dd'"
						@insert="insert"
						@edit="edit"
						@remove="remove"
						@detailEdit="detailEdit"
						@refreshData="refreshData"
						:zoomMode="true"
						:zoomModeType="'배출활동'"
						@Expansions2="Expansions2"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import settingGrid from "../../mixin/TheTable.js";

import { Field, FormElement } from "@progress/kendo-vue-form";
import { Button } from "@progress/kendo-vue-buttons";
import BasicButton from "../../components/common/button/BasicButton.vue";
import InputCell from "../../components/form/InputCell.vue";
import FormDropDownCell from "../../components/form/FormDropDownCell.vue";
import VTable from "../../components/grid/VTable.vue";

import {
	insertFacilityEmission,
	editFacilityEmission,
	deleteFacilityEmission,
	editFacilityEmissionParameter,
} from "@/api/index.js";

// 유효성 기능을 명시
import {
	facilityNameValidator,
	facilitySerialNoValidator,
	corporateNameValidator,
	facilitySizeValidator,
} from "../../validator/Validator";

export default {
	name: "facilityEmission",
	props: {
		org_id: Number,
	},
	watch: {
		org_id() {
			this.fetchList();
		},
	},
	components: {
		"form-element": FormElement,
		kbutton: Button,
		field: Field,
		BasicButton,
		InputCell,
		FormDropDownCell,
		VTable,
	},
	data() {
		return {
			ExpansionOn2: false,
			facilityNameValidator,
			facilitySerialNoValidator,
			corporateNameValidator,
			facilitySizeValidator,
			facilitySizeCodeId: 0,
			searchCodeData: "",
			newDataId: 0,
			newDetailDataId: 0,
			facilityEmissionColumns: [
				{
					field: "emission_code_name",
					title: "배출활동",
					cell: "dropdownEmissionActivityCell",
					minResizableWidth: 130,
					width: 130,
				},
				{
					field: "calculation_name",
					title: "산정식",
					cell: "dropdownCalculationCell",
					minResizableWidth: 150,
					width: 150,
				},
				{
					field: "activity_code_name",
					title: "활동자료",
					cell: "dropdownActivityDataCell",
					minResizableWidth: 150,
					width: 150,
				},
				{
					title: "배출량 산정",
					children: [
						{
							field: "cal_method_rate_name",
							title: "규정산정등급",
							editable: false,
							minResizableWidth: 140,
							width: 140,
						},
						{
							field: "cal_apply_rate_name",
							title: "적용산정등급",
							cell: "dropdownApplyRateCell",
							minResizableWidth: 140,
							width: 140,
						},
					],
				},
				{
					field: "cal_version",
					title: "산정식 버전",
					cell: "dropdownCalculationVersionCell",
					minResizableWidth: 150,
					width: 150,
				},

				{
					field: "state",
					title: "상태",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "disposal_date",
					title: "폐기일",
					cell: "datePickerCell",
					minResizableWidth: 230,
					width: 230,
				},
				{ cell: "commandCell", minResizableWidth: 160, width: 160 },
			],
			facilityEmissionParameterColumns: [
				{
					field: "parameter_type_value_label",
					title: "파라미터",
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "input_code_name",
					title: "입력구분",
					minResizableWidth: 210,
					width: 210,
				},
				{
					title: "산정등급",
					children: [
						{
							field: "cal_method_rate_name",
							title: "규정산정등급",
							minResizableWidth: 160,
							width: 160,
						},
						{
							field: "cal_apply_rate_name",
							title: "적용산정등급",
							cell: "dropdownCalculationApplyRateCell",
							minResizableWidth: 160,
							width: 160,
						},
					],
				},
				{
					field: "parameter_version",
					title: "파라미터값 버전",
					cell: "dropdownParameterVersionCell",
					minResizableWidth: 230,
					width: 230,
				},
				{
					field: "parameter_value",
					title: "값",
					minResizableWidth: 250,
					width: 250,
				},

				{
					field: "parameter_measure_name",
					title: "단위",
					minResizableWidth: 240,
					width: 240,
				},
			],
		};
	},
	inject: {
		kendoForm: {
			default: {},
		},
	},
	computed: {
		org_parent_id() {
			return this.$store.state.organizationStore.org_parent_id;
		},
		year() {
			return this.$store.state.organizationStore.year;
		},
		depth() {
			return this.$store.state.organizationStore.depth;
		},
	},
	created: async function () {
		this.searchCodeData = "";
		if (
			Object.keys(
				this.$store.getters["organizationStore/GET_ORGANIZATION_INIT"]
			).length != 0
		) {
			this.facilitySizeCodeId =
				this.$store.getters[
					"organizationStore/GET_ORGANIZATION_INIT"
				].facility_size_code.code_no;
		}
		this.$store.dispatch(
			"organizationStore/FETCH_FACILITY_NAME_CODE_LIST",
			this.searchCodeData + "?group=04"
		); // 04: 배출시설코드
		this.$store.dispatch(
			"organizationStore/FETCH_FACILITY_SIZE_CODE_LIST",
			this.searchCodeData + "?group=08"
		); // 08: 배출시설규모코드

		// 코드 목록 조회
		await this.$store.dispatch("codeStore/FETCH_CODE_LIST");
		this.$store.dispatch("codeStore/GET_CODE");

		// 산정식 목록 조회
		await this.$store.dispatch("calculationStore/FETCH_CALCULATION_LIST");
		// 산정식 등급 조회
		await this.$store.dispatch("calculationStore/FETCH_CALCULATION_TIER_LIST");

		// 활동자료 목록 조회
		await this.$store.dispatch(
			"emissionActivityStore/FETCH_EMISSION_ACTIVITY_LIST",
			this.year
		);
		this.fetchList();
	},
	methods: {
		fetchList: async function () {
			await this.$store.dispatch(
				"facilityEmissionStore/FETCH_FACILITY_EMISSION_LIST",
				this.org_id
			);
			this.$refs.facilityEmissionGrid.refreshData();
		},
		Expansions2() {
			this.ExpansionOn2 = !this.ExpansionOn2;
		},
		facilitySizeCodeList: function (e) {
			this.facilitySizeCodeId = e.value.id;
		},
		/* 취소 */
		clear: function () {
			// 폼초기화 함수
			this.kendoForm.onFormReset();
		},
		deleteData: function (dataItem) {
			this.$emit("deleteData", dataItem);
		},
		insert: function (data) {
			data.data.org_id = this.org_id;
			data.data.disposal_date = this.formatDate(data.data.disposal_date);
			return insertFacilityEmission(data)
				.then(({ data }) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		edit: function (data) {
			data.data.disposal_date = this.formatDate(data.data.disposal_date);
			editFacilityEmission(data);
		},
		formatDate: function (date) {
			// 날짜 format하여 요청
			let month = date.getMonth() + 1;
			let day = date.getDate();
			month = month >= 10 ? month : "0" + month;
			day = day >= 10 ? day : "0" + day;
			return date.getFullYear() + "-" + month + "-" + day;
		},
		remove: function (id) {
			deleteFacilityEmission(id);
		},
		detailEdit: function (data) {
			editFacilityEmissionParameter(data);
		},
		refreshData: async function () {
			await this.$store.dispatch(
				"facilityEmissionStore/FETCH_FACILITY_EMISSION_LIST",
				this.org_id
			);
			this.$refs.facilityEmissionGrid.refreshData();
		},
	},
	mixins: [settingGrid()],
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.grid-code {
	&.h-48 {
		box-shadow: none;
		border-radius: 0;
	}
	&.form {
		padding: 20px;
		box-shadow: none;
		border-radius: 0 !important;
		.k-input-inner {
			&::placeholder {
				color: #f37321 !important;
			}
		}

		.forminfo {
			height: 40px;

			span {
				font-family: AppleSDGothicNeoB00;
				font-size: 16px;
				color: #222222;
				display: flex;
				align-items: end;
			}

			.formButtonBox {
				button {
					margin-right: 0px;

					&:last-child {
						margin-right: 0px;
					}
				}
			}
		}

		.k-form-field {
			margin-top: 20px !important;

			.k-form-field-wrap {
				position: relative;

				.k-input {
					box-shadow: none !important;
				}

				.k-form-error {
					position: absolute;
					top: 0;
				}
			}
		}
	}

	.lastText {
		margin-top: 20px;
		margin-left: auto;
		font-size: 14px;
		font-family: AppleSDGothicNeoSB00;
		width: max-content;
	}

	.content {
		flex: 1;
	}
}
</style>
