<template>
	<div class="p-10" style="padding-top: 0 !important">
		<div class="p-10 m-0a" style="padding-top: 0 !important; paras">
			<div class="grid-code singles">
				<v-table
					v-if="
						$store.state.calculationStore.calculationList.length !== 0 &&
						$store.state.calculationStore.calculationParameterList.length !== 0
					"
					ref="calculationGrid"
					title="산정식 목록"
					detailTitle="산정식 파라미터 목록"
					gridName="calculation"
					:dataItems="$store.state.calculationStore.calculationList"
					:count="$store.state.calculationStore.calculationCount"
					:detailDataItems="
						$store.state.calculationStore.calculationParameterList
					"
					:columns="calculationColumns"
					:detailColumns="calculationParameterColumns"
					:customCellCheck="true"
					:detailGroup="[{ field: 'cal_param_group' }]"
					:newDataId="newDataId"
					:newDetailDataId="newDetailDataId"
					:selectoperatingBoundary="
						$store.state.codeStore.operatingBoundaryCode
					"
					:selectoperatingBoundaryLowRank="
						$store.state.codeStore.operationBoundaryLowRankCode
					"
					:selectCalculationGrade="$store.state.codeStore.calculationGradeCode"
					:deleteDataCheck="true"
					:insertDataCheck="true"
					:disabledState="true"
					insertButtonText="산정식작성"
					popupButtonText="산정등급관리"
					detailInsertGroupButtonText="산정식추가"
					detailInsertButtonText="파라미터추가"
					@insert="insert"
					@edit="edit"
					@remove="remove"
					@detailInsert="detailInsert"
					@detailEdit="detailEdit"
					@detailRemove="detailRemove"
					@isEditableChange="isEditableChange"
					@gridDialog="gridDialog"
				/>
			</div>
		</div>
	</div>
	<modal v-if="visibleDialog" title="산정등급관리" @close="gridDialog">
		<template v-slot:grid>
			<div class="code-modal">
				<div class="grid-code modal">
					<v-table
						v-if="
							$store.state.calculationStore.calculationTierList.length !== 0
						"
						ref="calculationTierGrid"
						gridName="calculationTier"
						:dataItems="$store.state.calculationStore.calculationTierList"
						:count="$store.state.calculationStore.calculationTierCount"
						:columns="calculationGradeColumns"
						:selectCalculationGrade="
							$store.state.codeStore.calculationGradeCode
						"
						saveButtonText="저장"
						@allDataSave="allDataSave"
					/>
				</div>
			</div>
		</template>
	</modal>
</template>

<script>
import settingGrid from "../../mixin/TheTable.js";

import VTable from "../../components/grid/VTable.vue";
import Modal from "../../components/modal/Modal.vue";

import {
	insertCalculation,
	editCalculation,
	deleteCalculation,
	editCalculationTier,
	insertCalculationParameter,
	editCalculationParameter,
	deleteCalculationParameter,
} from "@/api/index.js";

export default {
	name: "calculation",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		VTable,
		Modal,
	},
	data: function () {
		return {
			newDataId: "0",
			newDetailDataId: 0,
			calculationColumns: [
				{
					field: "calculation_name",
					title: "산정식명",
					minResizableWidth: 200,
					width: 200,
				},
				{
					field: "emission_code_name",
					title: "배출활동",
					cell: "dropdownEmissionActivityCell",
					minResizableWidth: 135,
					width: 135,
				},
				{
					field: "scope_code_name",
					title: "Scope",
					cell: "dropdownOperatingBoundaryCell",
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "boundary_code_name",
					title: "경계",
					cell: "dropdownOperatingBoundaryLowRankCell",
					minResizableWidth: 160,
					width: 160,
				},
				{
					field: "version",
					title: "버전",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "use_yn",
					title: "사용여부",
					cell: "checkboxCell",
					minResizableWidth: 75,
					width: 75,
				},
				{
					field: "remarks",
					title: "비고",
					minResizableWidth: 150,
					width: 150,
				},

				{
					field: "update_date",
					title: "최종변경일",
					format: "{0:yyyy.MM.dd}",
					editable: false,
					minResizableWidth: 100,
					width: 100,
				},
				{
					field: "update_user",
					title: "변경자",
					editable: false,
					minResizableWidth: 90,
					width: 90,
				},
				{ cell: "commandCell", minResizableWidth: 170, width: 170 },
			],
			calculationParameterColumns: [
				{
					field: "parameter_type_label",
					title: "파라미터형식",
					cell: "dropdownParameterTypeCell",
					minResizableWidth: 190,
					width: 190,
				},
				{
					field: "parameter_value_label",
					title: "파라미터",
					cell: "dropdownParameterValueCell",
					editable: false,
					minResizableWidth: 348,
					width: 348,
				},
				{
					field: "input_code_name",
					title: "입력구분",
					editable: false,
					width: 1000,
					minResizableWidth: 1000,
				},

				{ cell: "commandCell", width: 160, minResizableWidth: 160 },
			],
			calculationGradeColumns: [
				{ field: "cal_tier_id", title: "No", editable: false },
				{
					field: "emission_code_name",
					title: "배출활동",
					editable: false,
					width: 120,
					minResizableWidth: 120,
				},
				{
					field: "scope_code_name",
					title: "Scope",
					editable: false,
					width: 100,
					minResizableWidth: 100,
				},
				{
					field: "facility_size_code_name",
					title: "시설규모",
					editable: false,
					width: 120,
					minResizableWidth: 120,
				},
				{
					field: "cal_method_rate_name",
					title: "산정방법론등급",
					cell: "dropdownCalculationMethodCell",
					width: 120,
					minResizableWidth: 120,
				},
				{
					field: "usage_cal_rate_name",
					title: "사용량산정등급",
					cell: "dropdownUsageCalculationCell",
					width: 120,
					minResizableWidth: 120,
				},
				{
					field: "fever_cal_rate_name",
					title: "순발열량등급",
					cell: "dropdownCalorificCell",
					width: 120,
					minResizableWidth: 120,
				},
				{
					field: "emission_cal_name",
					title: "배출계수등급",
					cell: "dropdownEmissionCoefficientCell",
					width: 120,
					minResizableWidth: 120,
				},
				{
					field: "oxidation_cal_name",
					title: "산화계수등급",
					cell: "dropdownOxidationCoefficientCell",
					width: 120,
					minResizableWidth: 120,
				},
			],
			visibleDialog: false,
		};
	},

	created: async function () {
		// 코드 목록 조회
		await this.$store.dispatch("codeStore/FETCH_CODE_LIST");
		this.$store.dispatch("codeStore/GET_CODE");
		this.$store.dispatch("codeStore/FETCH_OPERATION_BOUNDARY_LOW_RANK");

		// 파라미터 그룹 목록 조회
		this.$store.dispatch("parameterStore/FETCH_PARAMETER_GROUP_LIST");

		this.$store.dispatch("calculationStore/FETCH_CALCULATION_LIST");
		this.$store.dispatch("calculationStore/FETCH_CALCULATION_PARAMETER_LIST");
		await this.$store.dispatch("calculationStore/FETCH_CALCULATION_TIER_LIST");
		this.$store.dispatch("calculationStore/INEDIT_CALCULATION_TIER_LIST");
	},
	methods: {
		insert: function (data) {
			return insertCalculation(data)
				.then(({ data }) => {
					this.newDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		edit: function (data) {
			editCalculation(data);
		},
		remove: function (id) {
			deleteCalculation(id);
		},
		detailInsert: function (data) {
			return insertCalculationParameter(data)
				.then((data) => {
					this.newDetailDataId = data.id;
				})
				.catch((error) => console.log(error));
		},
		detailEdit: function (data) {
			editCalculationParameter(data);
		},
		detailRemove: function (id) {
			deleteCalculationParameter(id);
		},
		gridDialog: function () {
			this.visibleDialog = !this.visibleDialog;
		},
		allDataSave: async function (data) {
			await editCalculationTier(data);
			await this.$store.dispatch(
				"calculationStore/FETCH_CALCULATION_TIER_LIST"
			);
			this.$store.dispatch("calculationStore/INEDIT_CALCULATION_TIER_LIST");
		},
	},
	mixins: [settingGrid()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";
.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;
		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px; // height:100px;
	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}
	.buttonBoxs {
		width: 200px;
	}
	.searchInputBox {
		position: relative;
		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}

// NOTE 배포를 위한 임시 스타일 수정
.grid-code {
	height: calc(100vh - 197px);
}
.k-widget {
	width: 90% !important;
}
</style>
